import IGame from "../../../../../../base/IGame";
import { Pair } from "../../../../../../base/Pair";
import { EntityCleanOption, EntityCleanType, EntityColor } from "../../../../defines/EntityID";
import IdentifierManager from "../../../../defines/IdentifierManager";
import GridBase from "../../../../grid/GridBase";
import FlyerBomb from "../../../flyer/FlyerBomb";
import FlyerCannon from "../../../flyer/FlyerCannon";
import FlyerMissile, { FlyerMissileType } from "../../../flyer/FlyerMissile";
import ItemBase, { ItemType } from "../../ItemBase";

// Learn TypeScript:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/typescript.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/typescript.html
// Learn Attribute:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/reference/attributes.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html
export enum ItemBombType {
    None,
    Butterfly,
    Row,
    Column,
    Cross,
    Range,
    Rainbow,
};

const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemBombBase extends ItemBase {

    private _bombType:ItemBombType = ItemBombType.None;
    public getBombType() : ItemBombType {
        return this._bombType;
    }

    private _isDud:boolean = false;
    public getIsDud() : boolean {
        return this._isDud;
    }
    public setIsDud(isDud:boolean) : void {
        this._isDud = isDud;
    }

    private _selfDestructIndex:boolean = false;
    public getSelfDestructIndex() : boolean {
        return this._selfDestructIndex;
    }
    public setSelfDestructIndex(selfDestructIndex:boolean) : void {
        this._selfDestructIndex = selfDestructIndex;
    }

    private _isGrassBomb:boolean = false;
    public getIsGrassBomb() : boolean {
        return this._isGrassBomb;
    }
    public setIsGrassBomb(isGrassBomb:boolean) : void {
        this._isGrassBomb = isGrassBomb;
    }

    public getShouldLayoutGrass() : boolean {
        return this.getIsGrassBomb() || this.getGrid().isGrassFloorAtLogicalPos(this.getLogicalPos());
    } 

    protected _canDrop() : boolean {
        return true;
    }
    protected _canSwap() : boolean {
        return true;
    }
    protected _canMatch() : boolean {
        return true;
    }
    protected _canChangeColor() : boolean {
        return true;
    }
    protected _canCleanByBomb(cleanColor:EntityColor) : boolean {
        return true;
    }
    protected _canCleanByMatch() : boolean {
        return true;
    }
    protected _canCleanByActivate() : boolean {
        return true;
    }
    protected _canShuffleWhenCreate() : boolean {
        return true;
    }

    protected _canBombCombineWithNeighbour(swapPos:cc.Vec2) : boolean {
        let neighbourItem = this.getGrid().getItemAtLogicalPos(swapPos);
        if (neighbourItem && neighbourItem.canMatch() && neighbourItem.getItemType() == ItemType.EffectBomb) {
            return true;
        }
        return false;
    }
    protected _onBombCombineWithNeighbour(swapPos:cc.Vec2) : void {
        let neighbourItem = this.getGrid().getItemAtLogicalPos(swapPos);
        let anotherBomb = neighbourItem as ItemBombBase;
        if (anotherBomb) {
            this._onBombCombine(anotherBomb);    
        }
    }
    protected _onBombCombine(bombB:ItemBombBase) : void {
        
        let typeA = this.getBombType();
        let typeB = bombB.getBombType();

        let minType = Math.min(typeA, typeB);
        let maxType = Math.max(typeA, typeB);
        let code = maxType * 100 + minType;
        
        let canLayoutGrass = this.getShouldLayoutGrass() || bombB.getShouldLayoutGrass();
        let grid = this.getGrid();
        let matchColor = this.getEntityColor();
        let startPos = this.getLogicalPos();

        switch (code) {
            case 101:{
                for (let i = 0; i < 3; i++) {
                    let missile = FlyerMissile.create(grid, FlyerMissileType.Dot, matchColor, canLayoutGrass);
                    grid.setFlyerAtLogicalPos(missile, startPos);
                }
            }
                break;
            case 201:{
                let rayDirections:Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(1, 0), true),
                    new Pair<cc.Vec2, boolean>(cc.v2(-1, 0), false),
                ];
                let grid = this.getGrid();
                for (let ray of rayDirections) {
                    let cannon = FlyerCannon.create(grid, matchColor, canLayoutGrass, startPos, ray.first, ray.second);
                    grid.setFlyerAtLogicalPos(cannon, startPos);
                }
                let missile = FlyerMissile.create(grid, FlyerMissileType.Row, matchColor, canLayoutGrass);
                grid.setFlyerAtLogicalPos(missile, startPos);

            }
                break;
            case 301:{
                let rayDirections:Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(0, 1), true),
                    new Pair<cc.Vec2, boolean>(cc.v2(0, -1), false),
                ];
                let grid = this.getGrid();
                for (let ray of rayDirections) {
                    let cannon = FlyerCannon.create(grid, matchColor, canLayoutGrass, startPos, ray.first, ray.second);
                    grid.setFlyerAtLogicalPos(cannon, startPos);

                }
                let missile = FlyerMissile.create(grid, FlyerMissileType.Column, matchColor, canLayoutGrass);
                grid.setFlyerAtLogicalPos(missile, startPos);
            }
                break;
            case 401:{
                IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_cross'));
                let rayDirections:Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(1, 1), true),
                    new Pair<cc.Vec2, boolean>(cc.v2(-1, -1), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(1, -1), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(-1, 1), false),
                ];

                for (let ray of rayDirections) {
                    let cannon = FlyerCannon.create(grid, matchColor, canLayoutGrass, startPos, ray.first, ray.second);
                    grid.setFlyerAtLogicalPos(cannon, startPos);
                }

                let missile = FlyerMissile.create(grid, FlyerMissileType.Cross, matchColor, canLayoutGrass);
                grid.setFlyerAtLogicalPos(missile, startPos);
            }
                break;
            case 501:{
                let bomb = FlyerBomb.create(grid, matchColor, canLayoutGrass, startPos, 2);
                grid.setFlyerAtLogicalPos(bomb, startPos);

                let missile = FlyerMissile.create(grid, FlyerMissileType.Range, matchColor, canLayoutGrass);
                grid.setFlyerAtLogicalPos(missile, startPos);

            }
                break;

            case 202:
            case 302:
            case 303:{
                IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_colorline'));
                let rayDirections:Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(1, 0), true),
                    new Pair<cc.Vec2, boolean>(cc.v2(-1, 0), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(0, 1), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(0, -1), false),
                ];
                for (let ray of rayDirections) {
                    let cannon = FlyerCannon.create(grid, matchColor, canLayoutGrass, startPos, ray.first, ray.second);
                    grid.setFlyerAtLogicalPos(cannon, startPos);
                }
            }
                break;

            case 402:
            case 403:
            case 404:{
                IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_linecross'));
                let rayDirections:Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(1, 0), true),
                    new Pair<cc.Vec2, boolean>(cc.v2(-1, 0), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(0, 1), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(0, -1), false),

                    new Pair<cc.Vec2, boolean>(cc.v2(1, 1), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(-1, -1), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(-1, 1), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(1, -1), false),
                ];
                for (let ray of rayDirections) {
                    let cannon = FlyerCannon.create(grid, matchColor, canLayoutGrass, startPos, ray.first, ray.second);
                    grid.setFlyerAtLogicalPos(cannon, startPos);
                }
            }
                break;
            case 502:{
                IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_linebomb'));
                let rayDirections:Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(1, 0), true),
                    new Pair<cc.Vec2, boolean>(cc.v2(-1, 0), false),
                ];

                let lineOffsets:cc.Vec2[] = [
                    cc.v2(0, 1),
                    cc.v2(0, 0),
                    cc.v2(0, -1),
                ];

                for (let offset of lineOffsets) {
                    for (let ray of rayDirections) {
                        let cannon = FlyerCannon.create(grid, matchColor, canLayoutGrass, startPos.add(offset), ray.first, ray.second);
                        grid.setFlyerAtLogicalPos(cannon, startPos.add(offset));
                    }
                }
            }
                break;
            case 503:{
                IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_linebomb'));
                let rayDirections:Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(0, 1), true),
                    new Pair<cc.Vec2, boolean>(cc.v2(0, -1), false),
                ];

                let lineOffsets:cc.Vec2[] = [
                    cc.v2(1, 0),
                    cc.v2(0, 0),
                    cc.v2(-1, 0),
                ];

                for (let offset of lineOffsets) {
                    for (let ray of rayDirections) {
                        let cannon = FlyerCannon.create(grid, matchColor, canLayoutGrass, startPos.add(offset), ray.first, ray.second);
                        grid.setFlyerAtLogicalPos(cannon, startPos.add(offset));
                    }
                }
            }
                break;
            case 504:{
                IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_bombcross'));
                let rayDirections1:Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(0, 0), true),
                    new Pair<cc.Vec2, boolean>(cc.v2(-1, 0), true),
                    new Pair<cc.Vec2, boolean>(cc.v2(0, -1), true),
                ];
                for (let ray of rayDirections1) {
                    let origin = startPos.add(ray.first);
                    let cannon = FlyerCannon.create(grid, 
                                                    matchColor, 
                                                    canLayoutGrass, 
                                                    origin, 
                                                    cc.v2(-1,-1), 
                                                    ray.second);
                    grid.setFlyerAtLogicalPos(cannon, origin);
                }

                let rayDirections2:Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(0, 0), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(1, 0), true),
                    new Pair<cc.Vec2, boolean>(cc.v2(0, 1), true),
                ];
                for (let ray of rayDirections2) {
                    let origin = startPos.add(ray.first);
                    let cannon = FlyerCannon.create(grid, 
                                                    matchColor, 
                                                    canLayoutGrass, 
                                                    origin, 
                                                    cc.v2(1,1), 
                                                    ray.second);
                    grid.setFlyerAtLogicalPos(cannon, origin);
                }

                let rayDirections3:Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(0, 0), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(1, 0), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(0, -1), false),
                ];
                for (let ray of rayDirections3) {
                    let origin = startPos.add(ray.first);
                    let cannon = FlyerCannon.create(grid, 
                                                    matchColor, 
                                                    canLayoutGrass, 
                                                    origin, 
                                                    cc.v2(1,-1), 
                                                    ray.second);
                    grid.setFlyerAtLogicalPos(cannon, origin);
                }

                let rayDirections4:Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(0, 0), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(-1, 0), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(0, 1), false),
                ];
                for (let ray of rayDirections4) {
                    let origin = startPos.add(ray.first);
                    let cannon = FlyerCannon.create(grid, 
                                                    matchColor, 
                                                    canLayoutGrass, 
                                                    origin, 
                                                    cc.v2(-1,1), 
                                                    ray.second);
                    grid.setFlyerAtLogicalPos(cannon, origin);
                }
            }
                break;
            case 505:{
                IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_bombbomb'));
                let bomb = FlyerBomb.create(grid, matchColor, canLayoutGrass, startPos, 3);
                grid.setFlyerAtLogicalPos(bomb, startPos);
            }
        
            default:
                break;
        }
        
        this.setIsDud(true);
        if (this._satisfyMatch()) {
            this._onSatisfyMatch(-1000);
        }else{
            this._onClean(EntityCleanOption.create(EntityCleanType.Match, 
                                                   this.getLogicalPos(), 
                                                   EntityColor.None, 
                                                   ItemBombType.None, 
                                                   true, 
                                                   true, 
                                                   canLayoutGrass));
        }
        
        bombB.setIsDud(true);
        if (bombB._satisfyMatch()) {
            bombB._onSatisfyMatch(-1000);
        }else{
            bombB._onClean(EntityCleanOption.create(EntityCleanType.Match, 
                                                    bombB.getLogicalPos(), 
                                                    EntityColor.None, 
                                                    ItemBombType.None, 
                                                    true, 
                                                    true, 
                                                    canLayoutGrass));
        }
    }
    protected _canMatchWithNeighbourSwap(swapPos:cc.Vec2) : boolean {
        if (this._canBombCombineWithNeighbour(swapPos)) {
            return true;
        }else{
            return super._canMatchWithNeighbourSwap(swapPos);
        }
    }
    protected _onMatchWithNeighbourSwap(swapPos:cc.Vec2) : void {
        if (this._canBombCombineWithNeighbour(swapPos)) {
            this._onBombCombineWithNeighbour(swapPos);
        }else{
            super._onMatchWithNeighbourSwap(swapPos);
        }
    }
    protected _onClean(cleanOption:EntityCleanOption) : void {

        this.setVisible(false);
        if (cleanOption.layoutGrass) {
            this.setIsGrassBomb(true);
        }
        this.progressDeathWish(cleanOption);
        this.selfDestory();
    }

    protected initBomb(grid:GridBase, bombType:ItemBombType) {
        super.initItem(grid, ItemType.EffectBomb);
        this._bombType = bombType;
        this.setIsDud(false);
        this._selfDestructIndex = false;
        this._isGrassBomb = false;
    }
}
