export enum FlyerMissileType {
    None,
    Dot,
    Row,
    Column,
    Cross,
    Range,
}

const { ccclass, property } = cc._decorator;

@ccclass
export default class FlyerMissile extends FlyerBase {

    private _missileType: FlyerMissileType = FlyerMissileType.None;
    private _layoutGrass: boolean = false;
    private _launched: boolean = false;

    public getEntityID(): EntityID {
        let result = EntityID.None;
        switch (this.getEntityColor()) {
            case EntityColor.Red:
                result = EntityID.FlyerMissileRed;
                break;
            case EntityColor.Green:
                result = EntityID.FlyerMissileGreen;
                break;
            case EntityColor.Blue:
                result = EntityID.FlyerMissileBlue;
                break;
            case EntityColor.Yellow:
                result = EntityID.FlyerMissileYellow;
                break;
            case EntityColor.Orange:
                result = EntityID.FlyerMissileOrange;
                break;
            case EntityColor.Purple:
                result = EntityID.FlyerMissilePurple;
                break;

            default:
                break;
        }
        return result;
    }

    public static create(grid: GridBase, missileType: FlyerMissileType, color: EntityColor, layoutGrass: boolean): FlyerMissile {


        let filePath = 'match_scene/prefab/entity/flyer/FlyerMissile';
        switch (color) {
            case EntityColor.Red:
                filePath += 'Red';
                break;
            case EntityColor.Green:
                filePath += 'Green';
                break;
            case EntityColor.Blue:
                filePath += 'Blue';
                break;
            case EntityColor.Yellow:
                filePath += 'Yellow';
                break;
            case EntityColor.Orange:
                filePath += 'Orange';
                break;
            case EntityColor.Purple:
                filePath += 'Purple';
                break;
            case EntityColor.None:
                filePath += 'White';
                break;

            default:
                break;
        }

        let className = 'FlyerMissile';
        let prefabFileName = filePath;
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerMissile;
        if (component) {
            component.init(grid, missileType, color, layoutGrass);
            return component;
        }
        return null;
    }

    public init(grid: GridBase, missileType: FlyerMissileType, color: EntityColor, layoutGrass: boolean) {
        super.initFlyer(grid, FlyerType.Missile);
        this.setEntityColor(color);
        this._missileType = missileType;
        this._layoutGrass = layoutGrass;
        this._launched = false;
    }

    protected getAttackPos(color: EntityColor, layoutGrass: boolean): cc.Vec2 {

        let targetPos = cc.v2(-1, -1);
        let targetPriority = EntityBase.DEFAULT_MISSILE_PRIORITY;
        let grid = this.getGrid();
        let entityTypes: EntityType[] = [
            EntityType.Item,
        ];

        let cellsMap = grid.getCellsMap();
        let checkPositions: cc.Vec2[] = [];
        let attackedPositions = grid.getAttackedPosition();
        for (let key in cellsMap) {
            let cell = cellsMap[key] as CellBase;
            let checkPos = cell.getLogicalPos();
            if (GameMath.indexOf(attackedPositions, checkPos) == -1) {
                checkPositions.push(checkPos);
            }
        }
        GameMath.shuffleArray(checkPositions);
        for (let checkPos of checkPositions) {
            if (GameMath.indexOf(attackedPositions, checkPos) == -1) {
                let minPriority = EntityBase.DEFAULT_MISSILE_PRIORITY;

                let canAttackTop = false;
                for (let entityType of entityTypes) {
                    let entity = grid.getEntity(entityType, checkPos);
                    if (entity) {

                        if (entityType == EntityType.Parasite || entityType == EntityType.Item) {
                            if (entity.canExcuteCommand(GridCommand.BombClean(this.getLogicalPos(), color, layoutGrass))) {
                                canAttackTop = true;
                            }
                        } else {
                            canAttackTop = true;
                        }

                        if (canAttackTop) {
                            let butterflyPriority = entity.getMissileAttackedPriority();
                            if (butterflyPriority < minPriority) {
                                minPriority = butterflyPriority;
                            }
                        } else {
                            break;
                        }
                    }
                }
                if (canAttackTop) {
                    if (minPriority < targetPriority) {
                        targetPriority = minPriority;
                        targetPos = checkPos;
                    }
                }
            }
        }

        if (targetPriority >= EntityBase.DEFAULT_MISSILE_PRIORITY || targetPos.equals(cc.v2(-1, -1))) {
            targetPos = super.getAttackPos(color, layoutGrass);
        }

        return targetPos;
    }

    public launch() {
        if (this._launched) {
            return;
        }

        this._launched = true;
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_cube'));
        let grid = this.getGrid();
        let targetPos = this.getAttackPos(this.getEntityColor(), this._layoutGrass);
        let targetPixelPos = grid.getPixelPosFromLogicalPos(targetPos);
        grid.addObstructs(this);
        this._puppet.angle = -45;
        let speed = 1100;
        let acceleration = 2.0;
        let cellSize = grid.getCellSize();
        let radius = GameMath.randomInt(cellSize.width, cellSize.width * 1.5);

        let actions: cc.FiniteTimeAction[] = [];
        let state = this.playAnimation('default');
        if (state) {
            actions.push(cc.delayTime(state.duration));
        }

        let move: cc.ActionInterval = OrbitTrack.createWithSpeed(speed, this.node.getPosition(), targetPixelPos, cc.v2(1, 1), radius, true).easing(cc.easeIn(acceleration));
        let scaleAction = cc.sequence(cc.scaleTo(move.getDuration() * 0.25, 1.2).easing(cc.easeIn(2)),
            cc.delayTime(move.getDuration() * 0.5),
            cc.scaleTo(move.getDuration() * 0.25, 1.0).easing(cc.easeOut(2)));
        actions.push(cc.spawn(move, scaleAction));

        let attackCallBack: cc.ActionInstant = null;
        let gridSize = grid.getGridSize();
        switch (this._missileType) {
            case FlyerMissileType.Dot: {
                grid.recordAttackedPosition(targetPos);
                attackCallBack = cc.callFunc(() => {
                    IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_cube2'));
                    grid.dispatchCommand(targetPos, GridCommand.BombClean(targetPos, this.getEntityColor(), this._layoutGrass), 0);
                })
            }
                break;
            case FlyerMissileType.Row: {
                let rayDirections: Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(1, 0), true),
                    new Pair<cc.Vec2, boolean>(cc.v2(-1, 0), false),
                ];

                for (let raydirection of rayDirections) {
                    let willAttackPos = targetPos.clone();
                    let direction = raydirection.first;
                    let validLogicalRect = cc.rect(0, 0, gridSize.width, gridSize.height);
                    while (1) {
                        if (!GameMath.rectContainsPoint(validLogicalRect, willAttackPos)) {
                            break;
                        }
                        grid.recordAttackedPosition(willAttackPos.clone());
                        willAttackPos.addSelf(direction);
                    }
                }

                attackCallBack = cc.callFunc(() => {
                    IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_line'));
                    let grid = this.getGrid();
                    for (let ray of rayDirections) {
                        let cannon = FlyerCannon.create(grid, this.getEntityColor(), this._layoutGrass, targetPos, ray.first, ray.second);
                        grid.setFlyerAtLogicalPos(cannon, targetPos);
                    }
                })
            }
                break;
            case FlyerMissileType.Column: {
                let rayDirections: Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(0, 1), true),
                    new Pair<cc.Vec2, boolean>(cc.v2(0, -1), false),
                ];
                for (let raydirection of rayDirections) {
                    let willAttackPos = targetPos.clone();
                    let direction = raydirection.first;
                    let validLogicalRect = cc.rect(0, 0, gridSize.width, gridSize.height);
                    while (1) {
                        if (!GameMath.rectContainsPoint(validLogicalRect, willAttackPos)) {
                            break;
                        }
                        grid.recordAttackedPosition(willAttackPos.clone());
                        willAttackPos.addSelf(direction);
                    }
                }
                attackCallBack = cc.callFunc(() => {
                    IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_line'));
                    let grid = this.getGrid();
                    for (let ray of rayDirections) {
                        let cannon = FlyerCannon.create(grid, this.getEntityColor(), this._layoutGrass, targetPos, ray.first, ray.second);
                        grid.setFlyerAtLogicalPos(cannon, targetPos);
                    }
                })


            }
                break;
            case FlyerMissileType.Cross: {
                let rayDirections: Pair<cc.Vec2, boolean>[] = [
                    new Pair<cc.Vec2, boolean>(cc.v2(1, 1), true),
                    new Pair<cc.Vec2, boolean>(cc.v2(-1, -1), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(1, -1), false),
                    new Pair<cc.Vec2, boolean>(cc.v2(-1, 1), false),
                ];
                for (let raydirection of rayDirections) {
                    let willAttackPos = targetPos.clone();
                    let direction = raydirection.first;
                    let validLogicalRect = cc.rect(0, 0, gridSize.width, gridSize.height);
                    while (1) {
                        if (!GameMath.rectContainsPoint(validLogicalRect, willAttackPos)) {
                            break;
                        }
                        grid.recordAttackedPosition(willAttackPos.clone());
                        willAttackPos.addSelf(direction);
                    }
                }
                attackCallBack = cc.callFunc(() => {
                    IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_cross'));
                    let grid = this.getGrid();
                    for (let ray of rayDirections) {
                        let cannon = FlyerCannon.create(grid, this.getEntityColor(), this._layoutGrass, targetPos, ray.first, ray.second);
                        grid.setFlyerAtLogicalPos(cannon, targetPos);
                    }
                })
            }
                break;
            case FlyerMissileType.Range: {
                let willAttackPositions = GameMath.enumerateRingPos(targetPos, 0, 2);
                for (let pos of willAttackPositions) {
                    this.getGrid().recordAttackedPosition(pos);
                }
                attackCallBack = cc.callFunc(() => {
                    IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_bomb'));
                    let bomb = FlyerBomb.create(grid, this.getEntityColor(), this._layoutGrass, targetPos, 2);
                    grid.setFlyerAtLogicalPos(bomb, targetPos);
                })
            }
                break;
        }
        if (attackCallBack) {
            actions.push(attackCallBack);
        }
        let destoryCallBack: cc.ActionInstant = cc.callFunc(() => {
            grid.highlightedCell(targetPos);
            grid.bombCell(targetPos);
            grid.removeObstructs(this);
            this.destoryWithRecycle(false);
        });
        actions.push(destoryCallBack);
        this.runAction(cc.sequence(actions));
    }
}
