import GridStateBase, { GridState } from "../GridStateBase";
import GridBase from "../../GridBase";
import GridStatePrepare from "./GridStatePrepare";


export default class GridStateDisappear extends GridStateBase {

    public static create() : GridStateDisappear {
        return new GridStateDisappear();
    }
    constructor() {
        super(GridState.Disappear);
    }

    enter(grid:GridBase):void {
        this.disappear(grid);
    }

    update(grid:GridBase, delta:number):void {
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()){
            grid.setCurrentState(GridStatePrepare.create());
        }
    }
}

