import { EntityID } from "./EntityID";

export enum EntityGroupID {
    None                        = 0,
    ItemNormal                  = 601,
    ItemBombButterfly           = 602,
    ItemBombRow                 = 603,
    ItemBombColumn              = 604,
    ItemBombCross               = 605,
    ItemBombRange               = 606,
    ItemPaw                     = 621,
    ParasiteGiftBox             = 704,
};

export default class EntityGroupIDManager {
    //
    private static instance:EntityGroupIDManager = null;
    public static getInstance():EntityGroupIDManager{
        if(this.instance == null){
            this.instance = new EntityGroupIDManager();
        }
        return this.instance;
    }
    private String_To_EntityGroupID_Map:Object = {};
    private initString_To_EntityGroupID_Map() : void {
        this.String_To_EntityGroupID_Map["fruit"] =         EntityGroupID.ItemNormal;
        this.String_To_EntityGroupID_Map["butterfly"] =     EntityGroupID.ItemBombButterfly;
        this.String_To_EntityGroupID_Map["hLine"] =         EntityGroupID.ItemBombRow;
        this.String_To_EntityGroupID_Map["vLine"] =         EntityGroupID.ItemBombColumn;
        this.String_To_EntityGroupID_Map["cross"] =         EntityGroupID.ItemBombCross;
        this.String_To_EntityGroupID_Map["boom"] =          EntityGroupID.ItemBombRange;
        this.String_To_EntityGroupID_Map["paw"] =           EntityGroupID.ItemPaw;
        this.String_To_EntityGroupID_Map["giftBox"] =       EntityGroupID.ParasiteGiftBox;
    }
    private EntityGroupID_To_EntityIDs_Map:Object = {};
    private initEntityGroupID_To_EntityIDs_Map() : void {
        this.EntityGroupID_To_EntityIDs_Map[EntityGroupID.ItemNormal] = [
            EntityID.ItemNormalRed,
            EntityID.ItemNormalGreen,
            EntityID.ItemNormalBlue,
            EntityID.ItemNormalYellow,
            EntityID.ItemNormalOrange,
            EntityID.ItemNormalPurple,
        ];
    
        this.EntityGroupID_To_EntityIDs_Map[EntityGroupID.ItemBombButterfly] = [
            EntityID.ItemBombButterflyRed,
            EntityID.ItemBombButterflyGreen,
            EntityID.ItemBombButterflyBlue,
            EntityID.ItemBombButterflyYellow,
            EntityID.ItemBombButterflyOrange,
            EntityID.ItemBombButterflyPurple,
        ];
        
        this.EntityGroupID_To_EntityIDs_Map[EntityGroupID.ItemBombRow] = [
            EntityID.ItemBombRowRed,
            EntityID.ItemBombRowGreen,
            EntityID.ItemBombRowBlue,
            EntityID.ItemBombRowYellow,
            EntityID.ItemBombRowOrange,
            EntityID.ItemBombRowPurple,
        ];
        
        this.EntityGroupID_To_EntityIDs_Map[EntityGroupID.ItemBombColumn] = [
            EntityID.ItemBombColumnRed,
            EntityID.ItemBombColumnGreen,
            EntityID.ItemBombColumnBlue,
            EntityID.ItemBombColumnYellow,
            EntityID.ItemBombColumnOrange,
            EntityID.ItemBombColumnPurple,
        ];
        
        this.EntityGroupID_To_EntityIDs_Map[EntityGroupID.ItemBombCross] = [
            EntityID.ItemBombCrossRed,
            EntityID.ItemBombCrossGreen,
            EntityID.ItemBombCrossBlue,
            EntityID.ItemBombCrossYellow,
            EntityID.ItemBombCrossOrange,
            EntityID.ItemBombCrossPurple,
        ];
        
        
        this.EntityGroupID_To_EntityIDs_Map[EntityGroupID.ItemBombRange] = [
            EntityID.ItemBombRangeRed,
            EntityID.ItemBombRangeGreen,
            EntityID.ItemBombRangeBlue,
            EntityID.ItemBombRangeYellow,
            EntityID.ItemBombRangeOrange,
            EntityID.ItemBombRangePurple,
        ];
        
        
        this.EntityGroupID_To_EntityIDs_Map[EntityGroupID.ItemPaw] = [
            EntityID.ItemPawRed,
            EntityID.ItemPawGreen,
            EntityID.ItemPawBlue,
            EntityID.ItemPawYellow,
            EntityID.ItemPawOrange,
            EntityID.ItemPawPurple,
        ];

        this.EntityGroupID_To_EntityIDs_Map[EntityGroupID.ParasiteGiftBox] = [
            EntityID.ParasiteGiftBoxRed,
            EntityID.ParasiteGiftBoxGreen,
            EntityID.ParasiteGiftBoxBlue,
            EntityID.ParasiteGiftBoxYellow,
            EntityID.ParasiteGiftBoxOrange,
            EntityID.ParasiteGiftBoxPurple,
        ];
    }
    private EntityGroupID_To_FrameName_Map:Object = {};
    private initEntityGroupID_To_FrameName_Map() : void {
        this.EntityGroupID_To_FrameName_Map[EntityGroupID.ItemNormal] =              "items/60701.png";
        this.EntityGroupID_To_FrameName_Map[EntityGroupID.ItemBombButterfly] =       "items/60207.png";
        this.EntityGroupID_To_FrameName_Map[EntityGroupID.ItemBombRow] =             "items/60307.png";
        this.EntityGroupID_To_FrameName_Map[EntityGroupID.ItemBombColumn] =          "items/60407.png";
        this.EntityGroupID_To_FrameName_Map[EntityGroupID.ItemBombCross] =           "items/60507.png";
        this.EntityGroupID_To_FrameName_Map[EntityGroupID.ItemBombRange] =           "items/60607.png";
        this.EntityGroupID_To_FrameName_Map[EntityGroupID.ItemPaw] =                 "items/62107.png";
        this.EntityGroupID_To_FrameName_Map[EntityGroupID.ParasiteGiftBox] =         "parasites/70407.png";
    }

    constructor() {
        this.initString_To_EntityGroupID_Map();
        this.initEntityGroupID_To_EntityIDs_Map();
        this.initEntityGroupID_To_FrameName_Map();
    }

    public getEntityGroupIDFromString(name:string) : EntityGroupID {
        let result = this.String_To_EntityGroupID_Map[name];
        if (result) {
            return result;
        }
        return EntityGroupID.None;
    }

    public getFrameNameFromEntityGroupID(groupID:EntityGroupID) : string {
        let rootPath = 'match_scene/texture/entities/';
        let result = this.EntityGroupID_To_FrameName_Map[groupID];
        if (result) {
            return rootPath + result;
        }
        return rootPath + "unknow/unknow";
    }

    public getEntityIDsFromEntityGroupID(groupID:EntityGroupID) : EntityID[] {
        let result = this.EntityGroupID_To_EntityIDs_Map[groupID];
        if (result) {
            return result;
        }
        return [];
    }

    public getEntityGroupIDFromEntityID(entityID:EntityID) : EntityGroupID {
        let tempMap = this.EntityGroupID_To_EntityIDs_Map;
        for (let key in tempMap) {
            if (tempMap.hasOwnProperty(key)) {
                const entityIDs:EntityID[] = tempMap[key];
                for(const id of entityIDs){
                    if (id == entityID) {
                        let result = Number(key) as EntityGroupID;
                        return result;     
                    }
                }
            }
        }
        return EntityGroupID.None;
    }
}
