

//CFGTripleLevelPopsicles
export interface CFGTripleLevelPopsiclesObj {
    LevelID: number;
    MapID: number;
    Step: number;
    StarScore: string;
    DropBox: string;
    DropProb: string;
}

export class CFGTripleLevelPopsicles {

    //
    private cfgPath: string = 'CFGTripleLevelPopsicles';
    //
    private cfgObj: Object[] = [];

    constructor() {

        let res = cc.loader.getRes('data/' + this.cfgPath, cc.JsonAsset) as cc.JsonAsset;
        if (res) {
            this.cfgObj = res.json
        }

    }

    public getCongfigByLevelID(levelID: number): CFGTripleLevelPopsiclesObj {
        let cfgList = this.cfgObj;
        for (const obj of cfgList) {
            if (obj['LevelID'] == levelID) {
                return obj as CFGTripleLevelPopsiclesObj;
            }
        }

        return null;
    }
}
