// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const { ccclass, property } = cc._decorator;

@ccclass
export default class EntityBase extends cc.Component {

    protected static getEntitySpriteFarme(frameName: string): cc.SpriteFrame {
        let result = null;
        let spriteFrame = cc.loader.getRes(frameName, cc.SpriteFrame);
        if (spriteFrame) {
            result = spriteFrame;
        }
        return result;
    }

    protected _puppet: cc.Node = null;
    public getPuppet(): cc.Node {
        return this._puppet;
    }
    protected setPuppetSpriteFrameName(frameName: string) {
        let spriteFrame = EntityBase.getEntitySpriteFarme(frameName);
        if (spriteFrame) {
            if (this._puppet) {
                let sprite = this._puppet.getComponent(cc.Sprite);
                if (sprite) {
                    sprite.spriteFrame = spriteFrame;
                }
            }
        }
    }

    protected updatePuppet(): void {
        let frameName = IdentifierManager.getInstance().getFrameNameFromEntityID(this.getEntityID());
        this.setPuppetSpriteFrameName(frameName);
    }

    private _stateMachine: EntityStateMachine = null;
    private _occupyArea: cc.Rect = cc.rect(0, 0, 0, 0);;
    private _tileSize: cc.Size = cc.size(0, 0);

    private _visible: boolean = true;
    public getVisible(): boolean {
        return this._visible;
    }
    public setVisible(visible: boolean): void {
        this._visible = visible;
        if (this._visible) {
            this.node.opacity = 255;
        } else {
            this.node.opacity = 0;
        }
    }

    public playAnimation(name: string): cc.AnimationState {
        let anim = this.node.getComponent(cc.Animation);
        if (anim) {
            return anim.play(name);
        }
        return null;
    }

    public pauseAnimation(name: string): void {
        let anim = this.node.getComponent(cc.Animation);
        if (anim && anim.getAnimationState(name) != null) {
            anim.pause(name);
        }
        return null;
    }

    public setCurrentAnimationTime(time?: number, name?: string): void {
        let anim = this.node.getComponent(cc.Animation);
        if (anim && anim.getAnimationState(name) != null) {
            anim.setCurrentTime(time, name);
        }
        return null;
    }

    public getAnimationState(name: string): cc.AnimationState {
        let anim = this.node.getComponent(cc.Animation);
        if (anim) {
            return anim.getAnimationState(name);
        }
        return null;
    }

    public getCurrentAnimationClipName(): string {
        let anim = this.getComponent(cc.Animation);
        if (anim) {
            let clip = anim.currentClip;
            if (clip) {
                return clip.name;
            }
        }
        return '';
    }

    public getContentSize(): cc.Size {
        return this.node.getContentSize();
    }
    public setContentSize(size: cc.Size): void {
        this.node.setContentSize(size);
    }

    public runAction(action: cc.Action): cc.Action {
        return this.node.runAction(action);
    }

    public clone(): EntityBase {
        return null;
    }

    public getEntityComponent(): EntityBase {
        return null;
    }

    private _grid: GridBase = null;
    public getGrid(): GridBase {
        return this._grid;
    }

    private _entityType: EntityType = EntityType.None;
    public getEntityType(): EntityType {
        return this._entityType;
    }

    public onCollected(): void {
        let collectFactor = 1;
        let floor = this.getGrid().getFloorAtLogicalPos(this.getLogicalPos());
        if (floor && floor.getEntityID() == EntityID.FloorDouble) {
            collectFactor = 2;
        }

        for (let i = 0; i < collectFactor; i++) {
            this.getGrid().chargeFlyer(this);
            this.getGrid().createCollectedEntityFlyer(this);
            this.getGrid().collectEntity(this);

        }
    }

    public isTargetCollection(): boolean {
        return this.getGrid().isTargetCollectionID(this.getCollectionID());
    }
    public removeFromGrid(): void {
        this.getGrid().removeEntity(this.getEntityType(), this.getLogicalPos());
    }

    public removeFromGridAndAddToEffect(): void {
        this.removeFromGrid();
        this.node.parent = this.getGrid().node;
        this.setPixelPos(this.getLogicalPos());
        this.node.zIndex = this.getTopZOrder();
    }

    public shiftToEffectLayer(): void {
        this.getGrid().shiftEntityToEffectLayer(this);
    }

    private _entityColor: EntityColor = EntityColor.None;
    public getEntityColor(): EntityColor {
        return this._entityColor;
    }
    public setEntityColor(color: EntityColor): void {
        this._entityColor = color;
        this.updatePuppet();
    }

    public static DEFAULT_MISSILE_PRIORITY = 9999;
    public static DEFAULT_FIREWORKS_ATTACKED_PRIORITY = 999;
    private _fireWorksAttackedPriority: number = EntityBase.DEFAULT_FIREWORKS_ATTACKED_PRIORITY;
    public getFireWorksAttackedPriority(): number {
        return this._fireWorksAttackedPriority;
    }
    public setFireWorksAttackedPriority(priority: number) {
        this._fireWorksAttackedPriority = priority;
    }

    private _missileAttackedPriority: number = EntityBase.DEFAULT_MISSILE_PRIORITY;
    public getMissileAttackedPriority(): number {
        return this._missileAttackedPriority;
    }
    public setMissileAttackedPriority(priority: number) {
        this._missileAttackedPriority = priority;
    }

    private _immuneDamage: boolean = false;
    public getImmuneDamage(): boolean {
        return this._immuneDamage;
    }
    public setImmuneDamage(immuneDamage: boolean): void {
        this._immuneDamage = immuneDamage;
    }

    public getCurrentStateType(): EntityStateType {
        let state = this.getCurrentState();
        return state.getType();
    }
    public getCurrentState(): EntityStateBase {
        let state = this._stateMachine.getCurrentState() as EntityStateBase;
        return state;
    }

    public setCurrentState(state: EntityStateBase): void {
        this._stateMachine.setCurrentState(state);
    }

    public dealWithConfigMap(config: Object): void {
        let fireworkAttackedPriority = config[TMXConfigInfoKeys.CFG_FIREWORK_ATTACKED_PRIORITY];
        if (fireworkAttackedPriority) {
            let priority = Number(fireworkAttackedPriority);
            this.setFireWorksAttackedPriority(priority);
        }

        let missileAttackedPriority = config[TMXConfigInfoKeys.CFG_MISSILE_ATTACKED_PRIORITY];
        if (missileAttackedPriority) {
            let priority = Number(missileAttackedPriority);
            this.setMissileAttackedPriority(priority);
        }
    }

    public getEntityID(): EntityID {
        return EntityID.None;
    }

    protected _prefabFileName = ''
    public getPrefabFileName(): string {
        return this._prefabFileName;
    }
    public setPrefabFileName(fileName: string): string {
        return this._prefabFileName = fileName;
    }

    public getCollectionID(): CollectionID {
        return CollectionID.None;
    }

    public getProducedTrainSupplyMaps(): Object {
        return {};
    }

    public getPowerID(): PowerID {
        return PowerID.None;
    }

    public getScore(): number {
        return 0;
    }

    public getButterflyPriority(cleanColor: EntityColor, willLayoutGrass: boolean): number {
        return IdentifierManager.getInstance().getDefaultButterflyPriorityFromEntityID(this.getEntityID());
    }

    public getLogicalPos(): cc.Vec2 {
        return this._occupyArea.origin;
    }

    public setLogicalPos(pos: cc.Vec2): void {
        this._occupyArea.origin = pos;
        this.node.setPosition(this.getPixelPos(pos));
        this.node.zIndex = this.getBottomZOrder();
    }

    public getPixelPos(logicalPos: cc.Vec2): cc.Vec2 {

        let contentSize = this.getContentSize();
        let offset = cc.v2(contentSize.width * 0.5 - this._tileSize.width * 0.5,
            contentSize.height * 0.5 - this._tileSize.height * 0.5);
        let pixePos = this.getGrid().getPixelPosFromLogicalPos(logicalPos).add(offset);
        return pixePos;
    }

    public setPixelPos(logicalPos: cc.Vec2): void {
        this.node.setPosition(this.getPixelPos(logicalPos));
    }

    public getTopZOrder(): number {
        return this.getGrid().getTopZOrderForEnity(this);
    }
    public getBottomZOrder(): number {
        return this.getGrid().getBottomZOrderForEnity(this);
    }

    public getLogicalSize(): cc.Size {
        return this._occupyArea.size;
    }

    public getOccupyArea(): cc.Rect {
        return this._occupyArea;
    }

    private _path: EntityDropStep[] = [];
    public getPath(): EntityDropStep[] {
        return this._path;
    }

    public addStepToPath(step: EntityDropStep): void {
        this._path.push(step);
    }

    public clearPath(): void {
        GameMath.clearArray(this._path);
    }

    public canExcuteCommand(command: GridCommand): boolean {
        return false;
    }

    public excuteCommand(command: GridCommand): void {

    }

    public initEntity(grid: GridBase, entityType: EntityType, width: number = 1, height: number = 1): void {
        this._grid = grid;
        if (this._grid) {
            this._puppet = this.node.getChildByName('puppet');
            this._entityType = entityType;
            this._occupyArea.width = width;
            this._occupyArea.height = height;
            this._tileSize = this._grid.getCellSize();
            let size = cc.size(this._tileSize.width * width, this._tileSize.height * height);
            this.node.setContentSize(size);
            this.node.setAnchorPoint(cc.v2(0.5, 0.5));
        }
        this._stateMachine = EntityStateMachine.create(this);
        this.setCurrentState(EntityStateWait.create());
    }

    onLoad() {

    }

    update(dt) {
        dt = dt * cc.director.getScheduler().getTimeScale();
        this._stateMachine.update(dt);
    }

    onDestroy() {
        this._puppet = null;
        this._stateMachine = null;
        this._occupyArea = null;
        this._tileSize = null;
        this._grid = null;
        this._prefabFileName = null;
        this._path = null;
    }

    public destoryWithRecycle(recycle: boolean = false) {
        if (recycle) {
            this.resetForRecycle();
            this.node.removeFromParent(true);
            EntityPrefabNodePool.getInstance().putPrefabNode(this);
        } else {
            this.node.destroy();
        }
    }

    protected resetForRecycle() {
        this.node.scale = 1;
        this.node.angle = 0;
        this.node.opacity = 255;
        this._grid = null;
        this._stateMachine = null;
        this._fireWorksAttackedPriority = EntityBase.DEFAULT_FIREWORKS_ATTACKED_PRIORITY;
        this._missileAttackedPriority = EntityBase.DEFAULT_MISSILE_PRIORITY;
        this.clearPath();
        this.setImmuneDamage(false);
        this.setVisible(true);
    }

    protected selfDestory(recycle: boolean = false): void {
        this.removeFromGridAndAddToEffect();
        this.getGrid().addObstructs(this);
        this.runAction(cc.sequence(cc.delayTime(0.05), cc.callFunc(() => {
            this.getGrid().removeObstructs(this);
            this.destoryWithRecycle(recycle);
        })));
    }
    protected selfHurt(): void {
        this.getGrid().addObstructs(this);
        this.runAction(cc.sequence(cc.delayTime(0.05), cc.callFunc(() => {
            this.getGrid().removeObstructs(this);
        })));
    }
}
