import GridStateBase, { GridState } from "../GridStateBase";
import GridBase from "../../GridBase";

export default class GridStateStalemate extends GridStateBase {

    public static create() : GridStateStalemate{
        return new GridStateStalemate();
    }
    constructor() {
        super(GridState.Stalemate);
    }

    public enter(grid:GridBase) {
        grid.stalemate();
    }

}
