    public getButterflyPriority(color: EntityColor, layoutGrass: boolean): number {
        let grid = this.getGrid();
        if (grid.isTargetCollectionID(CollectionID.ItemBone) &&
            this.canCleanByBomb(color)) {
            let checkPos = this.getLogicalPos().clone();
            while (1) {
                let cell = grid.getCellAtLogicalPos(checkPos);
                if (cell) {
                    if (cell.getIsWormholeExit()) {
                        checkPos = cell.getWormholeSource().clone();
                    } else {
                        checkPos.addSelf(cc.v2(0, 1));
                    }
                } else {
                    break;
                }
                let item = grid.getItemAtLogicalPos(checkPos);
                if (item && item.getItemType() == ItemType.Bone) {
                    return 1;
                }
            }
        }
        return super.getButterflyPriority(color, layoutGrass);
    }

    public getTopParasite(): ParasiteBase {
        let grid = this.getGrid();
        let occupyArea = this.getOccupyArea();
        for (let row = occupyArea.yMin; row < occupyArea.yMax; row++) {
            for (let column = occupyArea.xMin; column < occupyArea.xMax; column++) {
                let parasite = grid.getParasiteAtLogicalPos(cc.v2(column, row));
                if (parasite) {
                    return parasite;
                }
            }
        }

        return null;
    }

    public canLink(item: ItemBase): boolean {
        if (!item) {
            return false;
        }

        if (!item.canMatch()) {
            return false;
        }

        if (!this.canMatch()) {
            return false;
        }

        let color = this.getEntityColor();
        if (color >= EntityColor.Begin && color <= EntityColor.End) {
            return item.getEntityColor() == color;
        }
        return false;
    }
    /**
     * 
     */
    public getLinkedPositions(): cc.Vec2[] {
        let result: cc.Vec2[] = [];
        if (!this.canMatch()) {
            return result;
        }

        let linkedPos: cc.Vec2[] = [this.getLogicalPos()];
        let moveOffsets: cc.Vec2[] = [cc.v2(1, 0), cc.v2(-1, 0), cc.v2(0, 1), cc.v2(0, -1)];

        let grid = this.getGrid();

        for (let index = 0; index < linkedPos.length; index++) {
            let currentPos = linkedPos[index];

            for (let offset of moveOffsets) {
                let checkPos = currentPos.add(offset);

                if (GameMath.indexOf(linkedPos, checkPos) != -1) {
                    continue;
                }

                let checkItem = grid.getItemAtLogicalPos(checkPos);
                if (checkItem && this.canLink(checkItem)) {
                    linkedPos.push(checkPos);
                }
            }
        }
        linkedPos.sort(function (infoA: cc.Vec2, infoB: cc.Vec2): number {
            if (GameMath.vec2Less(infoA, infoB)) {
                return -1;
            } else {
                return 1;
            }
        });
        GameMath.filterInvalidMathPositions(linkedPos);
        return linkedPos;
    }

