import ItemBase, { ItemType } from "../ItemBase";
import { CollectionID } from "../../../defines/CollectionID";
import { EntityID, EntityCleanOption, EntityCleanType, EntityColor } from "../../../defines/EntityID";
import GridBase from "../../../grid/GridBase";
import IdentifierManager from "../../../defines/IdentifierManager";
import FlyerAnimation, { FlyerAnimationType } from "../../flyer/FlyerAnimation";
import { TMXJsonObjectInfo } from "../../../../../extend/utils/TMXJsonPhraser";
import IGame from "../../../../../base/IGame";
import EntityPrefabNodePool from "../../EntityPrefabNodePool";


const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemBlock extends ItemBase {

    public static create(grid:GridBase, healthPoint:number):ItemBlock{

        let className = 'ItemBlock';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemBlock';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemBlock;

        
        if (component) {
            component.init(grid, healthPoint); 
            return component;       
        }
        return null;
    }

    public clone():ItemBlock {
        let item = ItemBlock.create(this.getGrid(), this.getHealthPoint());
        return item;
    }
    protected init(grid:GridBase, healthPoint:number):void{
        super.initItem(grid, ItemType.Block);
        this.setHealthPoint(healthPoint);
    }
    
    private _healthPoint:number = 0;
    protected getHealthPoint():number{
        return this._healthPoint;
    }

    protected setHealthPoint(val:number){
        this._healthPoint = val;
        this.updatePuppet();
    }

    public getCollectionID():CollectionID{
        return CollectionID.ItemBlock;
    }

    public getEntityID():EntityID{
        let result = EntityID.None;
        switch(this.getHealthPoint()){
            case 1:{
                result = EntityID.ItemBlockHp1;
                break;
            }
            case 2:{
                result = EntityID.ItemBlockHp2;
                break;
            }
            case 3:{
                result = EntityID.ItemBlockHp3;
                break;
            }
            case 4:{
                result = EntityID.ItemBlockHp4;
                break;
            }
            case 5:{
                result = EntityID.ItemBlockHp5;
                break;
            }
            case 6:{
                result = EntityID.ItemBlockHp6;
                break;
            }
            default:{
                break;
            }
        }

        return result;
    }

    public getScore():number{
        return 100;
    }

    protected _canDrop():boolean{
        return false;
    }

    protected _canSwap():boolean{
        return false;
    }

    protected _canMatch():boolean{
        return false;
    }

    protected _canCleanByBomb(cleanColor:EntityColor):boolean{
        return true;
    }

    protected _canCleanByNeighbour(color:EntityColor):boolean{
        return true;
    }

    protected _onClean(cleanOption:EntityCleanOption):void{

        let rate = 1;
        if (cleanOption.cleanType == EntityCleanType.Terminate) {
            rate = this.getHealthPoint();
            this.setHealthPoint(1);
        }
        this.getGrid().gainScoreWithEntity(this, rate);
        if(this.getHealthPoint() <= 1){
            if (!this.isTargetCollection()) {
                let flyer = FlyerAnimation.createDieAnimation(this.getGrid(), this.getEntityID());
                this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
                IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('block'));
            }
            this.progressDeathWish(cleanOption);
            this.setVisible(false);
            this.selfDestory();
            
        }else{
            this.setHealthPoint(this.getHealthPoint() - 1);
            this.selfHurt();
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_0'));
            let flyer = FlyerAnimation.createAnimationWithType(this.getGrid(), FlyerAnimationType.CommonHurt);
            this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
            flyer.node.zIndex = this.getTopZOrder()
        }
    }
}
