import GridStateBase, { GridState } from "../GridStateBase";
import GridBase from "../../GridBase";

export default class GridStateFailSummary extends GridStateBase {

    public static create() : GridStateFailSummary{
        return new GridStateFailSummary();
    }
    constructor() {
        super(GridState.FailSummary);
    }

    public enter(grid:GridBase) {
        grid.showGameFail();
    }
}
