import { EntityID } from "../defines/EntityID";
import ItemBase from "./item/ItemBase";
import ItemFactory from "./item/ItemFactory";
import GridBase from "../grid/GridBase";
import GameMath from "../../../extend/utils/GameMath";
import IdentifierManager from "../defines/IdentifierManager";
import FlyerBase from "./flyer/FlyerBase";
import EntityBase from "./EntityBase";


export default class EntityPrefabNodePool {
    //
    private static instance:EntityPrefabNodePool = null;
    public static getInstance():EntityPrefabNodePool{
        if(this.instance == null){
            this.instance = new EntityPrefabNodePool();
        }
        return this.instance;
    }

    private _prefabNodePools:Object = {}
    constructor(){
        
    }

    public getPrefabNodeWithFileNameAndClassName(fileName:string, className:string) : EntityBase {
        let node:cc.Node = null;
        let nodePool = this._prefabNodePools[fileName] as cc.NodePool;
        if (nodePool && nodePool.size() > 0) {
            node = nodePool.get();
        }else{
            let res = cc.loader.getRes(fileName, cc.Prefab);
            if (res) {
                node = cc.instantiate(res);      
            }
        }

        if (node) {
            let component = node.getComponent(className);
            if (!component) {
                component = node.addComponent(className);
                component.setPrefabFileName(fileName); 
            }
            return component;
        }
        return null;
    }

    public putPrefabNode(entity:EntityBase) : void {
        if (!entity) {
            return;
        }
        let prefabFileName = entity.getPrefabFileName();
        if (prefabFileName != '') {
            if (!this._prefabNodePools.hasOwnProperty(prefabFileName)) {
                this._prefabNodePools[prefabFileName] = new cc.NodePool();
            }
            let nodePool = this._prefabNodePools[prefabFileName] as cc.NodePool;
            if(nodePool.size() < 100) {
                nodePool.put(entity.node);
            }
        }
    }
    public clear() : void {
        for (let key in this._prefabNodePools) {
            let pool = this._prefabNodePools[key] as cc.NodePool;
            pool.clear();
        }
        GameMath.clearObject(this._prefabNodePools);
    }
}