const {ccclass, property} = cc._decorator;

@ccclass
export default class TargetBar extends cc.Component {

    private _gameScene:GameSceneBase = null;
    private _targetItems:TargetItem[] = [];

    public hideAll(){
        for (let item of this._targetItems) {
            item.hideIco();
        }
    }

    public setTargetData(data:Object) {
        for (const key in data) {
            let needAmount = data[key]
            let collectionID = Number(key) as CollectionID;
            if (collectionID) {
                let item = this.getItemWithCollectionID(collectionID);
                if (item) {
                    item.setAmount(needAmount);
                }
            }
        }
    }

    public clearTargets(){
        for (let item  of this._targetItems) {
            item.node.destroy();
        }
        this._targetItems.length = 0;
    }

    public getTargetIconWorldPos(collectionID:CollectionID) :cc.Vec2 {
        return cc.Vec2.ZERO;
    }

    public getItemWithCollectionID(collectionID:CollectionID) : TargetItem {
        let result:TargetItem = null;
        for (let item of this._targetItems) {
            if (item.getCollectionID() == collectionID) {
                result = item;
                break;
            }
        }
        if(result == null){
            result = TargetItem.create(collectionID);
            if (result) {
                this.node.addChild(result.node);
                this._targetItems.push(result);
            }
        }
        return result;
    }

    public getWorldPosWithCollectionID(collectionID:CollectionID) : cc.Vec2 {
        let item = this.getItemWithCollectionID(collectionID);
        if (item) {
            return item.node.parent.convertToWorldSpaceAR(item.node.position);
        }
        return cc.Vec2.ZERO;
    }

    // LIFE-CYCLE CALLBACKS:
    onLoad () {
        let canvas = cc.find("Canvas");
        if (canvas) {
            this._gameScene = canvas.getComponent(GameSceneBase);
        }
    }
    start () {

    }
    // update (dt) {}
    onDestroy() {
        this._gameScene = null;
        this._targetItems = null;
    }
}
