    public getEntityIDFromKindID(kindID:KindID) : EntityID {
        let result = this.KindID_To_EntityID_Map[kindID];
        if (result) {
            return result;
        }
        return EntityID.None;
    }

    public needLayoutGrassFromKindID(kindID:KindID) : boolean {
        let needLayoutGrass = false;
        let entityID = this.getEntityIDFromKindID(kindID);
        switch(entityID) {
            case EntityID.CellNormal:
            case EntityID.CellMovablePlate:{
                needLayoutGrass = true;
            }
        }
        return needLayoutGrass;
    }
