

//CFGTripleMap
export interface CFGTripleMapObj {
    MapId: string;
    MapData: string;
}

export class CFGTripleMap {
    //
    private cfgPath: string = 'CFGTripleMap';
    constructor() {
    }

    public getMapDataByMapId(mapId: string): string {
        //
        let mapStr: string = '';

        let res = cc.loader.getRes('data/' + this.cfgPath + mapId, cc.JsonAsset) as cc.JsonAsset;
        if (res) {
            let tmpObj: CFGTripleMapObj = res.json as CFGTripleMapObj;
            mapStr = tmpObj.MapData;
        }
        return mapStr;
    }

}