import MatchScene from "../MatchScene";
import { CFGTripleLevel } from "../../../config/CFGTripleLevel";
import GameSceneBase from "../GameSceneBase";
import GameMath from "../../../extend/utils/GameMath";

// Learn TypeScript:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/typescript.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/typescript.html
// Learn Attribute:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/reference/attributes.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;

@ccclass
export default class LevelSelectorPanel extends cc.Component {

    @property(cc.Node)
    mask: cc.Node = null;

    @property(cc.Node)
    frame: cc.Node = null;

    @property(cc.Node)
    drawer: cc.Node = null;

    @property(cc.Node)
    previous: cc.Node = null;

    @property(cc.Node)
    next: cc.Node = null;

    @property(cc.EditBox)
    editBox: cc.EditBox = null;

    @property(cc.Node)
    updateCfgBtn:cc.Node = null;


    @property(cc.Node)
    speedUpButton:cc.Node = null;

    @property(cc.EditBox)
    speedEditBox: cc.EditBox = null;

    @property(cc.Node)
    speedDownButton:cc.Node = null;

    private _gameScene:GameSceneBase = null;
    private _levelConfig:CFGTripleLevel = null
    private _speed:number = 1.0;
    public static create() : LevelSelectorPanel {
        let res = cc.loader.getRes('match_scene/prefab/userInterface/LevelSelectorPanel', cc.Prefab);
        if (res) {
            let node = cc.instantiate(res);
            if (node) {
                let component = node.getComponent(LevelSelectorPanel) as LevelSelectorPanel;
                if (component) {
                    component.init();
                }
                return component;   
            }
        }
    }

    protected init() : void {
        this.drawer.on('touchend',this.onPull, this);
        this.previous.on('touchend',this.onPrevious, this);
        this.next.on('touchend',this.onNext, this);
        this.updateCfgBtn.on('touchend', this.onUpdateCfgBtnClick, this);

        
        this.mask.active = false;

        this.editBox.inputMode = cc.EditBox.InputMode.NUMERIC;
        this.editBox.maxLength = 20;
        this.editBox.placeholder = '1';

        this.editBox.node.on('editing-did-began',this.levelTextDidBegan, this);
        this.editBox.node.on('editing-did-ended',this.levelTextDidEnded, this);
        this.editBox.node.on('text-changed',this.levelTextChanged, this);


        this.speedUpButton.on('touchend',this.onSpeedUp, this);
        this.speedDownButton.on('touchend',this.onSpeedDown, this);

        this.speedEditBox.node.on('editing-did-began',this.speedTextDidBegan, this);
        this.speedEditBox.node.on('editing-did-ended',this.speedTextDidEnded, this);
        this.speedEditBox.node.on('text-changed',this.speedTextChanged, this);

        


        let canvas = cc.find("Canvas");
        if (canvas) {
            this._gameScene = canvas.getComponent(GameSceneBase);
        }
    }

    
    // LIFE-CYCLE CALLBACKS:
    onLoad () {
        
    }

    start () {

    }
    onDestroy() {
        this.mask = null;
        this.frame = null;
        this.drawer = null;
        this.previous = null;
        this.next = null;
        this.editBox = null;
        this._gameScene = null;
        this._levelConfig = null;
        cc.director.getScheduler().setTimeScale(1.0);
    }
    // update (dt) {}
    private onPull(evt:cc.Event.EventTouch) {
        let winSize = this.node.getContentSize();
        let frameSize = this.frame.getContentSize();
        let targetPos = cc.v2(-frameSize.width - (winSize.width - this.frame.width) * 0.5, this.frame.position.y);

        this.frame.stopAllActions();
        let duration = Math.abs(this.frame.position.x - targetPos.x) / 1000 * cc.director.getScheduler().getTimeScale();
        let pullAction = cc.moveTo(duration, targetPos).easing(cc.easeBackInOut());
        this.frame.runAction(pullAction);
    
        this.node.on('touchend',this.onPush, this);
        this.drawer.setPosition(-1000, this.drawer.position.y);
        this.mask.active = true;

    }

    private onPush(evt:cc.Event.EventTouch) {
        let targetPos = cc.v2(0, this.frame.position.y);
        this.frame.stopAllActions();
        let duration = Math.abs(this.frame.position.x - targetPos.x) / 1000 * cc.director.getScheduler().getTimeScale();
        let pushAction = cc.moveTo(duration, targetPos).easing(cc.easeBackInOut());
        this.frame.runAction(pushAction);

        this.node.off('touchend',this.onPush, this);
        this.drawer.setPosition(-20, this.drawer.position.y);
        this.mask.active = false;
    }

    private onPrevious(evt:cc.Event.EventTouch) {
        this._gameScene.cheatGameWithLevel(this._gameScene.getLevel() - 1)   
        this.onPush(null);
    }

    private onNext(evt:cc.Event.EventTouch) {
        let level = Number(this.editBox.string);
        if(level){
            this._gameScene.cheatGameWithLevel(level);
        }else{
            this._gameScene.cheatGameWithLevel(this._gameScene.getLevel() + 1);
        }
        this.onPush(null);
    }

    private onUpdateCfgBtnClick(evt:cc.Event.EventTouch):void{
        //
        cc.log('onUpdateCfgBtnClick');
    }

    public restartGame():void{
        this._gameScene.cheatGameWithLevel(this._gameScene.getLevel());
        this.onPush(null);
    }

    private levelTextDidBegan(event:cc.Event.EventCustom) {
        cc.log('levelTextDidBegan:' + this.editBox.string);
    }

    private levelTextChanged(event:cc.Event.EventCustom) {
        cc.log('levelTextChanged:' + this.editBox.string);
    }

    private levelTextDidEnded(event:cc.Event.EventCustom) {
        cc.log('levelTextDidEnded:' + this.editBox.string);
    }
    


    private setSpeed(speed:number){
        let newSpeed = GameMath.clamp(speed, 0.0625, 16);
        this._speed = newSpeed;
        this.speedEditBox.string = newSpeed.toString();
        cc.director.getScheduler().setTimeScale(this._speed);
    }
    private onSpeedUp(evt:cc.Event.EventTouch) {
        this.setSpeed(this._speed * 2);
    }

    private onSpeedDown(evt:cc.Event.EventTouch) {
        this.setSpeed(this._speed * 0.5);
    }

    private speedTextDidBegan(event:cc.Event.EventCustom) {
        cc.log('speedTextDidBegan:' + this.speedEditBox.string);
        
    }

    private speedTextChanged(event:cc.Event.EventCustom) {
        cc.log('speedTextChanged:' + this.speedEditBox.string);

        let number = Number(this.speedEditBox.string);
        if (number) {
            this.setSpeed(number);
        }
    }

    private speedTextDidEnded(event:cc.Event.EventCustom) {
        cc.log('speedTextDidEnded:' + this.speedEditBox.string);

        
    }
}
