//LD: (int)  0 1 2 4
export enum GameEndExtraRewardType {
    None = 0,
    Share = 1,
    WatchVideo = 2,
    Request = 4,
    ShareForReward = 7,
}

export class GameEndPanelData{
    public static createWithData(object:Object) : GameEndPanelData{
        let succeedRewardType = 0;
        if (object && object.hasOwnProperty('LD')) {
            succeedRewardType = object['LD'];
        }
        let failedRewardType = 0;
        if (object && object.hasOwnProperty('LT')) {
            failedRewardType = object['LT'];
        }
        return GameEndPanelData.create(succeedRewardType, failedRewardType);
    }

    public static create(succeedRewardType:number, failedRewardType:number) : GameEndPanelData{
        let result = new GameEndPanelData();
        result.succeedRewardType = succeedRewardType;
        result.failedRewardType = failedRewardType;
        
        return result;
    }
    public succeedRewardType:GameEndExtraRewardType = GameEndExtraRewardType.None;
    public failedRewardType:GameEndExtraRewardType = GameEndExtraRewardType.None;
}

const {ccclass, property} = cc._decorator;

@ccclass
export default class GameResultBasePanel extends cc.Component {

    @property(StarAndScoreFrame)
    starFrame:StarAndScoreFrame = null;

    @property(cc.Node)
    rewardItemsFrame:cc.Node = null;

    @property([GameEndItem])
    rewardItems:GameEndItem[] = [];

    @property(cc.Sprite)
    goldSprite:cc.Sprite = null;
    @property(cc.Sprite)
    goldDoubleIco:cc.Sprite = null;
    @property(cc.Label)
    goldLabel:cc.Label = null;

    @property(cc.Sprite)
    lifeSprite:cc.Sprite = null;
    @property(cc.Sprite)
    lifeDoubleIco:cc.Sprite = null;
    @property(cc.Label)
    lifeLabel:cc.Label = null;

    @property(cc.Sprite)
    backpack:cc.Sprite = null;

    @property(cc.Node)
    rewardButton:cc.Node = null;
    @property(cc.Node)
    cancelButton:cc.Node = null;

    @property(cc.Node)
    rankNode:cc.Node = null;
    @property(cc.Sprite)
    friendSprite: cc.Sprite = null;
    // @property(cc.Texture2D)
    // _tex:cc.Texture2D = null;

    protected _gameScene:GameSceneBase = null;
    protected _report:Object = null;

    protected _achievementReward:Object = null;//
    protected _timeLimitAward:Object = null;//
    protected _activeNextResidentGearType:number = 0;

    protected getExtraRewardType():GameEndExtraRewardType{
        return GameEndExtraRewardType.None;
    }

    protected showAdBanner(){}
    protected closeAdBanner(){}
    protected sendMarkID() {}
    protected recordBestScore(){}
    protected popUpRearAchievementPanel(){}
    protected playSoundEffect(){}
    protected initSupplyItem() { 
        let supplyItemIDs:GameCurrencyType[] = [
            GameCurrencyType.TrainFood,
            GameCurrencyType.TrainSoap,
            GameCurrencyType.TrainEyepatch,
            GameCurrencyType.TrainHoe,
        ];

        for (let index = 0; index < this.rewardItems.length; index++) {
            let item = this.rewardItems[index];
            item.setItemID(supplyItemIDs[index]);
        }

        //
        let IT:Object = this._report['IT'];
        //
        for (let key in this.rewardItems) {
            let supplyItem = this.rewardItems[key];

            let itemInfo = ItemInfoManager.getInstance().getItemInfoFromItemID(supplyItem.getItemID());
            if (itemInfo) {
                supplyItem.nameLabel.string = itemInfo.localization;
            }
            
            if(IT.hasOwnProperty(supplyItem.getItemID())){
                let itemNum = IT[supplyItem.getItemID()];
                if(itemNum){
                    supplyItem.numLabel.string = itemNum.toString();
                }
            }
        }
        // 
        if(IT[GameCurrencyType.GoldCoin]){
            let goldNum = IT[GameCurrencyType.GoldCoin];
            this.goldLabel.string = goldNum.toString();
        }

        if(IT[GameCurrencyType.LifeAddOnePoint]){
            let lifeNum = IT[GameCurrencyType.LifeAddOnePoint];
            this.lifeLabel.string = lifeNum.toString();
        }
    }
    protected initButtons(){
        this.rewardButton.on('touchend', this.onConfirmButtonClicked, this);
        this.cancelButton.on('touchend', this.onCancelButtonClicked, this);
    }
    protected popUpGiftBox(){}
    protected onConfirmButtonClicked(event:cc.Event.EventCustom){}
    protected onCancelButtonClicked(event:cc.Event.EventCustom){}
    
    protected onShareSuccess(){}
    protected onShareFail(){}

    protected onWatchVideoSuccess(){}
    protected onWatchVideoFail(){}
    
    protected reportResult(endType:MatchGameEndType, rewardType:MatchGameEndRewardType, hasExtraReward:boolean):void{
        this._gameScene.reportResult(endType, rewardType, hasExtraReward, this.onReportResultRespone, this);
    }

    protected onReportResultRespone(data):void{

    }

    //
    protected setRankView():void{

    }
    //
    protected setFriendView():void{

    }

    //
    private _updateSubDomainFixFrameRate:number = 0;
    //
    protected updateSubDomainCanvas () {

    }

    //
    onOpen(param:Object){

        let canvas = cc.find("Canvas");
        if (canvas) {
            this._gameScene = canvas.getComponent(GameSceneBase);
        }

        let report = param['report'];

        this._report = report;
        this.playSoundEffect();
        this.initSupplyItem();
        this.recordBestScore();
        this.setRankView();
        this.initButtons();
        this.sendMarkID();
        this.popUpRearAchievementPanel();
        this.popUpGiftBox();
        this.showAdBanner();
    }
    //
    onAnimationDone(){

        let starCount = 0;
        if (this._report.hasOwnProperty('starCount')) {
            starCount = this._report['starCount'];
        }
        let score = 0;
        if (this._report.hasOwnProperty('totalScore')) {
            score = this._report['totalScore'];
        }
        let highscore = 0;
        if (this._report.hasOwnProperty('historyHighscore')) {
            highscore = this._report['historyHighscore'];
        }

        highscore = Math.max(highscore, score);
        this.starFrame.setStarCount(starCount);
        this.starFrame.setScore(score);
        this.starFrame.setHighscore(highscore);
        this.starFrame.appear();
    }

    //
    onClose(){

    }

    update () {
        this.updateSubDomainCanvas();
    }

    start () {

    }

    protected rewardFlyToBackpack():void{
        this.rewardButton.active = false;
        this.cancelButton.active = false;

        let endPos = this.backpack.node.position;
        let defTime = 0;

        this.backpack.node.runAction(
            cc.fadeTo(0.3,255)
        );

        let aniDisappearFun = (node,dTime)=>{
            //node.setCascadeOpacityEnabled(true);
            node.runAction(
                cc.sequence(
                    cc.delayTime(dTime),
                    cc.fadeTo(0.2,0)
                )
            );
        }

        let aniBackpackFun = (ico)=>{
            ico.runAction(
                cc.sequence(
                    cc.delayTime(defTime),
                    CurveToAction.create(0.6, ico.position, endPos),
                    cc.callFunc(()=>{
                        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('getitem'));
                        this.backpack.node.runAction(
                            cc.sequence(
                                cc.scaleTo(0.1,2.2),
                                cc.scaleTo(0.1,2)
                            )
                        );
                    }),
                    cc.removeSelf(true),
                )
            );
        }

        for (let key in this.rewardItems) {
            let supplyItem = this.rewardItems[key];
            let ico = cc.instantiate(supplyItem.ico.node);//
            this.node.addChild(ico);
            let startWorldPos = supplyItem.node.parent.convertToWorldSpaceAR(supplyItem.node.position);
            let startPos = this.node.convertToNodeSpaceAR(startWorldPos);
            ico.position = startPos;

            aniDisappearFun(supplyItem.node,defTime);
            aniBackpackFun(ico);
            defTime += 0.3;
        }

        {
            //
            aniDisappearFun(this.goldSprite.node,0.5);
            for(let i=0;i<5;i++){
                let ico = cc.instantiate(this.goldSprite.node);//
                this.node.addChild(ico);
                let startWorldPos = this.goldSprite.node.parent.convertToWorldSpaceAR(this.goldSprite.node.position);
                let startPos = this.node.convertToNodeSpaceAR(startWorldPos);
                ico.position = startPos;
                ico.opacity = 0;

                
                for (let key in ico.children) {
                    let child = ico.children[key];
                    child.active = false;
                }

                let acitons:cc.FiniteTimeAction[] = []
                let minRadius = Math.round(50 * 0.75);
                let maxRadius = Math.round(50 * 1.5);
                let radius = GameMath.randomInt(minRadius,maxRadius);
                let angle = GameMath.randomInt(0, 360);
                let radian =  angle / 180.0 * Math.PI;
                let jumpTargetPos = cc.v2(startPos.x + radius * Math.cos(radian), startPos.y + radius * Math.sin(radian));
                
                acitons.push(cc.delayTime(0.1 * i));
                acitons.push(cc.fadeTo(0,255));

                acitons.push(cc.spawn(cc.scaleTo(0.3, 1).easing(cc.easeBackOut()),
                                    cc.moveTo(0.3, jumpTargetPos).easing(cc.easeOut(2))));
                
                acitons.push(cc.delayTime(0.1));
                acitons.push(cc.spawn(CurveToAction.create(0.8, startPos, endPos),
                                    cc.scaleTo(0.6, 0.5)));
                acitons.push(cc.callFunc(()=>{
                    IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('coincollect'));
                    ico.removeFromParent();
                    ico.destroy();
                }));
        
                ico.runAction(cc.sequence(acitons));
            }
        }

        {
            //
            let ico = cc.instantiate(this.lifeSprite.node);//
            this.node.addChild(ico);
            let startWorldPos = this.lifeSprite.node.parent.convertToWorldSpaceAR(this.lifeSprite.node.position);
            let startPos = this.node.convertToNodeSpaceAR(startWorldPos);
            ico.position = startPos;
            ico.opacity = 0;

           

            for (let key in ico.children) {
                let child = ico.children[key];
                child.active = false;
            }

            aniDisappearFun(this.lifeSprite.node,0.3);
            ico.runAction(
                cc.sequence(
                    // cc.delayTime(defTime),
                    cc.fadeTo(0,255),
                    cc.spawn(CurveToAction.create(0.8, ico.position, endPos), cc.scaleTo(0.4,0.5)),
                    cc.removeSelf(true)
                )
            );
            defTime += 0.3;
        }

        defTime += 0.8;
        this.node.runAction(
            cc.sequence(
                cc.delayTime(defTime),
                cc.callFunc(()=>{
                    this.onRewardFlyToBackpackComplete();
                })
            )
        );
    }

    protected onRewardFlyToBackpackComplete(){
        
    }
}
