
import { EntityID, EntityColor } from "../../defines/EntityID";
import IdentifierManager from "../../defines/IdentifierManager";
import { KindID } from "../../defines/KindID";
import FloorBase from "./FloorBase";
import FloorRoad, { FloorRoadDirection } from "./FloorRoad";
import FloorIce from "./FloorIce";
import FloorGrass from "./FloorGrass";
import FloorChanger from "./FloorChanger";
import FloorDoubler from "./FloorDoubler";

export default class FloorFactory {

    public static generateFloorWithEntityID(grid, entityID:EntityID) : FloorBase {
        let floor:FloorBase = null;

        switch (entityID) {
            case EntityID.FloorIceHp1:
            case EntityID.FloorIceHp2:
            case EntityID.FloorIceHp3:
            case EntityID.FloorIceHp4:{
                let color = entityID - EntityID.FloorIceHp1 + 1;
                floor = FloorIce.create(grid, color);
            }
                break;
                
            case EntityID.FloorRoadLineVU:{
                floor = FloorRoad.create(grid, FloorRoadDirection.Down, FloorRoadDirection.Up);
            }
                break;
            case EntityID.FloorRoadLineHR:{
                floor = FloorRoad.create(grid, FloorRoadDirection.Left, FloorRoadDirection.Right);
            }
                break;
            case EntityID.FloorRoadLineVD:{
                floor = FloorRoad.create(grid, FloorRoadDirection.Up, FloorRoadDirection.Down);
            }
                break;
            case EntityID.FloorRoadLineHL:{
                floor = FloorRoad.create(grid, FloorRoadDirection.Right, FloorRoadDirection.Left);
            }
                break;
            case EntityID.FloorRoadTurnLdD:{
                floor = FloorRoad.create(grid, FloorRoadDirection.Left, FloorRoadDirection.Down);
            }
                break;
            case EntityID.FloorRoadTurnLuL:{
                floor = FloorRoad.create(grid, FloorRoadDirection.Up, FloorRoadDirection.Left);
            }
                break;
            case EntityID.FloorRoadTurnRuU:{
                floor = FloorRoad.create(grid, FloorRoadDirection.Right, FloorRoadDirection.Up);
            }
                break;
            case EntityID.FloorRoadTurnRdR:{
                floor = FloorRoad.create(grid, FloorRoadDirection.Down, FloorRoadDirection.Right);
            }
                break;
            case EntityID.FloorRoadTurnRdD:{
                floor = FloorRoad.create(grid, FloorRoadDirection.Right, FloorRoadDirection.Down);
            }
                break;
            case EntityID.FloorRoadTurnRuR:{
                floor = FloorRoad.create(grid, FloorRoadDirection.Up, FloorRoadDirection.Right);
            }
                break;
            case EntityID.FloorRoadTurnLuU:{
                floor = FloorRoad.create(grid, FloorRoadDirection.Left, FloorRoadDirection.Up);
            }
                break;
            case EntityID.FloorRoadTurnLdL:{
                floor = FloorRoad.create(grid, FloorRoadDirection.Down, FloorRoadDirection.Left);
            }
                break;
            case EntityID.FloorChangerRed:
            case EntityID.FloorChangerGreen:
            case EntityID.FloorChangerBlue:
            case EntityID.FloorChangerYellow:
            case EntityID.FloorChangerOrange:
            case EntityID.FloorChangerPurple:{
                let color = entityID - EntityID.FloorChangerRed + EntityColor.Red;
                if(color >= EntityColor.Begin && color <= EntityColor.End){
                    floor = FloorChanger.create(grid, color as EntityColor);
                    if(floor){
                        floor.setEntityColor(color as EntityColor);
                    }
                }
            }
                break;
            
            case EntityID.FloorGrass:{
                floor = FloorGrass.create(grid);
            }
                break;
            case EntityID.FloorDouble:{
                floor = FloorDoubler.create(grid);
            }
                break;
            default:
                break;
        }

        return floor;
    }
    
    public static generateFloorWithKindID(grid, kindID:KindID) : FloorBase {
        let entityID = IdentifierManager.getInstance().getEntityIDFromKindID(kindID);
        return this.generateFloorWithEntityID(grid, entityID);
    }
}
