import { CollectionID } from "../../../defines/CollectionID";
import { EntityGroupID } from "../../../defines/EntityGroupID";
import { EntityCleanOption, EntityID, EntityColor, EntityCleanType } from "../../../defines/EntityID";
import IdentifierManager from "../../../defines/IdentifierManager";
import TMXConfigInfoKeys from "../../../defines/TMXConfigInfoKeys";
import GridBase from "../../../grid/GridBase";
import ItemBase, { ItemType } from "../ItemBase";
import EntityPrefabNodePool from "../../EntityPrefabNodePool";
import EntityBase from "../../EntityBase";


const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemMagicCrystal extends ItemBase {

    //
    private _curAttackCount:number = 0;
    //
    private _attrOutput:string = '';
    //
    private _attrNum:number = 1;
    //N
    private _attrMax:number = 1;
    //
    private _attrStatus:number = 0;

    //
    private _itemNode:cc.Node = null;

    public static create(grid:GridBase):ItemMagicCrystal{

        let className = 'ItemMagicCrystal';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemMagicCrystal';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemMagicCrystal;
        if (component) {
            component.init(grid); 
            return component;       
        }
        return null;
    }

    public clone() : ItemMagicCrystal {
        let item = ItemMagicCrystal.create(this.getGrid());
        item.initMagicItemImg(this._attrOutput);
        return item;
    }
  

    public init(grid:GridBase):void{
        super.initItem(grid, ItemType.MagicCrystal);
        this._itemNode = this._puppet.getChildByName('item_sprite');

        this._curAttackCount = 0;
        this._attrOutput = '';
        this._attrNum = 1;
        this._attrMax = 1;
        this._attrStatus = 0;
    }

    public getAttrNum():number{
        return this._attrNum;
    }

    public getEntityID():EntityID{
        return EntityID.ItemMagicCrystal;
    }

    public getCollectionID():CollectionID{
        return CollectionID.ItemMagicCrystal;
    }

    public getScore():number{
        return 50;
    }

    public dealWithConfigMap(configMap:Object):void{
        super.dealWithConfigMap(configMap);
        if(configMap[TMXConfigInfoKeys.CFG_MATIC_CRYSTAL_OUTPUT]){
            this._attrOutput = configMap[TMXConfigInfoKeys.CFG_MATIC_CRYSTAL_OUTPUT];
        }

        if(configMap[TMXConfigInfoKeys.CFG_MATIC_CRYSTAL_NUM]){
            this._attrNum = Number(configMap[TMXConfigInfoKeys.CFG_MATIC_CRYSTAL_NUM]);
        }

        if(configMap[TMXConfigInfoKeys.CFG_MATIC_CRYSTAL_MAX]){
            this._attrMax = Number(configMap[TMXConfigInfoKeys.CFG_MATIC_CRYSTAL_MAX]) - 1;
        }

        if(configMap[TMXConfigInfoKeys.CFG_MATIC_CRYSTAL_STATUS]){
            this._attrStatus = Number(configMap[TMXConfigInfoKeys.CFG_MATIC_CRYSTAL_STATUS]) - 1;
            this._curAttackCount = this._attrStatus;
            if(this._curAttackCount > this._attrMax){
                this._curAttackCount = this._attrMax;
                this._attrStatus = this._curAttackCount;
            }
        }else{
            this._curAttackCount = 0;
        }

        this.updateProgress();

        this.initMagicItemImg(this._attrOutput);
    }

    //id
    public getOutputEntityId():EntityID{
        let entityID = EntityID.None;

        entityID = IdentifierManager.getInstance().getEntityIDFromString(this._attrOutput);
        if(entityID != EntityID.None){
            return entityID;
        }

        let tmpGroupId:EntityGroupID = IdentifierManager.getInstance().getEntityGroupIDFromString(this._attrOutput);
        let groupIds = IdentifierManager.getInstance().getEntityIDsFromEntityGroupID(tmpGroupId);
        if(groupIds.length <= 0){
            return entityID;
        }

        let colorItemMap:Object = {};
        let dropMap = this.getGrid().getDropEntityMap();
        for(let i:number = 0; i < groupIds.length; i++){
            let NormalColorId:EntityID = IdentifierManager.getInstance().getCorrespondingNormlaEntityID(groupIds[i]);
            if(dropMap[NormalColorId]){
                colorItemMap[groupIds[i]] = dropMap[NormalColorId];
            }
        }

        let tempMap1:Object = {};
        let tempMap2:Object = {};
        entityID = this.getGrid().getRandomEntityID(colorItemMap, tempMap1, tempMap2);

        return entityID;
    }

    //
    public isCanOutput():boolean{

        if(this.getGrid().hasShowCompleteBanner()){
            return false;
        }

        if(this._curAttackCount >= this._attrMax){
            this._curAttackCount = 0;
            this.updateProgress();
            return true;
        }else{
            return false;
        }
    }

    //
    public initMagicItemImg(output:string):void{
        this._attrOutput = output;
        let imgPath:string = '';
        let entityID:EntityID = EntityID.None;
        entityID = IdentifierManager.getInstance().getEntityIDFromString(output);

        if(entityID != EntityID.None){
            imgPath = IdentifierManager.getInstance().getFrameNameFromEntityID(entityID);
        }

        if(imgPath == ''){
            let tmpGroupId:EntityGroupID = IdentifierManager.getInstance().getEntityGroupIDFromString(output);
            imgPath = IdentifierManager.getInstance().getFrameNameFromEntityGroupID(tmpGroupId);
        }

        if(imgPath != '' && this._itemNode){
            let itemNode:cc.Node = new cc.Node();
            let itemIcon:cc.Sprite = this._itemNode.getComponent(cc.Sprite);
            itemIcon.spriteFrame = EntityBase.getEntitySpriteFarme(imgPath);
        }
    }

    //
    public updateProgress():void{
        let progress:number = this._curAttackCount / this._attrMax;
        if(progress > 1.0){
            progress = 1.0;
        }
        let progressNode = this._puppet.getChildByName('magic_crystal_bottom');
        progressNode.scaleY = progress;
    }

    protected _canDrop():boolean{
        return false;
    }

    protected _canSwap():boolean{
        return false;
    }

    protected _canMatch():boolean{
        return false;
    }

    protected _canCleanByBomb(cleanColor:EntityColor):boolean{
        return true;
    }

    protected _canCleanByNeighbour(color:EntityColor):boolean{
        return true;
    }

    protected _converUnderForever():boolean{
        return true;
    }

    protected _onClean(cleanOption:EntityCleanOption):void{

        if (this._curAttackCount < this._attrMax) {
            let rate = 1
            if (cleanOption.cleanType == EntityCleanType.Terminate) {
                rate = this._attrMax - this._curAttackCount
                this._curAttackCount = this._attrMax;
            }
            this.getGrid().gainScoreWithEntity(this, rate);
            this._curAttackCount += 1;
            this.updateProgress();
        }
    }

    onDestroy() {
        super.onDestroy();
        this._attrOutput = null;
        this._itemNode = null;
    }
}
