import ItemBase, { ItemType } from "../ItemBase";
import GridBase from "../../../grid/GridBase";
import { CollectionID } from "../../../defines/CollectionID";
import { EntityID, EntityColor, EntityCleanOption, EntityCleanType } from "../../../defines/EntityID";
import TMXConfigInfoKeys from "../../../defines/TMXConfigInfoKeys";
import { EntityStateType } from "../../state/EntityStateBase";
import FlyerAnimation, { FlyerAnimationType } from "../../flyer/FlyerAnimation";
import IGame from "../../../../../base/IGame";
import IdentifierManager from "../../../defines/IdentifierManager";
import EntityPrefabNodePool from "../../EntityPrefabNodePool";


const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemFireWormBottle extends ItemBase {
    private _healthPoint:number = 0;

    private _fireWorms:cc.Node[] = [];
    public static create(grid:GridBase):ItemFireWormBottle{

        let className = 'ItemFireWormBottle';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemFireWormBottle';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemFireWormBottle;
        if (component) {
            component.init(grid); 
            return component;       
        }
        return null;
    }

    public clone() : ItemFireWormBottle {
        let bottle = ItemFireWormBottle.create(this.getGrid());
        bottle.setHealthPoint(this.getHealthPoint());
        return bottle;
    }

    public getCollectionID():CollectionID{
        return CollectionID.FlyerFireWorm;
    }

    public getEntityID():EntityID{
        return EntityID.ItemFireWormBottle;
    }

    public getScore():number{
        return 100;
    }

    public getButterflyPriority(color:EntityColor, layoutGrass:boolean):number{
        if(this.getGrid().isTargetCollectionID(CollectionID.FlyerFireWorm)){
            return 1;
        }
        return super.getButterflyPriority(color, layoutGrass);
    }

    public dealWithConfigMap(configMap:Object):void{
        super.dealWithConfigMap(configMap);
        if(configMap[TMXConfigInfoKeys.CFG_FIREWORM_BOTTLE_MAX]){
            this._healthPoint = configMap[TMXConfigInfoKeys.CFG_FIREWORM_BOTTLE_MAX];
        }

        this.setHealthPoint(this._healthPoint);
    }

    protected init(grid:GridBase){
        super.initItem(grid, ItemType.ItemFireWormBottle);
        for (let i = 0; i < 5; i++) {
            let key = 'FireWorm' + i;
            let node = this._puppet.getChildByName(key);
            if (node) {
                this._fireWorms.push(node);
            }
        }
    }

    protected _canDrop():boolean{
        return false;
    }

    protected _canSwap():boolean{
        return false;
    }

    protected _canMatch():boolean{
        return false;
    }

    protected _canCleanByBomb(cleanColor:EntityColor):boolean{
        return true;
    }

    protected _canCleanByNeighbour(color:EntityColor):boolean{
        return true;
    }

    protected _onClean(cleanOption:EntityCleanOption):void{
        let rate = 1;
        if (cleanOption.cleanType == EntityCleanType.Terminate) {
            rate = this.getHealthPoint();
            for (let i = this.getHealthPoint(); i > 1; i--) {
                this.onCollected();
            }
            this.setHealthPoint(1);
        }
        this.getGrid().gainScoreWithEntity(this, rate);
        if(this.getHealthPoint() <= 1){
            this.setVisible(false);
            this.progressDeathWish(cleanOption);
            this.selfDestory();

            let flyer = FlyerAnimation.createDieAnimation(this.getGrid(), this.getEntityID());
            this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('firewormfly'));
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('firewormbottle'));
        }else{
            this.onCollected();
            this.setHealthPoint(this.getHealthPoint() - 1);
            this.selfHurt();
            let flyer = FlyerAnimation.createAnimationWithType(this.getGrid(), FlyerAnimationType.CommonHurt);
            this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
            flyer.node.zIndex = this.getTopZOrder();
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('firewormfly'));
        }
    }

    protected getHealthPoint():number{
        return this._healthPoint;
    }

    protected setHealthPoint(val:number){
        this._healthPoint = val;
        this.updatePuppet();

        for (let i = 0; i < 5; i++) {
            let fireWorm = this._fireWorms[i];
            if (fireWorm) {
                fireWorm.active = (i < this._healthPoint)
            }
        }
    }

    onDestroy() {
        super.onDestroy();
        this._fireWorms = null;
    }
}
