/**
 * 
 */

import GameMath from "../../../../../extend/utils/GameMath";
import TMXJsonPhraser from "../../../../../extend/utils/TMXJsonPhraser";
import { EntityID, EntityDropStepType } from "../../../defines/EntityID";
import { KindID } from "../../../defines/KindID";
import TMXConfigInfoKeys from "../../../defines/TMXConfigInfoKeys";
import CellBase, { CellType } from "../../../entity/cell/CellBase";
import CellFactory from "../../../entity/cell/CellFactory";
import FenceFactory from "../../../entity/fence/FenceFactory";
import { FloorType } from "../../../entity/floor/FloorBase";
import FloorFactory from "../../../entity/floor/FloorFactory";
import FloorRoad from "../../../entity/floor/FloorRoad";
import FrameNormal from "../../../entity/frame/FrameNormal";
import ItemBase, { ItemType } from "../../../entity/item/ItemBase";
import ItemFactory from "../../../entity/item/ItemFactory";
import ItemBalloon from "../../../entity/item/special/ItemBalloon";
import MarkerNormal from "../../../entity/marker/MarkerNormal";
import ParasiteFactory from "../../../entity/parasite/ParasiteFactory";
import PopsicleFactory from "../../../entity/popsicle/PopsicleFactory";
import RugFactory from "../../../entity/rug/RugFactory";
import GridBase from "../../GridBase";
import { GridState } from "../GridStateBase";
import GridStateDrop from "./GridStateDrop";

export default class GridStateCreate extends GridStateDrop {

    public static createState(viewSize:cc.Size, mapData:Object) : GridStateCreate {
        return new GridStateCreate(viewSize, mapData);
    }
    constructor(viewSize:cc.Size, mapData:Object){
        super([], GridState.Create);
        this._viewSize = viewSize;
        this._tmxPhraser = TMXJsonPhraser.create(mapData);
        this._gridSize = this._tmxPhraser.getMapSize();
        this._cellSize = cc.size(75, 75);
    }
    private _viewSize:cc.Size = cc.Size.ZERO;
    private _tmxPhraser:TMXJsonPhraser = null;
    private _gridSize:cc.Size = cc.size(9, 10);
    private _cellSize:cc.Size = cc.size(75, 75);
    enter(grid:GridBase):void {
        grid.setGridSize(this._gridSize);
        grid.setCellSize(this._cellSize);
        let viewFixSize = cc.size(grid.getGridSize().width * grid.getCellSize().width, 
                                  grid.getGridSize().height * grid.getCellSize().height);
        grid.node.setContentSize(viewFixSize);

        let scale = GameMath.getScaleFromViewSize(this._viewSize, 
            cc.size(viewFixSize.width + grid.getCellSize().width * 0.5,
                    viewFixSize.height + grid.getCellSize().height * 0.5),
             0.5, 1);
        grid.node.setScale(scale);

        this.initCells(grid);
        this.initPopsicle(grid);
        this.initFloors(grid);
        this.initRugs(grid);
        this.initItems(grid);
        this.initFences(grid);
        this.initFrames(grid);

        this.markFences(grid);
        let shufflePositions = this.getShufflePositions();
        if (this.someItemCanDrop(grid)) {
            this.shuffleGrid(grid, shufflePositions, true, false, true);
        }else{
            this.shuffleGrid(grid, shufflePositions, true, true, true);
        }

        this.initParasites(grid);
        this.initConveyer(grid);
        this.initMarker(grid);
        this.initPath(grid);
    }
    public update(grid:GridBase, delta:number) {
    }
    public exit(grid:GridBase) {
        this._viewSize = null;
        this._tmxPhraser = null;
        this._gridSize = null;
        this._cellSize = null;
    }
    private initCells(grid:GridBase) : void {
        let cellTiles = this._tmxPhraser.getTileLayerWithName(TMXConfigInfoKeys.CELLS_LAYER_KEY);
        if (!cellTiles || cellTiles.length <= 0) {
            return;
        }
        let conveyersTiles = this._tmxPhraser.getTileLayerWithName(TMXConfigInfoKeys.CONVEYERS_LAYER_KEY);
        let mapSize = this._tmxPhraser.getMapSize();
        let tileSize = this._tmxPhraser.getTileSize();

        for (let column = 0; column < mapSize.width; column++) {
            let foundTopCell = false;
            for (let row = 0; row < mapSize.height; row++) {
                let cellGid = cellTiles[row * mapSize.width + column];
                let conveyerGid = conveyersTiles[row * mapSize.width + column];
                if (conveyerGid != 0) {
                    cellGid = conveyerGid;
                }

                let propertie = this._tmxPhraser.getPropertieFromGID(cellGid);
                if (propertie) {
                    let kindID = propertie['Kind'];
                    let cell = CellFactory.generateCellWithKindID(grid, kindID);
                    if (cell) {
                        let cellConfigMap = this.getConfigMap(TMXConfigInfoKeys.CELLS_LAYER_KEY, cc.v2(column, row));
                        let conveyerConfigMap = this.getConfigMap(TMXConfigInfoKeys.CONVEYERS_LAYER_KEY, cc.v2(column, row));

                        if (!GameMath.isEmptyObject(conveyerConfigMap)) {
                            for (let key in conveyerConfigMap) {
                                cellConfigMap[key] = conveyerConfigMap[key];
                            }
                            cellConfigMap[TMXConfigInfoKeys.CFG_CONVEYER_POSTAL_KEY_TYPE] = true;
                        }
                        

                        if (foundTopCell == false) {
                            cellConfigMap[TMXConfigInfoKeys.CFG_DROP_KEY_IS_TOP] = true;
                            foundTopCell = true;
                        }

                        grid.setCellAtLogicalPos(cell, cc.v2(column, mapSize.height - 1 - row));
                        cell.dealWithConfigMap(cellConfigMap);
                    }
                }
            }
        }
    
        let markerInfos = this._tmxPhraser.getObjectLayerWithName(TMXConfigInfoKeys.MARKS_LAYER_KEY);
        for (let info of markerInfos) {
            let valueMap = this._tmxPhraser.getPropertieFromGID(info.gid);

            if (!GameMath.isEmptyObject(valueMap)) {
                let centerPixelPos = cc.v2(info.rect.center.x, mapSize.height * tileSize.height - info.rect.center.y);
                let centerLogicalPos = cc.v2(Math.floor(centerPixelPos.x / tileSize.width), Math.floor(centerPixelPos.y / tileSize.height));

                let kindID = valueMap['Kind'];
                if (kindID) {
                    switch (kindID) {
                        case KindID.MarkWormholeEntrance:{
                            let entranceConfigMap = this.getConfigMap(TMXConfigInfoKeys.MARKS_LAYER_KEY, 
                                                                      cc.v2(centerLogicalPos.x, mapSize.height - 1 - centerLogicalPos.y));
                            entranceConfigMap[TMXConfigInfoKeys.CFG_WORMHOLE_KEY_TYPE] = TMXConfigInfoKeys.CFG_WORMHOLE_VALUE_ENTRANCE;
                            let entrance = grid.getCellAtLogicalPos(centerLogicalPos);
                            if (entrance) {
                                entrance.dealWithConfigMap(entranceConfigMap);
                            }

                            let exitConfigMap:Object = {};
                            exitConfigMap[TMXConfigInfoKeys.CFG_WORMHOLE_KEY_TYPE] = TMXConfigInfoKeys.CFG_WORMHOLE_VALUE_EXIT;
                            exitConfigMap[TMXConfigInfoKeys.CFG_WORMHOLE_POSTAL_KEY_TYPE] = GameMath.getConfigStringFromVec2(centerLogicalPos);
                            let postalPos = entranceConfigMap[TMXConfigInfoKeys.CFG_WORMHOLE_POSTAL_KEY_TYPE];

                            let pos = GameMath.getVec2FromConfigString(postalPos);
                            let exit = grid.getCellAtLogicalPos(pos);
                            if (exit) {
                                exit.dealWithConfigMap(exitConfigMap);
                            }
                        }
                            break;
                    
                        default:
                            break;
                    }
                }

            }
        }
    }
    private initPopsicle(grid:GridBase) : void {

        let layerKey = TMXConfigInfoKeys.POPSICLES_LAYER_KEY;
        let objectInfos = this._tmxPhraser.getObjectLayerWithName(layerKey);
        if (!objectInfos || objectInfos.length <= 0) {
            return;
        }
        let mapSize = this._tmxPhraser.getMapSize();
        let tileSize = this._tmxPhraser.getTileSize();

        for (let info of objectInfos) {
            let width = Math.ceil(info.rect.size.width / tileSize.width);
            let height = Math.ceil(info.rect.size.height / tileSize.height);

            let x = Math.floor(info.rect.origin.x / tileSize.width);
            let y = Math.floor((mapSize.height - 1) - info.rect.origin.y / tileSize.height - height + 1);
            let typeString = info.properties[TMXConfigInfoKeys.CFG_POPSICLE_KEY_TYPE];

            let kindID = KindID.PopsicleNormal;
            if (typeString) {
                if (typeString == TMXConfigInfoKeys.CFG_POPSICLE_VALUE_LINE) {
                    kindID = KindID.PopsicleLine;
                }else if(typeString == TMXConfigInfoKeys.CFG_POPSICLE_VALUE_BOOM) {
                    kindID = KindID.PopsicleBoom;
                }
            }

            let popsicle = PopsicleFactory.generatePopsicleWithKindID(grid, width, height, kindID);
            if (popsicle) {
                grid.setPopsicleAtLogicalPos(popsicle, cc.v2(x, y));
            }
        }
    }
    private initFloors(grid:GridBase) : void {
        let layerKey = TMXConfigInfoKeys.FLOORS_LAYER_KEY;
        let tiles = this._tmxPhraser.getTileLayerWithName(layerKey);
        if (!tiles || tiles.length <= 0) {
            return;
        }
        let mapSize = this._tmxPhraser.getMapSize();
        let needGrass = false;

        for (let row = 0; row < mapSize.height; row++) {
            for (let column = 0; column < mapSize.width; column++) {
                let gid = tiles[row * mapSize.width + column];
                let valueMap = this._tmxPhraser.getPropertieFromGID(gid);
                if (!GameMath.isEmptyObject(valueMap)) {
                    let kindID = valueMap['Kind'];
                    if (kindID) {
                        let floor = FloorFactory.generateFloorWithKindID(grid,kindID);    
                        if (floor) {
                            let configMap = this.getConfigMap(layerKey, cc.v2(column, row));
                            grid.setFloorAtLogicalPos(floor, cc.v2(column, mapSize.height - 1 - row));
                            floor.dealWithConfigMap(configMap);
                            if (floor.getEntityID() == EntityID.FloorGrass) {
                                needGrass = true;
                            }
                        }
                    }
                }
            }
        }

        if (needGrass) {
            let cellsMap = grid.getCellsMap();
            for (let key in cellsMap) {
                let cell = cellsMap[key] as CellBase;
                cell.setNeedGrass(true);    
            }
        }
    }
    private initRugs(grid:GridBase) {

        let layerKey = TMXConfigInfoKeys.JAM_LAYER_KEY;
        let tiles = this._tmxPhraser.getTileLayerWithName(layerKey);
        if (!tiles || tiles.length <= 0) {
            return;
        }
        
        let mapSize = this._tmxPhraser.getMapSize();

        for (let row = 0; row < mapSize.height; row++) {
            for (let column = 0; column < mapSize.width; column++) {
                let gid = tiles[row * mapSize.width + column];
                let valueMap = this._tmxPhraser.getPropertieFromGID(gid);
                if (!GameMath.isEmptyObject(valueMap)) {
                    let kindID = valueMap['Kind'];
                    if (kindID) {
                        let rug = RugFactory.generatePolishWithKindID(grid, kindID);
                        if (rug) {
                            grid.setRugAtLogicalPos(rug, cc.v2(column, mapSize.height - 1 - row));
                            let configMap = this.getConfigMap(layerKey, cc.v2(column, row));
                            rug.dealWithConfigMap(configMap);
                        }    
                    }
                }
            }
        }
    }
    private initItems(grid:GridBase) : void {

        let layerKey = TMXConfigInfoKeys.ITEMS_LAYER_KEY;
        let tiles = this._tmxPhraser.getTileLayerWithName(layerKey);
        if (!tiles || tiles.length <= 0) {
            return;
        }
        let mapSize = this._tmxPhraser.getMapSize();

        let totalRandomCount = 0; 
        for (let row = 0; row < mapSize.height; row++) {
            for (let column = 0; column < mapSize.width; column++) {
                let gid = tiles[row * mapSize.width + column];
                let valueMap = this._tmxPhraser.getPropertieFromGID(gid);
                if (!GameMath.isEmptyObject(valueMap)) {
                    let cell = grid.getCellAtLogicalPos(cc.v2(column, mapSize.height - 1 - row));
                    if (cell && cell.canHoldItem()) {
                        let kindID = valueMap['Kind'];
                        if (kindID && kindID != KindID.ItemNormalRandom) {
                            let item = ItemFactory.generateItemWithKindID(grid, kindID);
                            if (item) {
                                grid.setItemAtLogicalPos(item, cc.v2(column, mapSize.height - 1 - row));
                                let configMap = this.getConfigMap(layerKey, cc.v2(column, row));
                                item.dealWithConfigMap(configMap);
                            }
                        }else{
                            totalRandomCount += 1
                        }
                    }
                }
            }
        }
        
        for (let row = 0; row < mapSize.height; row++) {
            for (let column = 0; column < mapSize.width; column++) {
                let gid = tiles[row * mapSize.width + column];
                let valueMap = this._tmxPhraser.getPropertieFromGID(gid);
                if(!GameMath.isEmptyObject(valueMap)) {
                    let cell = grid.getCellAtLogicalPos(cc.v2(column, mapSize.height - 1 - row));
                    if (cell && cell.canHoldItem()) {
                        let kindID = valueMap['Kind'];
                        if (kindID && kindID == KindID.ItemNormalRandom) {
                            let item = ItemFactory.generateItemWithKindID(grid, kindID);
                            if (item) {
                                grid.setItemAtLogicalPos(item, cc.v2(column, mapSize.height - 1 - row));
                                let configMap = this.getConfigMap(layerKey, cc.v2(column, row));
                                item.dealWithConfigMap(configMap);
                            }
                        }
                    }
                }
            }
        }
    }
    private initFences(grid:GridBase) : void {
        let fenceInfos = this._tmxPhraser.getObjectLayerWithName(TMXConfigInfoKeys.MARKS_LAYER_KEY);
        if (!fenceInfos || fenceInfos.length <= 0) {
            return;
        }
        let mapSize = this._tmxPhraser.getMapSize();
        let tileSize = this._tmxPhraser.getTileSize();
        
        for (let info of fenceInfos) {
            let valueMap = this._tmxPhraser.getPropertieFromGID(info.gid);
            if (!GameMath.isEmptyObject(valueMap)) {
                let centerPixelPos = cc.v2(info.rect.center.x, mapSize.height * tileSize.height - info.rect.center.y);
                let centerLogicalPos = cc.v2(Math.floor(centerPixelPos.x / tileSize.width), Math.floor(centerPixelPos.y / tileSize.height));
                let kindID = valueMap['Kind'];
                let fence = FenceFactory.generateItemWithKindID(grid, centerLogicalPos, kindID);
                if (fence) {
                    grid.setFenceAtFencePos(fence, fence.getFencePos());
                }
            }
        }
    }
    private initParasites(grid:GridBase) :void {
        let layerKey = TMXConfigInfoKeys.PARASITES_LAYER_KEY;
        let tiles = this._tmxPhraser.getTileLayerWithName(layerKey);
        if (!tiles || tiles.length <= 0) {
            return;
        }
        let mapSize = this._tmxPhraser.getMapSize();
        
        for (let row = 0; row < mapSize.height; row++) {
            for (let column = 0; column < mapSize.width; column++) {
                let gid = tiles[row * mapSize.width + column];
                let valueMap = this._tmxPhraser.getPropertieFromGID(gid);
                if (!GameMath.isEmptyObject(valueMap)) {
                    let kindID = valueMap['Kind'];
                    let parasite = ParasiteFactory.generateParasiteWithKindID(grid, kindID);
                    if (parasite) {
                        let configMap = this.getConfigMap(layerKey, cc.v2(column, row));
                        parasite.dealWithConfigMap(configMap);
                        grid.setParasiteAtLogicalPos(parasite, cc.v2(column, mapSize.height - 1 - row));
                    }
                }
            }
            
        }
    }
    private initFrames(grid:GridBase) : void {

        for (let row = -1; row < this._gridSize.height + 1; row++) {
            for (let column = -1; column < this._gridSize.width + 1; column++) {
                let checkPos = cc.v2(column, row);
                
                let needFrame = false;
                let cell = grid.getCellAtLogicalPos(checkPos);
                if (cell) {
                    if (cell.getCellType() == CellType.MovablePlate) {
                        needFrame = true;    
                    }
                }else{
                    needFrame = true;
                }
                if (needFrame) {
                    let frame = FrameNormal.create(grid);    
                    if (frame) {
                        grid.setFrameAtLogicalPos(frame,checkPos);
                    }
                }
            }
        }
    }
    private initConveyer(grid:GridBase) : void {
        let cellsMap = grid.getCellsMap();
        let cropPositions:cc.Vec2[] = [];
        for (let key in cellsMap) {
            let cell = cellsMap[key] as CellBase;
            if (cell.getCellType() == CellType.Conveyer) {
                cropPositions.push(cell.getLogicalPos());
            }
        }
        grid.setConveyerLayerVisiblePos(cropPositions);
    }
    private initMarker(grid:GridBase) : void {
        let fenceInfos = this._tmxPhraser.getObjectLayerWithName(TMXConfigInfoKeys.MARKS_LAYER_KEY);
        if (!fenceInfos) {
            return;
        }
        let mapSize = this._tmxPhraser.getMapSize();
        let tileSize = this._tmxPhraser.getTileSize();

        for (let info of fenceInfos) {
            let valueMap = this._tmxPhraser.getPropertieFromGID(info.gid);
            if (!GameMath.isEmptyObject(valueMap)) {
                let centerPixelPos = cc.v2(info.rect.center.x, mapSize.height * tileSize.height - info.rect.center.y);
                let centerLogicalPos = cc.v2(Math.floor(centerPixelPos.x / tileSize.width), Math.floor(centerPixelPos.y / tileSize.height));

                let marker = grid.getMarkerAtLogicalPos(centerLogicalPos);
                if (!marker) {
                    marker = MarkerNormal.create(grid);
                    grid.setMarkerAtLogicalPos(marker, centerLogicalPos);
                }

                let configMap = this.getConfigMap(TMXConfigInfoKeys.MARKS_LAYER_KEY, cc.v2(centerLogicalPos.x, mapSize.height - 1 - centerLogicalPos.y));
                
                let kindID = valueMap['Kind'];
                if (kindID) {
                    switch (kindID) {
                        case KindID.MarkBoneCollector:{
                            configMap[TMXConfigInfoKeys.CFG_MARK_BONE_COLLECTOR_KEY_TYPE] = 
                                TMXConfigInfoKeys.CFG_MARK_BONE_COLLECTOR_VALUE_DEFAULT;
                        }
                            break;
                        case KindID.MarkWormholeEntrance:{
                            configMap[TMXConfigInfoKeys.CFG_WORM_HOLE_ENTRANCE_KEY_TYPE] = 
                                TMXConfigInfoKeys.CFG_WORM_HOLE_ENTRANCE_VALUE_DEFAULT;
                        }   
                            break;
                        case KindID.MarkWormholeExit:{
                            configMap[TMXConfigInfoKeys.CFG_WORM_HOLE_EXIT_KEY_TYPE] = 
                                TMXConfigInfoKeys.CFG_WORM_HOLE_EXIT_VALUE_DEFAULT;
                        }   
                            break;
                    
                        default:
                            break;
                    }
                }
                marker.dealWithConfigMap(configMap);
            }
        }
    }
    private initPath(grid:GridBase) : void {

        let startPositions:cc.Vec2[] = [];
        let itemsMap = grid.getItemsMap();
        for (let key in itemsMap) {
            let item = itemsMap[key] as ItemBase;
            if (item.getItemType() == ItemType.Puppy) {
                startPositions.push(item.getLogicalPos());
            }else if (item.getItemType() == ItemType.DogHouse) {
                startPositions.push(item.getLogicalPos());
            }else if (item.getItemType() == ItemType.Balloon) {
                startPositions.push(item.getLogicalPos());
                let ballon = item as ItemBalloon;
                if (ballon) {
                    let flyPos = ballon.getCurPostal();
                    startPositions.push(flyPos);
                }
            }
        }

        for (let pos of startPositions) {
            let floor = grid.getFloorAtLogicalPos(pos);
            if (floor && floor.getFloorType() == FloorType.Road) {
                let road = floor as FloorRoad;
                if (road) {
                    road.setIsStartPoint(true);
                }
            }
        }
    }
    private getShufflePositions() : cc.Vec2[] {
        let result:cc.Vec2[] = [];
        let items = this._tmxPhraser.getTileLayerWithName(TMXConfigInfoKeys.ITEMS_LAYER_KEY);

        for (let row = 0; row < this._gridSize.height; row++) {
            for (let column = 0; column < this._gridSize.width; column++) {
                let gid = items[row * this._gridSize.width + column];
                let valueMap = this._tmxPhraser.getPropertieFromGID(gid);
                if (!GameMath.isEmptyObject(valueMap)) {
                    let kindID = valueMap['Kind'];
                    if (kindID && ItemFactory.isShuffleKindID(kindID)) {
                        result.push(cc.v2(column, this._gridSize.height - 1 - row));
                    }    
                }
            }
        }
        return result;
    }
    private someItemCanDrop(grid:GridBase) : boolean {
        
        let itemsMap = grid.getItemsMap();
        for (let key in itemsMap) {
            let item = itemsMap[key];
            let result = this.tryDropItem(grid, item);
            if (result.type != EntityDropStepType.Stay) {
                return true;
            }
            result = this.tryDiagonalItem(grid, item);
            if (result.type != EntityDropStepType.Stay) {
                return true;
            }
        }

        let cellsMap = grid.getCellsMap();
        for (let key in cellsMap) {
            let cell = cellsMap[key];
            let bornPos = cell.getLogicalPos();
            if (grid.getItemAtLogicalPos(bornPos) == null) {
                return true;
            }
        }
        return false;
    }
    private getConfigMap(layerName:string, pos:cc.Vec2) : Object {

        let objectInfos = this._tmxPhraser.getObjectInfos(layerName, pos);
        let tileSize = this._tmxPhraser.getTileSize();
        let mapSize = this._tmxPhraser.getMapSize();
        
        let configMap:Object = {};
        
        for (let info of objectInfos) {
            for (const key in info.properties) {
                configMap[key] = info.properties[key];
            }

            if (info.path.length > 0) {
                let path:cc.Vec2[] = [];
                let count = info.path.length;

                for (let i = 0; i < count; i++) {
                    let pos = info.path[i];
                    pos.x = Math.floor(pos.x / tileSize.width);
                    pos.y = (mapSize.height - 1) - Math.floor(pos.y / tileSize.height);
                    path.push(pos);
                }
                configMap[TMXConfigInfoKeys.CFG_MOVABLE_PLATE_PATH_KEY_TYPE] = path;
            }
        }

        if (layerName == TMXConfigInfoKeys.CELLS_LAYER_KEY) {
            let objectInfos = this._tmxPhraser.getObjectInfos(TMXConfigInfoKeys.MARKS_LAYER_KEY, pos);
            for (let info of objectInfos) {
                let valueMap = this._tmxPhraser.getPropertieFromGID(info.gid);
                let kindID = valueMap['Kind'];
                if (kindID && kindID == KindID.MarkBoneCollector) {
                    configMap[TMXConfigInfoKeys.CFG_MARK_BONE_COLLECTOR_KEY_TYPE] = TMXConfigInfoKeys.CFG_MARK_BONE_COLLECTOR_VALUE_DEFAULT;
                }
            }
        }
        return configMap;
    }
    
}
