export class EventCollectTargets{
    public _collections:CollectionID[] = [];

    public static createWithData(data:Object){
        let targets = [];
        if (data && data.hasOwnProperty('TTID')) {
            targets = data['TTID'];
        }
        return EventCollectTargets.create(targets);
    }

    public static create(targets:number[]):EventCollectTargets{
        let result = new EventCollectTargets();
        result.setTargets(targets);
        return result;
    }

    public setTargets(targets:number[]){
        this._collections.length = 0;
        for (let target of targets) {
            this._collections.push(target);
        }
    }

    public getTargets():CollectionID[]{
        return this._collections
    }
}

