    //
    public static getLevelTypeByLevel(levelNum:number):GameType{
        let type = GameType.None;
        let cfgTripleLevelObj = CFGTripleLevel.getInstance().getCongfigByLevelNum(levelNum);
        if(cfgTripleLevelObj){
            type = cfgTripleLevelObj.LevelType;
        }
        return type;
    }

    //
    public static getMatchCfgByLevel(levelNum:number):Object{

        let retObj:Object = {};
        let cfgTripleLevelObj = CFGTripleLevel.getInstance().getCongfigByLevelNum(levelNum);
        if(!cfgTripleLevelObj){
            return retObj;
        }

        for(let key in cfgTripleLevelObj){
            retObj[key] = cfgTripleLevelObj[key];
        }

        let config = null;
        if(cfgTripleLevelObj.LevelType == GameType.Order){
            config = new CFGTripleLevelOrder();
        }else if(cfgTripleLevelObj.LevelType == GameType.Grass){
            config = new CFGTripleLevelGrass();
        }else if(cfgTripleLevelObj.LevelType == GameType.Popsicle){
            config = new CFGTripleLevelPopsicles();
        }else if(cfgTripleLevelObj.LevelType == GameType.Path){
            config = new CFGTripleLevelPath();
        }
        if(config){
            let configObj = config.getCongfigByLevelID(cfgTripleLevelObj.LevelID);

            for(let key in configObj){
                retObj[key] = configObj[key];
            }
        }
        return retObj;
    }

