// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html


export enum FlyerAnimationType{
    Bomb1x1,
    CommonHurt,
    FocusFrame,
    HighlightedCell,
    ItemMarked,
    BombCross,
}; 

const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerAnimation extends FlyerBase {

    private _autoRemoveOnFinish:boolean = false;
    public getAutoRemoveOnFinish():boolean {
        return this._autoRemoveOnFinish;
    }
    public setAutoRemoveOnFinish(auto:boolean) : void {
        this._autoRemoveOnFinish = auto;
    }

    public _entityID:EntityID = EntityID.None;
    public getEntityID() : EntityID {
        return this._entityID;
    }

    public launch() {
        let state = this.playAnimation('default');
        if (this._autoRemoveOnFinish) {
            if (state) {
                let action = cc.sequence(cc.delayTime(state.duration), cc.callFunc(()=>{
                    this.getGrid().removeObstructs(this);
                    this.destoryWithRecycle(true);
                }));
                this.runAction(action);
            }else{
                this.getGrid().removeObstructs(this);
                this.destoryWithRecycle(true);
            }
        }
    }

    public init(grid:GridBase,
                entityID:EntityID, 
                autoRemove:boolean){
        this._entityID = entityID;
        super.initFlyer(grid, FlyerType.Animation);
        this.setAutoRemoveOnFinish(autoRemove);
    }

    public static create(grid:GridBase, 
                         entityID:EntityID,
                         filePatth:string, 
                         autoRemove:boolean = true):FlyerAnimation{
        let className = 'FlyerAnimation';
        let prefabFileName = filePatth;
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerAnimation;
        if (component) {
            component.init(grid, entityID, autoRemove); 
            return component;       
        }
        return null;
    }

    public static createDieAnimation(grid:GridBase, entityID:EntityID, autoRemove:boolean = true) : FlyerAnimation {

        let info = IdentifierManager.getInstance().getHurtAnimationInfoPathFromEntityID(entityID);
        if (info) {
            return this.create(grid, info.entityID, info.fileName, autoRemove);
        }
        return null;
    }

    public static createAnimationWithType(grid:GridBase, type:FlyerAnimationType, autoRemove:boolean = true) : FlyerAnimation {

        let path:string = null;
        let entityID:EntityID = EntityID.None;
        switch (type) {
            case FlyerAnimationType.Bomb1x1:
                path = 'match_scene/prefab/entity/animation/AnimationBomb1x1';
                entityID = EntityID.FlyerAnimationBomb1x1;
                break;
            case FlyerAnimationType.CommonHurt:
                path = 'match_scene/prefab/entity/animation/AnimationCommonHurt';
                entityID = EntityID.FlyerAnimationCommonHurt;
                break;
            case FlyerAnimationType.FocusFrame:
                path = 'match_scene/prefab/entity/animation/AnimationFocusFrame';
                entityID = EntityID.FlyerAnimationFocusFrame;
                break;
            case FlyerAnimationType.HighlightedCell:
                path = 'match_scene/prefab/entity/animation/AnimationHighlightedCell';
                entityID = EntityID.FlyerAnimationHighlightedCell;
                break;
            case FlyerAnimationType.ItemMarked:
            
                path = 'match_scene/prefab/entity/animation/AnimationItemMarked';
                entityID = EntityID.FlyerAnimationItemMarked;
                break;
            case FlyerAnimationType.BombCross:
                path = 'match_scene/prefab/entity/animation/AnimationFireworkBoom';
                entityID = EntityID.FlyerAnimationBombCross;
                break;
            default:
                break;
        }

        if (path) {
            return this.create(grid, entityID, path, autoRemove);
        }
        return null;
    }

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}
}
