const {ccclass, property} = cc._decorator;

@ccclass
export default class GameStalematePanel extends cc.Component {

    private _report:Object = null;
    private _gameScene:GameSceneBase = null;
    onOpen(param:Object){
        this._report = param;
        let canvas = cc.find("Canvas");
        if (canvas) {
            this._gameScene = canvas.getComponent(GameSceneBase);
        }
    }

    onClose() {
        this._report = null;
        this._gameScene = null;
    }

    onCloseBtnClick(){
        this._gameScene.stalemateGame();
    }
}
