export class AudioInfo{

    public MusicId:number = -1;
    public Music:string = "";
    public Volume:number = 0;
    public Loop:boolean = false;

}

/**
 * Created by Goku on 2018/7/26.
 * 
 */
export default class AudioManager {

    //
    private static instance:AudioManager = null;
    
    private _AudioBGM:AudioInfo = null;    //
    private _AUdioRunTime:any = {};        //

    

    /**
     * @Content:
     * @Param 
     *      MusicId  :ID
     *      Volume   :
     *      loop     :(true:)
     *      bgm      :
     *      succFunc :
     */
    public play( Music:string, Volume:number = 1, loop:boolean = false, bgm:boolean = false, succFunc:Function = null){

        let Mid:number = 0;
        
        let MusicUrl:string = cc.loader.getRes( Music );    //
        if( !MusicUrl ){

            cc.loader.loadRes( Music, function( err, res ){

                if(!err){
                    //console.log("start"+err+ " ||| "+res);
                    MusicUrl = cc.loader.getRes( Music );
                    //cc.log ( "MusicUrl"+MusicUrl );
                    Mid = this._playAudio(MusicUrl, Volume, loop, bgm);

                    if(succFunc){
                        succFunc(Mid);
                    }
                }

            }.bind(this) );
        }else{
            Mid = this._playAudio(MusicUrl, Volume, loop, bgm);
            if(succFunc){
                succFunc(Mid);
            }
        }

    }

    private _playAudio(musicUrl:string, volume:number = 1, loop:boolean = false,bgm:boolean):number{

        let Mid:number = 0;

        if( bgm ){ //
            // if( this._AudioBGM.Music != Music ){//BGM
                 this._shopBGM();
                 Mid = cc.audioEngine.play( musicUrl,loop,volume  );
                 this._AudioBGM.MusicId = Mid;
                 this._AudioBGM.Volume = volume;
                 this._AudioBGM.Music = musicUrl;
                 this._AudioBGM.Loop = loop;
             //}
             
         }else{ //
 
             if( !this._checkPlay( musicUrl ) ) { //
                 //f
                 return Mid;
             }
             Mid = cc.audioEngine.play( musicUrl,loop,volume  );
         }

        return Mid;
    }

    public stopBGM() {
        this._shopBGM();
    }

    /**
     * @Content:
     * @Param 
     */
    private _shopBGM():boolean{

        //cc.log( "Music:"+this._AudioBGM.Music+"    MusicId:"+this._AudioBGM.MusicId );

        if( this._AudioBGM.MusicId < 0 ){ //()
            return false;
        }
        
        this.stop(this._AudioBGM.MusicId);
        this._AudioBGM.MusicId = -1;
        this._AudioBGM.Music = "";
        this._AudioBGM.Loop = false;
        this._AudioBGM.Volume = 0;
        
        return true;

    }
    
    /**
     * 
     */
    public resumeBGM():boolean{
        if( this._AudioBGM.MusicId < 0 ){
            return false;
        }

        cc.audioEngine.resume( this._AudioBGM.MusicId );
    }

    /**
     * @Content:
     * @Param 
     *      MusicId  :ID
     *      Volume   :
     */
    public setLoop( MusicId:number, Loop:boolean ):boolean{
        
        cc.audioEngine.setLoop( MusicId, Loop );
        
        return true;

    }


    /**
     * @Content:
     * @Param 
     *      MusicId  :ID
     *      Volume   :
     */
    public setVolume( MusicId:number, Volume:number ):boolean{
        
        cc.audioEngine.setVolume( MusicId, Volume );

        return true;

    }


    /**
     * @Content:
     * @Param 
     *      MusicId  :ID
     */
    public stop( MusicId:number ):boolean{

        cc.audioEngine.stop( MusicId );  //ID
        
        return true;
    }


    /**
     * @Content:
     */
    public stopAll():boolean{

        cc.audioEngine.stopAll();
        this._AUdioRunTime = {};
        return true;
    }

     


    /**
     * @Content:
     * @Param 
     */
    private _checkPlay( Music:string ):boolean{
        
        if( this._AUdioRunTime[ Music ] + 100 >= new Date().getTime() ){
            return false;
        }
        this._AUdioRunTime[ Music ] = new Date().getTime();
        return true;
    }


    public static getInstance():AudioManager{
        if(this.instance == null){
            this.instance = new AudioManager();
            
        }
        return this.instance;
    }


    public constructor() {
        this._AudioBGM = new AudioInfo();
    }


}