import IGame from "../../../../../base/IGame";
import GameConfig from "../../../../../define/GameConfig";
import GameMath from "../../../../../extend/utils/GameMath";
import GridCommand from "../../../commond/GridCommand";
import IdentifierManager from "../../../defines/IdentifierManager";
import { PowerID } from "../../../defines/PowerID";
import ItemBase from "../../../entity/item/ItemBase";
import ItemStateSwap from "../../../entity/item/state/ItemStateSwap";
import EntityEventBase from "../../../event/EntityEventBase";
import AvailableSwapInfo from "../../availableSwapInfo/AvailableSwapInfo";
import GridBase, { GridMode } from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateCompleteBanner from "./GridStateCompleteBanner";
import GridStateMatch from "./GridStateMatch";
import GridStateNoMoreStep from "./GridStateNoMoreStep";
import GridStateShuffle from "./GridStateShuffle";


export default class GridStateSwapNeighbour extends GridStateBase {

    
    
    public static create() : GridStateSwapNeighbour{
        return new GridStateSwapNeighbour();
    }
    constructor(){
        super(GridState.SwapNeighbour);
    }
    private _AIOpratedWaitDuration:number = 0;
    private _checkPositions:cc.Vec2[] = [];
    private _checkIndex:number = 0;

    private _availableSwaps:AvailableSwapInfo[] = [];
    private _availableSwapsSorted = false;
    private _tipSwapIndex = 0;
    
    private _swapPosA:cc.Vec2 = GameMath.INVALID_LOGICAL_POSITION.clone();
    private _swapPosB:cc.Vec2 = GameMath.INVALID_LOGICAL_POSITION.clone();
    
    private _itemSwaped:boolean = true;
    private _selectedItem:ItemBase = null;
    private _touchInterval:number = 0;
    private _tipInterval:number = 0;

    private static DOUBLE_CLICK_INTERVAL:number = 0.25;
    private static TIP_SWAP_INTERVAL:number = 5.0;
    private static MAX_TIP_SWAP_COUNT:number = 3;
    
    enter(grid:GridBase):void {
        grid.setPowerAmount(PowerID.MatchCombo, 0);
        if (grid.isComplete()) {
            grid.setCurrentState(GridStateCompleteBanner.create());
        }else {
            if (grid.isFailed()) {
                grid.setCurrentState(GridStateNoMoreStep.create());
            }else{
                let mode = grid.getMode();
                switch (mode) {
                    case GridMode.Normal:{
                        if (!this.hasAvailableSwap(grid)) {
                            grid.setCurrentState(GridStateShuffle.create());
                        }else{
                            this.statisticsCheckPositions(grid);
                        }
                    }
                        break;
                    case GridMode.ForceClean:
                    case GridMode.ForceSwap:
                    case GridMode.CrossBomb:{
                        grid.tryEnableMode(mode);
                        
                    }
                        break
                
                    default:
                        break;
                }
                this._itemSwaped = false;
                grid.beforePlayerOperated();
            }
            
        }
    }
    protected statisticsCheckPositions(grid:GridBase) : void {
        let itemsMap = grid.getItemsMap();
        GameMath.clearArray(this._availableSwaps);
        for (const key in itemsMap) {
            let item = itemsMap[key] as ItemBase;
            this._checkPositions.push(item.getLogicalPos());
        }
        this._checkIndex = 0;
        this._tipSwapIndex = -1;
        this._tipInterval = 0;
        this._availableSwapsSorted = false;
    }
    protected checkAvailableSwap(grid:GridBase, pos:cc.Vec2) : void {
        let offsets:cc.Vec2[] = [cc.v2(1, 0), cc.v2(-1, 0), cc.v2(0, 1), cc.v2(0, -1),];
        for (let offset of offsets) {
            let answer = this.getAvailableSwapInfo(grid, pos, pos.add(offset));
            if (answer) {
                this._availableSwaps.push(answer);
            }
        }
    }

    protected statisticsAvailableSwap(grid:GridBase) : void {
        if (!this._itemSwaped) {
            let startTime = new Date().getTime();
            let checkPosCount = 0;
            while(1) {
                let timeDelta = new Date().getTime() - startTime;
                if (timeDelta >= 40 && checkPosCount > 0) {
                    break;
                }

                if (this._checkIndex >= this._checkPositions.length) {
                    if (!this._availableSwapsSorted) {
                        this._availableSwaps.sort(function(lhs:AvailableSwapInfo, rhs:AvailableSwapInfo):number{
                            if (lhs.value > rhs.value) {
                                return -1;
                            }else{
                                return 1;
                            }
                        });
                        this._availableSwapsSorted = true;
                    }
                    break;
                }

                this.checkAvailableSwap(grid, this._checkPositions[this._checkIndex]);
                this._checkIndex += 1;
                checkPosCount += 1;
            }
        }
    }
    private tipSwap(grid:GridBase) : void {
        this._tipInterval = 0;
        if (this._availableSwaps.length <= 0) {
            return;
        }
        
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('hint'));
        let newIndex = ((this._tipSwapIndex + 1) % GridStateSwapNeighbour.MAX_TIP_SWAP_COUNT) % this._availableSwaps.length;
        this.unTipSwap(grid);
        this._tipSwapIndex = newIndex;
        let swapInfo = this._availableSwaps[this._tipSwapIndex];
        let item = grid.getItemAtLogicalPos(swapInfo.swapPair.first);
        item.tipSlide(swapInfo.swapPair.second);

        for (let pos of swapInfo.linkedPositions) {
            if (!pos.equals(swapInfo.swapPair.second)) {
                let item = grid.getItemAtLogicalPos(pos);
                if (item) {
                    item.shake();
                }
            }
        }
    }
    private unTipSwap(grid:GridBase) {
        if (this._availableSwaps.length <= 0) {
            return;
        }

        if (this._tipSwapIndex < 0) {
            return;
        }

        let swapInfo = this._availableSwaps[this._tipSwapIndex];
        let item = grid.getItemAtLogicalPos(swapInfo.swapPair.first);
        if (item) {
            item.untipSlide();
        }
        for (let pos of swapInfo.linkedPositions) {
            if (!pos.equals(swapInfo.swapPair.second)) {
                let item = grid.getItemAtLogicalPos(pos);
                if (item) {
                    item.unshake();
                }
            }
        }
        this._tipInterval = 0;

    }
    protected canEliminateBySwapAtPos(grid:GridBase, logicalPosA:cc.Vec2, logicalPosB:cc.Vec2) {
        let rootItem = grid.getItemAtLogicalPos(logicalPosA);
        if (!rootItem) {
            return false;
        }
        return rootItem.canExcuteCommand(GridCommand.NeighbourSwap(logicalPosB));
    }

    private trySwapItem(grid:GridBase, logicalPosA:cc.Vec2, logicalPosB:cc.Vec2) : boolean {
        if (this._itemSwaped || !grid.isObstructsEmpty()) {
            return false;
        }

        if (this.canSwapItem(grid, logicalPosA, logicalPosB)) {
            this.unTipSwap(grid);
            this._swapPosA = logicalPosA;
            this._swapPosB = logicalPosB;

            let itemA = grid.getItemAtLogicalPos(this._swapPosA);
            let itemB = grid.getItemAtLogicalPos(this._swapPosB);
            grid.swapItem(this._swapPosA, this._swapPosB);

            itemA.setCurrentState(ItemStateSwap.create(this._swapPosA, this._swapPosB));
            itemB.setCurrentState(ItemStateSwap.create(this._swapPosB, this._swapPosA));

            if (this._selectedItem) {
                this._selectedItem.unselected();
                this._selectedItem = null;
            }
            this._touchInterval = 0;
            this._itemSwaped = true;
            return true;
        }
        return false;
    }
    protected afterSwap(grid:GridBase) {
        if (!this._itemSwaped) {
            return;
        }

        if (this.canEliminateBySwapAtPos(grid, this._swapPosA, this._swapPosB) || 
            this.canEliminateBySwapAtPos(grid, this._swapPosB, this._swapPosA)) {
                grid.setPowerAmount(PowerID.DyeingItem, 1);
                grid.setPowerAmount(PowerID.SpreadHoney, 1);
                grid.setPowerAmount(PowerID.ConveyerMove, 1);
                
                grid.setPowerAmount(PowerID.HoneyResist, 0);
                grid.gainPower(PowerID.UsedSwap, 1);
                grid.updateStepBarUIWithDelta(-1);
                grid.lossPower(PowerID.AvailableSwap, 1);

                let commandA = GridCommand.NeighbourSwap(this._swapPosB);
                let commandB = GridCommand.NeighbourSwap(this._swapPosA);
                
                grid.dispatchCommand(this._swapPosB, commandB, 0);
                grid.dispatchCommand(this._swapPosA, commandA, 0);
                
                grid.onPlayerOperated();
                grid.setCurrentState(GridStateMatch.create());
                
        }else{
            let itemA = grid.getItemAtLogicalPos(this._swapPosA);
            let itemB = grid.getItemAtLogicalPos(this._swapPosB);
            grid.swapItem(this._swapPosA, this._swapPosB);

            itemA.setCurrentState(ItemStateSwap.create(this._swapPosA, this._swapPosB));
            itemB.setCurrentState(ItemStateSwap.create(this._swapPosB, this._swapPosA));
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('prohibition_exchange'));

            this._selectedItem = null;
            this._touchInterval = 0;
            this._itemSwaped = false;
        }
    }
    update(grid:GridBase, delta:number):void {

        
        this.statisticsAvailableSwap(grid);
        if (!this._itemSwaped && this._tipInterval < GridStateSwapNeighbour.TIP_SWAP_INTERVAL) {
            this._tipInterval += delta;
            if (this._tipInterval >= GridStateSwapNeighbour.TIP_SWAP_INTERVAL) {
                this.tipSwap(grid);
            }
        }
        
        if (this._itemSwaped && grid.isObstructsEmpty()) {
            this.afterSwap(grid);    
        }

        this._AIOpratedWaitDuration += delta;
        if (this._AIOpratedWaitDuration >= 0 && GameConfig.EnableAutoPilot && !this._itemSwaped && grid.isObstructsEmpty()) {
            if (this._availableSwapsSorted) {
                let swapIndex = GameMath.randomInt(0, this._availableSwaps.length - 1);
                let swapInfo = this._availableSwaps[swapIndex];
                let swapPosA = swapInfo.swapPair.first;
                let swapPosB = swapInfo.swapPair.second;
                this.trySwapItem(grid, swapPosA, swapPosB);
            }
        }

    }
    exit(grid:GridBase):void {
        this._checkPositions = null;
        this._availableSwaps = null;
        this._swapPosA = null;
        this._swapPosB = null;
        this._selectedItem = null;
    }
    
    public onTapBegan(grid:GridBase, logicalPos:cc.Vec2) : void {

        if (this._itemSwaped || !grid.isObstructsEmpty()) {
            return;
        }

        let item = grid.getItemAtLogicalPos(logicalPos);
        if (!item) {
            return;
        }
        
        if (item === this._selectedItem) {
            if (this._touchInterval <= GridStateSwapNeighbour.DOUBLE_CLICK_INTERVAL) {
                
            }else{
                this._touchInterval = 0;
            }
        }else{
            if (this._selectedItem) {
                let fromPos = this._selectedItem.getLogicalPos();
                if (!this.trySwapItem(grid, fromPos, logicalPos)) {
                    if (item.canSwap()) {
                        this._selectedItem.unselected();
                        item.selected();
                        this._selectedItem = item;
                        this._touchInterval = 0;
                    }
                }
            }else{
                if (item.canSwap()) {
                    item.selected();
                    this._selectedItem = item;
                    this._touchInterval = 0;
                }
            }
        }
    }
    public onTapMoved(grid:GridBase, logicalPos:cc.Vec2) : void {
        if (this._selectedItem == null) {
            return;
        }

        if (this._itemSwaped || !grid.isObstructsEmpty()) {
            return;
        }
        
        let fromPos = this._selectedItem.getLogicalPos();
        this.trySwapItem(grid, fromPos, logicalPos);
        
    }
    public onTapEnded(grid:GridBase, logicalPos:cc.Vec2) : void {

    }
    public onEvent(grid:GridBase, event:EntityEventBase) : void {

    }
    public onRespone(grid:GridBase, event:Object) : void {

    }
    public canChangeState() : boolean {
        return true;
    }
    public canPause() : boolean {
        return true;
    }
}
