import GameMath from "../../../../../extend/utils/GameMath";
import { EntityDropStepType } from "../../../defines/EntityID";
import EntityBase from "../../EntityBase";
import EntityStateWait from "../../state/concreteState/EntityStateWait";
import EntityStateBase, { EntityStateType } from "../../state/EntityStateBase";
import ItemBase from "../ItemBase";


export default class ItemStateDrop extends EntityStateBase {

    public static create() : ItemStateDrop {
        return new ItemStateDrop();
    }
    constructor() {
        super(EntityStateType.Drop);
    }

    private _pathIndex:number = 0;
    private _pathCount:number = 0;
    private _timeDelta:number = 0;
    private _stepDuraton:number = 0;

    private static MAX_DROP_DRUATION = 0.125;
    private static MIN_DROP_DRUATION = 0.065;
    private static DROP_DRUATION_DECAY_FACTOR = 0.9;

    private initDropPath(entity:EntityBase) : void {
        let grid = entity.getGrid();
        let path = entity.getPath();
        if (path.length < 2) {
            entity.setCurrentState(EntityStateWait.create());
            entity.clearPath();
            return;
        }

        let lastTargetIndex = path.length - 1;
        for (; lastTargetIndex >= 0; lastTargetIndex--) {
            if (path[lastTargetIndex].type != EntityDropStepType.Stay) {
                break;
            }
        }

        if (lastTargetIndex < 0) {
            entity.setCurrentState(EntityStateWait.create());
            entity.clearPath();
            return;
        }else{
            grid.addObstructs(entity);
            let firstStep = path[0];
            entity.setPixelPos(firstStep.logicalPos);
            entity.setVisible(firstStep.type != EntityDropStepType.Hide);

            this._pathCount = lastTargetIndex + 1;
            this._pathIndex = 1;
            this._timeDelta = 0;
            this._stepDuraton = ItemStateDrop.MAX_DROP_DRUATION;
        }
    }
    private updatePath(entity:EntityBase, delta:number) : void {
        this._timeDelta += delta;
        while(1) {
            let needNextLoop = false;
            if (this._pathIndex >= this._pathCount) {
                let item = entity as ItemBase;
                item.bounce();
                entity.getGrid().removeObstructs(entity);
                entity.clearPath();
                entity.setCurrentState(EntityStateWait.create());
                break;
            }

            let path = entity.getPath();
            let step = path[this._pathIndex];

            switch (step.type) {
                case EntityDropStepType.Hide: {
                    entity.setVisible(false);
                    if (this._timeDelta >= ItemStateDrop.MAX_DROP_DRUATION) {
                        this._timeDelta -= ItemStateDrop.MAX_DROP_DRUATION;
                        this._pathIndex += 1;
                        needNextLoop = true;
                        this._stepDuraton = ItemStateDrop.MAX_DROP_DRUATION;
                    }
                }
                    break;
                case EntityDropStepType.Born: {
                    entity.setVisible(true);
                    let startPos = entity.getPixelPos(step.logicalPos).add(cc.v2(0, entity.getContentSize().height * 0.5));
                    let offset = cc.v2(0, -entity.getContentSize().height * 0.5);
                    let startScale = 0;
                    let scaleDelta = 1.0;
                    let percentage = Math.min(this._timeDelta / ItemStateDrop.MAX_DROP_DRUATION, 1);
                    entity.node.setPosition(startPos.add(offset.mul(percentage)));
                    entity.node.setScale(startScale + scaleDelta * percentage);

                    if (this._timeDelta >= ItemStateDrop.MAX_DROP_DRUATION) {
                        this._timeDelta -= ItemStateDrop.MAX_DROP_DRUATION;
                        this._pathIndex += 1;
                        needNextLoop = true;
                        this._stepDuraton = ItemStateDrop.MAX_DROP_DRUATION;
                    }
                }   
                    break;
                case EntityDropStepType.Stay: {
                    entity.setVisible(true);
                    if (this._timeDelta >= ItemStateDrop.MAX_DROP_DRUATION) {
                        this._timeDelta -= ItemStateDrop.MAX_DROP_DRUATION;
                        this._pathIndex += 1;
                        needNextLoop = true;
                        this._stepDuraton = ItemStateDrop.MAX_DROP_DRUATION;
                    }
                }
                    break;

                case EntityDropStepType.Drop: {
                    let startPos = entity.getPixelPos(path[this._pathIndex - 1].logicalPos);
                    let offset = entity.getPixelPos(step.logicalPos).sub(startPos);
                    let percentage = Math.min(this._timeDelta / this._stepDuraton, 1);
                    entity.node.setPosition(startPos.add(offset.mul(percentage)));
                    if (this._timeDelta >= this._stepDuraton) {
                        this._timeDelta -= this._stepDuraton;
                        this._pathIndex += 1;
                        needNextLoop = true;
                        let newDuration = this._stepDuraton * ItemStateDrop.DROP_DRUATION_DECAY_FACTOR;
                        this._stepDuraton = GameMath.clamp(newDuration, ItemStateDrop.MIN_DROP_DRUATION, ItemStateDrop.MAX_DROP_DRUATION);
                    }
                }
                    break;
                case EntityDropStepType.Wormhole : {
                    let percentage = Math.min(this._timeDelta / this._stepDuraton, 1);
                    if (percentage <= 0.5) {
                        percentage = percentage / 0.5;
                        let startPos = entity.getPixelPos(path[this._pathIndex - 1].logicalPos);
                        let offset = cc.v2(0, -entity.getContentSize().height * 0.5);
                        let startScale = 1.0;
                        let scaleDelta = -1.0;
                        entity.node.setPosition(startPos.add(offset.mul(percentage)));
                        entity.node.setScale(startScale + scaleDelta * percentage);
                    }else{
                        percentage = Math.min((percentage - 0.5) / 0.5, 1);
                        let startPos = entity.getPixelPos(step.logicalPos).add(cc.v2(0, entity.getContentSize().height * 0.5));
                        let offset = cc.v2(0, -entity.getContentSize().height * 0.5);

                        let startScale = 0;
                        let scaleDelta = 1.0;
                        entity.node.setPosition(startPos.add(offset.mul(percentage)));
                        entity.node.setScale(startScale + scaleDelta * percentage);
                    }
                    
                    if (this._timeDelta >= ItemStateDrop.MAX_DROP_DRUATION) {
                        this._timeDelta -= ItemStateDrop.MAX_DROP_DRUATION;
                        this._pathIndex += 1;
                        needNextLoop = true;
                        let newDuration = this._stepDuraton * ItemStateDrop.DROP_DRUATION_DECAY_FACTOR;
                        this._stepDuraton = GameMath.clamp(newDuration, ItemStateDrop.MIN_DROP_DRUATION, ItemStateDrop.MAX_DROP_DRUATION);
                    }
                }
                    break;
                
                default:
                    break;
            }
            if (needNextLoop == false) {
                break;
            }
        }
    }
    enter(entity:EntityBase):void {
        this.initDropPath(entity);
    }
    update(entity:EntityBase, delta:number):void {
        this.updatePath(entity, delta * cc.director.getScheduler().getTimeScale());
    }
    exit(entity:EntityBase):void {
        entity.getGrid().removeObstructs(entity);
    }
}
