const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemBombRange extends ItemBombBase {

    public getCollectionID() : CollectionID {
        let result = CollectionID.None;
        switch (this.getEntityColor()) {
            case EntityColor.Red:
                result = CollectionID.ItemBombRangeRed;
                break;
            case EntityColor.Green:
                result = CollectionID.ItemBombRangeGreen;
                break;
            case EntityColor.Blue:
                result = CollectionID.ItemBombRangeBlue;
                break;
            case EntityColor.Yellow:
                result = CollectionID.ItemBombRangeYellow;
                break;
            case EntityColor.Orange:
                result = CollectionID.ItemBombRangeOrange;
                break;
            case EntityColor.Purple:
                result = CollectionID.ItemBombRangePurple;
                break;
                
            default:
                break;
        }
        return result;
    }
    
    public getEntityID():EntityID {
        
        let result = EntityID.None;
        switch (this.getEntityColor()) {
            case EntityColor.Red:
                result = EntityID.ItemBombRangeRed;
                break;
            case EntityColor.Green:
                result = EntityID.ItemBombRangeGreen;
                break;
            case EntityColor.Blue:
                result = EntityID.ItemBombRangeBlue;
                break;
            case EntityColor.Yellow:
                result = EntityID.ItemBombRangeYellow;
                break;
            case EntityColor.Orange:
                result = EntityID.ItemBombRangeOrange;
                break;
            case EntityColor.Purple:
                result = EntityID.ItemBombRangePurple;
                break;
                
            default:
                break;
        }
        return result;
    }
    public getProducedTrainSupplyMaps() : Object {
        let result = {};
        result[GameCurrencyType.GoldCoin] = 1;
        result[GameCurrencyType.TrainHoe] = 1;
        return result;
    }

    public getScore():number{
        return 500;
    }

    protected _onClean(cleanOption:EntityCleanOption) {
        super._onClean(cleanOption);
        this.getGrid().gainScoreWithEntity(this);


        if (!this.getIsDud()) {
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_bomb'));
            let grid = this.getGrid();
            let bomb = FlyerBomb.create(grid, this.getEntityColor(), this.getShouldLayoutGrass(), this.getLogicalPos(), 2);
            grid.setFlyerAtLogicalPos(bomb, this.getLogicalPos());
            this.setVisible(false);
        }
    }

    public static create(grid:GridBase, color:EntityColor):ItemBombRange{

        let className = 'ItemBombRange';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemBombRange';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemBombRange;
        if (component) {
            component.init(grid, color); 
            return component;       
        }
        return null;
    }

    public clone():ItemBombRange {
        let item = ItemBombRange.create(this.getGrid(),this.getEntityColor());
        return item;
    }

    protected init(grid:GridBase, entityColor:EntityColor) : void{
        super.initBomb(grid, ItemBombType.Range);
        this.setEntityColor(entityColor);
    }
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}
}
