export default class IGame{

    /*
    * 
    * @key
    * @replaceArry[""100]
    */
    public static init():boolean{
        return true;
    }

    /*
    * 
    * @key
    * @replaceArry[""100]
    */
    public static playAudio( Music:string, Volume:number = 1 , loop:boolean = false, succFunc:Function = null){
        return AudioManager.getInstance().play( Music, Volume, loop, false, succFunc);
    }

    /*
    * 
    * @key
    * @replaceArry[""100]
    */
    public static playAudioBGM( Music:string, Volume:number = 1, succFunc:Function = null){
        AudioManager.getInstance().play( Music, 1, true, true , succFunc);
    }

    public static stopBGM() {
        return AudioManager.getInstance().stopBGM();
    }

    /*
    * 
    * @key
    * @replaceArry[""100]
    */
    public static stopAudio( MusicId:number ):boolean{
        return AudioManager.getInstance().stop( MusicId );
    }

    /*
    * 
    * @key
    * @replaceArry[""100]
    */
    public static stopAudioAll( ):boolean{
        return AudioManager.getInstance().stopAll();
    }

}