import GameMath from "../../../../extend/utils/GameMath";

export enum FencePosType{
    None,
    Horizontal,
    Vertical,
};

export default class FencePos  {
    public static create(PosA:cc.Vec2, PosB:cc.Vec2) : FencePos {
        let offset = PosA.sub(PosB);
        if ((offset.x == 0 && Math.abs(offset.y) == 1) || (offset.y == 0 && Math.abs(offset.x) == 1)) {
            if (GameMath.vec2Less(PosA, PosB)) {
                return new FencePos(PosA, PosB);
            }else{
                return new FencePos(PosB, PosA);
            }
        }
        return null;
    }
    constructor (public posA:cc.Vec2, public posB:cc.Vec2) {}

    public getFencePosType() : FencePosType{
        let result:FencePosType = FencePosType.None;
        let offset = this.posA.sub(this.posB);
        if (offset.x == 0 && Math.abs(offset.y) == 1) {
            result = FencePosType.Horizontal;
        }else if (offset.y == 0 && Math.abs(offset.x) == 1) {
            result = FencePosType.Vertical;
        }
        return result;
    }

    public equals(fencePos:FencePos) : boolean {
        if (this.posA.equals(fencePos.posA) && this.posB.equals(fencePos.posB)) {
            return true;
        }
        return false;
    }
    
}
