const {ccclass, property} = cc._decorator;

@ccclass
export default class UISpriteExt extends cc.Component {

    private _imageUrl:string = null;

    private _curLoadCount:number = 0; //

    private _spriteSize:cc.Size = null;

    public setSpriteSize(size:cc.Size){
        this._spriteSize = size;
    }

    get src():string{
        return this._imageUrl;
    }

    set src(path:string){
        let spriteCp:cc.Sprite = this.getComponent(cc.Sprite);
        if(!spriteCp){
            spriteCp = this.addComponent(cc.Sprite);
        }

        if(this._imageUrl != path){
            this.node.active = false;
        }

        this._imageUrl = path;

        this._curLoadCount += 1;

        cc.loader.loadRes(path, cc.SpriteFrame, (err, spriteFrame) => {
            if(!err){

                if(!this.isValid){
                    return;
                }

                if(spriteFrame && this._imageUrl.indexOf(spriteFrame.name) == -1){
                    return;
                }

                spriteCp.spriteFrame = spriteFrame;
                this.node.active = true;

                if(this._spriteSize){
                    this.node.setContentSize(this._spriteSize);
                }
                
                
            }else{
                //
                if(this._curLoadCount <= 3){
                    this.src = this._imageUrl;
                }
            }
        });
    }
}
