    public getGridMode(): GridMode {
        return this._mode;
    }
    public setGridMode(mode: GridMode) {
        if (this._mode == mode) {
            return;
        }
        this._mode = mode;
        this._grid.tryEnableMode(mode);
        let itemID = GameCurrencyType.NoneType;
        switch (this._mode) {
            case GridMode.ForceClean:
                itemID = GameCurrencyType.AidHammer;
                break;
            case GridMode.CrossBomb:
                itemID = GameCurrencyType.AidBomb;
                break;
            case GridMode.ForceSwap:
                itemID = GameCurrencyType.AidSwap;
                break;
            default:
                break;
        }
        if (this.gearBar) {
            this.gearBar.focusItemWithGearID(itemID);
        }
    }

    public selectGear(itemID: GameCurrencyType) {
        let mode = GridMode.Normal;
        switch (itemID) {
            case GameCurrencyType.AidHammer: {
                if (this.getGridMode() == GridMode.ForceClean) {
                    mode = GridMode.Normal;
                } else {
                    mode = GridMode.ForceClean;
                }
            }
                break;
            case GameCurrencyType.AidBomb: {
                if (this.getGridMode() == GridMode.CrossBomb) {
                    mode = GridMode.Normal;
                } else {
                    mode = GridMode.CrossBomb;
                }
            }
                break;
            case GameCurrencyType.AidSwap: {
                if (this.getGridMode() == GridMode.ForceSwap) {
                    mode = GridMode.Normal;
                } else {
                    mode = GridMode.ForceSwap;
                }
            }
                break;
            default:
                break;
        }
        this.setGridMode(mode);
    }

    protected updateGears(event: cc.Event.EventCustom) {
        let data = event.getUserData();
        let itemID = data['itemID'];
        if (itemID && itemID != GameCurrencyType.NoneType) {
            this.updateUserInterface();
            this.selectGear(itemID);
        }
    }

