    public getStatisticsReport(): Object {

        let report: Object = {};
        report['hadOperated'] = this.hadOperated();
        report['usedStep_ST'] = this.getPowerAmount(PowerID.UsedSwap);
        report['leftStep_SL'] = this.getPowerAmount(PowerID.AvailableSwap);
        report['totalTime_T'] = this.getTickTime();
        report['shareTimes'] = this.getShareTimes();
        report['config'] = this.getMatchGameConfig();
        report['isCompleteWithLastStep'] = this.getCompleteWithLastStep();
        report['totalScore'] = this.getTotalScore();
        report['completeScore'] = this.getCompleteScore();
        report['historyHighscore'] = this.getHistoryHighscore();
        report['starCount'] = this.getStarsCount();
        if (this.hasShowCompleteBanner()) {
            report['isVeryFastComplete'] = this.getPowerAmount(PowerID.UsedSwap) < 10;
        } else {
            report['isVeryFastComplete'] = false;
        }


        let targetMap = this.getTargetMap();
        for (let key in targetMap) {
            let collectionId = Number(key) as CollectionID;
            let value: number = 0;
            if (collectionId == CollectionID.ItemPuppy) {
                value = this.getPuppyLeftSteps();
            } else {
                let left = Math.max(0, targetMap[key] - this.getCollectedEntityAmount(collectionId));
                value = left;
            }
            if (!report['leftTarget_TN']) {
                report['leftTarget_TN'] = {};
            }
            report['leftTarget_TN'][collectionId] = value;
        }

        report['leftTarget_TN'] = this.getRemainingTargets()



        let gearMap = this.getUsedGearMap();
        for (let key in gearMap) {
            let gear = gearMap[key];
            if (gear > 0) {
                if (!report['usedGear_GN']) {
                    report['usedGear_GN'] = {};
                }
                report['usedGear_GN'][key] = gear;
            }
        }

        let collectedSupplyMap = this.getCollectedSupplyMap();
        let IT: Object = {};
        for (let key in collectedSupplyMap) {
            let amount = collectedSupplyMap[key];
            let itemID = Number(key) as GameCurrencyType;
            IT[itemID] = amount;
        }
        report['IT'] = IT;
        return report;
    }

