
export interface CFGTripleLevelObj {
    LevelNum: number;
    LevelID: number;
    LevelType: number;
    LevelReward: string;
    DifficultyTime: number;
    DifficultyS1: number;
    DifficultyS2: number;
    Rank: number;
}

export class CFGTripleLevel {

    //
    private cfgPath: string = 'CFGTripleLevel';
    //
    private cfgObj: Object[] = [];

    //
    private static instance: CFGTripleLevel = null;

    public static getInstance(): CFGTripleLevel {
        if (this.instance == null) {
            this.instance = new CFGTripleLevel();
        }
        return this.instance;
    }

    constructor() {

        let res = cc.loader.getRes('data/' + this.cfgPath, cc.JsonAsset) as cc.JsonAsset;
        if (res) {
            this.cfgObj = res.json
        }
    }

    public getCongfigByLevelNum(levelNum: number): CFGTripleLevelObj {
        let cfgList = this.cfgObj;
        for (const obj of cfgList) {
            if (obj['LevelNum'] == levelNum) {
                return obj as CFGTripleLevelObj;
            }
        }

        return null;
    }
}
