const { ccclass, property } = cc._decorator;

@ccclass
export default class PopsicleNormal extends PopsicleBase {

    private static SizeToEntityInfoMap: Object = {
        '{1, 1}': {
            'EntityID': EntityID.PopsicleNormal_1x1,
            'NeedRotate': false
        },
        '{1, 2}': {
            'EntityID': EntityID.PopsicleNormal_1x2,
            'NeedRotate': false
        },
        '{2, 1}': {
            'EntityID': EntityID.PopsicleNormal_1x2,
            'NeedRotate': true
        },
        '{2, 2}': {
            'EntityID': EntityID.PopsicleNormal_2x2,
            'NeedRotate': false
        },
        '{2, 3}': {
            'EntityID': EntityID.PopsicleNormal_2x3,
            'NeedRotate': false
        },
        '{3, 2}': {
            'EntityID': EntityID.PopsicleNormal_2x3,
            'NeedRotate': true
        },
        '{3, 3}': {
            'EntityID': EntityID.PopsicleNormal_3x3,
            'NeedRotate': false
        },
        '{3, 4}': {
            'EntityID': EntityID.PopsicleNormal_3x4,
            'NeedRotate': false
        },
        '{4, 3}': {
            'EntityID': EntityID.PopsicleNormal_3x4,
            'NeedRotate': true
        },
    };

    private static getEntityInfoWithSize(size: cc.Size): Object {
        let key = '{' + size.width + ', ' + size.height + '}';
        return this.SizeToEntityInfoMap[key];
    }

    public getCollectionID(): CollectionID {
        return CollectionID.PopsicleNormal;
    }

    public getEntityID(): EntityID {

        let entityInfo = PopsicleNormal.getEntityInfoWithSize(this.getLogicalSize());
        if (entityInfo) {
            return entityInfo['EntityID'];
        }
        return EntityID.None;
    }

    public getScore(): number {
        let size = this.getLogicalSize()
        return 50 * size.width * size.height;
    }

    protected updatePopsicle(): void {
        super.updatePuppet();

        let rotation = 0;
        let entityInfo = PopsicleNormal.getEntityInfoWithSize(this.getLogicalSize());
        if (entityInfo) {
            let needRotate = entityInfo['NeedRotate'];
            if (needRotate) {
                rotation = GameMath.randomInt(0, 100) < 50 ? -90 : 90;
            }
        }
        this._puppet.angle = -rotation;
    }

    protected _onBePopsicleClean(): void {
        super._onBePopsicleClean();
        this.getGrid().gainScoreWithEntity(this);
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('popsicles'));
    }



    protected init(grid: GridBase, width: number, height: number): void {
        super.initPopsicle(grid, width, height, PopsicleType.Normal);
        this.updatePopsicle();
    }

    public static create(grid: GridBase, width: number, height: number): PopsicleNormal {

        let className = 'PopsicleNormal';
        let prefabFileName = 'match_scene/prefab/entity/popsicle/PopsicleNormal';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as PopsicleNormal;
        if (component) {
            component.init(grid, width, height);
            return component;
        }
        return null;
    }

    public clone(): PopsicleNormal {
        let item = PopsicleNormal.create(this.getGrid(), this.getLogicalSize().width, this.getLogicalSize().height);
        return item;
    }

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {

    }

    // update (dt) {}
}
