import ItemBase, { ItemType } from "../ItemBase";
import GridBase from "../../../grid/GridBase";
import { CollectionID } from "../../../defines/CollectionID";
import { EntityID, EntityCleanOption, EntityColor } from "../../../defines/EntityID";
import TMXConfigInfoKeys from "../../../defines/TMXConfigInfoKeys";
import FlyerAnimation from "../../flyer/FlyerAnimation";
import IGame from "../../../../../base/IGame";
import IdentifierManager from "../../../defines/IdentifierManager";
import EntityPrefabNodePool from "../../EntityPrefabNodePool";


const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemWatermelon extends ItemBase {

    @property([cc.Node])
    piecesNode:cc.Node[] =[];

    public _healthPoint:number = 1;

    public static create(grid:GridBase):ItemWatermelon{

        let className = 'ItemWatermelon';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemWatermelon';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemWatermelon;
        if (component) {
            component.init(grid); 
            return component;       
        }
        return null;
    }

    public clone() : ItemWatermelon {
        let item = ItemWatermelon.create(this.getGrid());
        item.setHealthPoint(this.getHealthPoint());
        return item;
    }

    public getCollectionID():CollectionID{
        return CollectionID.ItemWatermelon;
    }

    public getEntityID():EntityID{
        return EntityID.ItemWatermelon;
    }

    public getScore():number{
        return 100;
    }

    public dealWithConfigMap(configMap:Object):void{
        super.dealWithConfigMap(configMap);
        if(configMap[TMXConfigInfoKeys.CFG_WATERMELON_MAX]){
            this._healthPoint = Number(configMap[TMXConfigInfoKeys.CFG_WATERMELON_MAX]);
        }

        this.setHealthPoint(this._healthPoint);
    }

    protected init(grid:GridBase){
        super.initItem(grid, ItemType.ItemWatermelon, 2, 2);
    }

    protected _canDrop():boolean{
        return false;
    }

    protected _canSwap():boolean{
        return false;
    }

    protected _canMatch():boolean{
        return false;
    }

    protected _canCleanByTerminate(cleanColor:EntityColor) : boolean {
        return false;
    }

    protected _canCleanByBomb(cleanColor:EntityColor):boolean{
        return true;
    }

    protected _canCleanByNeighbour(color:EntityColor):boolean{
        return true;
    }

    protected _onClean(cleanOption:EntityCleanOption){
        this.getGrid().gainScoreWithEntity(this);
        if(this.getHealthPoint() <= 1){
            
            this.progressDeathWish(cleanOption);
            this.removeFromGridAndAddToEffect();
            this.getGrid().addObstructs(this);

            let size = cc.view.getVisibleSize();
            let worldPos = cc.v2(size.width + 2000, 0 - 500);
            let gridPos = this.getGrid().node.convertToNodeSpace(worldPos);

            let removeObstruct = cc.sequence(cc.delayTime(0.2), cc.callFunc(()=>{
                this.getGrid().removeObstructs(this);
            }));
            let disappear = cc.sequence(cc.spawn(cc.moveTo(1.0, gridPos),cc.fadeOut(1.0)),
                                cc.callFunc(()=>{
                                    this.getGrid().removeObstructs(this);
                                    this.node.removeFromParent();
                                }));
            let action = cc.spawn(removeObstruct, disappear);
            this.runAction(action);

            let flyer = FlyerAnimation.createDieAnimation(this.getGrid(), this.getEntityID());
            this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
            flyer.node.setPosition(this.node.getPosition());
            flyer.node.zIndex = this.getTopZOrder();
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('watermelon'));
            this.setHealthPoint(0);
        }else{
            this.onCollected();
            this.setHealthPoint(this.getHealthPoint() - 1);
            this.selfHurt();
            
            let flyer = FlyerAnimation.createDieAnimation(this.getGrid(), this.getEntityID());
            this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
            flyer.node.setPosition(this.node.getPosition());
            flyer.node.zIndex = this.getTopZOrder();
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('watermelon'));
        }
    }
    
    protected getHealthPoint():number{
        return this._healthPoint;
    }

    protected setHealthPoint(val:number):void{
        this._healthPoint = val;
        this.updatePuppet();
    }

    protected updatePuppet():void{
        for (let i = 0; i < 8; i++) {
            this.piecesNode[i].active = i < this._healthPoint;
        }   
    }
}
