import GameMath from "../utils/GameMath";

/**
 * Created by chenzd on 2018/8/3.
 * OrbitTrack
 */

export default class OrbitTrack extends cc.ActionInterval {

    private _startPos: cc.Vec2 = null;
    private _emitterDirection: cc.Vec2 = null;
    private _radius: number = 0;
    private _targetPos: cc.Vec2 = null;
    private _rotateHead: boolean = false;
    private _contactPos: cc.Vec2 = null;
    private _origin: cc.Vec2 = null;
    private _startAngle: number = 0;
    private _endAngle: number = 0;
    private _isClockWise: boolean = false;
    private _arcAngle: number = 0;
    private _arcLength: number = 0;
    private _stringLength: number = 0;

    protected originalTarget: cc.Node = null;
    protected target: cc.Node = null;

    // constructor(){
    //     super();
    // }

    //
    public static createWithDuration(duration: number, startPos: cc.Vec2, targetPos: cc.Vec2,
        emitterDirection: cc.Vec2, radius: number, rotateHead: boolean): OrbitTrack {

        let pRet: OrbitTrack = new OrbitTrack();
        pRet.initWithDuration(duration, startPos, targetPos, emitterDirection, radius, rotateHead);
        return pRet;
    }

    public static createWithSpeed(speed: number, startPos: cc.Vec2, targetPos: cc.Vec2, emitterDirection: cc.Vec2,
        radius: number, rotateHead: boolean): OrbitTrack {

        let pRet: OrbitTrack = new OrbitTrack();
        pRet.initWithSpeed(speed, startPos, targetPos, emitterDirection, radius, rotateHead);
        return pRet;
    }

    startWithTarget(target) {

        (cc.ActionInterval.prototype as any).startWithTarget.call(this, target);

        this.updateWithPercentage(0);
    }

    update(time: number) {
        this.updateWithPercentage(time);
    }

    protected initWithDuration(duration: number, startPos: cc.Vec2, targetPos: cc.Vec2, emitterDirection: cc.Vec2,
        radius: number, rotateHead: boolean): boolean {

        this._startPos = startPos;
        this._targetPos = targetPos;
        this._emitterDirection = emitterDirection;
        this._radius = radius;
        this._rotateHead = rotateHead;
        this.initPath();

        return (cc.ActionInterval.prototype as any).initWithDuration.call(this, duration);

    }

    protected initWithSpeed(speed: number, startPos: cc.Vec2, targetPos: cc.Vec2, emitterDirection: cc.Vec2,
        radius: number, rotateHead: boolean) {

        this._startPos = startPos;
        this._targetPos = targetPos;
        this._emitterDirection = emitterDirection;
        this._radius = radius;
        this._rotateHead = rotateHead;
        let duration: number = this.initPath(speed);

        return (cc.ActionInterval.prototype as any).initWithDuration.call(this, duration);
    }

    private initPath(speed: number = 0): number {
        let angle: number = GameMath.getAngle(this._startPos, this._targetPos, this._emitterDirection, true);
        let rotateAngle: number = Math.PI / 2.0;
        let round: number = 0.0;

        let minAngle: number = 0.5;
        if (Math.abs(angle) < minAngle) {
            rotateAngle = -Math.PI / 2.0;
            round = 1.0;
        } else if (angle > minAngle && angle < 180 - minAngle) {
            rotateAngle = Math.PI / 2.0;
        } else if (Math.abs(angle - 180) < minAngle) {
            rotateAngle = Math.PI / 2.0;
        } else if (angle > 180 + minAngle && angle < 360 + minAngle) {
            rotateAngle = -Math.PI / 2.0;
        }

        this._isClockWise = (rotateAngle < 0);

        let normalize = this._emitterDirection.normalize();
        let offset = normalize.mul(this._radius);

        let rotateOffset = cc.v2(offset.x * Math.cos(rotateAngle) - offset.y * Math.sin(rotateAngle),
            offset.x * Math.sin(rotateAngle) + offset.y * Math.cos(rotateAngle));

        this._origin = this._startPos.add(rotateOffset);
        this._contactPos = GameMath.getContactPoint(this._origin, this._radius, this._targetPos, this._isClockWise);
        this._startAngle = GameMath.getAngle(this._origin, this._startPos, cc.v2(1, 0), false);
        this._endAngle = GameMath.getAngle(this._origin, this._contactPos, cc.v2(1, 0), false);

        if (this._isClockWise) {
            this._endAngle = this._endAngle + round * -360.0;
        } else {
            this._endAngle = this._endAngle + round * 360.0;
        }

        this._arcAngle = 0.0;

        if (this._isClockWise) {
            this._arcAngle = Math.floor(this._startAngle + 360 - this._endAngle) % 360 + round * 360.0;
        } else {
            this._arcAngle = Math.floor(this._endAngle + 360 - this._startAngle) % 360 + round * 360.0;
        }

        this._arcLength = 2.0 * Math.PI * this._radius * this._arcAngle / 360.0;
        let strightOffset = this._contactPos.sub(this._targetPos);
        this._stringLength = strightOffset.mag();

        if (speed > 0) {
            return (this._arcLength + this._stringLength) / speed;
        }
        return 0;
    }

    private updateWithPercentage(percentage: number): void {
        let totalLength: number = this._arcLength + this._stringLength;

        let contactPosPercentage: number = this._arcLength / totalLength;

        if (percentage > contactPosPercentage) {
            let strightPercentage: number = (percentage - contactPosPercentage) / (1.0 - contactPosPercentage);

            let offset = this._targetPos.sub(this._contactPos);
            let offsetLength = offset.mag();
            let offsetNormalize = offset.normalize();
            let newPos = offsetNormalize.mul(strightPercentage * offsetLength).add(this._contactPos);

            this.getTarget().setPosition(newPos);

            if (this._rotateHead) {
                let angle = GameMath.getAngle(this._contactPos, this._targetPos, cc.v2(0, 1), true);
                let puppet = this.getTarget().getChildByName('puppet');
                if (puppet) {
                    puppet.angle = -angle;
                }
            }
        } else {
            let angleDelta = this._arcAngle * percentage / contactPosPercentage;
            let offset = this._startPos.sub(this._origin);
            let rotateOffset = cc.Vec2.ZERO;

            let rotateRadin = angleDelta / 180 * Math.PI;
            if (this._isClockWise) {
                rotateRadin = -angleDelta / 180 * Math.PI;
            }

            rotateOffset = cc.v2(offset.x * Math.cos(rotateRadin) - offset.y * Math.sin(rotateRadin),
                offset.x * Math.sin(rotateRadin) + offset.y * Math.cos(rotateRadin));

            let newPos = this._origin.add(rotateOffset);

            this.getTarget().setPosition(newPos);

            if (this._rotateHead) {
                let angle = GameMath.getAngle(this._origin, newPos, cc.v2(0, 1), true);
                let rotation = 0;
                if (this._isClockWise) {
                    rotation = angle + 90;
                } else {
                    rotation = angle - 90;
                }

                let puppet = this.getTarget().getChildByName('puppet');
                if (puppet) {
                    puppet.angle = -rotation;
                }
            }
        }
    }

}
