

export interface CFGTripleLevelGrassObj {
    LevelID: number;
    MapID: number;
    Step: number;
    StarScore: string;
    DropBox: string;
    DropProb: string;
}

export class CFGTripleLevelGrass {

    //
    private cfgPath: string = 'CFGTripleLevelGrass';
    //
    private cfgObj: Object[] = [];

    constructor() {
        let res = cc.loader.getRes('data/' + this.cfgPath, cc.JsonAsset) as cc.JsonAsset;
        if (res) {
            this.cfgObj = res.json
        }
    }

    public getCongfigByLevelID(levelID: number): CFGTripleLevelGrassObj {
        let cfgList = this.cfgObj;
        for (const obj of cfgList) {
            if (obj['LevelID'] == levelID) {
                return obj as CFGTripleLevelGrassObj;
            }
        }

        return null;
    }
}
