import { CollectionID } from "../defines/CollectionID";
import IdentifierManager from "../defines/IdentifierManager";
import UISpriteExt from "../../../component/UISpriteExt";

// Learn TypeScript:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/typescript.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/typescript.html
// Learn Attribute:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/reference/attributes.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;

@ccclass
export default class TargetItem extends cc.Component {

    @property(cc.Sprite)
    frame:cc.Sprite = null;

    @property(cc.Sprite)
    icon:cc.Sprite = null;

    @property(cc.Label)
    amountLabel:cc.Label = null;

    @property(cc.Node)
    checkMark:cc.Node = null;

    private _collectionID:CollectionID = CollectionID.None;
    public getCollectionID() : CollectionID {
        return this._collectionID;
    }
    public setCollectionID(collectionID:CollectionID) : void {
        this._collectionID = collectionID;
        let spriteFrameName = IdentifierManager.getInstance().getFrameNameFromCollectionID(this._collectionID);

        let component = this.icon.getComponent(UISpriteExt);
        if (component) {
            component.src = spriteFrameName;
        }
    } 

    private _amount:number = 0;
    public getAmount() : number {
        return this._amount;
    }
    public setAmount(amount:number, animated:boolean = true) : void {
        let oldAmount = this._amount;
        this._amount = amount;
        this.amountLabel.string = this._amount.toString();

        if (animated && oldAmount != this._amount) {

            let  anim = this.getComponent(cc.Animation); 
            if (anim) {
                anim.stop();
                anim.play('bling');
            }else{
                this.amountLabel.node.stopActionByTag(1001);
                this.amountLabel.node.scale = 1.0;
                let amountAction = cc.sequence(cc.scaleTo(0.1, 1.33).easing(cc.easeIn(2)),
                                            cc.scaleTo(0.3, 1.0).easing(cc.easeOut(2))
                                            );
                amountAction.setTag(1001);
                this.amountLabel.node.runAction(amountAction);

                this.icon.node.stopActionByTag(1001);
                this.icon.node.scale = 1.0;
                let iconAction = cc.sequence(cc.scaleTo(0.1, 1.33).easing(cc.easeIn(2)),
                                            cc.scaleTo(0.3, 1.0).easing(cc.easeOut(2))
                                            );
                iconAction.setTag(1001);
                this.icon.node.runAction(iconAction);
            }
        }else{
            this.amountLabel.node.scale = 1.0;
            this.icon.node.scale = 1.0;
        }
        
        this.checkMark.active = (amount <= 0);
        this.amountLabel.node.active = (amount > 0);
    }

    /**
     * 
     */
    public showIco() : void {
        this.icon.node.opacity = 255;
        this.checkMark.opacity = 255;
        this.amountLabel.node.opacity = 255;
    }
    /**
     * 
     */
    public hideIco() : void {
        this.icon.node.opacity = 0;
        this.checkMark.opacity = 0;
        this.amountLabel.node.opacity = 0;
    }

    /**
     * 
     * @param isAni 
     */
    public showFrame(isAni:boolean = true) : void {
        if(isAni){
            this.frame.node.opacity = 255;
            this.frame.node.runAction(cc.scaleTo(0.2, 1));
            this.checkMark.runAction(cc.sequence(cc.delayTime(0.2),cc.fadeTo(0.1, 255),null));
            this.amountLabel.node.runAction(cc.sequence(cc.delayTime(0.2),cc.fadeTo(0.1, 255),null));
        }else{
            this.frame.node.setScale(1);
            this.frame.node.opacity = 255;
            this.checkMark.opacity = 255;
            this.amountLabel.node.opacity = 255;
        }
    }
    /**
     * 
     * @param isAni 
     */
    public hideFrame(isAni:boolean = true) : void {
        if(isAni){
            let seqAction = cc.sequence(cc.scaleTo(0.2, 0.5),cc.fadeTo(0.1, 0),null);
            this.frame.node.runAction(seqAction);
            this.checkMark.runAction(cc.fadeTo(0.1, 0));
            this.amountLabel.node.runAction(cc.fadeTo(0.1, 0));
        }else{
            this.frame.node.opacity = 0;
            this.checkMark.opacity = 0;
            this.amountLabel.node.opacity = 0;
        }
    }
    
    public static create(collectionID:CollectionID, showFarme:boolean = true) : TargetItem{

        let res = cc.loader.getRes('match_scene/prefab/userInterface/TargetItem', cc.Prefab);
        if (res) {
            let node:cc.Node = cc.instantiate(res);
            if (node) {
                let component:TargetItem = node.getComponent(TargetItem);
                if (component) {
                    component.init(collectionID, showFarme);     
                    return component; 
                }
            }
        }
        return null;
    }

    protected init(collectionID:CollectionID, showFarme:boolean) {

        this.frame.node.active = showFarme;
        this.setCollectionID(collectionID);
        this.setAmount(0);
    }

    // LIFE-CYCLE CALLBACKS:
    onLoad () {
         
    }
    start () {
        
    }
    // update (dt) {}
    onDestroy() {
        this.frame = null;
        this.icon = null;
        this.amountLabel = null;
        this.checkMark = null;
    }
}
