const {ccclass, property} = cc._decorator;

@ccclass
export default class MarkerNormal extends MarkerBase {

    private static DropMachineDisplayConfig:Object = {
        '1' : [
            {'Position':cc.v2(0, 0),    'Scale':0.5,}
        ],

        '2' : [
            {'Position':cc.v2(-13, -2),   'Scale':0.4,},
            {'Position':cc.v2(13, -2),   'Scale':0.4,},
        ],

        '3' : [
            {'Position':cc.v2(0, -2),   'Scale':0.4,},
            {'Position':cc.v2(18, 0),   'Scale':0.3,},
            {'Position':cc.v2(-18, 0),   'Scale':0.3,},
        ],
    }

    private _boneCollector:cc.Node = null;
    private _dropMachine:cc.Node = null;
    private _wormholeEntrance:cc.Node = null;
    private _wormholeExit:cc.Node = null;

    public getCollectionID():CollectionID{
        return CollectionID.MarkerNormal;
    }

    public getEntityID():EntityID{
        return EntityID.MarkNormal;
    }

    public dealWithConfigMap(config:Object) : void {
        super.dealWithConfigMap(config);
        let showEntityString = config[TMXConfigInfoKeys.CFG_DROP_MACHINE_SHOW_KEY_TYPE];
        if (showEntityString) {
            let entityNames = (showEntityString as string).split('|')

            let sprites:cc.Node[] = [];
            for (let name of entityNames) {
                let entityID = IdentifierManager.getInstance().getEntityIDFromString(name);
                if (entityID != EntityID.None) {
                    let frameName = IdentifierManager.getInstance().getFrameNameFromEntityID(entityID);
                    let node = new cc.Node();
                    let sprite = node.addComponent(cc.Sprite);
                    sprite.spriteFrame = EntityBase.getEntitySpriteFarme(frameName);
                    sprites.push(node);
                }
            }

            for (let name of entityNames) {
                let groupID = IdentifierManager.getInstance().getEntityGroupIDFromString(name);
                if (groupID != EntityGroupID.None) {
                    let frameName = IdentifierManager.getInstance().getFrameNameFromEntityGroupID(groupID);
                    let node = new cc.Node();
                    let sprite = node.addComponent(cc.Sprite);
                    sprite.spriteFrame = EntityBase.getEntitySpriteFarme(frameName);
                    sprites.push(node);
                }
            }

            let dropMachineConfig = MarkerNormal.DropMachineDisplayConfig[sprites.length.toString()];
            if (dropMachineConfig) {
                for (let index = 0; index < sprites.length; index++) {
                    let sprite = sprites[index];
                    let spriteInfo = dropMachineConfig[index];
                    sprite.position = spriteInfo['Position'];
                    sprite.scale = spriteInfo['Scale'];
                    this._dropMachine.addChild(sprite, -index - 1);    
                }
            }

            this._dropMachine.active = true;
        }

        let wormholeEntrance = config[TMXConfigInfoKeys.CFG_WORM_HOLE_ENTRANCE_KEY_TYPE];
        if (wormholeEntrance) {
            this._wormholeEntrance.active = true;
        }

        let wormholeExit = config[TMXConfigInfoKeys.CFG_WORM_HOLE_EXIT_KEY_TYPE];
        if (wormholeExit) {
            this._wormholeExit.active = true;
        }
        

        let boneCollector = config[TMXConfigInfoKeys.CFG_MARK_BONE_COLLECTOR_KEY_TYPE];
        if (boneCollector) {
            this._boneCollector.active = true;
        }
    }

    public static create(grid:GridBase) : MarkerNormal {

        let className = 'MarkerNormal';
        let prefabFileName = 'match_scene/prefab/entity/marker/MarkerNormal';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as MarkerNormal;
        if (component) {
            component.init(grid); 
            return component;       
        }
        return null;
    }

    protected init(grid:GridBase) {
        super.initMarker(grid, MarkerType.Normal);
        this._boneCollector = this._puppet.getChildByName('boneCollector');
        this._boneCollector.active = false;
        this._dropMachine = this._puppet.getChildByName('dropMachine');
        this._dropMachine.active = false;
        this._wormholeEntrance = this._puppet.getChildByName('wormholeEntrance');
        this._wormholeEntrance.active = false;
        this._wormholeExit = this._puppet.getChildByName('wormholeExit');
        this._wormholeExit.active = false;
    }

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}

    onDestroy() {
        super.onDestroy();
        this._boneCollector = null;
        this._dropMachine = null;
        this._wormholeEntrance = null;
        this._wormholeExit = null;
    }
}
