// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

export enum ParasiteType {
    None = 0,
    Shackle,
    IceCube,
    Honey,
    GiftBox,
};

const {ccclass, property} = cc._decorator;

@ccclass
export default class ParasiteBase extends EntityBase {

    private _parasiteType:ParasiteType = ParasiteType.None;
    public getParasiteType() : ParasiteType {
        return this._parasiteType;
    }

    public hostCanDrop() : boolean {
        return this._hostCanDrop();
    }
    protected _hostCanDrop() : boolean {
        return false;
    }

    public hostCanSwap() : boolean {
        return this._hostCanSwap();
    }
    protected _hostCanSwap() : boolean {
        return false;
    }

    public hostCanMatch() : boolean {
        return this._hostCanMatch();
    }
    protected _hostCanMatch() : boolean {
        return false;
    }


    public canExcuteCommand(command:GridCommand):boolean{
        switch(command.type){
            case GridCommandType.BombClean:{
                return this.canCleanByBomb(command.cleanColor);
            }
            break;
            case GridCommandType.NeighborClean:{
                return this.canCleanByNeighbour(command.cleanColor);
            }
            break;
            case GridCommandType.MatchClean:{
                return this.canCleanByMatch();
            }
            break;
            case GridCommandType.TerminateClean:{
                return this.canCleanByTerminate(command.cleanColor);
            }
            break;
            default:{
                break;
            }
        }
        return false;
    }

    public excuteCommand(command:GridCommand):void{
        if(!this.canExcuteCommand(command)){
            return;
        }
        switch(command.type){
            case GridCommandType.BombClean:{
                this.onClean(EntityCleanOption.create(EntityCleanType.Bomb,
                    command.cleanRoot,
                    command.cleanColor,
                    command.createBombType,
                    command.hitNeighbour,
                    command.hitFloor,
                    command.layoutGrass));
            }
            break;
            case GridCommandType.NeighborClean:{
                this.onClean(EntityCleanOption.create(EntityCleanType.Neighbor,
                    command.cleanRoot,
                    command.cleanColor,
                    command.createBombType,
                    command.hitNeighbour,
                    command.hitFloor,
                    command.layoutGrass));
            }
            break;
            case GridCommandType.MatchClean:{
                this.onClean(EntityCleanOption.create(EntityCleanType.Match,
                    command.cleanRoot,
                    command.cleanColor,
                    command.createBombType,
                    command.hitNeighbour,
                    command.hitFloor,
                    command.layoutGrass));
            }
            break;
            case GridCommandType.TerminateClean:{
                this.onClean(EntityCleanOption.create(EntityCleanType.Terminate,
                    command.cleanRoot,
                    command.cleanColor,
                    command.createBombType,
                    command.hitNeighbour,
                    command.hitFloor,
                    command.layoutGrass));
            }
            break;
            default:{
                break;
            }
        }
    }

    public removeFromGrid():void{
        this.getGrid().removeParasiteAtLogicalPos(this.getLogicalPos());
    }

    public canCleanByBomb(cleanColor:EntityColor) : boolean {
        if (!this.getImmuneDamage()) {
            return this._canCleanByBomb(cleanColor);
        }
        return false;
    }
    protected _canCleanByBomb(cleanColor:EntityColor) : boolean {
        return false;
    }

    public canCleanByTerminate(cleanColor:EntityColor) : boolean {
        return this._canCleanByTerminate(cleanColor);
    }
    protected _canCleanByTerminate(cleanColor:EntityColor) : boolean {
        return this._canCleanByBomb(cleanColor);
    }

    public canCleanByMatch() : boolean {
        if (!this.getImmuneDamage()) {
            return this._canCleanByMatch();
        }
        return false;
    }
    protected _canCleanByMatch() : boolean {
        return false;
    }

    public canCleanByNeighbour(color:EntityColor) : boolean {
        if (!this.getImmuneDamage()) {
            return this._canCleanByNeighbour(color);
        }
        return false;
    }
    protected _canCleanByNeighbour(color:EntityColor) : boolean {
        return false;
    }
    
    public onClean(cleanOption:EntityCleanOption) : void {
        this._onClean(cleanOption);
        this.setImmuneDamage(true);
    }
    protected _onClean(cleanOption:EntityCleanOption) : void  {
        
    }

    public initParasite(grid:GridBase, parasiteType:ParasiteType, width:number = 1, height:number = 1) : void {
        super.initEntity(grid, EntityType.Parasite, width, height);
        this._parasiteType = parasiteType;
    }

    onDestroy() {
        super.onDestroy();
    }
}
