import GridBase from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateSwapNeighbour from "./GridStateSwapNeighbour";
import { PowerID } from "../../../defines/PowerID";
import ItemBase, { ItemType } from "../../../entity/item/ItemBase";
import { ParasiteType } from "../../../entity/parasite/ParasiteBase";
import GameMath from "../../../../../extend/utils/GameMath";
import ParasiteHoney from "../../../entity/parasite/ParasiteHoney";
import ParasiteFactory from "../../../entity/parasite/ParasiteFactory";
import ItemHoneyComb from "../../../entity/item/special/ItemHoneyComb";
import GridStateComboWord from "./GridStateComboWord";
import FencePos from "../../../entity/fence/FencePos";


export default class GridStateHoney extends GridStateBase {

    public static create() : GridStateHoney {
        return new GridStateHoney();
    }
    constructor() {
        super(GridState.Honey);
    }

    protected spreadHoney(grid:GridBase) {
        grid.lossPower(PowerID.SpreadHoney, 1);
        let itemsMap = grid.getItemsMap();

        let honeyPositions:Object[] = [];
        let offsets:cc.Vec2[] = [
            cc.v2(1, 0),
            cc.v2(-1, 0),
            cc.v2(0, 1),
            cc.v2(0, -1),
        ];

        for (let key in itemsMap) {
            let item = itemsMap[key] as ItemBase;            
            let canSpreadHoney = false;
            if (item.getItemType() == ItemType.HoneyComb) {
                canSpreadHoney = true;
            }
            let parasite = item.getTopParasite();
            if (parasite && parasite.getParasiteType() == ParasiteType.Honey) {
                canSpreadHoney = true;
            }

            if (canSpreadHoney) {
                let origin = item.getLogicalPos();
                for (let offset of offsets) {
                    let checkPos = origin.add(offset);
                    let item = grid.getItemAtLogicalPos(checkPos);
                    let fence = grid.getFenceAtFencePos(FencePos.create(origin, checkPos))
                    if (!fence) {
                        if (item && item.canCoveredByHoney()) {
                            honeyPositions.push({
                                'Source' : origin,
                                'Target' : checkPos,
                            });
                        }
                    }
                }
            }
        }


        if (honeyPositions.length > 0) {
            let randomIndex = GameMath.randomInt(0, honeyPositions.length - 1);
            let spreadInfo = honeyPositions[randomIndex];
            let spreadSource = spreadInfo['Source'];
            let spreadTarget = spreadInfo['Target'];
            let spreadHp = GameMath.randomInt(1, 2);

            let sourceItem = grid.getItemAtLogicalPos(spreadSource);
            let sourceHoney = sourceItem.getTopParasite() as ParasiteHoney;
            if (sourceHoney) {
                spreadHp = sourceHoney.getHealthPoint();
            }else{
                let honeyComb = sourceItem as ItemHoneyComb;
                spreadHp = honeyComb.getSpreadHoneyHp();
            }

            let targetItem = grid.getItemAtLogicalPos(spreadTarget);
            let newHoney = ParasiteHoney.create(grid, spreadHp);
            newHoney.grow();
            grid.setParasiteAtLogicalPos(newHoney, spreadTarget);
        }
    }

    public enter(grid:GridBase) {
        
        if(grid.getPowerAmount(PowerID.SpreadHoney) > 0){
            if(grid.getPowerAmount(PowerID.HoneyResist) <= 0){
                this.spreadHoney(grid);
            }
        }
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            grid.setCurrentState(GridStateComboWord.create());
        }
    }

    public update(grid:GridBase, delta:number) {
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            grid.setCurrentState(GridStateComboWord.create());
        }
    }
}
