    public getItemIDFromName(name:string) : GameCurrencyType {
        let result = this.Name_To_ItemID_Map[name];
        if (result) {
            return result;
        }
        return GameCurrencyType.NoneType;
    }

    public getItemInfoFromItemID(itemID:GameCurrencyType) : ItemInfo {
        let result = this.ItemID_To_FrameName_Map[itemID] as ItemInfo;
        if (result) {
            //
            if(itemID == 10140){
                if(!GameConfig.DigRedPacket){
                    result.description = 'itemDepict_10140_fix';
                }
            };

            let answer = ItemInfo.create('texture/icons/item/' + result.frameName,
                                          result.localization,
                                          result.description,
                                          result.isUnlimited,
                                          result.scaleX,
                                          result.scaleY);
            return answer;
        }
        return null;
    }

    public getUnlimitedItemIDs():GameCurrencyType[]{
        let unlimitedItemIDs = [
            GameCurrencyType.InfiniteBeginLine,
            GameCurrencyType.InfiniteBeginRange,
            GameCurrencyType.InfiniteBeginRainbow,
        ];
        return unlimitedItemIDs;
    }
