import GridCommand from "../../../commond/GridCommand";
import { EntityColor } from "../../../defines/EntityID";
import { PowerID } from "../../../defines/PowerID";
import FloorBase, { FloorType } from "../../../entity/floor/FloorBase";
import FloorRoad, { FloorRoadDirection } from "../../../entity/floor/FloorRoad";
import ItemBase, { ItemType } from "../../../entity/item/ItemBase";
import ItemBalloon from "../../../entity/item/special/ItemBalloon";
import ItemPuppy, { PuppyDirection } from "../../../entity/item/special/ItemPuppy";
import ItemStateSwap from "../../../entity/item/state/ItemStateSwap";
import GridBase from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateDrop from "./GridStateDrop";
import { GridStateMagicCrystal } from "./GridStateMagicCrystal";
import IGame from "../../../../../base/IGame";
import IdentifierManager from "../../../defines/IdentifierManager";
import GridCommandPair from "../../../commond/GridCommandPair";


export default class GridStateCollection extends GridStateBase {

    private _walkedItemPos:cc.Vec2[] = [];

    //item
    // private _curDogItem:ItemBase = null;

    public static create() : GridStateCollection {
        return new GridStateCollection();
    }
    constructor() {
        super(GridState.Collection);
    }

    public static setCurDogPawCount(grid:GridBase, count:number):void{
        grid.setPowerAmount(PowerID.PuppyVitality, count);
    }

    public static getCurDogPawCount(grid:GridBase):number{
        return grid.getPowerAmount(PowerID.PuppyVitality);
    }

    public enter(grid:GridBase) {

        //
        if(GridStateCollection.getCurDogPawCount(grid) > 0){
            //
            this.moveToNextItem(grid);
        }else{
            GridStateCollection.setCurDogPawCount(grid, 0);
            //
            grid.setCurrentState(GridStateMagicCrystal.create());
        }
    }

    public update(grid:GridBase, delta:number) {
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            if(GridStateCollection.getCurDogPawCount(grid) > 0){
                this.moveToNextItem(grid);
            }else{
                //
                this.CollectionDone(grid);
            }
        }
    }

    public exit(grid:GridBase) {
        this._walkedItemPos = null;
    }

    //
    public moveToItem(grid:GridBase, logicalPosA:cc.Vec2, logicalPosB:cc.Vec2):void{
        GridStateCollection.setCurDogPawCount(grid, Math.max(GridStateCollection.getCurDogPawCount(grid) - 1, 0));

        let itemA = grid.getItemAtLogicalPos(logicalPosA);
        let itemB = grid.getItemAtLogicalPos(logicalPosB);

        grid.swapItem(logicalPosA, logicalPosB);
        
        itemA.setCurrentState(ItemStateSwap.create(logicalPosA, logicalPosB, 0.5));

        if(itemB){
            itemB.setCurrentState(ItemStateSwap.create(logicalPosB, logicalPosA));
        }

        this._walkedItemPos.push(logicalPosA);
        this._walkedItemPos.push(logicalPosB);

        grid.gainPower(PowerID.PuppyMovedStep, 1);
    }

    //
    public attackPosition(grid:GridBase, pos:cc.Vec2):void{
        GridStateCollection.setCurDogPawCount(grid, Math.max(GridStateCollection.getCurDogPawCount(grid) - 1, 0));

        //
        this._walkedItemPos.push(pos);

        //
        grid.dispatchCommand(pos, GridCommand.BombClean(pos, EntityColor.None, false), 0.3);

        //
        let dogItem:ItemBase = this.getCurDogItem(grid);
        if(dogItem){
            grid.addObstructs(dogItem);

            let func = cc.callFunc(()=>{
                //
                let curRoad:FloorBase = grid.getFloorAtLogicalPos(dogItem.getLogicalPos());
                //TODO FloorType
                if(curRoad || curRoad.getType() == FloorType.Road){
                        let temRoad:FloorRoad = curRoad as FloorRoad;
                        let direction = PuppyDirection.None;

                        switch(temRoad.getOutDirection()){
                            case FloorRoadDirection.Up:{
                                direction = PuppyDirection.Back;
                                break;
                            }
                            case FloorRoadDirection.Right:{
                                direction = PuppyDirection.Right;
                                break;
                            }
                            case FloorRoadDirection.Down:{
                                direction = PuppyDirection.Front;
                                break;
                            }
                            case FloorRoadDirection.Left:{
                                direction = PuppyDirection.Left;
                                break;
                            }

                            default:{
                                break;
                            }
                        }

                        let puppy = dogItem as ItemPuppy;
                        puppy.attack(direction);
                }

            });
            
            let sequence = cc.sequence(func, cc.delayTime(0.5), cc.callFunc(()=>{
                grid.removeObstructs(dogItem);
            }));

            dogItem.runAction(sequence);
        }
    }

    //item
    protected getCurDogItem(grid:GridBase):ItemBase{
        // if(this._curDogItem ){
        //     return this._curDogItem;
        // }

        let itemMap = grid.getItemsMap();
        for(const key in itemMap){
            let tmpItem:ItemBase = itemMap[key];
            if(tmpItem.getItemType() == ItemType.Puppy){
                //this._curDogItem = tmpItem;
                return tmpItem;
            }
        }

        return null;
    }

    //
    protected moveToNextItem(grid:GridBase):void{
        //item
        let dogItem = this.getCurDogItem(grid);
        if(!dogItem){
            //
            this.CollectionDone(grid);
            return;
        }

        //
        let nextRoad:FloorBase = this.findNextRoadFloorItem(grid);
        if(nextRoad){
            let nextItem = grid.getItemAtLogicalPos(nextRoad.getLogicalPos());
            let nextParasite = grid.getParasiteAtLogicalPos(nextRoad.getLogicalPos());

            if (nextParasite && nextItem) {
                if (nextParasite.hostCanSwap() && nextItem.canSwap()) {
                    this.moveToItem(grid, dogItem.getLogicalPos(), nextRoad.getLogicalPos());
                }else{
                    nextParasite.setImmuneDamage(false);
                    if(nextParasite.canCleanByBomb(EntityColor.None)){
                        //()
                        this.attackPosition(grid, nextRoad.getLogicalPos());
                    }else{
                        //
                        this.CollectionDone(grid);
                    }
                }
            }else if (nextParasite && !nextItem) {
                if(!nextParasite.hostCanSwap()){
                    nextParasite.setImmuneDamage(false);
                    if(nextParasite.canCleanByBomb(EntityColor.None)){
                        //()
                        this.attackPosition(grid, nextRoad.getLogicalPos());
                    }else{
                        //
                        this.CollectionDone(grid);
                    }
                }else{
                    //
                    this.moveToItem(grid, dogItem.getLogicalPos(), nextRoad.getLogicalPos());
                }
            }else if (!nextParasite && nextItem) {

                if(nextItem.getItemType() == ItemType.DogHouse){
                    //
                    //TODO 
                    grid.addObstructs(dogItem);
                    let sequence = cc.sequence(cc.moveTo(1.0, nextRoad.node.getPosition()), cc.callFunc(()=>{
                        GridStateCollection.setCurDogPawCount(grid, 0);
    
                        dogItem.node.setPosition(0, 0);
                        //
                        dogItem.removeFromGrid();
                        let size = nextItem.node.getContentSize();
                        dogItem.node.parent = nextItem.node;
        
    
                        let puppy = dogItem as ItemPuppy;
                        puppy.win();
                        grid.removeObstructs(dogItem);
                        grid.setCurrentState(GridStateDrop.create(this._walkedItemPos));
                        grid.gainPower(PowerID.PuppyMovedStep, 1);
                    }))
    
                    dogItem.node.zIndex = GridBase.ZORDER_EFFECT;
                    dogItem.runAction(sequence);
                    // cc.log('dog win action!!' + nextRoad.node.getPosition().x + ',' + nextRoad.node.getPosition().y);
                    dogItem.onCollected();
                }else if(nextItem.getItemType() == ItemType.Balloon){
                    //
                    //->->
                    let flyFloor:FloorBase = grid.getFloorAtLogicalPos((nextItem as ItemBalloon).getCurPostal());
                    if(!flyFloor || flyFloor.getType() != FloorType.Road){
                        //
                        this.CollectionDone(grid);
                    }
    
                    //
                    grid.addObstructs(dogItem);
                    dogItem.node.zIndex = GridBase.ZORDER_EFFECT;
    
                    let sequence = cc.sequence(cc.moveTo(1.0, nextItem.node.getPosition()),
                    cc.callFunc(()=>{
                        let puppy = dogItem as ItemPuppy;
                        puppy.fly();
                        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('balloon'));
                        //
                        this.clearOneItem(grid, nextItem.getLogicalPos(), 1.0);
                    }),
                    cc.moveTo(1.6, flyFloor.node.getPosition()),
                    cc.delayTime(0.2),
                    cc.callFunc(()=>{
                        this.clearOneItem(grid, flyFloor.getLogicalPos(), 0);
                        GridStateCollection.setCurDogPawCount(grid, GridStateCollection.getCurDogPawCount(grid) - 1);
                        //
                        grid.swapItem(dogItem.getLogicalPos(), flyFloor.getLogicalPos());
                        grid.removeObstructs(dogItem);
    
                        let puppy = dogItem as ItemPuppy;
                        puppy.stay();
                        grid.gainPower(PowerID.PuppyMovedStep, 1);
                    })
                    );
    
                    dogItem.runAction(sequence);
                }else{
                    if(!nextItem.canSwap()){
                        nextItem.setImmuneDamage(false);
                        if(nextItem.canCleanByBomb(EntityColor.None)){
                            //()
                            this.attackPosition(grid, nextRoad.getLogicalPos());
                        }else{
                            //
                            this.CollectionDone(grid);
                        }
                    }else{
                        //
                        this.moveToItem(grid, dogItem.getLogicalPos(), nextRoad.getLogicalPos());
                    }
                }
            }else{
                this.moveToItem(grid, dogItem.getLogicalPos(), nextRoad.getLogicalPos());
            }
        }
    }

    //
    protected findNextRoadFloorItem(grid:GridBase):FloorBase{
        let dogItem:ItemBase = this.getCurDogItem(grid);
        if(!dogItem){
            return null;
        }

        //
        let curRoad:FloorBase = grid.getFloorAtLogicalPos(dogItem.getLogicalPos());
        if(!curRoad || curRoad.getType() != FloorType.Road){
            //
            return null;
        }

        //
        let nextRoadPos:cc.Vec2 = curRoad.getLogicalPos();
        let temRoad:FloorRoad = curRoad as FloorRoad;

        let dogAction:string = '';
        let direction:PuppyDirection = PuppyDirection.None;
        switch(temRoad.getOutDirection()){
            case FloorRoadDirection.Up:{
                nextRoadPos = nextRoadPos.add(cc.v2(0,1));
                direction = PuppyDirection.Back;
                break;
            }
            case FloorRoadDirection.Right:{
                nextRoadPos = nextRoadPos.add(cc.v2(1, 0));
                direction = PuppyDirection.Right;
                break;
            }
            case FloorRoadDirection.Down:{
                nextRoadPos = nextRoadPos.add(cc.v2(0, -1));
                direction = PuppyDirection.Front;
                break;
            }
            case FloorRoadDirection.Left:{
                nextRoadPos = nextRoadPos.add(cc.v2(-1, 0));
                direction = PuppyDirection.Left;
                break;
            }
            default:{
                nextRoadPos = nextRoadPos.add(cc.v2(-1, 0));
                break;
            }
        }

        let nextRoad:FloorBase = grid.getFloorAtLogicalPos(nextRoadPos);
        if(nextRoad && nextRoad.getType() == FloorType.Road){
            let puppy:ItemPuppy = dogItem as ItemPuppy;
            puppy.move(direction);
            return nextRoad;
        }

        return null;

    }

    //
    protected CollectionDone(grid:GridBase):void{
        GridStateCollection.setCurDogPawCount(grid, 0);

        let puppy:ItemPuppy = this.getCurDogItem(grid) as ItemPuppy;
        if(puppy){
            puppy.stay();
        }

        grid.setCurrentState(GridStateDrop.create(this._walkedItemPos));
    }

    protected clearOneItem(grid:GridBase, pos:cc.Vec2, delayTime:number):boolean{
        this._walkedItemPos.push(pos);
        let commandPair = GridCommandPair.create(pos, GridCommand.TerminateClean(pos, EntityColor.None, false), 0 , 0);
        grid.excuteCommandPair(commandPair);
        return true;
    }
}
