export class StringUtil{
    /**
     * 
     */
    public constructor() {
    }

    /**
     * 
     * @param str
     * @returns {string}
     */
    public static trimSpace(str:string):string {
        return str.replace(/^\s*(.*?)[\s\n]*$/g, '$1');
    }

    ///M/W...
    public static adjustFontByLength(str:string,maxLength:number):string{
        var temp_str:string;
        if(this.getStringLength(str) <= maxLength){
            return str;
        }else{
            for(var i=str.length-2;i>1;i--){
                if(this.getStringLength(str.substring(0,i)) <= (maxLength-1)){
                    return str.substring(0,i) + "...";
                }
            }
        }
    }

    /**
     * 
     * @param str
     */
    public static sliceStringByLength(str:string,maxLength:number):string {
        var strArr = str.split("");
        var length = 0;
        var temp="";
        for (var i = 0; i < str.length; i++) {
            var s = strArr[i];
            temp += s;
            if (this.isChinese(s)) {
                length += 2;
            }else {
                length += 1;
            }
            if(length >= maxLength){
                break;
            }
        }
        return temp;
    }

    /**
     * W/H/2
     * @param str
     */
    public static getStringLength(str:string):number {
        var strArr = str.split("");
        var length = 0;
        for (var i = 0; i < strArr.length; i++) {
            var s = strArr[i];
            if (this.isChinese(s)) {
                length += 2;
            }else if (this.isMW(s)) {
                length += 2;
            } else {
                length += 1;
            }
        }
        return length;
    }

    /**
     * 2
     * @param str
     */
    public static getStringLength_CH(str:string):number {
        var strArr = str.split("");
        var length = 0;
        for (var i = 0; i < strArr.length; i++) {
            var s = strArr[i];
            if (this.isChinese(s)) {
                length += 2;
            }else {
                length += 1;
            }
        }
        return length;
    }

    /**
     * 
     * @param str
     * @returns {boolean}
     */
    public static isChinese(str:string):boolean {
        var reg = /^[\u4E00-\u9FA5]+$/;
        if (reg.test(str)) {
            return true;
        }
        return false;
    }

    public static getClassName(cls:Object){
        let tmp:string = cls.toString();
        let re = /function\s*(\w*)/ig;
        var matches = re.exec(tmp); //  function xxx() 
        // alert(matches[1]);
        return matches[1];
    }

    /**
     * MW
     * @param str
     * @returns {boolean}
     */
    public static isMW(str:string):boolean {
        var reg = /^[MW]+$/;
        if (reg.test(str)) {
            return true;
        }
        return false;
    }
    
    /**
     * 
     */ 
    public static replaceByPos(str:string, pos:number ,rep:string) {
        //
        if(pos > str.length){
            return str;
        }
        var retStr = str.substr(0,pos - 1) + rep + str.substring(pos,str.length);
        return retStr;
    }
    
    /**
     * 
     */ 
    public static format(str:string, ...args: any[]):string {
        var result:string = str;
        if(args.length < 1) {
            return result;
        }

        for(var key in args) {
            var value = args[key];
            if(undefined != value) {
                result = result.replace("{" + key + "}",value);
            }
        }
        return result;
    }
    
    public static contain(srcStr: string,subStr: string): boolean {
        var index: number = srcStr.indexOf(subStr);
        if(index >= 0) {
            return true;
        } else {
            return false;
        }
    }


    /**
     * charlength
     * @param str
     * @param length
     * @param char
     */
    public static fill(str:string, length:number, char:string = "0"){
        while(str.length < length){
            str = char + str;
        }

        return str;
    }

    //json
    public static saleParseJson(str:string):Object{

        try {
            let tmpObj = JSON.parse(str);
            if (typeof tmpObj == "object") {
                return tmpObj;
            }
        } catch(e) {
        }

        return null;
    }
}