const { ccclass, property } = cc._decorator;

@ccclass
export default class PopsicleLine extends PopsicleBase {

    private static SizeToEntityInfoMap: Object = {
        '{1, 2}': {
            'EntityID': EntityID.PopsicleLine,
            'NeedRotate': false
        },
        '{2, 1}': {
            'EntityID': EntityID.PopsicleLine,
            'NeedRotate': true
        },
    };

    private static getEntityInfoWithSize(size: cc.Size): Object {
        let key = '{' + size.width + ', ' + size.height + '}';
        return this.SizeToEntityInfoMap[key];
    }

    public getCollectionID(): CollectionID {
        return CollectionID.PopsicleLine;
    }

    public getEntityID(): EntityID {
        return EntityID.PopsicleLine;
    }

    public getScore(): number {
        let size = this.getLogicalSize()
        return 100 * size.width * size.height;
    }

    protected updatePopsicle(): void {
        super.updatePuppet();

        let rotation = 0;
        let entityInfo = PopsicleLine.getEntityInfoWithSize(this.getLogicalSize());
        if (entityInfo) {
            let needRotate = entityInfo['NeedRotate'];
            if (needRotate) {
                rotation = GameMath.randomInt(0, 100) < 50 ? -90 : 90;
            }
        }
        this._puppet.angle = -rotation;
    }

    protected _onBePopsicleClean(): void {
        super._onBePopsicleClean();
        this.getGrid().gainScoreWithEntity(this);
        let rayDirections: Pair<cc.Vec2, boolean>[] = [];
        let size = this.getLogicalSize();
        if (size.width == 2) {
            rayDirections.push(new Pair<cc.Vec2, boolean>(cc.v2(1, 0), true));
            rayDirections.push(new Pair<cc.Vec2, boolean>(cc.v2(-1, 0), false));
        } else if (size.height == 2) {
            rayDirections.push(new Pair<cc.Vec2, boolean>(cc.v2(0, 1), true));
            rayDirections.push(new Pair<cc.Vec2, boolean>(cc.v2(0, -1), false));
        }

        let grid = this.getGrid();
        for (let ray of rayDirections) {
            let cannon = FlyerPopsicleLine.create(grid,
                this.getEntityColor(),
                false,
                this.getLogicalPos(),
                ray.first,
                ray.second);
            grid.setFlyerAtLogicalPos(cannon, this.getLogicalPos());
        }
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('popsiclesline'));
    }



    protected init(grid: GridBase, width: number, height: number): void {
        super.initPopsicle(grid, width, height, PopsicleType.Line);
        this.updatePopsicle();
    }

    public static create(grid: GridBase, width: number, height: number): PopsicleLine {

        let className = 'PopsicleLine';
        let prefabFileName = 'match_scene/prefab/entity/popsicle/PopsicleLine';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as PopsicleLine;
        if (component) {
            component.init(grid, width, height);
            return component;
        }
        return null;
    }

    public clone(): PopsicleLine {
        let item = PopsicleLine.create(this.getGrid(), this.getLogicalSize().width, this.getLogicalSize().height);
        return item;
    }

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {

    }

    // update (dt) {}
}
