import { EntityID } from "../../defines/EntityID";
import IdentifierManager from "../../defines/IdentifierManager";
import { KindID } from "../../defines/KindID";
import GridBase from "../../grid/GridBase";
import CellBase from "./CellBase";
import CellNormal from "./CellNormal";
import CellConveyer, { CellConveyerDirection } from "./CellConveyer";
import CellMovablePlate from "./CellMovablePlate";

export default class CellFactory  {

    public static generateCellWithEntityID(grid:GridBase, entityID:EntityID) : CellBase {
        let cell:CellBase = null;

        switch (entityID) {
            case EntityID.CellNormal:{
                cell = CellNormal.create(grid);
            }
                break;
            case EntityID.CellConveyerL2R:{

                cell = CellConveyer.createConveyer(grid, CellConveyerDirection.Left, CellConveyerDirection.Right);
            }
                break;
            case EntityID.CellConveyerL2U:{
                cell = CellConveyer.createConveyer(grid, CellConveyerDirection.Left, CellConveyerDirection.Up);
            }
                break;
            case EntityID.CellConveyerL2D:{
                cell = CellConveyer.createConveyer(grid, CellConveyerDirection.Left, CellConveyerDirection.Down);
            }
                break;
            case EntityID.CellConveyerR2L:{
                cell = CellConveyer.createConveyer(grid, CellConveyerDirection.Right, CellConveyerDirection.Left);
            }
                break;
            case EntityID.CellConveyerR2U:{
                cell = CellConveyer.createConveyer(grid, CellConveyerDirection.Right, CellConveyerDirection.Up);
            }
                break;
            case EntityID.CellConveyerR2D:{
                cell = CellConveyer.createConveyer(grid, CellConveyerDirection.Right, CellConveyerDirection.Down);
            }
                break;
            case EntityID.CellConveyerU2L:{
                cell = CellConveyer.createConveyer(grid, CellConveyerDirection.Up, CellConveyerDirection.Left);
            }
                break;
            case EntityID.CellConveyerU2R:{
                cell = CellConveyer.createConveyer(grid, CellConveyerDirection.Up, CellConveyerDirection.Right);
            }
                break;
            case EntityID.CellConveyerU2D:{
                cell = CellConveyer.createConveyer(grid, CellConveyerDirection.Up, CellConveyerDirection.Down);
            }
                break;
            case EntityID.CellConveyerD2L:{
                cell = CellConveyer.createConveyer(grid, CellConveyerDirection.Down, CellConveyerDirection.Left);
            }
                break;
            case EntityID.CellConveyerD2R:{
                cell = CellConveyer.createConveyer(grid, CellConveyerDirection.Down, CellConveyerDirection.Right);
            }
                break;
            case EntityID.CellConveyerD2U:{
                cell = CellConveyer.createConveyer(grid, CellConveyerDirection.Down, CellConveyerDirection.Up);
            }
                break;
            case EntityID.CellMovablePlate:{
                cell = CellMovablePlate.create(grid);
            }
                break;
            
            default:
                break;
        }
        return cell; 
    }

    public static generateCellWithKindID(grid:GridBase, kindID:KindID) : CellBase {
        let entityID = IdentifierManager.getInstance().getEntityIDFromKindID(kindID);
        return this.generateCellWithEntityID(grid, entityID);
    }


}