import EntityEventBase from "../../event/EntityEventBase";
import StateMachineBase from "../../fsm/StateMachineBase";
import GridBase from "../GridBase";
import GridStateBase from "./GridStateBase";


export default class GridStateMachine extends StateMachineBase {   

    public static create(grid:GridBase) : GridStateMachine {
        return new GridStateMachine(grid);
    }

    public onTapBegan(logicalPos:cc.Vec2):void{
        if (this._currentState) {
            if (this._currentState instanceof GridStateBase) {
                let currentState = this._currentState as GridStateBase;

                if(this._owner instanceof GridBase) {
                    let onwer = this._owner as GridBase;
                    currentState.onTapBegan(onwer, logicalPos)
                }
            }
        }

        if (this._globalState) {
            if (this._globalState instanceof GridStateBase) {
                let globalState = this._globalState as GridStateBase;

                if(this._owner instanceof GridBase) {
                    let onwer = this._owner as GridBase;
                    globalState.onTapBegan(onwer, logicalPos)
                }
            }
        }
    }

    public onTapMoved(logicalPos:cc.Vec2):void{
        if (this._currentState) {
            if (this._currentState instanceof GridStateBase) {
                let currentState = this._currentState as GridStateBase;

                if(this._owner instanceof GridBase) {
                    let onwer = this._owner as GridBase;
                    currentState.onTapMoved(onwer, logicalPos)
                }
            }
        }

        if (this._globalState) {
            if (this._globalState instanceof GridStateBase) {
                let globalState = this._globalState as GridStateBase;

                if(this._owner instanceof GridBase) {
                    let onwer = this._owner as GridBase;
                    globalState.onTapMoved(onwer, logicalPos)
                }
            }
        }
    }

    public onTapEnded(logicalPos:cc.Vec2):void{
        if (this._currentState) {
            if (this._currentState instanceof GridStateBase) {
                let currentState = this._currentState as GridStateBase;

                if(this._owner instanceof GridBase) {
                    let onwer = this._owner as GridBase;
                    currentState.onTapEnded(onwer, logicalPos)
                }
            }
        }

        if (this._globalState) {
            if (this._globalState instanceof GridStateBase) {
                let globalState = this._globalState as GridStateBase;

                if(this._owner instanceof GridBase) {
                    let onwer = this._owner as GridBase;
                    globalState.onTapEnded(onwer, logicalPos)
                }
            }
        }
    }

    public onEvent(event:EntityEventBase):void{
        if (this._currentState) {
            if (this._currentState instanceof GridStateBase) {
                let currentState = this._currentState as GridStateBase;

                if(this._owner instanceof GridBase) {
                    let onwer = this._owner as GridBase;
                    currentState.onEvent(onwer, event)
                }
            }
        }

        if (this._globalState) {
            if (this._globalState instanceof GridStateBase) {
                let globalState = this._globalState as GridStateBase;

                if(this._owner instanceof GridBase) {
                    let onwer = this._owner as GridBase;
                    globalState.onEvent(onwer, event)
                }
            }
        }
    }

    public onRespone(event:Object):void{
        if (this._currentState) {
            if (this._currentState instanceof GridStateBase) {
                let currentState = this._currentState as GridStateBase;

                if(this._owner instanceof GridBase) {
                    let onwer = this._owner as GridBase;
                    currentState.onRespone(onwer, event)
                }
            }
        }

        if (this._globalState) {
            if (this._globalState instanceof GridStateBase) {
                let globalState = this._globalState as GridStateBase;

                if(this._owner instanceof GridBase) {
                    let onwer = this._owner as GridBase;
                    globalState.onRespone(onwer, event)
                }
            }
        }
    }
}
