
export class BehaviourEvent extends cc.Event.EventCustom {
    public static BehaviourEventName = 'BehaviourEventName';
    
    public static create(behaviourName:string) : BehaviourEvent {
        let event =  new BehaviourEvent();
        event.behaviourName = behaviourName;
        return event;
    }
    public behaviourName:string = '';
    constructor() {
        super(BehaviourEvent.BehaviourEventName, true);
    }
} 

