import ItemBase, { ItemType } from "../ItemBase";
import GridBase from "../../../grid/GridBase";
import { CollectionID } from "../../../defines/CollectionID";
import { EntityID, EntityCleanOption, EntityColor } from "../../../defines/EntityID";
import GridCommand from "../../../commond/GridCommand";
import FlyerAnimation, { FlyerAnimationType } from "../../flyer/FlyerAnimation";
import IGame from "../../../../../base/IGame";
import IdentifierManager from "../../../defines/IdentifierManager";
import EntityPrefabNodePool from "../../EntityPrefabNodePool";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemBone extends ItemBase {

    public static create(grid:GridBase):ItemBone{

        let className = 'ItemBone';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemBone';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemBone;
        if (component) {
            component.init(grid); 
            return component;       
        }
        return null;
    }

    public clone():ItemBone {
        let item = ItemBone.create(this.getGrid());
        return item;
    }

    public getCollectionID():CollectionID{
        return CollectionID.ItemBone;
    }

    public getEntityID():EntityID{
        return EntityID.ItemBone;
    }

    public getScore():number{
        return 500;
    }

    protected init(grid:GridBase){
        super.initItem(grid, ItemType.Bone);
    }

    protected _canDrop():boolean{
        return true;
    }

    protected _canSwap():boolean{
        return true;
    }

    protected _canMatch():boolean{
        return false;
    }

    protected _canCleanBySuicide():boolean{
        return true;
    }

    protected _satisfyMatch():boolean{
        let cell = this.getGrid().getCellAtLogicalPos(this.getLogicalPos());
        if(cell && cell.getIsBoneCollector()){
            return true;
        }
        return false;
    }

    protected _onSatisfyMatch(basePriority:number):void{
        this.getGrid().dispatchCommand(this.getLogicalPos(), GridCommand.SuicideClean(this.getLogicalPos(), 
                    EntityColor.None, false), 0);
    }

    protected _canMatchWithNeighbourSwap(swapPos:cc.Vec2):boolean{
        let item = this.getGrid().getItemAtLogicalPos(swapPos);
        if(item){
            if(item.getEntityID() == EntityID.ItemBone){
                return false;
            }

            if(item.getEntityID() == EntityID.ItemBombRainbow ||
                item.canExcuteCommand(GridCommand.NeighbourSwap(this.getLogicalPos()))){
                    return this._satisfyMatch();
                }
        }
        return false;
    }

    protected _onMatchWithNeighbourSwap(swapPos:cc.Vec2):void{
        this.getGrid().dispatchCommand(this.getLogicalPos(), GridCommand.SuicideClean(this.getLogicalPos(),
            EntityColor.None, false), 0);
    }

    protected _onClean(cleanOption:EntityCleanOption):void{
        this.setVisible(false);
        this.getGrid().gainScoreWithEntity(this);
        this.progressDeathWish(cleanOption);
        this.selfDestory(true);
        let flyer = FlyerAnimation.createAnimationWithType(this.getGrid(), FlyerAnimationType.CommonHurt);
        this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
        flyer.node.zIndex = this.getTopZOrder();
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('bone'));
    }
}
