import { EntityCleanOption, EntityType, EntityCleanType, EntityColor } from "../../defines/EntityID";
import GridBase from "../../grid/GridBase";
import EntityBase from "../EntityBase";
import GridCommand, { GridCommandType } from "../../commond/GridCommand";


export enum FloorType {
    Ice,
    Double,
    Road,
    Changer,
    Grass,
};


const {ccclass, property} = cc._decorator;

@ccclass
export default class FloorBase extends EntityBase {

    private _type:FloorType = null;
    public getFloorType() : FloorType{
        return this._type;
    }

    public initFloor(grid:GridBase, type:FloorType):void{
        super.initEntity(grid, EntityType.Floor);
        this._type = type;
    }

    public getType():FloorType{
        return this._type;
    }

    public canExcuteCommand(command:GridCommand):boolean{
        switch(command.type){
            case GridCommandType.BombClean:{
                return this.canCleanByBomb(command.cleanColor);
            }
            break;
            case GridCommandType.FloorClean:{
                return this.canBeFloorClean();
            }
            break;
            case GridCommandType.TerminateClean:{
                return this.canCleanByTerminate(command.cleanColor);
            }
            break;
            default:{
                break;
            }
        }
        return false;
    }

    public excuteCommand(command:GridCommand):void{
        if(!this.canExcuteCommand(command)){
            return;
        }
        switch(command.type){
            case GridCommandType.BombClean:{
                this.onClean(EntityCleanOption.create(EntityCleanType.Bomb,
                    command.cleanRoot,
                    command.cleanColor,
                    command.createBombType,
                    command.hitNeighbour,
                    command.hitFloor,
                    command.layoutGrass));
            }
            break;
            case GridCommandType.FloorClean:{
                this.onClean(EntityCleanOption.create(EntityCleanType.Floor,
                    command.cleanRoot,
                    command.cleanColor,
                    command.createBombType,
                    command.hitNeighbour,
                    command.hitFloor,
                    command.layoutGrass));
            }
            break;
            case GridCommandType.TerminateClean:{
                this.onClean(EntityCleanOption.create(EntityCleanType.Terminate,
                    command.cleanRoot,
                    command.cleanColor,
                    command.createBombType,
                    command.hitNeighbour,
                    command.hitFloor,
                    command.layoutGrass));
            }
            break;
            default:{
                break;
            }
        }
    }

    public canCleanByTerminate(cleanColor:EntityColor) : boolean {
        return this._canCleanByTerminate(cleanColor);
    }
    

    public canCleanByBomb(cleanColor:EntityColor):boolean{
        if(!this.getImmuneDamage()){
            return this._canCleanByBomb(cleanColor);
        }
        return false;
    }

    public canBeFloorClean():boolean{
        if(!this.getImmuneDamage()){
            return this._canBeFloorClean();
        }
        return false;
    }

    public onClean(cleanOption:EntityCleanOption){
        this._onClean(cleanOption);
        this.setImmuneDamage(true);
    }

    public removeFromGrid():void{
        this.getGrid().removeFloorAtLogicalPos(this.getLogicalPos());
    }

    protected _canCleanByTerminate(cleanColor:EntityColor) : boolean {
        return false;
    }

    protected _canCleanByBomb(cleanColor:EntityColor):boolean{
        return false;
    }

    protected _canBeFloorClean():boolean{
        return false;
    }

    protected _onClean(cleanOption:EntityCleanOption):void{

    }

}
