import { EntityID } from "../../defines/EntityID";
import IdentifierManager from "../../defines/IdentifierManager";
import { KindID } from "../../defines/KindID";
import GridBase from "../../grid/GridBase";
import FenceBase from "./FenceBase";
import FenceVine from "./FenceVine";
import FencePos from "./FencePos";

export default class FenceFactory  {

    public static generateItemWithEntityID(grid:GridBase, logicalPos:cc.Vec2, entityID:EntityID) : FenceBase {
        let fence:FenceBase = null;
        
        switch (entityID) {
            case EntityID.FenceVineHorizontal:{
                fence = FenceVine.create(grid);
                fence.setFencePos(FencePos.create(logicalPos, logicalPos.add(cc.v2(0, -1))));
            }
                break;
            case EntityID.FenceVineVertical:{
                fence = FenceVine.create(grid);
                fence.setFencePos(FencePos.create(logicalPos, logicalPos.add(cc.v2(-1, 0))));
            }
                break;
        
            default:
                break;
        }
        return fence;
    }

    public static generateItemWithKindID(grid:GridBase, logicalPos:cc.Vec2, kindID:KindID) : FenceBase {
        let entityID = IdentifierManager.getInstance().getEntityIDFromKindID(kindID);
        return this.generateItemWithEntityID(grid, logicalPos, entityID);
    }
}
