
export enum EntityEventType{
    EntityChanged,
};

export default class EntityEventBase extends cc.Event.EventCustom {
    public static ENTITY_EVENT_KEY:string = 'ENTITY_EVENT_KEY';
    private _type:EntityEventType;
    public getEventType() : EntityEventType {
        return this._type;
    }
    constructor(type:EntityEventType) {
        super(EntityEventBase.ENTITY_EVENT_KEY, true);
        this._type = type;
    }
}
