import EntityStateBase, { EntityStateType } from "../../state/EntityStateBase";
import GameMath from "../../../../../extend/utils/GameMath";
import EntityBase from "../../EntityBase";
import EntityStateWait from "../../state/concreteState/EntityStateWait";
import GridBase from "../../../grid/GridBase";


export default class ItemStateShuffle extends EntityStateBase {

    public static SHUFFLE_DRUATION:number = 1;
    public static create(fromPos:cc.Vec2, toPos:cc.Vec2, duration:number = ItemStateShuffle.SHUFFLE_DRUATION) : ItemStateShuffle {
        return new ItemStateShuffle(fromPos, toPos, duration);
    }

    private _fromPos:cc.Vec2 = GameMath.INVALID_LOGICAL_POSITION.clone();
    private _toPos:cc.Vec2 = GameMath.INVALID_LOGICAL_POSITION.clone();
    private _duration:number = ItemStateShuffle.SHUFFLE_DRUATION; 

    constructor(fromPos:cc.Vec2, toPos:cc.Vec2, duration:number) {
        super(EntityStateType.Shuffle);
        this._fromPos = fromPos;
        this._toPos = toPos;
        this._duration = duration;
    }

    enter(entity:EntityBase):void {
        let grid = entity.getGrid();
        grid.addObstructs(entity);
        
        let cellSize = grid.getCellSize();
        let startPos = entity.getPixelPos(this._fromPos);
        entity.node.setPosition(startPos);
        entity.node.zIndex = GridBase.ZORDER_EFFECT;
        entity.shiftToEffectLayer();
        let duration = this._duration + GameMath.random(this._duration * -0.5, this._duration * 0.5);

        let fly = cc.spawn(cc.moveTo(duration * 0.33, startPos.add(cc.v2(0, cellSize.height * 0.75))),
                           cc.scaleTo(duration * 0.33, 1.2));

        let stay = cc.delayTime(duration * 0.34);

        let move = cc.spawn(cc.moveTo(duration * 0.33, entity.getPixelPos(this._toPos)),
                            cc.scaleTo(duration, 1));
        let callback = cc.callFunc(function() {
            grid.setEntity(entity,entity.getLogicalPos());
            entity.setCurrentState(EntityStateWait.create());
        }, this);
        entity.runAction(cc.sequence(fly, stay, move, callback));
    }

    update(entity:EntityBase, delta:number):void {
        
    }
    
    exit(entity:EntityBase):void {
        entity.getGrid().removeObstructs(entity);
    }
}
