const {ccclass, property} = cc._decorator;

@ccclass
export default class CompleteBanner extends cc.Component {

    @property(cc.Node)
    maskView:cc.Node = null;

    @property(cc.Label)
    title:cc.Label = null;


    private _completeCallback:Function = null;
    // LIFE-CYCLE CALLBACKS:
    // onLoad () {}
    private _displayDuraion:number = 0; 

    public static create(level:number, completeCallback:(object:Object) => void = null, hideLevel:boolean = false) : CompleteBanner {
        let res = cc.loader.getRes('match_scene/prefab/userInterface/CompleteBanner', cc.Prefab);
        if (res) {
            let node = cc.instantiate(res);
            if (node) {
                let component = node.getComponent(CompleteBanner) as CompleteBanner;
                if (component) {
                    component.init(level, completeCallback, hideLevel);
                }
                return component;   
            }
        }
    }

    protected init(level:number, completeCallback:(object:Object) => void, hideLevel:boolean) : void {
        this._completeCallback = completeCallback;
        if (hideLevel) {
            this.title.string = ' ';
        }else{
            this.title.string = '' + level.toString() + '';
        }
    }

    start () {

        let canvas = cc.find("Canvas");
        if (canvas) {
            let gameScene = canvas.getComponent(GameSceneBase);
            gameScene.reportStepComplete();
        }
        let anim = this.node.getComponent(cc.Animation);
        anim.play('appear');
        
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('voice_level_complete'));
    }

    public didAppear () : void{
        this._displayDuraion = 2.2;

        this.maskView.on("touchstart", this.onTouchStart, this);
        let anim = this.node.getComponent(cc.Animation);
        anim.play('idle');

    }

    update (dt) {
        dt = dt * cc.director.getScheduler().getTimeScale();
        if (this._displayDuraion > 0) {
            this._displayDuraion -= dt;   
            if (this._displayDuraion <= 0) {
                let anim = this.node.getComponent(cc.Animation);
                anim.play('disappear');
            } 
        }
    }

    public didDisappear () : void{
        if (this._completeCallback) {
            this._completeCallback.call(this, null);
        }
        this.node.destroy();
    }

    private onTouchStart(evt:cc.Event.EventTouch) {
        if (this._displayDuraion > 0) {
            this._displayDuraion = 0;
            let anim = this.node.getComponent(cc.Animation);
            anim.play('disappear');
        }
    }

    onDestroy() {
        this._completeCallback = null;
    }
    
}
