const {ccclass, property} = cc._decorator;

@ccclass
export default class FrameBase extends EntityBase {
    
    protected getPositionCode(pos:cc.Vec2) {

        let bitOffsets:cc.Vec2[] = [
            cc.v2(1, 0),
            cc.v2(1, 1),
            cc.v2(0, 1),
            cc.v2(-1, 1),
            cc.v2(-1, 0),
            cc.v2(-1, -1),
            cc.v2(0, -1),
            cc.v2(1, -1),
        ];
        let cell = this.getGrid().getCellAtLogicalPos(pos);
        if (cell) {
            if (cell.getCellType() != CellType.MovablePlate) {
                return 0;
            }
        }
        
        let code = 0;
        let bitValue = 1;
    
        for (let offset of bitOffsets) {
            let checkPos = pos.add(offset);
            let cell = this.getGrid().getCellAtLogicalPos(checkPos);
            if (cell) {
                if (cell.getCellType() != CellType.MovablePlate) {
                    code += bitValue;
                }
            }
            bitValue *= 2;
        }
        return code;
    }

    protected initFrame(grid:GridBase) : void {
        super.initEntity(grid, EntityType.Frame, 1, 1);
    }
}
