import CurveToAction from "../../../extend/action/CurveToAction";
import { CollectionID } from "../defines/CollectionID";
import GameSceneBase from "../GameSceneBase";
import TargetItem from "./TargetItem";

// Learn TypeScript:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/typescript.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/typescript.html
// Learn Attribute:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/reference/attributes.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;

@ccclass
export default class TargetBanner extends cc.Component {

    @property(cc.Node)
    maskView:cc.Node = null;

    @property(cc.Label)
    title:cc.Label = null;

    @property(cc.Node)
    targetFrame:cc.Node = null;

    private _displayDuraion:number = 0; 
    private _gameScene:GameSceneBase = null;
    private _targetItems:TargetItem[] = [];

    public static create(level:number, targets:Object) : TargetBanner {
        let res = cc.loader.getRes('match_scene/prefab/userInterface/TargetBanner', cc.Prefab);
        if (res) {
            let node = cc.instantiate(res);
            if (node) {
                let component = node.getComponent(TargetBanner) as TargetBanner;
                if (component) {
                    component.init(level, targets);
                }
                return component;   
            }
        }
    }

    protected init(level:number, targets:Object) : void {

        this.title.string = '' + level.toString() + '';;
        for (const key in targets) {
            let targetAmount = targets[key]
            let collectionID = Number(key) as CollectionID;
            if (collectionID) {
                let needAmount = targetAmount;
                let item = TargetItem.create(collectionID);
                item.setAmount(needAmount, false);
                this.targetFrame.addChild(item.node);
                this._targetItems.push(item);
            }
        }
        let canvas = cc.find("Canvas");
        if (canvas) {
            this._gameScene = canvas.getComponent(GameSceneBase);
        }
    }

    start () {
        let anim = this.node.getComponent(cc.Animation);
        anim.play('appear');
    }

    public didAppear () : void{
        this._displayDuraion = 1.5;
        this._gameScene.createGrid();
        this.maskView.on("touchstart", this.onTouchStart, this);
    }

    update (dt) {
        dt = dt * cc.director.getScheduler().getTimeScale();
        if (this._displayDuraion > 0) {
            this._displayDuraion -= dt;
            if (this._displayDuraion <= 0) {
                this.onDisappear();
            } 
        }
    }

    onDestroy() {
        this.maskView = null;
        this.title = null;
        this.targetFrame = null;
        this._gameScene = null;
        this._targetItems = null;
    }

    public didDisappear () : void{
        this._gameScene.runGame();
        this.node.destroy();
    }

    private onTouchStart(evt:cc.Event.EventTouch) {
        if (this._displayDuraion > 0) {
            this._displayDuraion = 0;
            this.onDisappear();
        }
    }

    /**
     *  
     */
    private onDisappear () : void{
        for (const key in this._targetItems) {
            let tItem = this._targetItems[key];
            if(this._gameScene){

                let cItemNode = cc.instantiate(tItem.node);//
                let cItem = cItemNode.getComponent('TargetItem') as TargetItem;
                cItem.node.parent = this._gameScene.node;
                cItem.node.zIndex = this.node.zIndex+1;//1

                let startWorldPos = tItem.node.parent.convertToWorldSpaceAR(tItem.node.position);
                let startPos = cItem.node.parent.convertToNodeSpaceAR(startWorldPos);
                
                cItem.node.position = startPos;
                tItem.hideIco();
                cItem.hideFrame(false);
                
                let barItem = this._gameScene.targetBar.getItemWithCollectionID(tItem.getCollectionID());
                barItem.hideIco();

                let endWorldPos = this._gameScene.getWorldPosForTarget(tItem.getCollectionID());
                let endPos = cItem.node.parent.convertToNodeSpaceAR(endWorldPos);
                
                //
                let seqAction = cc.sequence(
                    
                    cc.spawn(cc.scaleTo(0.3,1.2,0.8),cc.moveBy(0.3,cc.v2(0,-30))),
                    cc.spawn(cc.scaleTo(0.2,1,1),CurveToAction.createNotRandom(0.8, cc.v2(startPos.x,startPos.y - 30), endPos)),
                    cc.sequence(cc.delayTime(0),cc.callFunc(()=>{
                        barItem.showIco();

                        let anim = barItem.node.getComponent(cc.Animation);
                        anim.play('bling');//
                        
                        // cItem.node.opacity = 0;
                        cItem.node.active = false;
                        cItem.node.destroy();
                    }))
                );
                cItem.node.runAction(seqAction);
            }

            //
            this.node.runAction(cc.sequence(cc.delayTime(0.2),cc.callFunc(()=>{
                let anim = this.node.getComponent(cc.Animation);
                anim.play('disappear');
            })));
            
        }
    }


}
