import { GameCurrencyType } from "../../../define/GameCurrencyType";
import GearBar from "./GearBar";
import UISpriteExt from "../../../component/UISpriteExt";
import UINodeIdentifier from "../../../component/UINodeIdentifier";
import { NodeID } from "../../../extend/identify/nodeIdentify/IdentifyNodeManager";
import IGame from "../../../base/IGame";

