const {ccclass, property} = cc._decorator;

export enum GearItemState{
    None,
    Normal,
    Disable,
}

@ccclass
export default class GearItem extends cc.Component {

    @property(cc.Node)
    amountFramePos1:cc.Node = null;

    @property(cc.Node)
    amountFramePos2:cc.Node = null;

    @property(cc.Node)
    itemRoot:cc.Node = null;

    @property(cc.Sprite)
    icon:cc.Sprite = null;

    @property(cc.Node)
    normalAmountRoot:cc.Node = null;
    @property(cc.Node)
    normalAmountFrame:cc.Node = null;
    @property(cc.Label)
    normalAmountLabel:cc.Label = null;
    @property(cc.Node)
    buyButton:cc.Node = null;

    @property(cc.Node)
    timeLimitedAmountRoot:cc.Node = null;
    @property(cc.Node)
    timeLimitedAmountFrame:cc.Node = null;
    @property(cc.Label)
    timeLimitedAmountLabel:cc.Label = null;
    @property(cc.Label)
    timeLimitedCountDownLabel:cc.Label = null;

    @property(cc.Node)
    lockButton:cc.Node = null;

    @property(cc.Node)
    lockIcon:cc.Node = null;

    @property(UINodeIdentifier)
    identifyComponent:UINodeIdentifier = null;

    private _isLocked:boolean = false
    public getIsLocked():boolean{
        return this._isLocked;
    }
    public setIsLocked(isLocked:boolean):void{
        this._isLocked = isLocked
        this.lockIcon.active = this._isLocked;
        this.itemRoot.active = !this._isLocked
    }
    private _normalItemID:GameCurrencyType = GameCurrencyType.NoneType;
    public getItemID() : GameCurrencyType {
        return this._normalItemID;
    }
    public setItemID(itemID:GameCurrencyType) : void {
        this._normalItemID = itemID;
        let frameName = ''
        switch (this._normalItemID) {
            case GameCurrencyType.AidHammer:{
                if (this.identifyComponent) {
                    this.identifyComponent.setIdentifier(NodeID.MatchScene_HammerButton);
                }
                frameName = 'match_scene/texture/userInterface/match_hammer'
            }
                break;
            case GameCurrencyType.AidBomb:{
                if (this.identifyComponent) {
                    this.identifyComponent.setIdentifier(NodeID.MatchScene_BombButton);
                }
                frameName = 'match_scene/texture/userInterface/match_bomb'
            }
                break;
            case GameCurrencyType.AidSwap:{
                if (this.identifyComponent) {
                    this.identifyComponent.setIdentifier(NodeID.MatchScene_SwapButton);
                }
                frameName = 'match_scene/texture/userInterface/match_swap'
            }
                break;
        
            default:
                break;
        }

        let component = this.icon.getComponent(UISpriteExt);
        if (component) {
            component.src = frameName;
        }
        this.updataAmount();
        this.setIsLocked(false);
    } 

    public updataAmount(){
        if (this._gearBar) {
            this.timeLimitedAmountRoot.active = false;
            this.normalAmountRoot.active = false; 
        }
    }

    private _usedAmount:number = 0;
    public getUsedAmount() : number {
        return this._usedAmount;
    }
    public setUsedAmount(amount:number) : void {
        this._usedAmount = amount;
        this.updateGearState();
    }

    private _limitedAmount:number = 0;
    public getLimitedAmount() : number {
        return this._limitedAmount;
    }
    public setLimitedAmount(amount:number) : void {
        this._limitedAmount = amount;
        this.updateGearState();
    }

    private _state:GearItemState = GearItemState.Normal
    public getState() : number {
        return this._state;
    }
    public setState(state:GearItemState) : void {
        this._state = state;
        switch (this._state) {
            case GearItemState.Normal:{
                this.icon.node.opacity = 255;
            }
                break;
            case GearItemState.Disable:{
                this.icon.node.opacity = 128;
            }
                break;
        
            default:
                break;
        }
    }

    public getIsAvailable():boolean{
        return !this._isLocked && this._state == GearItemState.Normal
    }
    private updateGearState(){
        let state = GearItemState.Normal;
        if(this._limitedAmount >= 0 && this._usedAmount >= this._limitedAmount){
            state = GearItemState.Disable;
        }
        this.setState(state);
    }
    
    private onIconTabbed(event:cc.Event.EventCustom) : void {
        this._gearBar.onItemTabbed(this);
    }

    private onBuyTabbed(event:cc.Event.EventCustom) : void {
        this._gearBar.onItemTabbed(this);    
    }
    
    private _gearBar:GearBar = null;
    public setGearBar(gearBar:GearBar){
        this._gearBar = gearBar;
    }
    public getGearBar():GearBar{
        return this._gearBar;
    }
    public playAlertAnimation(delay:number){
        switch (this._state) {
            case GearItemState.Normal:{
                let anim = this.getComponent(cc.Animation);
                if (anim) {
                    anim.play('alert');
                }
            }
                break;
        }
    }
    // LIFE-CYCLE CALLBACKS:
    onLoad () {
        this.icon.node.on('touchend', this.onIconTabbed,this);
        this.buyButton.on('touchend', this.onBuyTabbed,this);
    }

    start () {

    }
    // update (dt) {}

    onDestroy() {
        this.icon = null;
        this.normalAmountLabel = null;
        this.itemRoot = null;
        this.buyButton = null;
        this.lockButton = null;
        this.identifyComponent = null;
        this.lockIcon = null;
        this._gearBar = null;
    }
}
