// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

export enum PopsicleType {
    None,
    Normal,
    Line,
    Range,
};

const { ccclass, property } = cc._decorator;

@ccclass
export default class PopsicleBase extends EntityBase {

    private _popsicleType: PopsicleType = PopsicleType.None;
    public getPopsicleType(): PopsicleType {
        return this._popsicleType;
    }

    protected _canBePopsicleClean(): boolean {
        let occupyArea = this.getOccupyArea();
        let grid = this.getGrid();

        for (let row = 0; row < occupyArea.height; row++) {
            for (let column = 0; column < occupyArea.width; column++) {
                let pos = occupyArea.origin.add(cc.v2(column, row));
                let floor = grid.getFloorAtLogicalPos(pos);
                if (floor) {
                    return false;
                }
            }
        }
        return true;
    }

    protected _onBePopsicleClean(): void {
        this.removeFromGridAndAddToEffect();
        this.node.zIndex = GridBase.ZORDER_EFFECT;
        this.shiftToEffectLayer();
        this.getGrid().addObstructs(this);
        let state = this.playAnimation('die');
        if (state) {
            let rotation = this._puppet.angle;

            if (rotation >= 0) {
                rotation = -15;
            } else if (rotation < 0) {
                rotation = 15;
            } else {
                rotation = -15;
            }

            let duration = state.duration;
            let action = cc.sequence(cc.rotateTo(duration * 0.25, rotation).easing(cc.easeInOut(2)),
                cc.rotateTo(duration * 0.25, -rotation * 0.5).easing(cc.easeInOut(2)),
                cc.rotateTo(duration * 0.25, rotation * 0.25).easing(cc.easeInOut(2)),
                cc.rotateTo(duration * 0.25, 0).easing(cc.easeInOut(2)),
                cc.callFunc(() => {
                    this.onDidClean();
                }),
            )
            this.getPuppet().runAction(action);
        }
    }

    protected onDidClean(): void {
        if (this.getGrid().isTargetCollectionID(this.getCollectionID())) {
            let startPos = this.node.position;
            let targetPos = this.node.parent.convertToNodeSpaceAR(this.getGrid().getWorldPosForTarget(this.getCollectionID()));
            let distance = GameMath.getDistance(startPos, cc.v2(targetPos.x, targetPos.y));
            let speed = 1000;
            let duration = distance / speed;

            let size = this.getLogicalSize();

            let move = cc.spawn(CurveToAction.create(duration, startPos, cc.v2(targetPos.x, targetPos.y)),
                cc.scaleTo(duration * 0.5, 0.75 / Math.min(size.width, size.height),
                    0.75 / Math.max(size.width, size.height)));
            let callback = cc.callFunc(() => { this.getGrid().updateTargetBarUIWithDelta(this.getCollectionID(), -1); this.destoryWithRecycle(); });
            let action = cc.sequence(move, callback);
            this.runAction(action);

        } else {
            this.selfDestory();
        }
        this.getGrid().collectEntity(this);
        this.getGrid().removeObstructs(this);
    }

    public canExcuteCommand(command: GridCommand): boolean {
        switch (command.type) {
            case GridCommandType.PopsicleClean: {
                return this._canBePopsicleClean();
            }
                break;
            default:
                break;
        }
        return false;
    }

    public excuteCommand(command: GridCommand): void {
        if (!this.canExcuteCommand(command)) {
            return;
        }
        switch (command.type) {
            case GridCommandType.PopsicleClean: {
                this._onBePopsicleClean();
            }
                break;
            default:
                break;
        }
    }

    public onCollected(): void {

    }

    protected initPopsicle(grid: GridBase, width: number, height: number, popsicleType: PopsicleType) {
        super.initEntity(grid, EntityType.Popsicle, width, height);
        this._popsicleType = popsicleType;
    }

}
