
export class MathUtils
{
    public static round(f:number):number
    {
        let tmp:number = Math.pow(10, 3);
        let temf:number = Math.round(f * tmp) / tmp
        return temf;
    }
    public static shuffle(arr){
        let len = arr.length;
        for(let i = 0; i < len - 1; i++){
            let idx = Math.floor(Math.random() * (len - i));
            let temp = arr[idx];
            arr[idx] = arr[len - i - 1];
            arr[len - i -1] = temp;
        }
        return arr;
    }
}
