import GridBase from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateLaunchGears from "./GridStateLaunchGears";

export default class GridStateLaunchResidentGear extends GridStateBase {

    public static create() : GridStateLaunchResidentGear {
        return new GridStateLaunchResidentGear();
    }
    constructor() {
        super(GridState.LaunchResidentGear);
    }
    public enter(grid:GridBase) {
        grid.setCurrentState(GridStateLaunchGears.create());
    }
}
