import GridStateBase, { GridState } from "../GridStateBase";
import GridBase from "../../GridBase";
import ItemBase from "../../../entity/item/ItemBase";
import { PowerID } from "../../../defines/PowerID";
import GridCommand from "../../../commond/GridCommand";
import GridStateDrop from "./GridStateDrop";
import ItemStateReplace from "../../../entity/item/state/ItemStateReplace";
import { ItemBombType } from "../../../entity/item/color/bomb/ItemBombBase";
import { EntityColor } from "../../../defines/EntityID";
import ItemFactory from "../../../entity/item/ItemFactory";
import GridStateComplete from "./GridStateComplete";
import GameMath from "../../../../../extend/utils/GameMath";
import IGame from "../../../../../base/IGame";
import IdentifierManager from "../../../defines/IdentifierManager";
import { GameCurrencyType } from "../../../../../define/GameCurrencyType";


export default class GridStateBousTime extends GridStateBase {

    private _curGrid:GridBase = null;
    private _boomItems:ItemBase[] = [];
    //
    private _isSendBoomMsg:boolean = false;
    private _delayTime:number = 0.0;

    public static create() : GridStateBousTime{
        return new GridStateBousTime();
    }
    constructor() {
        super(GridState.BousTime);
    }

    public enter(grid:GridBase) {
        this._curGrid = grid;

        if (grid.canGetBonusFromLeftStep()) {
            //--no---no-
            this.initAllBoomItem();
            if(this._boomItems.length > 0){
                //
                this.sendBoomMsg();
            }else if(grid.getPowerAmount(PowerID.AvailableSwap) > 0){
                //
                this.stepNumberToBoom();
            }else{
                //
                this.onBousTimeDone();
            }
        }else{
            this.onBousTimeDone();
        }
    }

    public update(grid:GridBase, delta:number) {

        if(this._isSendBoomMsg){
            this._delayTime += delta;
            if(this._delayTime >= 0.2){
                this.initAllBoomItem();
                if(this._boomItems.length > 0){
                    let items:ItemBase = this._boomItems[0];
                    let position = items.getLogicalPos();
                    this._curGrid.dispatchCommand(position, GridCommand.ActivateClean(position), 0);
                    this._delayTime = 0.0;
                }else{
                    if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
                        if(this._isSendBoomMsg && this._boomItems.length <= 0){
                            this._isSendBoomMsg = false;
                            this._curGrid.setCurrentState(GridStateDrop.create());
                        }
                    }
                }
            }
        }
    }

    public exit(grid:GridBase) {
        this._curGrid = null;
        this._boomItems = null;
    }
    //
    private initAllBoomItem():void{
        this._boomItems = [];
        let itemMap = this._curGrid.getItemsMap();
        for(let key in itemMap){
            let tmpItem:ItemBase = itemMap[key];
            if(tmpItem.canCleanByActivate()){
                this._boomItems.push(tmpItem);
                break;
            }
        }
    }

    //
    private sendBoomMsg():void{
        this._isSendBoomMsg = true;
    }

    //
    private stepNumberToBoom():void{

        let grid = this._curGrid;

        //
        let hasStep = grid.getPowerAmount(PowerID.AvailableSwap);

        //,
        let setpItems:ItemBase[] = [];
        let itemMap = grid.getItemsMap();
        for(let key in itemMap){
            let tmpItem:ItemBase = itemMap[key];
            if(tmpItem && tmpItem.canUpgradeByRainbow()){
                setpItems.push(tmpItem);
            }
        }

        //
        GameMath.shuffleArray(setpItems);
        
        //TODO 
        let stepLen = setpItems.length;
        for(let i:number = 0; i < hasStep; i++){
            if(i < stepLen){
                let newItem:ItemBase = this.getOneRandBoom();
                if(newItem){
                    grid.gainBonusStepCount(1);
                    newItem.node.setPosition(grid.node.convertToNodeSpace(grid.getWorldPosForStep()));
                    grid.node.addChild(newItem.node, grid.getTopZOrder(newItem.getEntityType(), setpItems[i].getLogicalPos()));
                    let gainScore = Math.min(grid.getBonusStepCount() * 1000 + 1000, 5000) ;
                    let rewardSupply = {};
                   
                    newItem.setCurrentState(ItemStateReplace.create(setpItems[i].getLogicalPos(), i * 0.2,gainScore, rewardSupply));
                    newItem.setVisible(false);
                    newItem.runAction(cc.sequence(cc.delayTime(i * 0.2),
                            cc.callFunc(()=>{
                                IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('bonus'));
                                newItem.setVisible(true);
                                grid.updateStepBarUIWithDelta(-1);
                                grid.lossPower(PowerID.AvailableSwap, 1);
                                if(i == Math.min(hasStep, stepLen) - 1){
                                    this.initAllBoomItem();
                                    this.sendBoomMsg();
                                }
                    })));
                }
            }else{
                break;
            }
        }

    }

    //
    private getOneRandBoom():ItemBase{

        let m_bombType:ItemBombType[] = [];
        m_bombType.push(ItemBombType.Row);
        m_bombType.push(ItemBombType.Column);
        GameMath.shuffleArray(m_bombType);
        

        let m_color:EntityColor[] = [];
        m_color.push(EntityColor.Red);
        m_color.push(EntityColor.Green);
        m_color.push(EntityColor.Blue);
        m_color.push(EntityColor.Yellow);
        m_color.push(EntityColor.Orange);
        m_color.push(EntityColor.Purple);
        GameMath.shuffleArray(m_color);


        let  newItem = ItemFactory.generateEffectBombItem(this._curGrid, m_bombType[0], m_color[0]);
        if(newItem){
            return newItem;
        }

        return null;
    }

    //
    private onBousTimeDone():void{
        this._curGrid.setCurrentState(GridStateComplete.create());
    }
}
