import { EntityID } from "../../defines/EntityID";
import IdentifierManager from "../../defines/IdentifierManager";
import { KindID } from "../../defines/KindID";
import GridBase from "../../grid/GridBase";
import { RugBase } from "./RugBase";
import RugJam from "./RugJam";


export default class RugFactory {

    public static generatePolishWithEntityID(grid:GridBase, entityID:EntityID) : RugBase {
        let rug:RugBase = null;
        switch(entityID){
            case EntityID.RugJam:{
                rug = RugJam.create(grid);
            }
            break;
            default:
                break;
        }
        return rug;
    }

    public static generatePolishWithKindID(grid:GridBase, kindID:KindID) : RugBase {
        let entityID = IdentifierManager.getInstance().getEntityIDFromKindID(kindID);
        return this.generatePolishWithEntityID(grid, entityID);
    }
}
