import ItemBase, { ItemType } from "../ItemBase";
import GridBase from "../../../grid/GridBase";
import { CollectionID } from "../../../defines/CollectionID";
import { EntityID, EntityCleanOption, EntityColor } from "../../../defines/EntityID";
import RugJam from "../../rug/RugJam";
import FlyerAnimation from "../../flyer/FlyerAnimation";
import IGame from "../../../../../base/IGame";
import IdentifierManager from "../../../defines/IdentifierManager";
import EntityPrefabNodePool from "../../EntityPrefabNodePool";


const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemJamBottle extends ItemBase {

    public static create(grid:GridBase):ItemJamBottle{

        let className = 'ItemJamBottle';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemJamBottle';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemJamBottle;
        if (component) {
            component.init(grid); 
            return component;       
        }
        return null;
    }

    public clone() : ItemJamBottle {
        let item = ItemJamBottle.create(this.getGrid());
        return item;
    }

    public getCollectionID():CollectionID{
        return CollectionID.ItemJamBottle;
    }

    public getEntityID():EntityID{
        return EntityID.ItemJamBottle;
    }

    public getScore():number{
        return 200;
    }

    protected init(grid:GridBase):void{
        super.initItem(grid, ItemType.JamBottle);
    }

    protected _canDrop():boolean{
        return true;
    }

    protected _canSwap():boolean{
        return true;
    }

    protected _canCleanByBomb(cleanColor:EntityColor):boolean{
        return true;
    }

    protected _canCleanByNeighbour(color:EntityColor):boolean{
        return true;
    }

    protected _onClean(cleanOption:EntityCleanOption):void{
        this.getGrid().gainScoreWithEntity(this);
        this.setVisible(false);
        this.progressDeathWish(cleanOption);
        this.selfDestory();
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('jambottle'));

        let flyer = FlyerAnimation.createDieAnimation(this.getGrid(), this.getEntityID());
        this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
        let offsets:cc.Vec2[] = [
            cc.v2(-1, -1),
            cc.v2(0, -1),
            cc.v2(1, -1),

            cc.v2(-1, 0),
            cc.v2(0, 0),
            cc.v2(1, 0),

            cc.v2(-1, 1),
            cc.v2(0, 1),
            cc.v2(1, 1)
        ];

        for(let offset of offsets){
            let checkPos = this.getLogicalPos().add(offset);
            let cell = this.getGrid().getCellAtLogicalPos(checkPos);
            if(cell && cell.canHoldItem()){
                let item = this.getGrid().getItemAtLogicalPos(checkPos);
                if(item && item.coverUnderForever()){
                    continue;
                }

                let oldJam = this.getGrid().getRugAtLogicalPos(checkPos);
                if(!oldJam){
                    let jam = RugJam.create(this.getGrid());
                    jam.setImmuneDamage(true);
                    jam.grow();
                    this.getGrid().setRugAtLogicalPos(jam, checkPos);
                }
            }
        }
    }
}
