import GameMath from "../../../../extend/utils/GameMath";
import { CollectionID } from "../../defines/CollectionID";
import { EntityID, EntityType } from "../../defines/EntityID";
import TMXConfigInfoKeys from "../../defines/TMXConfigInfoKeys";
import GridBase from "../../grid/GridBase";
import EntityPrefabNodePool from "../EntityPrefabNodePool";
import { CellType } from "./CellBase";
import CellNormal from "./CellNormal";

// Learn TypeScript:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/typescript.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/typescript.html
// Learn Attribute:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/reference/attributes.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

export enum  CellConveyerDirection{
    None,
    Left,
    Right,
    Up,
    Down,
};

const {ccclass, property} = cc._decorator;

@ccclass
export default class CellConveyer extends CellNormal {

    public getCollectionID():CollectionID{
        return CollectionID.CellConveyer;
    }

    public getEntityID() : EntityID {
        let result = EntityID.None;
        switch (this._inDirection) {
            case CellConveyerDirection.Left:{
                switch (this._outDirection) {
                    case CellConveyerDirection.Right:{
                        result = EntityID.CellConveyerL2R;
                    }
                        break;
                    case CellConveyerDirection.Up:{
                        result = EntityID.CellConveyerL2U;
                    }
                        break;
                    case CellConveyerDirection.Down:{
                        result = EntityID.CellConveyerL2D;
                    }
                        break;
                        
                    default:
                        break;
                }
            }
                break;
            case CellConveyerDirection.Right:{
                switch (this._outDirection) {
                    case CellConveyerDirection.Left:{
                        result = EntityID.CellConveyerR2L;
                    }
                        break;
                    case CellConveyerDirection.Up:{
                        result = EntityID.CellConveyerR2U;
                    }
                        break;
                    case CellConveyerDirection.Down:{
                        result = EntityID.CellConveyerR2D;
                    }
                        break;
                        
                    default:
                        break;
                }
            }
                break;
            case CellConveyerDirection.Up:{
                switch (this._outDirection) {
                    case CellConveyerDirection.Left:{
                        result = EntityID.CellConveyerU2L;
                    }
                        break;
                    case CellConveyerDirection.Right:{
                        result = EntityID.CellConveyerU2R;
                    }
                        break;
                    case CellConveyerDirection.Down:{
                        result = EntityID.CellConveyerU2D;
                    }
                        break;
                        
                    default:
                        break;
                }
            }
                break;
            case CellConveyerDirection.Down:{
                switch (this._outDirection) {
                    case CellConveyerDirection.Left:{
                        result = EntityID.CellConveyerD2L;
                    }
                        break;
                    case CellConveyerDirection.Right:{
                        result = EntityID.CellConveyerD2R;
                    }
                        break;
                    case CellConveyerDirection.Up:{
                        result = EntityID.CellConveyerD2U;
                    }
                        break;
                        
                    default:
                        break;
                }
            }
                break;
                
            default:
                break;
        }
        return result;
    }

    private _inDirection:CellConveyerDirection = CellConveyerDirection.None;
    public getInDirection() : CellConveyerDirection {
        return this._inDirection;
    }
    public setInDirection(direction:CellConveyerDirection) : void {
        this._inDirection = direction;
    }

    private _outDirection:CellConveyerDirection = CellConveyerDirection.None;
    public getOutDirection() : CellConveyerDirection {
        return this._outDirection;
    }
    public setOutDirection(direction:CellConveyerDirection) : void {
        this._outDirection = direction;
    }

    private _postalPos:cc.Vec2 = GameMath.INVALID_LOGICAL_POSITION.clone();
    public getPostalPos() : cc.Vec2 {
        return this._postalPos;
    }
    public setPostalPos(postal:cc.Vec2) : void {
        this._postalPos = postal;
    }

    public dealWithConfigMap(configMap:Object){
        super.dealWithConfigMap(configMap);
        let conveyerString = configMap[TMXConfigInfoKeys.CFG_CONVEYER_POSTAL_KEY_TYPE];
        if (conveyerString) {
            let postalString = configMap[TMXConfigInfoKeys.CFG_CONVEYER_POSTAL_VALUE_KEY];
            if (postalString) {
                this._postalPos = GameMath.getVec2FromConfigString(postalString);
            }
        }
    }
    
    protected getInLogicalPos() : cc.Vec2  {
        let result = this.getLogicalPos();
        if (!this._postalPos.equals(GameMath.INVALID_LOGICAL_POSITION)) {
            
            let cell = this.getGrid().getCellAtLogicalPos(this._postalPos);
            if (cell && cell.getCellType() == CellType.Conveyer) {
                let conveyer = cell as CellConveyer;
                
                switch (conveyer.getInDirection()) {
                    case CellConveyerDirection.Left:{
                        result = this._postalPos.add(cc.v2(-1, 0)) ;
                    }
                        break;
                    case CellConveyerDirection.Right:{
                        result = this._postalPos.add(cc.v2(1, 0));
                    }
                        break;
                    case CellConveyerDirection.Up:{
                        result = this._postalPos.add(cc.v2(0, 1));
                    }
                        break;
                    case CellConveyerDirection.Down:{
                        result = this._postalPos.add(cc.v2(0, -1));
                    }
                        break;
                        
                    default:
                        break;
                }
            }
        }
        return result;
    }
    
    public getOutLogicalPos() : cc.Vec2 {
    
        let result = this.getLogicalPos().clone();
        
        if (!this._postalPos.equals(GameMath.INVALID_LOGICAL_POSITION)) {
            result = this._postalPos;
        }else{
            switch (this._outDirection) {
                case CellConveyerDirection.Left:{
                    result.addSelf(cc.v2(-1, 0));
                }
                    break;
                case CellConveyerDirection.Right:{
                    result.addSelf(cc.v2(1, 0));
                }
                    break;
                case CellConveyerDirection.Up:{
                    result.addSelf(cc.v2(0, 1));
                }
                    break;
                case CellConveyerDirection.Down:{
                    result.addSelf(cc.v2(0, -1));
                }
                    break;
                    
                default:
                    break;
            }
        }
        
        return result;
    }

    protected getDoppelgangerInLogicalPos() : cc.Vec2 {
        return this.getLogicalPos();
    }
    
    protected getDoppelgangerOutLogicalPos() : cc.Vec2 {
        let result = this.getLogicalPos().clone();
        switch (this._outDirection) {
            case CellConveyerDirection.Left:{
                result.addSelf(cc.v2(-1, 0));
            }
                break;
            case CellConveyerDirection.Right:{
                result.addSelf(cc.v2(1, 0));
            }
                break;
            case CellConveyerDirection.Up:{
                result.addSelf(cc.v2(0, 1));
            }
                break;
            case CellConveyerDirection.Down:{
                result.addSelf(cc.v2(0, -1));
            }
                break;
                
            default:
                break;
        }
        return result;
    }


    protected conveyerEntity(type:EntityType, inPos:cc.Vec2, outPos:cc.Vec2) {
        let grid = this.getGrid();
        let logicalPos = this.getLogicalPos();
        let entity = grid.getEntity(type, logicalPos);
        if (entity) {
            entity.removeFromGrid();
            let layer = grid.getConveyerLayer();
            layer.addChild(entity.node);
            entity.node.setPosition(entity.getPixelPos(inPos));
            grid.addObstructs(entity);
            
            let targetPos = entity.getPixelPos(outPos);
            let action = cc.sequence(cc.moveTo(0.4, targetPos),
                        cc.callFunc(()=>{
                            grid.setEntity(entity, outPos);
                            grid.removeObstructs(entity);
                        }));
            entity.runAction(action);
        }
    }
    
    protected conveyerDoppelganger(type:EntityType, inPos:cc.Vec2, outPos:cc.Vec2){
        
        let grid = this.getGrid();
        let logicalPos = this.getLogicalPos();
        let entity = grid.getEntity(type, logicalPos);
        if (entity) {
            let doppelganger = entity.clone();
            if (doppelganger) {
                let layer = grid.getConveyerLayer();
                layer.addChild(doppelganger.node);
                
               
                doppelganger.node.setPosition(entity.getPixelPos(inPos));
                doppelganger.node.zIndex = entity.getTopZOrder();
                
                let targetPos = entity.getPixelPos(outPos);
                let action = cc.sequence(cc.moveTo(0.4, targetPos),
                                         cc.callFunc(()=>{
                                            doppelganger.node.destroy();
                                        }));
                doppelganger.runAction(action);
            }
        }
    }
    
    public onConvey() : cc.Vec2 {
    
        let conveyerTypes:EntityType[] = [
            EntityType.Popsicle,
            EntityType.Floor,
            EntityType.Rug,
            EntityType.Item,
            EntityType.Parasite,
        ]
        
        
        let inPos = this.getInLogicalPos();
        let outPos = this.getOutLogicalPos();
        let doppelgangerInPos = this.getDoppelgangerInLogicalPos();
        let doppelgangerOutPos = this.getDoppelgangerOutLogicalPos();
        let needDoppelganger = false;
        if (!this.getPostalPos().equals(GameMath.INVALID_LOGICAL_POSITION)) {
            needDoppelganger = true;
        }

        for (let type of conveyerTypes) {
            if (needDoppelganger) {
                this.conveyerDoppelganger(type, doppelgangerInPos, doppelgangerOutPos);
            }
            this.conveyerEntity(type, inPos, outPos);
        }
        
        return outPos;
    }

    protected initConveyer(grid:GridBase, inDirection:CellConveyerDirection, outDirection : CellConveyerDirection){
        super.initCell(grid, CellType.Conveyer);
        
        this._inDirection = inDirection;
        this._outDirection = outDirection;
        this._postalPos = GameMath.INVALID_LOGICAL_POSITION.clone();
        this.updatePuppet();
    }
    
    public static createConveyer(grid:GridBase, inDirection:CellConveyerDirection, outDirection : CellConveyerDirection) : CellConveyer{
        let className = 'CellConveyer';
        let prefabFileName = 'match_scene/prefab/entity/cell/CellConveyer';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as CellConveyer;
        if (component) {
            component.initConveyer(grid, inDirection, outDirection);
            return component;       
        }
        return null;
    }

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}

    onDestroy() {
        super.onDestroy();
        this._postalPos = null;
    }
}
