const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerMoreStep extends FlyerBase {

    @property(cc.Label)
    amountLabel:cc.Label = null;

    private _extraStep:number = 0;
    public static create(grid:GridBase, 
                         stepAmout:number,
                         origin:cc.Vec2) : FlyerMoreStep {

        let className = 'FlyerMoreStep';
        let prefabFileName = 'match_scene/prefab/entity/flyer/FlyerMoreStep';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerMoreStep;
        if (component) {
            component.init(grid, stepAmout, origin); 
            return component;       
        }
        return null;
    }
    protected init(grid:GridBase, 
                stepAmout:number,
                origin:cc.Vec2){
        super.initFlyer(grid, FlyerType.MoreStep);
        this.setOrigin(origin);
        this._extraStep = stepAmout;
        this.amountLabel.string = '+' + this._extraStep.toString();
    }

    protected updatePuppet():void{
        
    }
    
    public getCollectionID():CollectionID{
        return CollectionID.None;
    }

    public getEntityID():EntityID{
        return EntityID.FlyerMoreStep;
    }

    private _origin:cc.Vec2 = cc.Vec2.ZERO;
    public getOrigin():cc.Vec2 {
        return this._origin;
    }
    public setOrigin(origin:cc.Vec2) : void {
        this._origin = origin;
    }

    public launch() : void {
        
        let startPos = this.getGrid().getPixelPosFromLogicalPos(this._origin);
        let targetWorldPos = this.getGrid().getWorldPosForStep();
        let targetPos = this.getGrid().node.convertToNodeSpaceAR(targetWorldPos);
        let distance = GameMath.getDistance(startPos, targetPos);
        let speed = 1000;
        let duration = distance / speed;

        this.node.setPosition(startPos);

        let jumpOut = cc.scaleTo(0.3, 2.0).easing(cc.easeBackOut());
        let move = cc.spawn(cc.moveTo(duration, targetPos),
                            cc.scaleTo(duration, 0.75))
        let callback = cc.callFunc(()=>{
            this.getGrid().updateStepBarUIWithDelta(this._extraStep);
            this.destoryWithRecycle(true);
        })
        let action = cc.sequence(jumpOut, move, callback);
        this.node.runAction(action);
    }

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}

    onDestroy() {
        super.onDestroy();
        this.amountLabel = null;
        this._origin = null;
    }
}
