import GameMath from "../extend/utils/GameMath";

/**
 * Created by chenzd on 2018/7/16.
 * 
 */

const { ccclass, property } = cc._decorator;

@ccclass
export default class LoadingScene extends cc.Component {

    @property(cc.ProgressBar)
    progressBar: cc.ProgressBar = null;

    @property(cc.Label)
    progressLabel: cc.Label = null;

    start() {
        this.loadData();
    }

    private getProgress(): number {
        if (this.progressBar) {
            return this.progressBar.progress;
        }
        return 0;
    }

    private setProgress(progress: number): void {
        if (this.progressBar) {
            this.progressBar.progress = GameMath.clamp(progress, 0, 1);
        }

        if (this.progressLabel) {
            this.progressLabel.string = Math.floor(progress * 100).toString() + '%';
        }
    }

    private loadData() {
        this.setProgress(0);
        cc.loader.loadResDir('data',
            () => {

            },
            () => {
                this.loadCommon()
            });
    }

    private loadCommon() {
        this.setProgress(0);
        cc.loader.loadResDir('common', this.onLoadCommonProgressChanged.bind(this), this.onLoadCommonComplete.bind(this));
    }
    private onLoadCommonProgressChanged(completedCount: number, totalCount: number, item: any) {
        let progress = Math.max(this.getProgress(), completedCount / totalCount * 0.5)
        this.setProgress(progress);
    }
    private onLoadCommonComplete(error: Error, resource: any[], urls: string[]) {
        this.loadMatch();
    }


    private loadMatch() {
        this.setProgress(0);
        cc.loader.loadResDir('match_scene', this.onLoadMatchProgressChanged.bind(this), this.onLoadMatchComplete.bind(this));
    }
    private onLoadMatchProgressChanged(completedCount: number, totalCount: number, item: any) {
        let progress = Math.max(this.getProgress(), completedCount / totalCount * 0.5 + 0.5)
        this.setProgress(progress);
    }
    private onLoadMatchComplete(error: Error, resource: any[], urls: string[]) {
        cc.director.loadScene('MatchScene');
    }
}
