import { GameCurrencyType } from "../../../define/GameCurrencyType";
import IdentifierManager from "../defines/IdentifierManager";
import UISpriteExt from "../../../component/UISpriteExt";

// Learn TypeScript:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/typescript.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/typescript.html
