const {ccclass, property} = cc._decorator;

@ccclass
export default class CollectedTrainSupplyItem extends cc.Component {

    @property(cc.Sprite)
    icon:cc.Sprite = null;

    @property(cc.Label)
    amountLabel:cc.Label = null;

    private _itemID:GameCurrencyType = GameCurrencyType.NoneType;
    public getItemID() : GameCurrencyType {
        return this._itemID;
    }
    public setItemID(itemID:GameCurrencyType) : void {
        this._itemID = itemID;
        let frameName = '';
        switch (this._itemID) {
            case GameCurrencyType.GoldCoin:{
                frameName = 'match_scene/texture/userInterface/match_goldcoin'
            }
                break;
            case GameCurrencyType.TrainFood:{
                frameName = 'match_scene/texture/userInterface/match_food'
            }
                break;
            case GameCurrencyType.TrainSoap:{
                frameName = 'match_scene/texture/userInterface/match_soap'
            }
                break;
            case GameCurrencyType.TrainEyepatch:{
                frameName = 'match_scene/texture/userInterface/match_eyepatch'
            }
                break;
            case GameCurrencyType.TrainHoe:{
                frameName = 'match_scene/texture/userInterface/match_hoe'
            }
                    break;
        
            default:
                break;
        }

        let component = this.icon.getComponent(UISpriteExt);
        if (component) {
            component.src = frameName;
        }
    } 

    private _amount:number = 0;
    public getAmount() : number {
        return this._amount;
    }
    public setAmount(amount:number) : void {

        let oldAmount = this._amount;
        this._amount = amount;
        this.amountLabel.string = this._amount.toString();
        if (oldAmount != this._amount) {
            this.amountLabel.node.stopActionByTag(1001);

            this.amountLabel.node.scale = 1.0;
            let amountAction = cc.sequence(cc.scaleTo(0.05, 1.5).easing(cc.easeIn(2)),
                                           cc.scaleTo(0.2, 1.0).easing(cc.easeOut(2))
                                          );
            amountAction.setTag(1001);
            this.amountLabel.node.runAction(amountAction);

            this.icon.node.stopActionByTag(1001);
            this.icon.node.scale = 1.0;
            let iconAction = cc.sequence(cc.scaleTo(0.05, 1.5).easing(cc.easeIn(2)),
                                         cc.scaleTo(0.2, 1.0).easing(cc.easeOut(2))
                                        );
            iconAction.setTag(1001);
            this.icon.node.runAction(iconAction);
        }else{
            this.amountLabel.node.scale = 1.0;
            this.icon.node.scale = 1.0;
        }
    }

    public static create(itemID:GameCurrencyType) : CollectedTrainSupplyItem{

        let res = cc.loader.getRes('match_scene/prefab/userInterface/CollectedTrainSupplyItem', cc.Prefab);
        if (res) {
            let node:cc.Node = cc.instantiate(res);
            if (node) {
                let component:CollectedTrainSupplyItem = node.getComponent(CollectedTrainSupplyItem);
                if (component) {
                    component.init(itemID);     
                    return component; 
                }
            }
        }
        return null;
    }

    protected init(itemID:GameCurrencyType) {
        this.setItemID(itemID);
        this.setAmount(0);
    }

    // LIFE-CYCLE CALLBACKS:
    onLoad () {
         this.setAmount(0);
    }
    start () {
        
    }
    // update (dt) {}
    onDestroy() {
        this.icon = null;
        this.amountLabel = null;
    }
}
