import AudioManager from "../extend/audio/AudioManager";
import IGame from "../base/IGame";

/**
 * Created by chenzd on 2018/7/16.
 *  ()
 */

const {ccclass, property} = cc._decorator;

@ccclass
export default class UIButtonExt extends cc.Component {

    @property(cc.Label)
    btnText:cc.Label = null; //

    @property
    pressedScale:number = 1.1; //

    @property
    transDuration:number = 0.1; //

    @property
    touchSound:boolean = true; //

    // LIFE-CYCLE CALLBACKS:
    @property
    private initScale:number = -1; //

    @property
    isStopBubbleEvent:boolean = false;

    @property
    limitTouchTime:number = 1000;  //

    //
    public touchEnable:boolean = true;

    //
    private _fontColor:cc.Color = null;
    private _outLineColor:cc.Color = null;

    private _touchendCallBackFun:Function = null;
    private _touchTarget:Object = null;

    //
    private _lastTouchedTime:number = 0; //
    string: string;


    start(){

    }

    onLoad () {

        if(this.initScale == -1){
            this.initScale = this.node.scale;
        }  
        // this.node.on('touchstart', this.onTouchDown, this);
        // this.node.on('touchend', this.onTouchUp, this);
        // this.node.on('touchcancel', this.onTouchUp, this);

        this.setTouchEnable(this.touchEnable);
    }

    private onTouchDown(event){

        if (!this.touchEnable || !this.enabledInHierarchy) return;

        let scaleDownAction = cc.scaleTo(this.transDuration, this.pressedScale);
        scaleDownAction.setTag(1001);
        this.node.stopActionByTag(1001);
        this.node.runAction(scaleDownAction);

        if(this.isStopBubbleEvent){
            event.stopPropagation();
        }
    }
    /*
    addTouchSound(){
        //IGame.playAudio( "audio/common/common_button" );
        
        let state:number = AudioManager.getInstance().getSoundEffectStatus();
        if(state == 1)
        {
            AudioManager.getInstance().setSoundEffectStatus(1);
        }
        
    }
    */
    private onTouchUp(event){

        if (!this.touchEnable || !this.enabledInHierarchy) return;

        if(this._touchendCallBackFun && this._touchTarget){
            let canTouch:boolean = false;
            let curTouchTime:number = new Date().getTime();
            if(this._lastTouchedTime == 0){
                canTouch = true;
                this._lastTouchedTime = curTouchTime;
            }else{
                if(Math.abs(this._lastTouchedTime - curTouchTime) > this.limitTouchTime){
                    canTouch = true;
                    this._lastTouchedTime = curTouchTime;
                }
            }

            if(canTouch){
                this._touchendCallBackFun.call(this._touchTarget, event);
            }
        }

        let scaleUpAction = cc.scaleTo(this.transDuration, this.initScale);
        scaleUpAction.setTag(1002);
        this.node.stopActionByTag(1002);
        this.node.runAction(scaleUpAction);

        if( this.touchSound ){
            IGame.playAudio( "audio/common/common_button" );
        }
    }

    private onTouchCancel(event){
        if (!this.touchEnable || !this.enabledInHierarchy) return;

        let scaleUpAction = cc.scaleTo(this.transDuration, this.initScale);
        scaleUpAction.setTag(1002);
        this.node.stopActionByTag(1002);
        this.node.runAction(scaleUpAction);
    }

    //
    public setBtnGray(isGray:boolean):void{

        if(isGray){
            let btnSprite:any = this.node.getComponent(cc.Sprite);
            if(btnSprite){
                btnSprite.setState(1);
            }

            if(this.btnText){
                if(this._fontColor == null){
                    this._fontColor = this.btnText.node.color;
                }
                
                this.btnText.node.color = cc.Color.WHITE;
    
                //
                let outLine = this.btnText.getComponent(cc.LabelOutline);
                if(outLine){
                    if(this._outLineColor == null){
                        this._outLineColor = outLine.color;
                    }
                    
                    outLine.color = outLine.color.fromHEX("#333333");
                }
            }
        }else{
            let btnSprite:any = this.node.getComponent(cc.Sprite);
            if(btnSprite){
                btnSprite.setState(0);
            }

            if(this.btnText){
                if(this._fontColor){
                    this.btnText.node.color = this._fontColor;
                }
                
                if(this._outLineColor){
                    //
                    let outLine = this.btnText.getComponent(cc.LabelOutline);
                    if(outLine){
                        outLine.color = this._outLineColor;
                    }
                }
            }
        }

    }

    //
    public setTouchEnable(enable:boolean):void{
        this.touchEnable = enable;
        // if(enable){
        //     this.node.resumeSystemEvents(true);
        // }else{
        //     this.node.pauseSystemEvents(true);
        // }

        if(!this.touchEnable){
            this.node.targetOff(this);
        }else{
            this.node.on('touchstart', this.onTouchDown, this);
            this.node.on('touchend', this.onTouchUp, this);
            this.node.on('touchcancel', this.onTouchCancel, this);
        }
        
    }

    //
    public setTouchCallback(fun:Function, target:Object):void{
        this._touchendCallBackFun = fun;
        this._touchTarget = target;
    }

    public removeTouchCallBack():void{
        this._touchendCallBackFun = null;
        this._touchTarget = null;
    }

    //
    public clearTouchLimitTime():void{
        this._lastTouchedTime = 0;
    }

    onDestroy(){
        this.node.off('touchstart', this.onTouchDown, this);
        this.node.off('touchend', this.onTouchUp, this);
        this.node.off('touchcancel', this.onTouchCancel, this);
        this.removeTouchCallBack();
    }

}
