export enum CollectionGroupID {
    None                        = 0,
    ItemBombButterfly           = 602,
    ItemBombRow                 = 603,
    ItemBombColumn              = 604,
    ItemBombCross               = 605,
    ItemBombRange               = 606,
    ItemPaw                     = 621,
};

export default class CollectionGroupIDManager {
    //
    private static instance:CollectionGroupIDManager = null;
    public static getInstance():CollectionGroupIDManager{
        if(this.instance == null){
            this.instance = new CollectionGroupIDManager();
        }
        return this.instance;
    }
    private String_To_CollectionGroupID_Map:Object = {};
    private initString_To_CollectionGroupID_Map() : void {
        this.String_To_CollectionGroupID_Map["butterfly"] =     CollectionGroupID.ItemBombButterfly;
        this.String_To_CollectionGroupID_Map["hLine"] =         CollectionGroupID.ItemBombRow;
        this.String_To_CollectionGroupID_Map["vLine"] =         CollectionGroupID.ItemBombColumn;
        this.String_To_CollectionGroupID_Map["cross"] =         CollectionGroupID.ItemBombCross;
        this.String_To_CollectionGroupID_Map["boom"] =          CollectionGroupID.ItemBombRange;
        this.String_To_CollectionGroupID_Map["paw"] =           CollectionGroupID.ItemPaw;
    }
    private CollectionGroupID_To_CollectionIDs_Map:Object = {};
    private initCollectionGroupID_To_CollectionIDs_Map() : void {
        this.CollectionGroupID_To_CollectionIDs_Map[CollectionGroupID.ItemBombButterfly] = [
            CollectionID.ItemBombButterflyRed,
            CollectionID.ItemBombButterflyGreen,
            CollectionID.ItemBombButterflyBlue,
            CollectionID.ItemBombButterflyYellow,
            CollectionID.ItemBombButterflyOrange,
            CollectionID.ItemBombButterflyPurple,
        ];
        
        this.CollectionGroupID_To_CollectionIDs_Map[CollectionGroupID.ItemBombRow] = [
            CollectionID.ItemBombRowRed,
            CollectionID.ItemBombRowGreen,
            CollectionID.ItemBombRowBlue,
            CollectionID.ItemBombRowYellow,
            CollectionID.ItemBombRowOrange,
            CollectionID.ItemBombRowPurple,
        ];
        
        this.CollectionGroupID_To_CollectionIDs_Map[CollectionGroupID.ItemBombColumn] = [
            CollectionID.ItemBombColumnRed,
            CollectionID.ItemBombColumnGreen,
            CollectionID.ItemBombColumnBlue,
            CollectionID.ItemBombColumnYellow,
            CollectionID.ItemBombColumnOrange,
            CollectionID.ItemBombColumnPurple,
        ];
        
        this.CollectionGroupID_To_CollectionIDs_Map[CollectionGroupID.ItemBombCross] = [
            CollectionID.ItemBombCrossRed,
            CollectionID.ItemBombCrossGreen,
            CollectionID.ItemBombCrossBlue,
            CollectionID.ItemBombCrossYellow,
            CollectionID.ItemBombCrossOrange,
            CollectionID.ItemBombCrossPurple,
        ];
        
        
        this.CollectionGroupID_To_CollectionIDs_Map[CollectionGroupID.ItemBombRange] = [
            CollectionID.ItemBombRangeRed,
            CollectionID.ItemBombRangeGreen,
            CollectionID.ItemBombRangeBlue,
            CollectionID.ItemBombRangeYellow,
            CollectionID.ItemBombRangeOrange,
            CollectionID.ItemBombRangePurple,
        ];
        
        
        this.CollectionGroupID_To_CollectionIDs_Map[CollectionGroupID.ItemPaw] = [
            CollectionID.ItemPawRed,
            CollectionID.ItemPawGreen,
            CollectionID.ItemPawBlue,
            CollectionID.ItemPawYellow,
            CollectionID.ItemPawOrange,
            CollectionID.ItemPawPurple,
        ];
    }
    private CollectionGroupID_To_FrameName_Map:Object = {};
    private initCollectionGroupID_To_FrameName_Map() : void {
        this.CollectionGroupID_To_FrameName_Map[CollectionGroupID.ItemBombButterfly] =       "butterfly_chromatic";
        this.CollectionGroupID_To_FrameName_Map[CollectionGroupID.ItemBombRow] =             "hline_chromatic";
        this.CollectionGroupID_To_FrameName_Map[CollectionGroupID.ItemBombColumn] =          "vline_chromatic";
        this.CollectionGroupID_To_FrameName_Map[CollectionGroupID.ItemBombCross] =           "cross_chromatic";
        this.CollectionGroupID_To_FrameName_Map[CollectionGroupID.ItemBombRange] =           "boom_chromatic";
        this.CollectionGroupID_To_FrameName_Map[CollectionGroupID.ItemPaw] =                 "paw_chromatic";
    }

    constructor() {
        this.initString_To_CollectionGroupID_Map();
        this.initCollectionGroupID_To_CollectionIDs_Map()
        this.initCollectionGroupID_To_FrameName_Map();
    }

    public getCollectionGroupIDFromString(name:string) : CollectionGroupID {
        let result = this.String_To_CollectionGroupID_Map[name];
        if (result) {
            return result;
        }
        return CollectionGroupID.None;
    }

    public getFrameNameFromCollectionGroupID(groupID:CollectionGroupID) : string {
        let rootPath = 'texture/match/entities/';
        let result = this.CollectionGroupID_To_FrameName_Map[groupID];
        if (result) {
            return rootPath + result;
        }
        return rootPath + "unknow/unknow";
    }

    public getCollectionIDsFromCollectionGroupID(groupID:CollectionGroupID) : CollectionID[] {
        let result = this.CollectionGroupID_To_CollectionIDs_Map[groupID];
        if (result) {
            return result;
        }
        return [];
    }

    public getCollectionGroupIDFromCollectionID(collectionID:CollectionID) : CollectionGroupID {
        let tempMap = this.CollectionGroupID_To_CollectionIDs_Map;
        for (let key in tempMap) {
            if (tempMap.hasOwnProperty(key)) {
                const entityIDs:CollectionID[] = tempMap[key];
                for(const id of entityIDs){
                    if (id == collectionID) {
                        let result = Number(key) as CollectionGroupID;
                        return result;     
                    }
                }
            }
        }
        return CollectionGroupID.None;
    }
}

