import ParasiteBase, { ParasiteType } from "./ParasiteBase";
import ItemBase from "../item/ItemBase";
import { CollectionID } from "../../defines/CollectionID";
import { EntityID, EntityCleanOption, EntityCleanType, EntityColor } from "../../defines/EntityID";
import { PowerID } from "../../defines/PowerID";
import { EntityStateType } from "../state/EntityStateBase";
import FlyerAnimation, { FlyerAnimationType } from "../flyer/FlyerAnimation";
import IGame from "../../../../base/IGame";
import IdentifierManager from "../../defines/IdentifierManager";
import EntityEventChanged from "../../event/EntityEventChanged";
import EntityPrefabNodePool from "../EntityPrefabNodePool";
import GridBase from "../../grid/GridBase";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ParasiteHoney extends ParasiteBase {

    private _healthPoint:number = 0;

    public static create(grid:GridBase, healthPoint:number):ParasiteHoney{

        let className = 'ParasiteHoney';
        let prefabFileName = 'match_scene/prefab/entity/parasite/ParasiteHoney';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ParasiteHoney;
        if (component) {
            component.init(grid, healthPoint); 
            return component;       
        }
        return null;
    }

    public clone() : ParasiteHoney {
        return ParasiteHoney.create(this.getGrid(), this.getHealthPoint());
    }
    
    public getCollectionID():CollectionID{
        return CollectionID.ParasiteHoney;
    }

    public getEntityID():EntityID{
        let result = EntityID.None;
        switch(this.getHealthPoint()){
            case 1:{
                result = EntityID.ParasiteHoneyHp1;
                break;
            }
            case 2:{
                result = EntityID.ParasiteHoneyHp2;
                break;
            }
            default:{
                break;
            }
        }
        return result;
    }

    public getPowerID():PowerID{
        return PowerID.HoneyResist;
    }

    public getScore():number{
        return 100;
    }

    public getHealthPoint():number{
        return this._healthPoint;
    }

    public setHealthPoint(val:number):void{
        this._healthPoint = val;
        this.updatePuppet();
    }

    public grow():void{
        this.playAnimation('grow');
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('honeycreate'));
    }

    protected _canCleanByBomb(cleanColor:EntityColor):boolean{
        return true;
    }

    protected _canCleanByNeighbour(cleanColor:EntityColor):boolean{
        return true;
    }

    protected _onClean(cleanOption:EntityCleanOption):void{
        let rate = 1;
        if (cleanOption.cleanType == EntityCleanType.Terminate) {
            rate = this.getHealthPoint();
            this.setHealthPoint(1);
        }
        this.getGrid().gainScoreWithEntity(this, rate);
        if(this.getHealthPoint() <= 1){
            this.setVisible(false);
            if (!this.isTargetCollection()) {
                let flyer = FlyerAnimation.createDieAnimation(this.getGrid(), this.getEntityID());
                this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
                IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('honey'));
            }
            let changedEvent = EntityEventChanged.create(this);
            this.node.dispatchEvent(changedEvent);
            this.onCollected();
            this.selfDestory(true);
  
        }else{
            this.setHealthPoint(this.getHealthPoint() - 1);
            this.selfHurt();
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_0'));
            this.getGrid().gainPower(this.getPowerID(), 1);
            let flyer = FlyerAnimation.createDieAnimation(this.getGrid(), this.getEntityID());
            this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('honey'));
            flyer.node.zIndex = this.getTopZOrder();
        }
    }

    protected init(grid:GridBase, healthPoint:number):void{
        super.initParasite(grid, ParasiteType.Honey);
        this.setHealthPoint(healthPoint);
    }
}
