export enum FlyerType{
    None,
    Missile,
    Cannon,
    Mower,
    Bomb,
    NuclearBomb,
    LasetBullet,
    Laser,
    Hammer,
    PopsicleLine,
    PopsicleBomb,
    Animation,
    CollectedEntity,
    CollectedSupply,
    MoreStep,
    Charger,
    ComboWord,
    FireworksMissile,
    FireworksCannon,
    Score,
};

const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerBase extends EntityBase {

    private _flyerType:FlyerType = FlyerType.None;
    public getFlyerType() : FlyerType {
        return this._flyerType;
    }

    public getCollectionID():CollectionID{
        return CollectionID.None;
    }

    public launch() : void {
        
    }

    protected initFlyer(grid:GridBase, flyerType:FlyerType) : void {
        super.initEntity(grid, EntityType.Flyer);
        this._flyerType = flyerType;
    }

    protected getAttackPos(color:EntityColor, layoutGrass:boolean) : cc.Vec2 {
        let targetPos = cc.v2(-1, -1); 
        let targetPriority = EntityBase.DEFAULT_MISSILE_PRIORITY;
        let grid = this.getGrid();
        let entityTypes:EntityType[] = [
            EntityType.Parasite,
            EntityType.Item,
            EntityType.Rug,
            EntityType.Floor,
            EntityType.Popsicle,
            EntityType.Cell,
        ];

        let cellsMap = grid.getCellsMap();
        let checkPositions:cc.Vec2[] = [];
        let attackedPositions = grid.getAttackedPosition();
        for (let key in cellsMap) {
            let cell = cellsMap[key] as CellBase;
            let checkPos = cell.getLogicalPos();
            if (GameMath.indexOf(attackedPositions, checkPos) == -1) {
                checkPositions.push(checkPos);
            }
        }
        GameMath.shuffleArray(checkPositions);
    
        for (let checkPos of checkPositions) {
            if (GameMath.indexOf(attackedPositions, checkPos) == -1) {
                let minPriority = EntityBase.DEFAULT_MISSILE_PRIORITY;

                let canAttackTop = false;
                for (let entityType of entityTypes) {
                    let entity = grid.getEntity(entityType, checkPos);
                    if (entity) {

                        if (entityType == EntityType.Parasite || entityType == EntityType.Item) {
                            if (entity.canExcuteCommand(GridCommand.BombClean(this.getLogicalPos(), color, layoutGrass))) {
                                canAttackTop = true;
                            }
                        }else{
                            canAttackTop = true;
                        }
                        if (canAttackTop) {
                            let butterflyPriority = entity.getButterflyPriority(color, layoutGrass);
                            if (butterflyPriority < minPriority) {
                                minPriority = butterflyPriority;
                            }
                        }else{
                            break;
                        }
                    }
                }
                if (canAttackTop) {
                    if (minPriority < targetPriority) {
                        targetPriority = minPriority;
                        targetPos = checkPos;
                    }
                }
            }
        }

        if (targetPos.equals(cc.v2(-1, -1))) {
            if (checkPositions.length > 0) {
                let index = GameMath.randomInt(0, checkPositions.length - 1);
                targetPos = checkPositions[index];
            }else{
                while (1) {
                    let x = GameMath.randomInt(0, grid.getGridSize().width - 1);
                    let y = GameMath.randomInt(0, grid.getGridSize().height - 1);
                    let checkPos = cc.v2(x, y);
                    
                    let cell = grid.getCellAtLogicalPos(checkPos);
                    if (cell) {
                        targetPos = checkPos;
                        break;
                    }
                }
            }
        }
        return targetPos;
    } 
}
