import FlyerBase, { FlyerType } from "./FlyerBase";
import { EntityID, EntityColor } from "../../defines/EntityID";
import GameMath from "../../../../extend/utils/GameMath";
import GridCommand from "../../commond/GridCommand";
import GridBase from "../../grid/GridBase";
import EntityPrefabNodePool from "../EntityPrefabNodePool";
import IdentifierManager from "../../defines/IdentifierManager";
import IGame from "../../../../base/IGame";

// Learn TypeScript:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/typescript.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/typescript.html
// Learn Attribute:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/reference/attributes.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html
export enum FlyerMowerDirection {
    Up = 0,
    Right,
    Down,
    Left,
};
const { ccclass, property } = cc._decorator;

@ccclass
export default class FlyerMower extends FlyerBase {

    protected static FLYER_MOWER_MIN_SPEED = 500.0;
    protected static FLYER_MOWER_MAX_SPEED = 2000.0
    protected static FLYER_MOWER_START_SPEED = 500.0
    protected static FLYER_MOWER_ACCELERATION = 1000.0

    public static create(grid: GridBase,
        layoutGrass: boolean,
        origin: cc.Vec2,
        direction: FlyerMowerDirection): FlyerMower {
        let filePath = 'match_scene/prefab/entity/flyer/FlyerMower';
        let className = 'FlyerMower';
        let prefabFileName = filePath;
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerMower;
        if (component) {
            component.init(grid, layoutGrass, origin, direction);
            return component;
        }
        return null;
    }
    public init(grid: GridBase,
        layoutGrass: boolean,
        origin: cc.Vec2,
        direction: FlyerMowerDirection) {
        super.initFlyer(grid, FlyerType.Mower);
        this.setLayourGrass(layoutGrass);
        this.setOrigin(origin);
        this.setDirection(direction);
        this._speed = 0;
        this._lastCellPostion = cc.v2(-1, -1);
        this._launched = false;
        this._startFly = false;
        this._hadPlayMoveAction = false;

        this._aliveArea = cc.rect(-2000, -2000, 4000, 4000);
    }

    protected _launched: boolean = false;
    protected _aliveArea: cc.Rect = cc.rect(0, 0, 0, 0);
    protected _speed: number = 0;
    protected _lastCellPostion: cc.Vec2 = cc.Vec2.ZERO;
    protected _startFly: boolean = false;
    protected _hadPlayMoveAction: boolean = false;

    public getEntityID(): EntityID {
        return EntityID.FlyerMower;
    }

    private _layoutGrass: boolean = false;
    public getLayoutGrass(): boolean {
        return this._layoutGrass;
    }
    public setLayourGrass(layoutGrass: boolean): void {
        this._layoutGrass = layoutGrass;
    }

    private _origin: cc.Vec2 = cc.Vec2.ZERO;
    public getOrigin(): cc.Vec2 {
        return this._origin;
    }
    public setOrigin(origin: cc.Vec2): void {
        this._origin = origin;
    }

    private _direction: FlyerMowerDirection = FlyerMowerDirection.Up;
    public getDirection(): FlyerMowerDirection {
        return this._direction;
    }
    public setDirection(direction: FlyerMowerDirection): void {
        this._direction = direction;
        let stepDirection = this.getStepDirection();
        let rotation = GameMath.getAngle(cc.Vec2.ZERO, stepDirection);
        this._puppet.angle = -rotation;
    }
    protected getStepDirection(): cc.Vec2 {
        let result = cc.v2(0, 1);
        switch (this._direction) {
            case FlyerMowerDirection.Up:
                result = cc.v2(0, 1);
                break;
            case FlyerMowerDirection.Right:
                result = cc.v2(1, 0);
                break;
            case FlyerMowerDirection.Down:
                result = cc.v2(0, -1);
                break;
            case FlyerMowerDirection.Left:
                result = cc.v2(-1, 0);
                break;

            default:
                break;
        }
        return result;
    }

    public launch() {
        if (this._launched) {
            return;
        }
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('mower'));
        let willAttackPos = this._origin.clone();
        let grid = this.getGrid();
        let validLogicalRect = cc.rect(0, 0, grid.getGridSize().width, grid.getGridSize().height);
        while (1) {
            if (!GameMath.rectContainsPoint(validLogicalRect, willAttackPos)) {
                break;
            }
            grid.recordAttackedPosition(willAttackPos.clone());
            willAttackPos.addSelf(this.getStepDirection());
        }

        this._launched = true;
        this.setOrigin(this.getOrigin());
        this._lastCellPostion = this._origin;

        this._launched = true;
        this._speed = FlyerMower.FLYER_MOWER_START_SPEED;


        grid.addObstructs(this);

        let state = this.playAnimation('jump');
        if (state) {
            let action = cc.sequence(cc.delayTime(state.duration),
                cc.callFunc(() => {
                    this._startFly = true;
                }));
            this.runAction(action);
        } else {
            this._startFly = true;
        }
    }

    update(dt) {
        if (this._launched == false) {
            return;
        }

        if (this._startFly == false) {
            return;
        }

        if (this._hadPlayMoveAction == false) {
            this.playAnimation('move');
            this._hadPlayMoveAction = true;
        }

        dt = dt * cc.director.getScheduler().getTimeScale();
        let grid = this.getGrid();
        let stepDirection = this.getStepDirection();
        this._speed = GameMath.clamp(this._speed + FlyerMower.FLYER_MOWER_ACCELERATION * dt,
            FlyerMower.FLYER_MOWER_MIN_SPEED,
            FlyerMower.FLYER_MOWER_MAX_SPEED);

        let newPosition = this.node.getPosition().add(stepDirection.normalize().mul(this._speed).mul(dt));
        this.node.setPosition(newPosition);
        let newCellPos = grid.getLogicalPosFromPixelPos(newPosition);

        if (!newCellPos.equals(this._lastCellPostion)) {
            let offset = newCellPos.sub(this._lastCellPostion);
            let count = offset.mag() / stepDirection.mag();
            for (let i = 0; i < count; i++) {
                let pos = this._lastCellPostion.add(stepDirection.mul(i + 1));

                let cell = grid.getCellAtLogicalPos(pos);
                if (cell) {
                    grid.dispatchCommand(pos, GridCommand.TerminateClean(this.getOrigin(), this.getEntityColor(), this._layoutGrass), 0);
                    grid.highlightedCell(pos);
                }

                if (grid.isGrassFloorAtLogicalPos(pos)) {
                    let item = grid.getItemAtLogicalPos(pos);
                    if (item) {
                        if (item.canMatch() && item.canSwap() && item.canLayoutGrass()) {
                            this._layoutGrass = true;
                        }
                    } else {
                        this._layoutGrass = true;
                    }
                }
            }
            this._lastCellPostion = newCellPos;
        }

        if (!GameMath.rectContainsPoint(this._aliveArea, this.node.getPosition())) {
            grid.removeObstructs(this);
            this.destoryWithRecycle(false);
        }
    }

    onDestroy() {
        super.onDestroy();
        this._aliveArea = null;
        this._lastCellPostion = null;
        this._origin = null;
    }
}
