const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerNuclear extends FlyerBase {

    private FLYER_NUCLEAR_BOMB_DURATION = 2.0;

    private _shouldLayoutGrass:boolean = false;
    public getShouldLayoutGrass() : boolean {
        return this._shouldLayoutGrass;
    }
    public setShouldLayoutGrass(shouldLayoutGrass:boolean) : void {
        this._shouldLayoutGrass = shouldLayoutGrass;
    }
    
    private _origin:cc.Vec2 = cc.Vec2.ZERO;
    public getOrigin():cc.Vec2 {
        return this._origin;
    }
    public setOrigin(origin:cc.Vec2) : void {
        this._origin = origin;
    }
    
    private _radius:number = 0;
    public getRadius():number {
        return this._radius;
    }
    public setRadius(radius:number) : void {
        this._radius = radius;
    }

    public getEntityID() : EntityID {
        return EntityID.FlyerNuclear;
    }

    private _launched:boolean = false;
    private _bombDuration:number = 0;
    private _lastBombRingIndex:number = 0;

    public init(grid:GridBase, 
                layoutGrass:boolean, 
                origin:cc.Vec2, 
                raidus:number) : void{
        super.initFlyer(grid, FlyerType.NuclearBomb);
        this.setRadius(raidus);
        this.setOrigin(origin);
        this.setEntityColor(EntityColor.None);
        this.setShouldLayoutGrass(layoutGrass);
        this._launched = false;
        this._bombDuration = 0;
        this._lastBombRingIndex = 0;
    }


    public static create(grid:GridBase, 
                         layoutGrass:boolean, 
                         origin:cc.Vec2, 
                         raidus:number = GridBase.MAX_GRID_HEIGHT + GridBase.MAX_GRID_WIDTH):FlyerNuclear{
        let className = 'FlyerNuclear';
        let prefabFileName = 'match_scene/prefab/entity/flyer/FlyerNuclear';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerNuclear;
        if (component) {
            component.init(grid, layoutGrass, origin, raidus); 
            return component;       
        }
        return null;
    }

    public launch() : void {
    
        if (this._launched) {
            return;
        }
        this._launched = true;
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_colorcolor'));
        this._bombDuration = 0;
        this._lastBombRingIndex = -1;
        
        let grid = this.getGrid();
        grid.addObstructs(this);
        
        let size = grid.getGridSize();
        let conners:cc.Vec2[] = [
            cc.v2(0,                0),
            cc.v2(size.width - 1,   0),
            cc.v2(0,                size.height - 1),
            cc.v2(size.width - 1,   size.height - 1),
        ];
        
        let maxDistanceSquared = 0;

        let originPixel = grid.getPixelPosFromLogicalPos(this._origin);
        for (let conner of conners) {
            let connerPixel = grid.getPixelPosFromLogicalPos(conner);
            let distanceSquared = GameMath.getDistanceSQ(originPixel, connerPixel);
            if (distanceSquared > maxDistanceSquared) {
                maxDistanceSquared = distanceSquared;
            }
        }
        
        let distance = Math.min(Math.sqrt(maxDistanceSquared), 1270);
        this.node.setScale(distance / 135);

        let state = this.playAnimation('default');
        if (state) {
            let action = cc.sequence(cc.delayTime(state.duration), cc.callFunc(()=>{
                this.destoryWithRecycle(false);
            }));
            this.runAction(action);
        }
    }
    

    update(delta:number):void {

        if (this._launched == false) {
            return;
        }
        this._bombDuration += delta;
        let currentRingIndex = Math.min(this._radius,  Math.floor(this._radius * this._bombDuration / this.FLYER_NUCLEAR_BOMB_DURATION));

        if (currentRingIndex > this._lastBombRingIndex) {
            let positions = GameMath.enumerateRingPos(this._origin, this._lastBombRingIndex + 1, currentRingIndex);
            let grid = this.getGrid();
            for (let pos of positions) {
                let cell = grid.getCellAtLogicalPos(pos);
                if (cell) {
                    grid.dispatchCommand(pos, GridCommand.BombClean(this._origin, this.getEntityColor(), this._shouldLayoutGrass), 0);
                    grid.highlightedCell(pos);
                }
            }
            this._lastBombRingIndex = currentRingIndex;
            if (this._lastBombRingIndex >= this._radius) {
                grid.removeObstructs(this);
            }
        }
    }

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}

    onDestroy() {
        super.onDestroy();
        this._origin = null;
    }
}
