import FloorBase, { FloorType } from "./FloorBase";
import GridBase from "../../grid/GridBase";
import { CollectionID } from "../../defines/CollectionID";
import { EntityID, EntityColor, EntityCleanOption, EntityCleanType } from "../../defines/EntityID";
import GridCommand from "../../commond/GridCommand";
import FlyerAnimation, { FlyerAnimationType } from "../flyer/FlyerAnimation";
import IGame from "../../../../base/IGame";
import IdentifierManager from "../../defines/IdentifierManager";
import EntityPrefabNodePool from "../EntityPrefabNodePool";

const {ccclass, property} = cc._decorator;

@ccclass
export default class FloorIce extends FloorBase {

    private _healthPoint:number = 0;

    public static create(grid:GridBase, healthPoint:number):FloorIce{

        let className = 'FloorIce';
        let prefabFileName = 'match_scene/prefab/entity/floor/FloorIce';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FloorIce;
        if (component) {
            component.init(grid, healthPoint);
            return component;       
        }
        return null;
    }

    public clone():FloorIce {
        return FloorIce.create(this.getGrid(),this.getHealthPoint());
    }

    public getCollectionID():CollectionID{
        return CollectionID.FloorIce;
    }

    public getEntityID():EntityID{
        let result = EntityID.None;
        switch(this.getHealthPoint()){
            case 1:{
                result = EntityID.FloorIceHp1;
                break;
            }
            case 2:{
                result = EntityID.FloorIceHp2;
                break;
            }
            case 3:{
                result = EntityID.FloorIceHp3;
                break;
            }
            case 4:{
                result = EntityID.FloorIceHp4;
                break;
            }
            default:{
                break;
            }
        }
        return result;
    }

    public getScore():number{
        return 50;
    }

    public getButterflyPriority(color:EntityColor, layoutGrass:boolean):number{
        let popsicle = this.getGrid().getPopsicleAtLogicalPos(this.getLogicalPos());
        if(popsicle){
            let coverItem = this.getGrid().getItemAtLogicalPos(this.getLogicalPos());
            if (coverItem && coverItem.canExcuteCommand(GridCommand.BombClean(this.getLogicalPos(), color, layoutGrass))) {
                return 1;    
            }else{
                return 1;
            }
        }
        return super.getButterflyPriority(color, layoutGrass);
    }
    
    protected _canCleanByTerminate(cleanColor:EntityColor) : boolean {
        return true;
    }

    public _canCleanByBomb(cleanColor:EntityColor):boolean{
        return false;
    }

    public _canBeFloorClean():boolean{
        return true;
    }

    public _onClean(cleanOption:EntityCleanOption):void{
        let rate = 1;
        if (cleanOption.cleanType == EntityCleanType.Terminate) {
            rate = this.getHealthPoint()
            this.setHealthPoint(1);
        }
        this.getGrid().gainScoreWithEntity(this, rate);
        if(this.getHealthPoint() <= 1){
            this.setVisible(false);
            if (!this.isTargetCollection()) {
                let flyer = FlyerAnimation.createDieAnimation(this.getGrid(), this.getEntityID());
                this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
                IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('ice'));
            }
            this.onCollected();
            this.selfDestory();
            this.getGrid().dispatchCommand(this.getLogicalPos(), GridCommand.PopsicleClean(), 0);
        }else{
            this.setHealthPoint(this.getHealthPoint() - 1);
            this.selfHurt();
            let flyer = FlyerAnimation.createDieAnimation(this.getGrid(), this.getEntityID());
            this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('ice'));
            flyer.node.zIndex = this.getTopZOrder();
        }
    }

    protected init(grid:GridBase, healthPoint:number):void{
        super.initFloor(grid, FloorType.Ice);
        this.setHealthPoint(healthPoint);
    }

    protected getHealthPoint():number{
        return this._healthPoint;
    }

    protected setHealthPoint(val:number):void{
        this._healthPoint = val;
        this.updatePuppet();
    }
}
