export enum GearBarState{
    None,
    Down,
    Up,
}

export enum GearBarType{
    Normal,
    Drawer,
}

export class GearBarData{
    public type = GearBarType.Normal;

    public static createWithData(data:Object):GearBarData{
        let type = GearBarType.Normal;
        if (data && data.hasOwnProperty('BT')) {
            let value = data['BT'];
            switch (value) {
                case 0:{
                    type = GearBarType.Normal;
                }
                    break;
                case 3:{
                    type = GearBarType.Drawer;
                }
                    break;
            
                default:
                    break;
            }
        }
        return GearBarData.create(type);
    }

    public static create(type:GearBarType):GearBarData{
        let result = new GearBarData();
        result.type = type;
        return result;
    }
}

const {ccclass, property} = cc._decorator;

@ccclass
export default class GearBar extends cc.Component {

    public static AdBannerDefaultHeight = 250;
    public static GearBarDefaultHeight = 170;

    @property([cc.Node])
    itemsNode:cc.Node[] =[];

    @property(cc.Label)
    instroductionLabelDown: cc.Label = null;

    @property(cc.Node)
    drawUpButton : cc.Node = null;

    @property(cc.Node)
    drawUpButtonFrame : cc.Node = null;

    @property(cc.Label)
    instroductionLabelUp: cc.Label = null;

    @property(cc.Node)
    fallDownButton : cc.Node = null;

    @property(cc.Node)
    fallDownLayer : cc.Node = null;

    @property(cc.Node)
    GearBarRoot : cc.Node = null;

    @property(cc.Node)
    pullUpCover : cc.Node = null;
    
    private _gameScene:GameSceneBase = null;
    private _gearItems:GearItem[] = [];
    private _lastFocusID:GameCurrencyType = GameCurrencyType.NoneType;
    private _state:GearBarState = GearBarState.Down;
    private _type:GearBarType = GearBarType.Normal;
    private _pullUpHeight:number = GearBar.AdBannerDefaultHeight;
    private _fallDownHeight:number = 0;
    private _lastLeftStepCount:number = 0;
    private _hasShownAdBanner:boolean = false;

    public getAvailableAmount(itemID:GameCurrencyType){
        return this._gameScene.getAvailableGearItemAmount(itemID);
    }

    public drawUp(animated:boolean = false){
        if (animated) {
            let action = cc.moveTo(0.15, cc.v2(0, this._pullUpHeight));
            action.setTag(1001);
            this.GearBarRoot.stopActionByTag(1001);
            this.GearBarRoot.runAction(action);
            if(this._gameScene.getPowerAmount(PowerID.AvailableSwap) <= 5){
                this.playAlertGeatItemsAnimation(0.15);
            }
        }else{
            this.GearBarRoot.setPosition(cc.v2(0, this._pullUpHeight));
        }
    }

    public fallDown(animated:boolean = false){

        if (animated) {
            let action = cc.moveTo(0.15, cc.v2(0, this._fallDownHeight));
            action.setTag(1001);
            this.GearBarRoot.stopActionByTag(1001);
            this.GearBarRoot.runAction(action);
        }else{
            this.GearBarRoot.setPosition(cc.v2(0, this._fallDownHeight));
        }
    }

    public playAlertGeatItemsAnimation(delay:number){
        if (!this._gameScene.hasShowCompleteBanner()) {
            for (let index = 0; index < this._gearItems.length; index++) {
                let gearItem = this._gearItems[index];
                gearItem.playAlertAnimation(index * 0.4 + delay)
            }
        }
    }

    public getState():GearBarState{
        return this._state;
    }
    public setState(state:GearBarState, animated:boolean = false){
        let oldState = this._state;
        this._state = state;
        switch (this._state) {
            case GearBarState.Down:{
                if (this._type == GearBarType.Drawer){
                    this.drawUpButtonFrame.active = this._hasShownAdBanner;    
                }else{
                    this.drawUpButtonFrame.active = false;
                }
                this.fallDownButton.active = false;
                this.fallDownLayer.active = false;
                this.instroductionLabelUp.node.active = true;
                this.instroductionLabelDown.node.active = false;
                
                if (animated && oldState != this._state) {
                    this.fallDown(animated);
                }else{
                    this.fallDown(animated);
                }
            }
                break;
            case GearBarState.Up:{
                this.drawUpButtonFrame.active = false;
                this.fallDownButton.active = true;
                this.fallDownLayer.active = true;
                this.instroductionLabelUp.node.active = false;
                this.instroductionLabelDown.node.active = true;
                if (animated && oldState != this._state) {
                    this.drawUp(animated);
                }else{
                    this.drawUp(animated);
                }

            }
                    break;
        
            default:
                break;
        }
    }
    public getType():GearBarType{
        return this._type;
    }
    public setType(type:GearBarType){
        this._type = type;
        switch (this._type) {
            case GearBarType.Normal:{
                this.setState(GearBarState.Down);
                this.drawUpButtonFrame.active = false;
                this.instroductionLabelDown.node.active = true;
                this.instroductionLabelUp.node.active = false;
                let widget = this._gameScene.gridFrame.getComponent(cc.Widget);
                if (widget) {
                    widget.bottom = GearBar.GearBarDefaultHeight;
                }
                this.pullUpCover.active = false;
            }
                break;
            default:
                break;
        }
    }
    
    public setData(data:GearBarData){
        this.setType(data.type);
    }
    public focusItemWithGearID(itemID:GameCurrencyType) : void {
        let gearName:string = '';
        let instrodution:string = '';
        switch(itemID) {
            case GameCurrencyType.AidHammer:
            case GameCurrencyType.AidBomb:
            case GameCurrencyType.AidSwap:{
                // let itemInfo = IdentifierManager.getInstance().getItemInfoFromItemID(itemID);
                // if (itemInfo) {
                //     gearName = itemInfo.localization;
                //     instrodution = itemInfo.description;
                // }
            }
                break;
        }

        
        if (this.instroductionLabelUp) {
            if (gearName != '' && instrodution != '') {
                this.instroductionLabelUp.string = gearName + ':' + instrodution;
            }
        }

        if (this.instroductionLabelDown) {
            if (gearName != '' && instrodution != '') {
                this.instroductionLabelDown.string = gearName + ':' + instrodution;
            }
        }

        if (itemID == GameCurrencyType.NoneType) {
            if (this._lastFocusID != GameCurrencyType.NoneType) {
                if (this._gameScene.backgroundMask) {
                    this._gameScene.backgroundMask.runAction(cc.fadeTo(0.25, 0).easing(cc.easeIn(2)));
                }
                if (this._gameScene.cancelGearLayer) {
                    this._gameScene.cancelGearLayer.active = false;
                }

                if (this.instroductionLabelUp) {
                    this.instroductionLabelUp.node.opacity = 0;
                }
                if (this.instroductionLabelDown) {
                    this.instroductionLabelDown.node.opacity = 0;
                }
            }
        }else{
            if (this._lastFocusID == GameCurrencyType.NoneType) {
                if (this._gameScene.backgroundMask) {
                    this._gameScene.backgroundMask.runAction(cc.fadeTo(0.25, 150).easing(cc.easeIn(2)));
                }
                if (this._gameScene.cancelGearLayer) {
                    this._gameScene.cancelGearLayer.active = true;
                }
                if (this.instroductionLabelUp) {
                    this.instroductionLabelUp.node.opacity = 255;
                }
                if (this.instroductionLabelDown) {
                    this.instroductionLabelDown.node.opacity = 255;
                }
            }
        }
        
        for (let gearItem of this._gearItems) {
            if (gearItem.getItemID() == itemID) {
                gearItem.node.zIndex = 2;
            }else{
                gearItem.node.zIndex = 0;
            }
        }   
        this._lastFocusID = itemID;

        switch (this._type) {
            case GearBarType.Drawer:{
                this.setState(GearBarState.Down, true);
            }
                break;
        
            default:
                break;
        }
        
    }
    public onItemTabbed(item:GearItem) : void{
        this._gameScene.selectGear(item.getItemID());
    } 
    public updateGears(){
        for (let item of this._gearItems) {
            item.updataAmount()
            item.setLimitedAmount(this._gameScene.getLimitedGearAmount(item.getItemID()));
            item.setUsedAmount(this._gameScene.getUsedGearAmount(item.getItemID()));
        }

        let currentLeftStepCount = this._gameScene.getPowerAmount(PowerID.AvailableSwap)
        if ( currentLeftStepCount <= 5 && this._lastLeftStepCount != currentLeftStepCount) {
            this.playAlertAnimation();
            this.playAlertGeatItemsAnimation(0);
        }
        this._lastLeftStepCount = currentLeftStepCount
    }
    public getItemWithGearID(itemID:GameCurrencyType) : GearItem {
        let result:GearItem = null;
        for (let item of this._gearItems) {
            if (item.getItemID() == itemID) {
                result = item;
                break;
            }
        }
        return result;
    }
    public getGearIconWorldPos(itemID:GameCurrencyType) :cc.Vec2 {

        let item = this.getItemWithGearID(itemID);
        if (item) {
            return item.node.parent.convertToWorldSpaceAR(item.node.position);
        }
        return cc.Vec2.ZERO;
    }
    public playAlertAnimation(){
        if (!this._gameScene.hasShowCompleteBanner()) {
            let hasAvailableItem = false
            for (let gearItem of this._gearItems) {
                if (gearItem.getIsAvailable()) {
                    hasAvailableItem = true;
                    break
                }
            }

            if (hasAvailableItem) {
                let anim = this.getComponent(cc.Animation);
                if (anim) {
                    let currentClip = anim.currentClip;
                    if (currentClip && currentClip.name == 'alert1') {
                        return;
                    }
                    anim.play('alert1');
                }
            }
        }
    }
    private onDrawUpButtonTabbed(event:cc.Event.EventCustom) : void {
        this.setState(GearBarState.Up, true);
    }
    private onFallDownButtonTabbed(event:cc.Event.EventCustom) : void {
        this.setState(GearBarState.Down, true);
    }


    // LIFE-CYCLE CALLBACKS:

    onLoad () {

        let canvas = cc.find("Canvas");
        if (canvas) {
            this._gameScene = canvas.getComponent(GameSceneBase);
        }

        let itemIds:GameCurrencyType[] = [GameCurrencyType.AidHammer, GameCurrencyType.AidBomb, GameCurrencyType.AidSwap];
        for (let index = 0; index < this.itemsNode.length; index++) {
            let gearItem = this.itemsNode[index].getComponent(GearItem);
            gearItem.setItemID(itemIds[index]);
            gearItem.setGearBar(this);
            this._gearItems[index] = gearItem;
        }

        this.instroductionLabelUp.node.opacity = 0;
        this.instroductionLabelDown.node.opacity = 0;
        this.drawUpButton.on('touchend', this.onDrawUpButtonTabbed, this);
        this.fallDownButton.on('touchend', this.onFallDownButtonTabbed, this);
        this.fallDownLayer.on('touchend', this.onFallDownButtonTabbed, this);
        this.setType(GearBarType.Normal);
        this.setState(GearBarState.Down);
        this.pullUpCover.active = false;

        this.schedule(this.updateGears , 1)
    }

    start () {

    }
    onDestroy() {
        this.itemsNode = null;
        this._gameScene = null;
        this._gearItems = null;
        
    }
}
