import { EntityColor, EntityID } from "../../defines/EntityID";
import GridBase from "../../grid/GridBase";
import { ItemBombType } from "../item/color/bomb/ItemBombBase";
import ItemBombRainbow from "../item/color/bomb/ItemBombRainbow";
import FlyerBase, { FlyerType } from "./FlyerBase";
import GameMath from "../../../../extend/utils/GameMath";
import FlyerBullet from "./FlyerBullet";
import GridCommand from "../../commond/GridCommand";
import FlyerAnimation, { FlyerAnimationType } from "./FlyerAnimation";
import EntityPrefabNodePool from "../EntityPrefabNodePool";
import ItemBase from "../item/ItemBase";

// Learn TypeScript:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/typescript.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/typescript.html
// Learn Attribute:
