// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

enum FlyerLaserState {
    None,
    LockTarget,
    WaitLastLaser,
    DestroyTarget,
};

const { ccclass, property } = cc._decorator;

@ccclass
export default class FlyerLaser extends FlyerBase {

    private _shootOneLaser(duration: number): boolean {
        let grid = this.getGrid();

        let effectPostions: cc.Vec2[] = [];
        let itemsMap = grid.getItemsMap();

        for (let key in itemsMap) {
            let item = itemsMap[key] as ItemBase;
            if (item.canMatch() && item.getEntityColor() == this._laserColor) {
                if (GameMath.indexOf(this._targetPositions, item.getLogicalPos()) == -1) {
                    effectPostions.push(item.getLogicalPos());
                }
            }
        }

        if (effectPostions.length <= 0) {
            return false;
        }

        let index = GameMath.randomInt(0, effectPostions.length - 1);
        let pos = effectPostions[index];
        this._targetPositions.push(pos);

        let bullet = FlyerBullet.create(grid, duration, this._origin, pos, this._laserColor, this._laserBombType, this._laserLayoutGrass);
        grid.setFlyerAtLogicalPos(bullet, pos);

        let mark = FlyerAnimation.createAnimationWithType(grid, FlyerAnimationType.ItemMarked, false);
        grid.setFlyerAtLogicalPos(mark, pos);
        mark.setVisible(false);

        let showMark = cc.sequence(cc.delayTime(duration),
            cc.callFunc(() => { mark.setVisible(true) }));
        mark.runAction(showMark);
        this._marks.push(mark);

        return true;
    }
    private _marks: FlyerBase[] = [];
    private _targetPositions: cc.Vec2[] = [];
    private _origin: cc.Vec2 = cc.Vec2.ZERO;
    private _findTargetInterval: number = 0;
    private _state: FlyerLaserState = FlyerLaserState.None;
    private _laserColor: EntityColor = EntityColor.None
    private _laserBombType: ItemBombType = ItemBombType.None;
    private _laserLayoutGrass: boolean = false;
    private _lastLaserDuration: number = 0;
    private _rainbowItem: ItemBombRainbow = null;
    private _launched: boolean = false;

    public getEntityID(): EntityID {
        return EntityID.FlyerLaser;
    }

    public launch(): void {
        if (this._launched) {
            return;
        }
        let grid = this.getGrid();
        let itemsMap = grid.getItemsMap();
        for (let key in itemsMap) {
            let item = itemsMap[key] as ItemBase;
            if (item.canMatch() && item.getEntityColor() == this._laserColor) {
                grid.recordAttackedPosition(item.getLogicalPos())
            }
        }

        this._launched = true;
        if (this._rainbowItem) {
            this._rainbowItem.node.setScale(1.2);
            this._rainbowItem.node.zIndex = GridBase.ZORDER_EFFECT;
            this._rainbowItem.shiftToEffectLayer();
        }
        this._state = FlyerLaserState.LockTarget;
        this._lastLaserDuration = 0.2;
        this.getGrid().addObstructs(this);
    }

    public update(dt): void {

        switch (this._state) {
            case FlyerLaserState.LockTarget: {
                let angleDelta = GameMath.clamp(180 + this._targetPositions.length * 30, 180, 3581);
                this._rainbowItem.node.angle += -(angleDelta * dt);
                this._findTargetInterval -= dt;
                if (this._findTargetInterval <= 0) {
                    let duration = GameMath.clamp(0.5 - this._targetPositions.length * 0.03, 0.2, 0.5);
                    let interval = GameMath.clamp(0.2 - this._targetPositions.length * 0.015, 0.05, 0.2);
                    this._lastLaserDuration = duration;
                    this._findTargetInterval = interval;
                    let launched = this._shootOneLaser(duration);
                    if (!launched) {
                        this._state = FlyerLaserState.WaitLastLaser;
                        this._findTargetInterval += this._lastLaserDuration;
                    }
                }
            }
                break;
            case FlyerLaserState.WaitLastLaser: {
                let angleDelta = GameMath.clamp(180 + this._targetPositions.length * 30, 180, 3581);
                this._rainbowItem.node.angle += -(angleDelta * dt);
                this._findTargetInterval -= dt;
                if (this._findTargetInterval <= 0) {
                    this._rainbowItem.runAction(cc.rotateBy(1, angleDelta).repeatForever());
                    this._state = FlyerLaserState.DestroyTarget;
                }

            }
                break;
            case FlyerLaserState.DestroyTarget: {
                this._state = FlyerLaserState.None;
                let grid = this.getGrid();
                for (let mark of this._marks) {
                    mark.destoryWithRecycle(true);
                }
                this._marks.length = 0;

                this._rainbowItem.excuteCommand(GridCommand.SuicideClean(this._origin, EntityColor.None, this._laserLayoutGrass));
                for (let checkPos of this._targetPositions) {
                    grid.dispatchCommand(checkPos, GridCommand.MatchClean(checkPos, 0, this._laserLayoutGrass), 0);
                    grid.highlightedCell(checkPos);
                    grid.bombCell(checkPos);
                }

                grid.removeObstructs(this);
                this.destoryWithRecycle(true);
            }
                break;
            default:
                break;
        }
    }

    public static create(grid: GridBase,
        rainbow: ItemBombRainbow,
        origin: cc.Vec2,
        color: EntityColor,
        type: ItemBombType,
        layoutGrass: boolean): FlyerLaser {

        let className = 'FlyerLaser';
        let prefabFileName = 'match_scene/prefab/entity/flyer/FlyerLaser';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerLaser;
        if (component) {
            component.init(grid, rainbow, origin, color, type, layoutGrass);
            return component;
        }
        return null;
    }

    protected init(grid: GridBase, rainbow: ItemBombRainbow, origin: cc.Vec2, color: EntityColor, type: ItemBombType, layoutGrass: boolean) {
        super.initFlyer(grid, FlyerType.Laser);
        this._marks.length = 0;
        this._targetPositions.length = 0;
        this._findTargetInterval = 0;
        this._state = FlyerLaserState.None;
        this._lastLaserDuration = 0;
        this._rainbowItem = rainbow;
        this._origin = origin;
        this._laserColor = color;
        this._laserBombType = type;
        this._laserLayoutGrass = layoutGrass;
        this._launched = false;
    }
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {

    }

    // update (dt) {}

    onDestroy() {
        super.onDestroy();
        this._marks = null;
        this._targetPositions = null;
        this._origin = null;
        this._rainbowItem = null;
    }
}
