import { GameCurrencyType } from "../../../../../define/GameCurrencyType";
import GridCommand from "../../../commond/GridCommand";
import ItemBase from "../../../entity/item/ItemBase";
import ItemStateSwap from "../../../entity/item/state/ItemStateSwap";
import GridBase, { GridMode } from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateMatch from "./GridStateMatch";
import IGame from "../../../../../base/IGame";
import IdentifierManager from "../../../defines/IdentifierManager";
import FlyerAnimation, { FlyerAnimationType } from "../../../entity/flyer/FlyerAnimation";
import BehaviourID, { BehaviourEvent } from "../../../../../extend/identify/behaviourIdentify/BehaviourDefines";

export default class GridStateForceSwap extends GridStateBase {
    public static create() : GridStateForceSwap {
        return new GridStateForceSwap();
    }
    constructor(){
        super(GridState.ForceSwap);
    }

    enter(grid:GridBase):void {
        
    }
    public canChangeState() : boolean {
        if (this._itemSwaped) {
            return false;
        }
        return true;
    }
    public canPause() : boolean {
        return true;
    }

    private _itemSwaped:boolean = false;
    private _swapPosA:cc.Vec2 = cc.Vec2.ZERO;
    private _swapPosB:cc.Vec2 = cc.Vec2.ZERO;
    private _focusFrame:FlyerAnimation = null;
    public update(grid:GridBase, delta:number) : void {
        if (this._itemSwaped == false) {
            return;
        }

        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            let commandA = GridCommand.ForceSwap(this._swapPosB);
            let commandB = GridCommand.ForceSwap(this._swapPosA);

            grid.dispatchCommand(this._swapPosA, commandA, 0);
            grid.dispatchCommand(this._swapPosB, commandB, 0);
            grid.setCurrentState(GridStateMatch.create());
        }
    }
    private _selectedItem:ItemBase = null;
    public exit(grid:GridBase) {
        if (this._selectedItem) {
            this._selectedItem.unselected();
        }

        if (this._focusFrame) {
            this._focusFrame.destoryWithRecycle();
        }

        this._swapPosA = null;
        this._swapPosB = null;
        this._focusFrame = null;
        this._targetPos = null;
    }
    
    private trySwapItem(grid:GridBase, logicalPosA:cc.Vec2, logicalPosB:cc.Vec2) : boolean {
        let itemA = grid.getItemAtLogicalPos(logicalPosA);
        let itemB = grid.getItemAtLogicalPos(logicalPosB);

        if (itemA && itemB && itemA.canSwap() && itemB.canSwap()) {
            this._swapPosA = logicalPosA;
            this._swapPosB = logicalPosB;
            grid.requestUseGear(GameCurrencyType.AidSwap, 1);
            return true;
        }else{
            //
            return false;
        }
    }

    private _targetPos:cc.Vec2 = cc.Vec2.ZERO;
    public onTapBegan(grid:GridBase, logicalPos:cc.Vec2) {
        if (this._itemSwaped) {
            return;
        }

        let item = grid.getItemAtLogicalPos(logicalPos);
        if (!item) {
            grid.setMode(GridMode.Normal);
            return;
        }
        
        if (item == this._selectedItem) {
            return;
        }

        if (this._selectedItem) {
            this.trySwapItem(grid, this._selectedItem.getLogicalPos(), logicalPos);
        }else{
            if (item.canForceSwap()) {
                item.selected();
                this._selectedItem = item;


                if (!this._focusFrame) {
                    this._focusFrame = FlyerAnimation.createAnimationWithType(grid, FlyerAnimationType.FocusFrame, false);
                    grid.setFlyerAtLogicalPos(this._focusFrame, logicalPos)
                }
                IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('prop_switch1'));
                
            }else{
                //
            }
        }
    }

    public onRespone(grid:GridBase, event:Object) : void {
        
        grid.swapItem(this._swapPosA, this._swapPosB);
        let itemA = grid.getItemAtLogicalPos(this._swapPosB);
        let itemB = grid.getItemAtLogicalPos(this._swapPosA);
        itemA.setCurrentState(ItemStateSwap.create(this._swapPosA, this._swapPosB));
        itemB.setCurrentState(ItemStateSwap.create(this._swapPosB, this._swapPosA));

        this._selectedItem.unselected();
        this._selectedItem = null;
        this._itemSwaped = true;
        grid.setMode(GridMode.Normal);

        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('prop_switch2'));

        
    }
}
