const {ccclass, property} = cc._decorator;

@ccclass
export default class CellNormal extends CellBase {

    public static create(grid:GridBase) : CellNormal {
        let className = 'CellNormal';
        let prefabFileName = 'match_scene/prefab/entity/cell/CellNormal';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as CellNormal;
        if (component) {
            component.init(grid);
            return component;       
        }
        return null;
    }


    protected init(grid:GridBase) : void {
        super.initCell(grid, CellType.Normal);
    }

    public canPassThrough() : boolean {
        if (this.canHoldItem()) {
            let grid = this.getGrid();
            if (grid) {
                let item = grid.getItemAtLogicalPos(this.getLogicalPos());
                if (item) {
                    return false;
                }else{
                    let parasite = grid.getParasiteAtLogicalPos(this.getLogicalPos())
                    if (parasite) {
                        return parasite.hostCanDrop();
                    }else{
                        return true;
                    }
                }
            }
        }
        return false;
    }

    public getCollectionID():CollectionID{
        return CollectionID.CellNormal;
    }

    public getEntityID():EntityID{
        return EntityID.CellNormal;
    }

    public canHoldItem():boolean{
        return true;
    }

    public getButterflyPriority(color:EntityColor, layoutGrass:boolean):number{
        if(layoutGrass && this._canLayoutGrass()){
            let item = this.getGrid().getItemAtLogicalPos(this.getLogicalPos());
            if(item && item.canLayoutGrass() && item.canExcuteCommand(GridCommand.BombClean(this.getLogicalPos(), color, layoutGrass))){
                return 1;
            }
        }
        return super.getButterflyPriority(color, layoutGrass);
    }

    protected _canCleanByBomb(cleanColor:EntityColor):boolean{
        return this._canLayoutGrass();
    }

    protected _canLayoutGrass():boolean{
        if(this.canHoldItem() && this.getNeedGrass()){
            return !this.getGrid().isGrassFloorAtLogicalPos(this.getLogicalPos());
        }
        return false;
    }

    protected _onClean(cleanOption:EntityCleanOption):void{
        if(cleanOption.layoutGrass){
            let grass = FloorGrass.create(this.getGrid());
            this.getGrid().setFloorAtLogicalPos(grass, this.getLogicalPos());
            grass.grow();
            grass.onCollected();
        }
    }
}
