import IGame from "../../../../base/IGame";
import GameMath from "../../../../extend/utils/GameMath";
import GridCommand from "../../commond/GridCommand";
import { EntityColor, EntityID, EntityIDManager, EntityType } from "../../defines/EntityID";
import IdentifierManager from "../../defines/IdentifierManager";
import GridBase from "../../grid/GridBase";
import EntityPrefabNodePool from "../EntityPrefabNodePool";
import FlyerBase, { FlyerType } from "./FlyerBase";
import FlyerAnimation, { FlyerAnimationType } from "./FlyerAnimation";
import EntityBase from "../EntityBase";
import CellBase from "../cell/CellBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class FlyerFireworksMissile extends FlyerBase {

    private static maxSpeed: number = 1000;
    private static maxForce: number = 5000;
    private static maxCentrifugalForce: number = FlyerFireworksMissile.maxForce
    private static startCentrifugalForce: number = 800


    private _layoutGrass: boolean = false;
    private _launched: boolean = false;


    private _currentMaxCentrifugalForce: number = 0;
    private _targetPos: cc.Vec2 = cc.v2(0, 0);
    private _targetLogicalPos: cc.Vec2 = cc.v2(0, 0);
    private _velocity: cc.Vec2 = cc.v2(0, 0);
    private setVelocity(velocity: cc.Vec2) {
        this._velocity = velocity
        if (this._velocity.mag() > 0.000001) {
            let angle = GameMath.getAngle(cc.v2(0, 100), this._velocity, cc.v2(0, 100), true)
            this._puppet.angle = -angle
        }
    }

    public static create(grid: GridBase, layoutGrass: boolean): FlyerFireworksMissile {
        let className = 'FlyerFireworksMissile';
        let filePath = 'match_scene/prefab/entity/flyer/FlyerFireworksMissile';
        let prefabFileName = filePath;
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerFireworksMissile;
        if (component) {
            component.init(grid, layoutGrass);
            return component;
        }
        return null;
    }

    public init(grid: GridBase, layoutGrass: boolean) {
        super.initFlyer(grid, FlyerType.FireworksMissile);
        this._layoutGrass = layoutGrass;
        this._launched = false;
    }

    public getEntityID(): EntityID {
        return EntityID.FlyerFireworks;
    }

    protected getAttackPos(color: EntityColor, layoutGrass: boolean): cc.Vec2 {

        let targetPos = cc.v2(-1, -1);
        let targetPriority = EntityBase.DEFAULT_FIREWORKS_ATTACKED_PRIORITY;
        let grid = this.getGrid();
        let entityTypes: EntityType[] = [
            EntityType.Parasite,
            EntityType.Item,
            EntityType.Rug,
            EntityType.Floor,
            EntityType.Popsicle,
            EntityType.Cell,
        ];

        let cellsMap = grid.getCellsMap();
        let checkPositions: cc.Vec2[] = [];
        let attackedPositions = grid.getAttackedPosition();
        for (let key in cellsMap) {
            let cell = cellsMap[key] as CellBase;
            let checkPos = cell.getLogicalPos();
            if (GameMath.indexOf(attackedPositions, checkPos) == -1) {
                checkPositions.push(checkPos);
            }
        }
        GameMath.shuffleArray(checkPositions);
        for (let checkPos of checkPositions) {
            if (GameMath.indexOf(attackedPositions, checkPos) == -1) {
                let minPriority = EntityBase.DEFAULT_FIREWORKS_ATTACKED_PRIORITY;

                let canAttackTop = false;
                for (let entityType of entityTypes) {
                    let entity = grid.getEntity(entityType, checkPos);
                    if (entity) {

                        if (entityType == EntityType.Parasite || entityType == EntityType.Item) {
                            if (entity.canExcuteCommand(GridCommand.BombClean(this.getLogicalPos(), color, layoutGrass))) {
                                canAttackTop = true;
                            }
                        } else {
                            canAttackTop = true;
                        }

                        if (canAttackTop) {
                            let butterflyPriority = entity.getFireWorksAttackedPriority();
                            if (butterflyPriority < minPriority) {
                                minPriority = butterflyPriority;
                            }
                        } else {
                            break;
                        }
                    }
                }
                if (canAttackTop) {
                    if (minPriority < targetPriority) {
                        targetPriority = minPriority;
                        targetPos = checkPos;
                    }
                }
            }
        }

        if (targetPriority >= EntityBase.DEFAULT_FIREWORKS_ATTACKED_PRIORITY || targetPos.equals(cc.v2(-1, -1))) {
            targetPos = super.getAttackPos(color, layoutGrass);
        }

        return targetPos;
    }

    public launch() {
        if (this._launched) {
            return;
        }

        this._launched = true;
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('fireworks'));
        let grid = this.getGrid();
        this._targetLogicalPos = this.getAttackPos(this.getEntityColor(), this._layoutGrass);
        this._targetPos = grid.getPixelPosFromLogicalPos(this._targetLogicalPos);

        let offsets: cc.Vec2[] = [
            cc.v2(0, 0),
            cc.v2(0, 1),
            cc.v2(0, -1),
            cc.v2(1, 0),
            cc.v2(-1, 0),
        ];
        for (let offset of offsets) {
            let attackPos = this._targetLogicalPos.add(offset)
            grid.recordAttackedPosition(attackPos);
        }
        grid.addObstructs(this);

        this.setVelocity(cc.v2(0, FlyerFireworksMissile.maxSpeed))
        this._currentMaxCentrifugalForce = FlyerFireworksMissile.startCentrifugalForce
    }


    update(dt) {
        if (this._launched == false) {
            return;
        }

        this._currentMaxCentrifugalForce = Math.min(
            FlyerFireworksMissile.maxCentrifugalForce,
            (FlyerFireworksMissile.maxCentrifugalForce - FlyerFireworksMissile.startCentrifugalForce) * dt + this._currentMaxCentrifugalForce
        )

        let offset = this._targetPos.sub(this.node.getPosition());
        let grid = this.getGrid()
        let cellSize = grid.getCellSize()
        if (offset.mag() < cellSize.width * 0.5) {

            let flyer = FlyerAnimation.createAnimationWithType(grid, FlyerAnimationType.BombCross, true)
            grid.setFlyerAtLogicalPos(flyer, this._targetLogicalPos);

            let offsets: cc.Vec2[] = [
                cc.v2(0, 0),
                cc.v2(0, 1),
                cc.v2(0, -1),
                cc.v2(1, 0),
                cc.v2(-1, 0),
            ];

            for (let offset of offsets) {
                let attackPos = this._targetLogicalPos.add(offset)
                grid.dispatchCommand(attackPos, GridCommand.BombClean(attackPos, EntityColor.None, this._layoutGrass), 0);
                grid.highlightedCell(this._targetLogicalPos);
                grid.bombCell(attackPos);
            }
            grid.removeObstructs(this);
            this.destoryWithRecycle(false);
        }

        let desireVelocity = offset.normalize().mul(FlyerFireworksMissile.maxSpeed)
        let desireForce = desireVelocity.sub(this._velocity).mul(60)
        let accelerateShadow = (this._velocity.x * desireForce.x + this._velocity.y * desireForce.y) / this._velocity.mag()
        let accelerateForce = this._velocity.normalize().mul(accelerateShadow)
        let centrifugalForce = desireForce.sub(accelerateForce)

        if (centrifugalForce.mag() > this._currentMaxCentrifugalForce) {
            desireForce = desireForce.mul(this._currentMaxCentrifugalForce / centrifugalForce.mag())
        }
        let force = desireForce.normalize().mul(Math.min(FlyerFireworksMissile.maxForce, desireForce.mag()))
        let acceleration = force

        let newVelocity = this._velocity.add(acceleration.mul(dt))
        let speed = Math.min(newVelocity.mag(), FlyerFireworksMissile.maxSpeed)
        this.setVelocity(newVelocity.normalize().mul(speed))
        this.node.setPosition(this.node.getPosition().add(this._velocity.mul(dt)))
    }
}
