export default class GameTargetSingle extends GameTargetBase {

    private collectionID:CollectionID = CollectionID.None;
    public static create(collectionID:CollectionID, amount:number):GameTargetSingle{
        let result = new GameTargetSingle()
        result.init(collectionID, amount);
        return result;
    }

    public init(collectionID:CollectionID, amount:number){
        super.initWithType(GameTargetType.Single);
        this.collectionID = collectionID;
        this.setTargetAmount(amount);
    }

    public getCollectionIDs():CollectionID[]{
        return [this.collectionID]
    }

    public getIconFrameName():string{
        return IdentifierManager.getInstance().getFrameNameFromCollectionID(this.collectionID);
    }
}
