export class IdentifyNodeRegistData{
    public retainCount:number = 0;
    public node:cc.Node = null

    public static create(count:number, node:cc.Node){
        let result = new IdentifyNodeRegistData()
        result.retainCount = count;
        result.node = node;
        return result;
    }
}

export default class IdentifyNodeManager  {

    private static instance:IdentifyNodeManager = null;
    public static getInstance():IdentifyNodeManager{
        if(this.instance == null){
            this.instance = new IdentifyNodeManager();
        }
        return this.instance;
    }
    private _nodeMap:Object = {};
    constructor(){   
       
    }

    public regist(identifier:string, node:cc.Node) {
        if (identifier && identifier != '') {
            if (this._nodeMap.hasOwnProperty(identifier)) {
                let oldData = this._nodeMap[identifier] as IdentifyNodeRegistData;
                oldData.retainCount += 1
            }else{
                this._nodeMap[identifier] = IdentifyNodeRegistData.create(1, node);
            }
        }
    }

    public logout(identifier:string) {
        if (identifier != '') {
            let data = this._nodeMap[identifier] as IdentifyNodeRegistData
            if (data) {
                data.retainCount -= 1
                if (data.retainCount <= 0) {
                    delete this._nodeMap[identifier]; 
                }
            }
        }
    }

    public getNode(identifier:string) : cc.Node {
        if (identifier == NodeID.WholeWindow) {
            return cc.find("Canvas");
        }else{
            let data = this._nodeMap[identifier] as IdentifyNodeRegistData
            if (data) {
                return data.node
            }else{
                return null
            }
        }
    }
}
