const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemChestnut extends ItemBase {

    public static create(grid:GridBase):ItemChestnut{

        let className = 'ItemChestnut';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemChestnut';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemChestnut;
        if (component) {
            component.init(grid); 
            return component;       
        }
        return null;
    }

    public clone():ItemChestnut {
        let item = ItemChestnut.create(this.getGrid());
        return item;
    }

    public getCollectionID():CollectionID{
        return CollectionID.ItemChestnut;
    }

    public getEntityID():EntityID{
        return EntityID.ItemChestnut;
    }

    public getScore():number{
        return 150;
    }

    public getButterflyPriority(color:EntityColor, layoutGrass:boolean):number{
        if(this.getGrid().isTargetCollectionID(this.getCollectionID())){
            return 1;
        }

        return super.getButterflyPriority(color, layoutGrass);
    }

    protected init(grid:GridBase){
        super.initItem(grid, ItemType.Chestnut);
    }

    protected _canDrop():boolean{
        return true;
    }

    protected _canSwap():boolean{
        return true;
    }

    protected _canBlockBombRay() : boolean {
        return true;
    }

    protected _canCleanByBomb(cleanColor:EntityColor):boolean{
        return true;
    }

    protected _canCleanByNeighbour(color:EntityColor):boolean{
        return true;
    }

    protected _onClean(cleanOption:EntityCleanOption):void{
        this.getGrid().gainScoreWithEntity(this);
        if (!this.isTargetCollection()) {
            let flyer = FlyerAnimation.createDieAnimation(this.getGrid(), this.getEntityID());
            this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('nut'));
        }
        this.setVisible(false);
        this.progressDeathWish(cleanOption);
        this.selfDestory(true);
    }
}
