
export default class ItemInfo  {

    public static create(FrameName:string = '', 
                         Localization:string = '', 
                         Description:string = '',
                         isUnlimited:boolean = false, 
                         ScaleX:number = 1.0, 
                         ScaleY:number = 1.0) : ItemInfo {
        return new ItemInfo(FrameName, Localization, Description,isUnlimited, ScaleX, ScaleY);
    }

    public frameName:string = '';
    public localization:string = '';
    public description:string = '';
    public isUnlimited = false;
    public scaleX:number = 1.0;
    public scaleY:number = 1.0;
    

    constructor(FrameName:string, Localization:string, Description:string, isUnlimited:boolean, ScaleX:number, ScaleY:number) {
        this.frameName = FrameName;
        this.localization = Localization;
        this.description = Description;
        this.isUnlimited = isUnlimited;
        this.scaleX = ScaleX;
        this.scaleY = ScaleY;
    }
}
