// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerBomb extends FlyerBase {

    protected FLYER_BOMB_DURATION = 0.2;

    protected _launched:boolean = false;
    protected _bombDuration:number = 0;
    protected _lastBombRingIndex:number = 0;
    protected _canLayoutGrass:boolean = false;
    public getCanLayoutGrass():boolean {
        return this._canLayoutGrass;
    }
    public setCanLayoutGrass(canLayoutGrass:boolean) : void {
        this._canLayoutGrass = canLayoutGrass;
    }
    
    private _origin:cc.Vec2 = cc.Vec2.ZERO;
    public getOrigin():cc.Vec2 {
        return this._origin;
    }
    public setOrigin(origin:cc.Vec2) : void {
        this._origin = origin;
    }

    private _radius:number = 0;
    public getRadius():number {
        return this._radius;
    }
    public setRadius(radius:number) : void {
        this._radius = radius;
    }

    public getEntityID() : EntityID {
        let result = EntityID.FlyerBombWhite;
        switch (this.getEntityColor()) {
            case EntityColor.Red:
                result = EntityID.FlyerBombRed;
                break;
            case EntityColor.Green:
                result = EntityID.FlyerBombGreen;
                break;
            case EntityColor.Blue:
                result = EntityID.FlyerBombBlue;
                break;
            case EntityColor.Yellow:
                result = EntityID.FlyerBombYellow;
                break;
            case EntityColor.Orange:
                result = EntityID.FlyerBombOrange;
                break;
            case EntityColor.Purple:
                result = EntityID.FlyerBombPurple;
                break;
                
            default:
                break;
        }
        return result;
    }
    
    public launch() {
        if (this._launched) {
            return;
        }

        let willAttackPositions = GameMath.enumerateRingPos(this._origin, 0, this._radius);
        for (let pos of willAttackPositions) {
            this.getGrid().recordAttackedPosition(pos);
        }

        this._launched = true;
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_bomb'));

        this._bombDuration = 0
        this._lastBombRingIndex = -1;
        this._launched = true;
        this.getGrid().addObstructs(this);

        let state = this.playAnimation('default');
        if (state) {
            let action = cc.sequence(cc.delayTime(state.duration), cc.callFunc(()=>{
                this.getGrid().removeObstructs(this);
                this.destoryWithRecycle(true);
            }));
            this.runAction(action);
        }
    }

    update (dt) {
        if (this._launched == false) {
            return;
        }
        dt = dt * cc.director.getScheduler().getTimeScale();
        this._bombDuration += dt;

        let currentRingIndex = Math.min(this._radius,  Math.floor(this._radius * this._bombDuration / this.FLYER_BOMB_DURATION));
        if (currentRingIndex > this._lastBombRingIndex) {
            let positions = GameMath.enumerateRingPos(this._origin, this._lastBombRingIndex + 1, currentRingIndex);
            let grid = this.getGrid();
            for (let pos of positions) {
                grid.dispatchCommand(pos, GridCommand.BombClean(this._origin, this.getEntityColor(), this.getCanLayoutGrass()), 0);
                grid.highlightedCell(pos);
            }
            this._lastBombRingIndex = currentRingIndex;

            if (this._lastBombRingIndex >= this._radius) {
                grid.removeObstructs(this);
            }
        }
    }

    onDestroy(){
        super.onDestroy();
        this._origin = null;
    }

    public static create(grid:GridBase, 
                         color:EntityColor, 
                         layoutGrass:boolean, 
                         origin:cc.Vec2, 
                         raidus:number):FlyerBomb{

        let filePath = 'match_scene/prefab/entity/flyer/FlyerBomb';
        switch (color) {
            case EntityColor.Red:
                filePath += 'Red';
                break;
            case EntityColor.Green:
                filePath += 'Green';
                break;
            case EntityColor.Blue:
                filePath += 'Blue';
                break;
            case EntityColor.Yellow:
                filePath += 'Yellow';
                break;
            case EntityColor.Orange:
                filePath += 'Orange';
                break;
            case EntityColor.Purple:
                filePath += 'Purple';
                break;
            case EntityColor.None:
                filePath += 'White';
                break;
        
            default:
                break;
        }

        let className = 'FlyerBomb';
        let prefabFileName = filePath;
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerBomb;
        if (component) {
            component.init(grid, color, layoutGrass, origin, raidus); 
            return component;       
        }
        return null;
    }

    public init(grid:GridBase, 
                color:EntityColor, 
                layoutGrass:boolean, 
                origin:cc.Vec2, 
                raidus:number){
        super.initFlyer(grid, FlyerType.Bomb);
        this.setEntityColor(color)
        this.setCanLayoutGrass(layoutGrass);
        this.setOrigin(origin);
        this.setRadius(raidus);
        this.FLYER_BOMB_DURATION = 0.2;
        this._launched = false;
        this._bombDuration = 0;
        this._lastBombRingIndex = 0;
    }
}
