
//CFGTripleMapTileset
export interface CFGTripleMapTilesetObj {
    TilesetName: string;
    Properties: string;

}

export class CFGTripleMapTileset {

    //
    private cfgPath: string = 'CFGTripleMapTileset';
    //
    private cfgObj: Object[] = [];

    constructor() {

        let res = cc.loader.getRes('data/' + this.cfgPath, cc.JsonAsset) as cc.JsonAsset;
        if (res) {
            this.cfgObj = res.json
        }

    }

    public getTilesetByName(tilesetName: string): CFGTripleMapTilesetObj {

        for (let obj of this.cfgObj) {
            let tmpObj: CFGTripleMapTilesetObj = obj as CFGTripleMapTilesetObj;
            if (tmpObj.TilesetName == tilesetName) {
                return tmpObj;
            }
        }

        return null;
    }

}