const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemBombRow extends ItemBombBase {

    public getCollectionID() : CollectionID {
    
        let result = CollectionID.None;
        switch (this.getEntityColor()) {
            case EntityColor.Red:
                result = CollectionID.ItemBombRowRed;
                break;
            case EntityColor.Green:
                result = CollectionID.ItemBombRowGreen;
                break;
            case EntityColor.Blue:
                result = CollectionID.ItemBombRowBlue;
                break;
            case EntityColor.Yellow:
                result = CollectionID.ItemBombRowYellow;
                break;
            case EntityColor.Orange:
                result = CollectionID.ItemBombRowOrange;
                break;
            case EntityColor.Purple:
                result = CollectionID.ItemBombRowPurple;
                break;
                
            default:
                break;
        }
        return result;
    }
    
    public getEntityID():EntityID {
        
        let result = EntityID.None;
        switch (this.getEntityColor()) {
            case EntityColor.Red:
                result = EntityID.ItemBombRowRed;
                break;
            case EntityColor.Green:
                result = EntityID.ItemBombRowGreen;
                break;
            case EntityColor.Blue:
                result = EntityID.ItemBombRowBlue;
                break;
            case EntityColor.Yellow:
                result = EntityID.ItemBombRowYellow;
                break;
            case EntityColor.Orange:
                result = EntityID.ItemBombRowOrange;
                break;
            case EntityColor.Purple:
                result = EntityID.ItemBombRowPurple;
                break;
                
            default:
                break;
        }
        return result;
    }

    public getProducedTrainSupplyMaps() : Object {
        let result = {};
        result[GameCurrencyType.GoldCoin] = 1;
        result[GameCurrencyType.TrainSoap] = 1;
        return result;
    }

    public getScore():number{
        return 300;
    }

    protected _onClean(cleanOption:EntityCleanOption) {
        super._onClean(cleanOption);
        this.getGrid().gainScoreWithEntity(this);
        if (!this.getIsDud()) {
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_line'));
            let rayDirections:Pair<cc.Vec2, boolean>[] = [
                new Pair<cc.Vec2, boolean>(cc.v2(1, 0), true),
                new Pair<cc.Vec2, boolean>(cc.v2(-1, 0), false),
            ];
            let grid = this.getGrid();
            for (let ray of rayDirections) {
                let cannon = FlyerCannon.create(grid, 
                                                this.getEntityColor(), 
                                                this.getShouldLayoutGrass(), 
                                                this.getLogicalPos(), 
                                                ray.first, 
                                                ray.second);
                grid.setFlyerAtLogicalPos(cannon, this.getLogicalPos());
            }

            this.setVisible(false);
        }
        
    }

    public static create(grid:GridBase, color:EntityColor):ItemBombRow{

        let className = 'ItemBombRow';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemBombRow';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemBombRow;
        if (component) {
            component.init(grid, color); 
            return component;       
        }
        return null;
    }

    public clone():ItemBombRow {
        let item = ItemBombRow.create(this.getGrid(),this.getEntityColor());
        return item;
    }
    protected init(grid:GridBase, entityColor:EntityColor) : void{
        super.initBomb(grid, ItemBombType.Row);
        this.setEntityColor(entityColor);
    }
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}
}
