import FloorBase, { FloorType } from "./FloorBase";
import GridBase from "../../grid/GridBase";
import { EntityColor, EntityID } from "../../defines/EntityID";
import { CollectionID } from "../../defines/CollectionID";
import EntityBase from "../EntityBase";
import EntityPrefabNodePool from "../EntityPrefabNodePool";

const {ccclass, property} = cc._decorator;

@ccclass
export default class FloorChanger extends FloorBase {

    public static create(grid:GridBase, entityColor:EntityColor):FloorChanger{
        let className = 'FloorChanger';
        let prefabFileName = 'match_scene/prefab/entity/floor/FloorChanger';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FloorChanger;
        if (component) {
            component.init(grid, entityColor);    
            return component;       
        }
        return null;
    }

    public clone():FloorChanger {
        return FloorChanger.create(this.getGrid(), this.getEntityColor());
    }

    public getCollectionID():CollectionID{
        return CollectionID.FloorChanger;
    }

    public getEntityID():EntityID{
        let result = EntityID.None;
        switch(this.getEntityColor()){
            case EntityColor.Red:{
                result = EntityID.FloorChangerRed;
                break;
            }
            case EntityColor.Green:{
                result = EntityID.FloorChangerGreen;
                break;
            }
            case EntityColor.Blue:{
                result = EntityID.FloorChangerBlue;
                break;
            }
            case EntityColor.Yellow:{
                result = EntityID.FloorChangerYellow;
                break;
            }
            case EntityColor.Orange:{
                result = EntityID.FloorChangerOrange;
                break;
            }
            case EntityColor.Purple:{
                result = EntityID.FloorChangerPurple;
                break;
            }
            default:
                break;
        }
        return result;
    }

    public _canCleanByBomb(cleanColor:EntityColor):boolean{
        return false;
    }

    public _canBeFloorClean():boolean{
        return false;
    }

    protected init(grid:GridBase, entityColor:EntityColor):void{
        super.initFloor(grid, FloorType.Changer);
        this.setEntityColor(entityColor);
    }
}
