import { CFGTripleMapTileset, CFGTripleMapTilesetObj } from "../../config/CFGTripleMapTileset";
import GameMath from "./GameMath";

/**
 * Created by chenzd on 2018/7/25.
 * 
 */

export class TMXJsonObjectInfo {
    public gid: number = 0;
    public id: number = 0;
    public nane: string = '';
    public type: string = '';
    public rect: cc.Rect = null;
    public properties: Object = {};
    public path: cc.Vec2[] = [];
}

export default class TMXJsonPhraser {

    private _mapSize: cc.Size = null;
    private _tileSize: cc.Size = null;
    private _tileLayers: Object = {};
    private _objectLayers: Object = {};
    private _tileProperties: Object = {};

    // private static _nullValueMap:Object = {};
    // private static _nullObjectLayer:TMXJsonObjectInfo[] = [];
    // private static _nullTileLayer:number[] = [];

    public static create(data: Object): TMXJsonPhraser {
        return new TMXJsonPhraser(data);
    }
    //json

    public constructor(jsonObject: Object) {
        this.initMapInfo(jsonObject);
        this.initLayers(jsonObject);
        this.initTileProperties(jsonObject);
    }

    public getMapSize(): cc.Size {
        return this._mapSize;
    }

    public getTileSize(): cc.Size {
        return this._tileSize;
    }

    public getTileLayerWithName(name: string): number[] {
        if (this._tileLayers[name]) {
            return this._tileLayers[name];
        }
        return null;
    }

    public getObjectLayerWithName(name: string): TMXJsonObjectInfo[] {
        if (this._objectLayers[name]) {
            return this._objectLayers[name];
        }
        return null;
    }

    public getObjectInfos(name: string, pos: cc.Vec2): TMXJsonObjectInfo[] {
        let result: TMXJsonObjectInfo[] = [];

        let layer = this.getObjectLayerWithName(name);
        if (layer) {
            let cellRect = cc.rect(pos.x * this._tileSize.width + 1,
                pos.y * this._tileSize.height + 1,
                this._tileSize.width - 2,
                this._tileSize.height - 2);

            for (let objectInfo of layer) {
                if (GameMath.rectIntersectsRect(objectInfo.rect, cellRect)) {
                    result.push(objectInfo);
                }
            }
        }
        return result;
    }

    public getPropertieFromGID(gid: number): Object {
        if (this._tileProperties[gid]) {
            return this._tileProperties[gid];
        }
        return null;
    }

    private initMapInfo(jsonObj: Object): void {
        if (!jsonObj.hasOwnProperty('width') || !jsonObj.hasOwnProperty('height')
            || !jsonObj.hasOwnProperty('tilewidth') || !jsonObj.hasOwnProperty('tilewidth')) {
            cc.error("initMapInfo error");
            return;
        }

        this._mapSize = cc.size(jsonObj['width'], jsonObj['height']);

        this._tileSize = cc.size(jsonObj['tilewidth'], jsonObj['tilewidth']);

    }

    private initLayers(jsonObj: Object): void {
        this._tileLayers = {};

        if (!jsonObj.hasOwnProperty('layers')) {
            cc.error('No Layers');
            return;
        }
        let layers: Object = jsonObj['layers'];
        for (let key in layers) {
            let layer: Object = layers[key];
            let type: string = layer['type'];
            if (type == 'tilelayer') {
                let data: number[] = [];
                let values: number[] = layer['data'];
                for (let val of values) {
                    data.push(val);
                }

                let name: string = layer['name'];
                this._tileLayers[name] = data;
            } else if (type == 'objectgroup') {
                let data: TMXJsonObjectInfo[] = [];
                let values: Object[] = layer['objects'];
                for (let key in values) {
                    let obj: Object = values[key];

                    let info: TMXJsonObjectInfo = new TMXJsonObjectInfo();
                    info.id = obj['id'];
                    info.nane = obj['name'];
                    info.type = obj['type'];
                    info.rect = cc.rect(obj['x'], obj['y'], obj['width'], obj['height'])
                    info.gid = 0;
                    if (obj.hasOwnProperty('gid')) {
                        info.gid = obj['gid'];
                        info.rect.y = info.rect.y - info.rect.height;
                    }

                    if (obj.hasOwnProperty('properties') && obj.hasOwnProperty('propertytypes')) {
                        let properties: Object = obj['properties'];
                        // let propertytypes:Object = obj['propertytypes'];

                        for (let key in properties) {
                            let value = properties[key];

                            info.properties[key] = value;
                        }
                    }

                    if (obj.hasOwnProperty('polyline')) {
                        let origin = info.rect.origin;
                        let tileSize = this._tileSize;
                        let offsets: Object[] = obj['polyline'];
                        for (let key in offsets) {
                            let offset = offsets[key];
                            info.path.push(origin.add(cc.v2(offset['x'], offset['y'])));
                        }

                        // auto origin = info.rect.origin;
                        // auto tileSize = m_tileSize;
                        // rapidjson::Document::ConstArray offsets = object["polyline"].GetArray();
                        // for (const auto& offset : offsets){
                        //     auto offsetX = offset["x"].GetFloat();
                        //     auto offsetY = offset["y"].GetFloat();
                        //     info.path.push_back(origin + Vec2(offsetX, offsetY));
                        // }
                    }

                    data.push(info);
                }

                let name: string = layer['name'];
                this._objectLayers[name] = data;

                // cc.log(this._objectLayers);
            }
        }

    }

    private initTileProperties(genericValue: Object): void {
        this._tileProperties = {};
        let tileSets: Object[] = genericValue['tilesets'];

        for (let objItem of tileSets) {
            let firstGID: number = objItem['firstgid'];

            if (objItem.hasOwnProperty('source')) {
                let source: string = objItem['source'];
                let lastSlashIndex: number = source.lastIndexOf('/');
                let lastDotIndex: number = source.lastIndexOf('.');

                let tilesetName = source;
                tilesetName = source.substr(lastSlashIndex + 1, lastDotIndex - lastSlashIndex - 1);

                let jsonObj = this.getTilesetJsonString(tilesetName);
                // //TODO 
                // let jsonObj = JSON.parse(jsonString);
                let tileProperties = this.getTileProperties(jsonObj, firstGID);

                for (let key in tileProperties) {
                    this._tileProperties[key] = tileProperties[key];
                }
            } else {
                let tileCount: number = objItem['tilecount'];
                if (!objItem.hasOwnProperty('tileproperties')) {
                    continue;
                }

                let tileproperties: Object = objItem['tileproperties'];
                // let tilepropertytypes:Object = objItem['tilepropertytypes'];

                for (let gidIndex: number = 0; gidIndex < tileCount; gidIndex++) {
                    let key: string = gidIndex.toString();
                    if (tileproperties.hasOwnProperty(key)) {
                        let values: Object = tileproperties[key];

                        // for(let key in values){

                        // }

                        this._tileProperties[firstGID + gidIndex] = values;
                    }


                }



            }
        }
    }

    private getTilesetJsonString(key: string): Object {
        let result: string = '';
        // let fileName = 'maps/tilesets/' + key;
        // result = cc.loader.getRes(fileName);

        let tileset: CFGTripleMapTileset = new CFGTripleMapTileset();
        let tilesetInfo: CFGTripleMapTilesetObj = tileset.getTilesetByName(key);
        if (tilesetInfo) {
            result = JSON.parse(tilesetInfo.Properties);
        }

        return result;
    }

    private getTileProperties(genericValue: Object, beginGid: number): Object {
        let tileProperties: Object = {};

        if (genericValue.hasOwnProperty('tileproperties')) {
            let tileCount: number = 0;
            if (genericValue.hasOwnProperty('tilecount')) {
                tileCount = genericValue['tilecount'];
            }

            let tileproperties: Object = genericValue['tileproperties'];
            // let tilepropertytypes:Object = genericValue['tilepropertytypes'];

            for (let gidIndex: number = 0; gidIndex < tileCount; gidIndex++) {
                let keyStr: string = gidIndex.toString();
                let key: string = keyStr;
                if (tileproperties.hasOwnProperty(key)) {
                    let values: Object = tileproperties[key];

                    tileProperties[beginGid + gidIndex] = values;

                }
            }
        }

        return tileProperties;
    }


}
