import GridStateBase, { GridState } from "../GridStateBase";
import GridBase from "../../GridBase";
import GridStateBousTime from "./GridStateBousTime";


export default class GridStateCompleteBanner extends GridStateBase {

    public static create() : GridStateCompleteBanner {
        return new GridStateCompleteBanner();
    }

    constructor() {
        super(GridState.CompleteBanner);
    }

    public enter(grid:GridBase) {

        if (grid.hasShowCompleteBanner()) {
            grid.setCurrentState(GridStateBousTime.create());
        }else{
            grid.resetDropConfig();
            grid.showCompleteBanner();
        }
    }

    public update(grid:GridBase, delta:number) {
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
        
            if (grid.hasShowCompleteBanner()) {
                grid.setCurrentState(GridStateBousTime.create());
            }
        }
    }
}
