const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemHoneyComb extends ItemBase {

    private _spreadHoneyIDs:EntityID[] = [];
    public static create(grid:GridBase):ItemHoneyComb{

        let className = 'ItemHoneyComb';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemHoneyComb';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemHoneyComb;
        if (component) {
            component.init(grid); 
            return component;       
        }
        return null;
    }

    public clone() : ItemHoneyComb {
        let item = ItemHoneyComb.create(this.getGrid());
        return item;
    }

    public init(grid:GridBase):void{
        super.initItem(grid, ItemType.HoneyComb);
        this._spreadHoneyIDs.length = 0;
    }

    public getSpreadHoneyHp() : number {

        let hp = 1;
        if (this._spreadHoneyIDs.length > 0) {
            let index = GameMath.randomInt(0, this._spreadHoneyIDs.length - 1);
            let entityID = this._spreadHoneyIDs[index];
            hp = entityID - EntityID.ParasiteHoneyHp1 + 1;
        }

        return hp;
    }

    public getEntityID():EntityID{
        return EntityID.ItemHoneyComb;
    }

    public getCollectionID():CollectionID{
        return CollectionID.ItemHoneyComb;
    }

    public dealWithConfigMap(configMap:Object):void{
        super.dealWithConfigMap(configMap);
        let entityNameString:string = configMap[TMXConfigInfoKeys.CFG_HONEY_COMB_OUTPUT];
        if (entityNameString) {
            let entityNames = entityNameString.split('|');
            for (let entityName of entityNames) {
                let entityID = IdentifierManager.getInstance().getEntityIDFromString(entityName);
                if (entityID == EntityID.ParasiteHoneyHp1 || entityID == EntityID.ParasiteHoneyHp2) {
                    this._spreadHoneyIDs.push(entityID);   
                }
            }
        }
    }

    protected _canDrop():boolean{
        return false;
    }

    protected _canSwap():boolean{
        return false;
    }

    protected _canMatch():boolean{
        return false;
    }

    protected _canCleanByBomb(cleanColor:EntityColor):boolean{
        return false;
    }

    protected _canCleanByNeighbour(color:EntityColor):boolean{
        return false;
    }

    protected _converUnderForever():boolean{
        return true;
    }

    onDestroy() {
        super.onDestroy();
        this._spreadHoneyIDs = null;
    }
}
