export enum CellType {
    None,
    Normal,
    Conveyer,
    MovablePlate,
};

export enum GenerateType{
    None,
    Default,
    Probabilistic,
    List,
};

const {ccclass, property} = cc._decorator;

@ccclass
export default class CellBase extends EntityBase {

    private _cellType:CellType = CellType.None;
    public getCellType() : CellType {
        return this._cellType;
    }

    private _crystalAttackPriority:number = 999;
    public getCrystalAttackPriority() : number {
        return this._crystalAttackPriority;
    }
    public setCrystalAttackPriority(priority:number) : void {
        this._crystalAttackPriority = priority;
    }

    private _isBoneCollector:boolean = false;
    public getIsBoneCollector() : boolean {
        return this._isBoneCollector;
    }
    public setIsBoneCollector(isBoneCollector:boolean) : void {
        this._isBoneCollector = isBoneCollector;
    }

    private _needGrass:boolean = false;
    public getNeedGrass() : boolean {
        return this._needGrass;
    }
    public setNeedGrass(needGrass:boolean) : void{
        this._needGrass = needGrass;
    }

    private _generateType:GenerateType = GenerateType.None;
    public getGenerateType() : GenerateType {
        return this._generateType;
    }
    public setGenerateType(generateType:GenerateType) : void {
        this._generateType = generateType;
    }

    private _generateCount:number = 0;
    public getGenerateCount() : GenerateType {
        return this._generateCount;
    }
    public setGenerateCount(generateCount:number) : void {
        this._generateCount = generateCount;
    }

    public canGenerateItem() : boolean {
        switch (this._generateType) {
            case GenerateType.Default:
            case GenerateType.Probabilistic:
            case GenerateType.List:
                return true;
                break;
                
            default:
                break;
        }
        return false;
    }
    public nextItemID() : EntityID {

        let result:EntityID = EntityID.None; 
        
        switch (this._generateType) {
            case GenerateType.Default:{
                result =  this.getGrid().getGlobalRandomEntityID();
            }
                break;
            case GenerateType.Probabilistic:{
                let grid = this.getGrid();
                result = grid.getRandomEntityID(this._dropEntityMap, 
                                                grid.getLimitedEntityMap(), 
                                                grid.getLimitedGroupMap());
                if (this._generateCount > 0) {
                    this._generateCount--;
                    if (this._generateCount <= 0) {
                        this._generateType = GenerateType.Default;
                    }
                }
            }
                break;
            case GenerateType.List:{
                if (this._dropIndex < this._dropList.length) {
                    let name = this._dropList[this._dropIndex];
                    let entityID = IdentifierManager.getInstance().getEntityIDFromString(name);
                    if (entityID != EntityID.None) {
                        result = entityID;
                    }else{
                        let groupID = IdentifierManager.getInstance().getEntityGroupIDFromString(name);
                        if (groupID != EntityGroupID.None) {
                            let globalDropEntityMap = this.getGrid().getDropEntityMap();
                            let basicEntityIDs = IdentifierManager.getInstance().getEntityIDsFromEntityGroupID(EntityGroupID.ItemNormal);
                            let totalProbability = 0;
                            for (const entityID of basicEntityIDs) {
                                let dropValue = globalDropEntityMap[entityID];
                                if (dropValue) {
                                    totalProbability += dropValue;
                                }
                            }

                            let availableEntityIDs = IdentifierManager.getInstance().getEntityIDsFromEntityGroupID(groupID);
                            let randomValue = GameMath.randomInt(0, totalProbability);

                            let sum = 0;
                            for (const entityID of availableEntityIDs) {
                                let basicEntityID = IdentifierManager.getInstance().getCorrespondingNormlaEntityID(entityID);
                                let dropValue = globalDropEntityMap[basicEntityID];
                                if (dropValue) {
                                    sum += dropValue;
                                    if (randomValue <= sum) {
                                        result = entityID;
                                        break;
                                    }
                                }
                            }
                        }
                    }

                    if (result != EntityID.None) {
                        this._dropIndex = (this._dropIndex + 1) % this._dropList.length;

                        if (this._generateCount > 0) {
                            this._generateCount--;
                            if (this._generateCount <= 0) {
                                this._generateType = GenerateType.Default;
                            }
                        }
                    }
                }
                
            }
                break;
        
            default:
                break;
        }                     
        return result;
    }
    
    private _dropEntityMap:Object = {};
    public getDropEntityMap() : Object {
        return this._dropEntityMap;
    }
    public setDropEntityMap(dropEntityMap:Object) : void {
        this._dropEntityMap = dropEntityMap;
    }

    private _dropList:string[] = [];
    public getDropList() : string[] {
        return this._dropList;
    }
    public setDropList(dropList:string[]) : void {
        this._dropList = dropList;
    }
    
    private _dropIndex:number = 0;
    public getDropIndex() : number {
        return this._dropIndex;
    }
    public setDropIndex(dropIndex:number) : void {
        this._dropIndex = dropIndex;
    }
  
    private _isWormholeEntrance:boolean = false;
    public getIsWormholeEntrance() : boolean {
        return this._isWormholeEntrance;
    }
    public setIsWormholeEntrance(isWormholeEntrance:boolean) : void {
        this._isWormholeEntrance = isWormholeEntrance;
    }

    private _wormholeDestination:cc.Vec2 = cc.Vec2.ZERO;
    public getWormholeDestination() : cc.Vec2 {
        return this._wormholeDestination;
    }
    public setWormholeDestination(wormholeDestination:cc.Vec2) : void {
        this._wormholeDestination = wormholeDestination;
    }

    private _isWormholeExit:boolean = false;
    public getIsWormholeExit() : boolean {
        return this._isWormholeExit;
    }
    public setIsWormholeExit(isWormholeExit:boolean) : void {
        this._isWormholeExit = isWormholeExit;
    }

    private _wormholeSource:cc.Vec2 = cc.Vec2.ZERO;
    public getWormholeSource() : cc.Vec2 {
        return this._wormholeSource;
    }
    public setWormholeSource(wormholeSource:cc.Vec2) : void {
        this._wormholeSource = wormholeSource;
    }

    public canHoldItem() : boolean {
        return true;
    }
    public canPassThrough() : boolean {
        return false;
    }

    private initDropEntityMap(keys:string, values:string) {
        this._dropEntityMap = {};
        let valuePair = IdentifierManager.getInstance().phraseKeyValuePairs(keys, values);

        for (const name in valuePair) {
            let value = Number(valuePair[name]);
            let entityID = IdentifierManager.getInstance().getEntityIDFromString(name);
            if (entityID != EntityID.None) {
                this._dropEntityMap[entityID] = value * 100;
            }
        }
        
        let globalDropEntityMap = this.getGrid().getDropEntityMap();
        let basicColorEntityIDs = IdentifierManager.getInstance().getEntityIDsFromEntityGroupID(EntityGroupID.ItemNormal);

        let totalProbability = 0;
        for (const entityID of basicColorEntityIDs) {
            let value = globalDropEntityMap[entityID];
            if (value) {
                totalProbability += value;
            }
        }

        for (const name in valuePair) {
            let value = Number(valuePair[name]) * 100;
            let groupID = IdentifierManager.getInstance().getEntityGroupIDFromString(name);
            if (groupID != EntityGroupID.None) {
                let entityIDs = IdentifierManager.getInstance().getEntityIDsFromEntityGroupID(groupID);
                for (const entityID of entityIDs) {
                    let basicEntityID = IdentifierManager.getInstance().getCorrespondingNormlaEntityID(entityID);
                    let globalValue = globalDropEntityMap[basicEntityID];
                    if (globalValue) {
                        this._dropEntityMap[entityID] = value * (globalValue / totalProbability);
                    }
                }
            }  
        }
    }
    public dealWithConfigMap(config:Object) : void {
        super.dealWithConfigMap(config);

        let crystalAttackPriority = config[TMXConfigInfoKeys.CFG_MATIC_CRYSTAL_ATK_POINT];
        if (crystalAttackPriority) {
            this.setCrystalAttackPriority(Number(crystalAttackPriority));
        }

        let dropTypeString = config[TMXConfigInfoKeys.CFG_DROP_KEY_TYPE];
        if (dropTypeString) {
            if(dropTypeString == TMXConfigInfoKeys.CFG_DROP_VALUE_TYPE_DEFAULT){
                this._generateType = GenerateType.Default;
            }else if(dropTypeString == TMXConfigInfoKeys.CFG_DROP_VALUE_TYPE_NOT){
                this._generateType = GenerateType.None;
            }else if(dropTypeString == TMXConfigInfoKeys.CFG_DROP_VALUE_TYPE_PROB){
                this._generateType = GenerateType.Probabilistic;
                
                let types = config[TMXConfigInfoKeys.CFG_DROP_KEY_BOX];
                let values = config[TMXConfigInfoKeys.CFG_DROP_KEY_PROB];
                if (types && values) {
                    this.initDropEntityMap(types, values);
                }
                
                let generateCount = config[TMXConfigInfoKeys.CFG_DROP_KEY_NUM];
                if (generateCount) {
                    this._generateCount = Number(generateCount); 
                }
                
                if (GameMath.isEmptyObject(this._dropEntityMap)) {
                    this._generateType = GenerateType.Default;
                }

            }else if(dropTypeString == TMXConfigInfoKeys.CFG_DROP_VALUE_TYPE_LIST){
                
                this._generateType = GenerateType.List;
                this._dropIndex = 0;
                GameMath.clearArray(this._dropList);

                let typesString = config[TMXConfigInfoKeys.CFG_DROP_KEY_BOX];
                if (typesString) {
                    let types = IdentifierManager.getInstance().phraseTokens(typesString);
                    for (const typeName of types) {
                        let entityID = IdentifierManager.getInstance().getEntityIDFromString(typeName);
                        if (entityID != EntityID.None) {
                            this._dropList.push(typeName);
                        }else{
                            let groupID = IdentifierManager.getInstance().getEntityGroupIDFromString(typeName);
                            if (groupID != EntityGroupID.None) {
                                this._dropList.push(typeName);
                            }
                        }
                    }

                    if (GameMath.isEmptyObject(this._dropList)) {
                        this._generateType = GenerateType.Default;
                    }
                    
                    let generateCount = config[TMXConfigInfoKeys.CFG_DROP_KEY_NUM];
                    if (generateCount) {
                        this._generateCount = Number(generateCount); 
                    }
                }
            }
        }else{
            let isTop = config[TMXConfigInfoKeys.CFG_DROP_KEY_IS_TOP];
            if (isTop) {
                this._generateType = GenerateType.Default;
            }
        }
        
        let isBoneCollectorString = config[TMXConfigInfoKeys.CFG_MARK_BONE_COLLECTOR_KEY_TYPE];
        if (isBoneCollectorString) {
            if (isBoneCollectorString == TMXConfigInfoKeys.CFG_MARK_BONE_COLLECTOR_VALUE_DEFAULT) {
                this.setIsBoneCollector(true);
            }
        }
     
        let wormHoleString = config[TMXConfigInfoKeys.CFG_WORMHOLE_KEY_TYPE];
        if (wormHoleString) {
            if (wormHoleString == TMXConfigInfoKeys.CFG_WORMHOLE_VALUE_ENTRANCE) {
                let postalString = config[TMXConfigInfoKeys.CFG_WORMHOLE_POSTAL_KEY_TYPE];
                if (postalString) {
                    let postalPos = GameMath.getVec2FromConfigString(postalString);
                    if (postalPos) {
                        this.setWormholeDestination(postalPos);
                        this.setIsWormholeEntrance(true);        
                    }
                }
            }else if (wormHoleString == TMXConfigInfoKeys.CFG_WORMHOLE_VALUE_EXIT) {
                let postalString = config[TMXConfigInfoKeys.CFG_WORMHOLE_POSTAL_KEY_TYPE];
                if (postalString) {
                    let postalPos = GameMath.getVec2FromConfigString(postalString);
                    if (postalPos) {
                        this.setWormholeSource(postalPos);
                        this.setIsWormholeExit(true);    
                        this.setGenerateType(GenerateType.None);    
                    }
                }
            }else if (wormHoleString == TMXConfigInfoKeys.CFG_WORMHOLE_VALUE_NONE) {
                this.setIsWormholeEntrance(false);
                this.setWormholeDestination(GameMath.INVALID_LOGICAL_POSITION);
                this.setIsWormholeExit(false);
                this.setWormholeSource(GameMath.INVALID_LOGICAL_POSITION);
            }
        }
    }

    public onConvey() : cc.Vec2 {
        return GameMath.INVALID_LOGICAL_POSITION;
    }

    public canExcuteCommand(command:GridCommand) : boolean {
        switch (command.type) {
            case GridCommandType.LayoutGrass:{
                return this.canLayoutGrass();
            }
                break;
            case GridCommandType.BombClean:{
                return this.canCleanByBomb(command.cleanColor);
            }
                break;
            default:
                break;
        }
        return false;
    }
    public excuteCommand(command:GridCommand) : void {
        if (!this.canExcuteCommand(command)) {
            return;
        }
        switch (command.type) {
            case GridCommandType.LayoutGrass:{
                let cleanOption = EntityCleanOption.create(EntityCleanType.LayoutGrass,
                                                           command.cleanRoot,
                                                           command.cleanColor,
                                                           command.createBombType,
                                                           command.hitNeighbour,
                                                           command.hitFloor,
                                                           command.layoutGrass);
                this.onClean(cleanOption);
            }
                break;
            default:
                break;
        }
    } 

    public onClean(cleanOption : EntityCleanOption) {
        this._onClean(cleanOption);
        this.setImmuneDamage(true);
    }
    protected _onClean(cleanOption : EntityCleanOption) {

    }
    
    public canCleanByBomb(cleanColor:EntityColor) : boolean { 
        if (!this.getImmuneDamage()) {
            return this._canCleanByBomb(cleanColor);
        }
        return false;
    }
    protected _canCleanByBomb(cleanColor:EntityColor) : boolean {
        return false;
    }

    public canLayoutGrass() : boolean {
        if (!this.getImmuneDamage()) {
            return this._canLayoutGrass();
        }
        return false;
    }
    protected _canLayoutGrass() : boolean {
        return false;
    }

    protected initCell(grid:GridBase, cellType:CellType) : void {
        super.initEntity(grid, EntityType.Cell);
        this._cellType = cellType;
        
        this._crystalAttackPriority = 999;
        this._isBoneCollector = false;
        this._needGrass = false;
        this._generateType = GenerateType.None;
        this._generateCount = 0;
        this._dropEntityMap = {};
        this._dropList.length = 0;
        this._dropIndex = 0;
        this._isWormholeEntrance = false;
        this._wormholeDestination = cc.Vec2.ZERO;
        this._isWormholeExit = false;
        this._wormholeSource = cc.Vec2.ZERO;
    }
    
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}

    onDestroy() {
        super.onDestroy();
        this._dropEntityMap = null;
        this._dropList = null;
        this._wormholeDestination = null;
        this._wormholeSource = null;
    }
}
