    constructor(){
        this.initString_To_CollectID_Map();
        this.initCollectionID_To_FrameName_Map();
    };

    public getCollectionIDFromString(name:string) : CollectionID {
        let result = this.String_To_CollectID_Map[name];
        if (result) {
            return result;
        }
        return CollectionID.None;
    }

    public getCollectionNameFromID(collectionID:CollectionID) : string {
        let tmpMap = this.String_To_CollectID_Map;
        for(let key in tmpMap){
            if(tmpMap[key] == collectionID){
                return key;
            }
        }
        return "";
    }

    public getFrameNameFromCollectionID(collectionID : CollectionID) : string {
        let result = this.CollectionID_To_FrameName_Map[collectionID];
        if (result) {
            return 'common/texture/icons/match/' + result;
        }
        return 'icons_unknow.png';
    }
