import IGame from "../../../../base/IGame";
import { CollectionID } from "../../defines/CollectionID";
import { EntityID } from "../../defines/EntityID";
import IdentifierManager from "../../defines/IdentifierManager";
import GridBase from "../../grid/GridBase";
import EntityPrefabNodePool from "../EntityPrefabNodePool";
import FloorBase, { FloorType } from "./FloorBase";

const {ccclass, property} = cc._decorator;

@ccclass
export default class FloorGrass extends FloorBase {

    public static create(grid:GridBase):FloorGrass{
        let className = 'FloorGrass';
        let prefabFileName = 'match_scene/prefab/entity/floor/FloorGrass';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FloorGrass;
        if (component) {
            component.init(grid);
            return component;       
        }
        return null;
    }

    public clone():FloorGrass {
        return FloorGrass.create(this.getGrid());
    }

    public getCollectionID():CollectionID{
        return CollectionID.FloorGrass;
    }

    public getEntityID():EntityID{
        return EntityID.FloorGrass;
    }

    public getScore():number{
        return 200;
    }

    public onCollected():void{
        if (this.getGrid().isTargetCollectionID(this.getCollectionID())) {
            this.getGrid().updateTargetBarUIWithDelta(this.getCollectionID(), -1);
        }
        this.getGrid().collectEntity(this);
    }

    public grow():void{
        this.getGrid().gainScoreWithEntity(this);
        this.playAnimation('grow');
        this.selfHurt();
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('grass'));
    }

    protected init(grid:GridBase):void{
        super.initFloor(grid, FloorType.Grass);
    }
}
