import { EntityColor, EntityID } from "../defines/EntityID";
import { ItemBombType } from "../entity/item/color/bomb/ItemBombBase";
import { CollectionID } from "../defines/CollectionID";



export enum GridCommandType{
    None = -1,
// MARK: @
    DoubleTabbed,
    NeighbourSwap, //
    ForceSwap,
    CheckMatch,
    
    Change,
    Charge,
// MARK: @
    BombClean,
    MatchClean,
    NeighborClean,
    FloorClean,
    PopsicleClean,
    SuicideClean,
    ActivateClean,
    LayoutGrass,
    TerminateClean,
};

export default class GridCommand {

    public static DoubleTabbed() : GridCommand {
        let command = new GridCommand(2004);
        command.type = GridCommandType.DoubleTabbed;
        return command;
    }

    public static NeighbourSwap(swapPos:cc.Vec2) : GridCommand {
        let command = new GridCommand(2003);
        command.type = GridCommandType.NeighbourSwap;
        command.swapPos = swapPos;
        return command;
    }

    public static ForceSwap(swapPos:cc.Vec2) : GridCommand {
        let command = new GridCommand(2002);
        command.type = GridCommandType.ForceSwap;
        command.swapPos = swapPos;
        return command;
    }
    
    public static CheckMatch() : GridCommand {
        let command = new GridCommand(2001);
        command.type = GridCommandType.CheckMatch;
        return command;
    }
    
    public static Change(targetID:EntityID, collectOld:boolean) : GridCommand {
        let command = new GridCommand(2001);
        command.type = GridCommandType.Change;
        command.targetID = targetID;
        command.collectOld = collectOld;
        return command;
    }

    public static Charge(collectionID:CollectionID, amount:number) : GridCommand {
        let command = new GridCommand(2001);
        command.type = GridCommandType.Charge;
        command.chargeID = collectionID;
        command.chargeAmount = amount;
        return command;
    }
    
    public static MatchClean(celanRoot:cc.Vec2, createBombType:ItemBombType, layoutGrass:boolean) : GridCommand {
        let command = new GridCommand(1101);
        command.type = GridCommandType.MatchClean;
        command.cleanRoot = celanRoot;
        command.createBombType = createBombType;
        command.hitNeighbour = true;
        command.hitFloor = true;
        command.layoutGrass = layoutGrass;
        return command;
    }
    
    public static LayoutGrass() : GridCommand {
        let command = new GridCommand(1004);
        command.type = GridCommandType.LayoutGrass;
        command.layoutGrass = true;
        return command;
    }
    
    public static BombClean(cleanRoot:cc.Vec2, cleanColor:EntityColor, layoutGrass:boolean, ignoreImmunity:boolean = false) : GridCommand {
        let command = new GridCommand(1003);
        command.type = GridCommandType.BombClean;
        command.cleanRoot = cleanRoot;
        command.cleanColor = cleanColor;
        command.ignoreImmunity = ignoreImmunity;
        command.layoutGrass = layoutGrass;
        command.hitNeighbour = false;
        command.hitFloor = true;
        return command;
    }
    
    public static ActivateClean(cleanRoot:cc.Vec2) : GridCommand {
        let command = new GridCommand(1003);
        command.type = GridCommandType.ActivateClean;
        command.cleanRoot = cleanRoot;
        command.cleanColor = EntityColor.None;
        command.hitNeighbour = false;
        command.hitFloor = false;
        command.layoutGrass = false;
        return command;
    }
    
    public static SuicideClean(cleanRoot:cc.Vec2, cleanColor:EntityColor, layoutGrass:boolean) : GridCommand {
        let command = new GridCommand(1003);
        command.type = GridCommandType.SuicideClean;
        command.cleanRoot = cleanRoot;
        command.cleanColor = cleanColor;
        command.hitNeighbour = true;
        command.hitFloor = true;
        command.layoutGrass = layoutGrass;
        return command;
    }
    
    public static NeighborClean(cleanRoot:cc.Vec2, cleanColor:EntityColor) : GridCommand {
        let command = new GridCommand(1001);
        command.type = GridCommandType.NeighborClean;
        command.cleanRoot = cleanRoot;
        command.cleanColor = cleanColor;
        return command;
    }
    
    public static FloorClean() : GridCommand {
        let command = new GridCommand(1003);
        command.type = GridCommandType.FloorClean;
        return command;
    }
    
    public static PopsicleClean() : GridCommand {
        let command = new GridCommand(1002);
        command.type = GridCommandType.PopsicleClean;
        return command;
    }

    public static TerminateClean(cleanRoot:cc.Vec2, cleanColor:EntityColor, layoutGrass:boolean) : GridCommand {
        let command = new GridCommand(1003);
        command.type = GridCommandType.TerminateClean;
        command.cleanRoot = cleanRoot;
        command.cleanColor = cleanColor;
        command.layoutGrass = layoutGrass;
        return command;
    }
    
    public type:GridCommandType = GridCommandType.None;
    public swapPos:cc.Vec2 = cc.Vec2.ZERO;
    public cleanRoot:cc.Vec2 = cc.Vec2.ZERO;
    public cleanColor:EntityColor = EntityColor.None;
    public ignoreImmunity:boolean = false;
    public createBombType:ItemBombType = ItemBombType.None;
    public hitNeighbour:boolean = false;
    public hitFloor:boolean = false;
    public layoutGrass:boolean = false;
    public targetID:EntityID = EntityID.None;
    public collectOld:boolean = false;
    public chargeID:CollectionID = CollectionID.None;
    public chargeAmount:number = 0;
    private _priority:number = 0;
    
    public getPriority() : number {
        return this._priority;
    }

    constructor(priority:number) {
        this._priority = priority;
    }
    
}
