const {ccclass, property} = cc._decorator;

@ccclass
export default class FloorDoubler extends FloorBase {
    
    public static create(grid:GridBase):FloorDoubler{

        let className = 'FloorDoubler';
        let prefabFileName = 'match_scene/prefab/entity/floor/FloorDoubler';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FloorDoubler;
        if (component) {
            component.init(grid);    
            return component;       
        }
        return null;
    }

    public clone():FloorDoubler {
        return FloorDoubler.create(this.getGrid());
    }

    public getCollectionID():CollectionID{
        return CollectionID.FloorDouble;
    }

    public getEntityID():EntityID{
        return EntityID.FloorDouble;
    }

    public _canCleanByBomb(cleanColor:EntityColor):boolean{
        return false;
    }

    public _canBeFloorClean():boolean{
        return false;
    }

    protected init(grid:GridBase):void{
        super.initFloor(grid, FloorType.Double);
    }
}
