const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerScoreLabel extends FlyerBase {

    private _score = 0;
    private _pixelPos:cc.Vec2 = cc.v2(0, 0)
    public static create(grid:GridBase, pixelPos:cc.Vec2, score:number, needObstruct:boolean = false, autoRemove:boolean = true):FlyerScoreLabel{

        let className = 'FlyerScoreLabel';
        let prefabFileName = 'match_scene/prefab/entity/flyer/FlyerScoreLabel';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerScoreLabel;
        if (component) {
            component.init(grid, pixelPos, score, needObstruct, autoRemove); 
            return component;       
        }
        return null;
    }

    public init(grid:GridBase, pixelPos:cc.Vec2, score:number, needObstruct:boolean = false, autoRemove:boolean){
        super.initFlyer(grid, FlyerType.Score);
        this._score = score;
        let label = this._puppet.getComponent(cc.Label);
        if (label) {
            label.string = this._score.toString();
        }
        this._pixelPos = pixelPos;
        this.setAutoRemoveOnFinish(autoRemove);
        this._needObstruct = needObstruct;
    }

    private _needObstruct = false;
    private _autoRemoveOnFinish:boolean = false;
    public getAutoRemoveOnFinish():boolean {
        return this._autoRemoveOnFinish;
    }
    public setAutoRemoveOnFinish(auto:boolean) : void {
        this._autoRemoveOnFinish = auto;
    }

    public getEntityID() : EntityID {
        return EntityID.None;
    }

    public launch() {
        this.node.setPosition(this._pixelPos);
        this.getGrid().addFlyerObstructs(this);
        
        let duration = 1;
        let state = this.playAnimation('default');
        if (state) {
            duration = state.duration;
        }
        let delay = cc.delayTime(duration);
        let action = cc.sequence(delay,
                                cc.callFunc(()=>{
                                    this.getGrid().removeFlyerObstructs(this);
                                    this.destoryWithRecycle(true);
                                }));
        this.runAction(action);
        
    }
}
