const { ccclass, property } = cc._decorator;

@ccclass
export default class MatchScene extends GameSceneBase {

    private _lastStarsCount: number = 0;
    public canGenerateBomb(): boolean {
        return true
    };
    public canGenerateTrainSupply(): boolean {
        return true;
    };

    public canCountScore(): boolean {
        return true
    }

    public canGetBonusFromLeftStep(): boolean {
        return true;
    };

    public initMatchScene(level: number): void {
        super.initGameScene(level);
        this.phrasePrewarData(null);
        this.updateLevelBar();
        this.updateStepBar();
        this.updateTargetBar();
        this.updateGearBar();
        this.updateScore();
        this.updateSupplyBar();
        this.showTargetBanner();

        let panel = LevelSelectorPanel.create();
        this.node.addChild(panel.node, 1000);
        this.reportStepInit();
    }

    protected phrasePrewarData(data: Object) {
        let parram = null;
        this.gearBar.setData(GearBarData.createWithData(parram));
        this.setEndUIData(GameEndPanelData.createWithData(parram));
        this.setEventTargets(EventCollectTargets.createWithData(parram));
        this.setHistoryHighscore(0);
    }

    public gainPowerAmount(powerID: PowerID, amount: number): void {
        super.gainPowerAmount(powerID, amount);
        if (powerID == PowerID.UsedSwap) {
            let usedSwap = this.getPowerAmount(powerID);
            this.reportStepSwap(usedSwap);
        }
    }

    public isFailed() {
        return super.isFailed();
    }

    public beforePlayerOperated() {
        super.beforePlayerOperated();
        let currentStarsCount = this.getStarsCount()
        if (currentStarsCount > this._lastStarsCount) {
            this._lastStarsCount = currentStarsCount;
        }
    }

    public onRecevieGiftBox(data: Object) {
        let items = data['items'];

        for (let key in items) {
            let itemID = Number(key) as GameCurrencyType
            let amount = items[key];
            if (itemID != GameCurrencyType.NoneType && amount > 0) {
                this.setPrewarGearAmount(itemID, amount);
            }
        }
        this.setPowerAmount(PowerID.LaunchGears, 1);
        this._grid.setCurrentState(GridStateLaunchGears.create());
    }

    public onCancleGiftBox(data: Object) {

    }

    public updateUserInterface(): void {
        this.updateGearBar();
    }

    public showCompleteBanner(): void {
        this.setCompleteWithLastStep(this.getPowerAmount(PowerID.AvailableSwap) == 0);
        super.showCompleteBanner();
    }

    public congratulation(): void {
        super.congratulation();
        let completeReward = this.getCompleteReward();
        for (let key in completeReward) {
            let amount = completeReward[key];
            let itemID = Number(key) as GameCurrencyType;
            this.setCollectedSupplyAmount(itemID, this.getCollectedSupplyAmount(itemID) + amount);
        }
        this.updateSupplyBar();
        this.updateScore();
        this.hideAdBanner();
    }

    public noMoreStep(): void {

    }
    public onNoMoreStep(object: Object) {

        let usedItemIDs = object['items'];
        if (usedItemIDs) {
            for (let key in usedItemIDs) {
                let amount = usedItemIDs[key];
                let id = Number(key);

                if (id == GameCurrencyType.ExtraStep1 ||
                    id == GameCurrencyType.ExtraStep2 ||
                    id == GameCurrencyType.ExtraStep3 ||
                    id == GameCurrencyType.ExtraStep5) {
                    this.setPrewarGearAmount(id, amount);
                }
            }
            this.setPowerAmount(PowerID.LaunchGears, 1);
            this.updateUserInterface();
            this._grid.resumeGame();
        }
    }



    public stalemate() {

    }

    public showReplayPrepareUI() {

    }

    public backToMainScene(prepareNext: boolean, prepareNextInMatchScene: boolean = false) {

    }

    public nextGame() {
        this.reportStepNextGame();
        this.backToMainScene(true);
    }

    public quitGame() {

        this.swallowTouchLayer.active = true;
        this.reportResult(MatchGameEndType.GiveUp, MatchGameEndRewardType.None, false, this.onQuitGameRespone, this);
        this.hideAdBanner();
    }

    public onQuitGameRespone(object: Object) {
        this.endGameWithReplay(false);
    }

    public replayGame() {

        this.swallowTouchLayer.active = true;
        this.reportResult(MatchGameEndType.GiveUp, MatchGameEndRewardType.None, false, this.onReplayGameRespone, this);
        this.hideAdBanner();
    }

    public onReplayGameRespone(object: Object) {
        let replay = true;
        this.endGameWithReplay(replay);
    }

    public gameOver() {
        this._grid.setCurrentState(GridStateFail.create());
    }

    public showGameFail() {

    }

    public stalemateGame() {
        this.swallowTouchLayer.active = true;
        this.endGameWithReplay(true);
    }

    public cheatGameWithLevel(level: number) {
        this.initMatchScene(level);
    }

    /// Special Behavior
    public onPauseTabbed(evt: cc.Event.EventTouch) {
        if (this.canPauseGame()) {

        }
    }

    public requestUseGear(itemID: GameCurrencyType,
        amount: number,
        allbackFunc: Function = null,
        callbackObj: any = null) {

        allbackFunc.call(callbackObj, null);
    }

    public requestBuyMoreStep(currencyID: GameCurrencyType,
        price: number,
        itemID: GameCurrencyType,
        allbackFunc: Function = null,
        callbackObj: any = null) {

    }

    public reportResult(endType: MatchGameEndType,
        rewardType: MatchGameEndRewardType,
        hasExtraReward: boolean,
        callbackFunc: Function = null,
        callbackObj: any = null) {


    }
    start() {
        this.initMatchScene(1);
    }
}
