import { EntityColor } from "../../../defines/EntityID";
import ItemBase, { ItemType } from "../../../entity/item/ItemBase";
import ItemFactory from "../../../entity/item/ItemFactory";
import ItemMagicCrystal from "../../../entity/item/special/ItemMagicCrystal";
import ItemStateReplace from "../../../entity/item/state/ItemStateReplace";
import GridBase from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateConveyer from "./GridStateConveyer";
import GridStateMatch from "./GridStateMatch";
import CellBase from "../../../entity/cell/CellBase";
import GameMath from "../../../../../extend/utils/GameMath";
import IGame from "../../../../../base/IGame";
import IdentifierManager from "../../../defines/IdentifierManager";

export class AtkPointData{
    priority:number = 1;
    x:number = 0;
    y:number = 0;
    beAttacked:boolean = false; //
}

export class GridStateMagicCrystal extends GridStateBase {

    private _magicCrystalVec:ItemMagicCrystal[] = [];
    // private _atkPoints:
    
    private _atkPoints:AtkPointData[] = [];

    private _changeItemPos:cc.Vec2[] = [];

    private _outputIng:boolean = false;

    private _curGrid:GridBase = null;

    public static create() : GridStateMagicCrystal {
        return new GridStateMagicCrystal();
    }
    constructor(){
        super(GridState.MagicCrystal);
    }

    public getCurGrid():GridBase{
        return this._curGrid;
    }

    
    public enter(grid:GridBase) {
        this._curGrid = grid;

        this.initMagicCryStalItems();
        this.initAtkPotins();

        //->
        //: ->  ->
        if(this._magicCrystalVec.length > 0){
            //TODO 
            this.doOutput();
        }else{
            //
            grid.setCurrentState(GridStateConveyer.create());
        }

        
    }

    public update(grid:GridBase, delta:number) {
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            if(this._outputIng){
                this._outputIng = false;
                this.onMagicCrystalDone();
            }
            // grid.setCurrentState( GridStateMatch.create(this._changeItemPos));
        }
    }

    public eixt(grid:GridBase) {
        this._magicCrystalVec = null;
        this._atkPoints = null;
        this._changeItemPos = null;
        this._curGrid = null;
    }

    //
    private initMagicCryStalItems():void{
        this._magicCrystalVec = [];
        let itemMap = this.getCurGrid().getItemsMap();
        for(let key in itemMap){
            let tmpItem:ItemBase = itemMap[key];
            if(tmpItem.getItemType() == ItemType.MagicCrystal){
                let magicItem:ItemMagicCrystal = tmpItem as ItemMagicCrystal;
                if(magicItem.isCanOutput()){
                    //
                    this._magicCrystalVec.push(magicItem);
                }
            }
        }
    }

    //
    private initAtkPotins():void{
        this._atkPoints = [];

        let cellMap = this.getCurGrid().getCellsMap();
        for(let key in cellMap){
            let tmpItem:CellBase = cellMap[key];
            let ptData:AtkPointData = new AtkPointData();
            ptData.x = tmpItem.getLogicalPos().x;
            ptData.y = tmpItem.getLogicalPos().y;
            ptData.priority = tmpItem.getCrystalAttackPriority();
            ptData.beAttacked = false;
            this._atkPoints.push(ptData);
        }

        //->
        //
        GameMath.shuffleArray(this._atkPoints)

        //
        this._atkPoints.sort((v1:AtkPointData, v2:AtkPointData):number=>{
            if(v1.priority < v2.priority){
                return -1;
            }else{
                return 1;
            }
        })
    }

    //
    private doOutput():void{
        this._outputIng = true;
        for(let i:number = 0; i < this._magicCrystalVec.length; i++){
            let magicItem:ItemMagicCrystal = this._magicCrystalVec[i];
            //test 
            if(magicItem){
                let sequence = cc.sequence(cc.scaleTo(0.2, 1.2), cc.scaleTo(0.1, 1.0));
                magicItem.runAction(sequence);
            }

            //
            for(let j:number = 0; j < magicItem.getAttrNum(); j++){
                let newItem = ItemFactory.generateItemWithEntityID(this.getCurGrid(), magicItem.getOutputEntityId());
                if(newItem){
                    let nextPoint:AtkPointData = this.getNextAtkPoint(newItem.getEntityColor());
                    if(nextPoint == null){
                        break;
                    }

                    this._changeItemPos.push(cc.v2(nextPoint.x, nextPoint.y));

                    newItem.node.setPosition(magicItem.node.getPosition());
                    this.getCurGrid().node.addChild(newItem.node, this.getCurGrid().getTopZOrder(newItem.getEntityType(), cc.v2(nextPoint.x, nextPoint.y)));
                    newItem.setCurrentState(ItemStateReplace.create(cc.v2(nextPoint.x, nextPoint.y)));
                    IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('magiccrystal'));
                }
            }
        }
    }

    //
    private removeAttackedPoints():void{
        //
        let index:number = 0;
        let len:number = this._atkPoints.length;
        for(let i:number = 0; i < len; i++){
            let atkPoint:AtkPointData = this._atkPoints[index];
            if(!atkPoint){
                break;
            }
            if(atkPoint.beAttacked){
                this._atkPoints.splice(index, 0);
            }else{
                index += 1;
            }
        }
    }

    private getNextAtkPoint(color:EntityColor):AtkPointData{
        //
        this.removeAttackedPoints();
        //
        if(this._atkPoints.length > 0){
            for(let i:number = 0; i < this._atkPoints.length; i++){
                if(!this._atkPoints[i].beAttacked){
                    let tempItem:ItemBase = this.getCurGrid().getItemAtLogicalPos(cc.v2(this._atkPoints[i].x, this._atkPoints[i].y));
                    if(tempItem){
                        if(tempItem.canTransformByMagicCrystal()){
                            if(color != EntityColor.None){
                                if(tempItem.getEntityColor() != color){
                                    this._atkPoints[i].beAttacked = true;
                                    return this._atkPoints[i];
                                }
                            }else{
                                this._atkPoints[i].beAttacked = true;
                                return this._atkPoints[i];
                            }
                        }
                    }
                }
            }
        }
        return null;
    }

    private onMagicCrystalDone():void{
        this.getCurGrid().setCurrentState(GridStateMatch.create(this._changeItemPos));
    }
}
