export class StarAndScoreFrameData {
    public starCount:number = 0;
    public score:number = 0;
    public highscore:number = 0;

    public static create(starCount:number, score:number, highscore:number):StarAndScoreFrameData{
        let result = new StarAndScoreFrameData();
        result.starCount = starCount;
        result.score = score;
        result.highscore = highscore;
        return result;
    }
}

const {ccclass, property} = cc._decorator;

@ccclass
export default class StarAndScoreFrame extends cc.Component {

    private _starCount:number = 0;
    private _score:number = 0;
    private _highscore:number = 0;
    public getStarCount():number{
        return this._starCount;
    }
    public setStarCount(count:number) {
        this._starCount = count;

        for (let index = 0; index < this.starsNode.length; index++) {
            let node = this.starsNode[index];
            node.active = (index < this._starCount)            
        }
    }

    public getScore():number{
        return this._score;
    }
    public setScore(score:number) {
        this._score = score;
        this.scoreLabel.string = this._score.toString();
    }

    public getHighscore():number{
        return this._highscore;
    }
    public setHighscore(highscore:number) {
        this._highscore = highscore;
        this.highscoreLabel.string = this._highscore.toString();
    }

    @property([cc.Node])
    starsNode:cc.Node[] =[];

    @property(cc.Label)
    scoreLabel:cc.Label = null;

    @property(cc.Label)
    highscoreLabel:cc.Label = null;

    public setData(data:StarAndScoreFrameData){
        this.setStarCount(data.starCount);
        this.setScore(data.score);
        this.setHighscore(data.highscore);
    }

    public appear(){
        let anim = this.getComponent(cc.Animation);
        if (anim) {
            let state = anim.play('appear');
            if(state){
                let delay = cc.delayTime(state.duration);
                let callback = cc.callFunc(()=>{
                    this.shine();
                })
                this.node.runAction(cc.sequence(delay, callback));
            }
        }
    }

    public shine(){
        let anim = this.getComponent(cc.Animation);
        if (anim) {
            anim.play('shine');
        }
    }
    
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}
}
