const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerHammer extends FlyerBase {

    public static create(grid:GridBase, targetPos:cc.Vec2):FlyerHammer{

        let className = 'FlyerHammer';
        let prefabFileName = 'match_scene/prefab/entity/flyer/FlyerHammer';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerHammer;
        if (component) {
            component.init(grid, targetPos); 
            return component;       
        }
        return null;
    }

    public init(grid:GridBase, targetPos:cc.Vec2){
        super.initFlyer(grid, FlyerType.Hammer);
        this._targetPos = targetPos;
        this._launched = false;
    }
    
    private _targetPos:cc.Vec2 = cc.Vec2.ZERO;
    private _launched:boolean = false;
    public getEntityID():EntityID {
        return EntityID.FlyerHammer;
    }

    public launch() {
        if (this._launched) {
            return;
        }
        this._launched = true;
        let worldPos = this.getGrid().getWorldPosForGear(GameCurrencyType.AidHammer);
        let nodePos = this.getGrid().node.convertToNodeSpaceAR(worldPos);
        this.node.setPosition(nodePos);
        
        let grid = this.getGrid();
        let targetPixelPos = grid.getPixelPosFromLogicalPos(this._targetPos);
        grid.addObstructs(this);
        
        let action = cc.sequence(cc.moveTo(0.33, targetPixelPos),
                                 cc.callFunc(()=>{
                                    let state = this.playAnimation('default');
                                    if (state) {
                                        let action = cc.sequence(cc.delayTime(state.duration),
                                                                 cc.callFunc(()=>{
                                                                     grid.removeObstructs(this)
                                                                     this.destoryWithRecycle(true);
                                                                    }))
                                        this.runAction(action);
                                    }else{
                                        grid.removeObstructs(this)
                                        this.destoryWithRecycle(true);
                                    }
                                 }));
        this.runAction(action);
        
    }

    private onHitted() {
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('prop_hammer'));    
        this.getGrid().dispatchCommand(this._targetPos, 
                                       GridCommand.BombClean(this._targetPos, EntityColor.None, false),
                                       0);
    }

    onDestroy() {
        super.onDestroy();
        this._targetPos = null;
    }
}
