import { GameCurrencyType } from "../../../define/GameCurrencyType";
import { CollectionID, CollectionIDManager } from "./CollectionID";
import EntityGroupIDManager, { EntityGroupID } from "./EntityGroupID";
import { EntityColor, EntityID, EntityIDManager, HurtAnimationInfo } from "./EntityID";
import ItemInfo from "./ItemInfo";
import ItemInfoManager from "./ItemInfoManager";
import { KindID, KindIDManager } from "./KindID";
import CollectionGroupIDManager, { CollectionGroupID } from "./CollectionGroupID";

export default class IdentifierManager {
    //
    private static instance:IdentifierManager = null;
    public static getInstance():IdentifierManager{
        if(this.instance == null){
            this.instance = new IdentifierManager();
        }
        return this.instance;
    }

    constructor(){
        CollectionIDManager.getInstance();
        EntityIDManager.getInstance();
        EntityGroupIDManager.getInstance();
        KindIDManager.getInstance();
        ItemInfoManager.getInstance();
    }

    public getColorWithEntityColor(color:EntityColor) : cc.Color {
        let result = cc.Color.WHITE;
        switch (color) {
            case EntityColor.Red:
                result = cc.color(0xFF, 0x00, 0x00, 0xFF);
                break;
            case EntityColor.Green:
                result = cc.color(0x00, 0xFF, 0x07, 0xFF);
                break;
            case EntityColor.Blue:
                result = cc.color(0x00, 0x89, 0xFF, 0xFF);
                break;
            case EntityColor.Yellow:
                result = cc.color(0xFF, 0xFF, 0x00, 0xFF);
                break;
            case EntityColor.Orange:
                result = cc.color(0xFF, 0x7C, 0x00, 0xFF);
                break;
            case EntityColor.Purple:
                result = cc.color(0xFF, 0x19, 0xC1, 0xFF);
                break;
            default:
                break;
        }
        return result;
    }

    public getCollectionIDFromString(name:string) : CollectionID {
        return CollectionIDManager.getInstance().getCollectionIDFromString(name);
    }

    public getCollectionNameFromID(collectionID:CollectionID) : string {
        return CollectionIDManager.getInstance().getCollectionNameFromID(collectionID);
    }

    public getFrameNameFromCollectionID(collectionID:CollectionID) : string {
        return CollectionIDManager.getInstance().getFrameNameFromCollectionID(collectionID);
    }

    public getCollectionGroupIDFromString(name:string) : CollectionGroupID {
        return CollectionGroupIDManager.getInstance().getCollectionGroupIDFromString(name);
    }

    public getCollectionIDsFromCollectionGroupID(collectionGroupID:CollectionGroupID) : CollectionID[] {
        return CollectionGroupIDManager.getInstance().getCollectionIDsFromCollectionGroupID(collectionGroupID);
    }

    public getEntityIDFromString(name:string) : EntityID {
        return EntityIDManager.getInstance().getEntityIDFromString(name);
    }

    public getFrameNameFromEntityID(entityID:EntityID) : string {
        return EntityIDManager.getInstance().getFrameNameFromEntityID(entityID);
    }

    public getHurtAnimationInfoPathFromEntityID(entityID:EntityID) : HurtAnimationInfo {
        return EntityIDManager.getInstance().getHurtAnimationInfoFromEntityID(entityID);
    }
    
    public getEntityIDFromKindID(kindID:KindID) : EntityID {
        return KindIDManager.getInstance().getEntityIDFromKindID(kindID);
    }

    public getEntityColorWithEntityID(entityID:EntityID) : EntityColor {
        return EntityIDManager.getInstance().getEntityColorWithEntityID(entityID);
    }
    
    public needLayoutGrassFromKindID(kindID:KindID) : boolean {
        return KindIDManager.getInstance().needLayoutGrassFromKindID(kindID);
    }

    public getEntityGroupIDFromString(name:string) : EntityGroupID {
        return EntityGroupIDManager.getInstance().getEntityGroupIDFromString(name);
    }
    
    public getFrameNameFromEntityGroupID(groupID:EntityGroupID) : string {
        return EntityGroupIDManager.getInstance().getFrameNameFromEntityGroupID(groupID);
    }

    public getEntityIDsFromEntityGroupID(groupID:EntityGroupID) : EntityID[] {
        return EntityGroupIDManager.getInstance().getEntityIDsFromEntityGroupID(groupID);
    }
   
    public getEntityGroupIDFromEntityID(entityID:EntityID) : EntityGroupID {
        return EntityGroupIDManager.getInstance().getEntityGroupIDFromEntityID(entityID);
    }

    public getItemInfoFromItemID(itemID:GameCurrencyType) : ItemInfo {
        return ItemInfoManager.getInstance().getItemInfoFromItemID(itemID);
    }

    public getItemIDFromName(name:string) : GameCurrencyType {
        return ItemInfoManager.getInstance().getItemIDFromName(name);
    }

    public getDefaultButterflyPriorityFromEntityID(entityID:EntityID) : number {
        return EntityIDManager.getInstance().getDefaultButterflyPriorityFromEntityID(entityID);
    }

    public getCorrespondingNormlaEntityID(entityID:EntityID) : EntityID {
        return EntityIDManager.getInstance().getCorrespondingNormlaEntityID(entityID);
    }

    public phraseTokens(types:string) : string[] {
        let result = types.split('|');
        return result;
    }

    public phraseKeyValuePairs(keyString:string, valueString:string) : Object {
        let result:Object = {};
        let keys = this.phraseTokens(keyString);
        let values = this.phraseTokens(valueString);
        let count = keys.length;
        for (let index = 0; index < count; index++) {
            let key = keys[index];
            let value = values[index];
            result[key] = value;
        }
        return result;
    }

    public getMatchAudioFilePath(fileName:string) : string{
        return 'audio/match/' + fileName;
    }

}
