const {ccclass, property} = cc._decorator;

@ccclass
export default class PowerupItem extends cc.Component {

    @property(UISpriteExt)
    icon:UISpriteExt = null;

    @property(UISpriteExt)
    frame:UISpriteExt = null;

    @property(cc.Node)
    unlimitedIcon:cc.Node = null;

    public static create(itemID:GameCurrencyType) : PowerupItem{

        let res = cc.loader.getRes('match_scene/prefab/userInterface/PowerupItem', cc.Prefab);
        if (res) {
            let node:cc.Node = cc.instantiate(res);
            if (node) {
                let component:PowerupItem = node.getComponent(PowerupItem);
                if (component) {
                    component.init(itemID);     
                    return component; 
                }
            }
        }
        return null;
    }

    protected init(itemID:GameCurrencyType) {
        this.setItemID(itemID);
    }


    private _itemID:GameCurrencyType = GameCurrencyType.NoneType;
    public getItemID() : GameCurrencyType {
        return this._itemID;
    }
    public setItemID(itemID:GameCurrencyType) : void {
        this._itemID = itemID;
        let itemInfo = IdentifierManager.getInstance().getItemInfoFromItemID(this._itemID);
        this.icon.src = itemInfo.frameName;
        this.unlimitedIcon.active = itemInfo.isUnlimited;

        if (itemInfo.isUnlimited) {
            this.frame.src = 'texture/start_game/unrelease/start_game_btn_yuan2';
        }else{
            this.frame.src = 'texture/start_game/unrelease/start_game_btn_yuan';
        }
        
    } 

    // LIFE-CYCLE CALLBACKS:
    onLoad () {
        
    }

    start () {

    }
    // update (dt) {}

    onDestroy() {
        
    }
}
