const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerPopsicleBomb extends FlyerBase {

    protected FLYER_BOMB_DURATION = 0.2;

    protected _launched:boolean = false;
    protected _bombDuration:number = 0;
    protected _lastBombRingIndex:number = 0;
    protected _canLayoutGrass:boolean = false;
    public getCanLayoutGrass():boolean {
        return this._canLayoutGrass;
    }
    public setCanLayoutGrass(canLayoutGrass:boolean) : void {
        this._canLayoutGrass = canLayoutGrass;
    }

    private _origin:cc.Vec2 = cc.Vec2.ZERO;
    public getOrigin():cc.Vec2 {
        return this._origin;
    }
    public setOrigin(origin:cc.Vec2) : void {
        this._origin = origin;
    }
    
    private _bombPositions:cc.Vec2[] = [];

    public getEntityID() : EntityID {
        return EntityID.FlyerPopsicelBomb;;
    }

    
    public launch() {
        if (this._launched) {
            return;
        }
        this._launched = true;
        this._bombDuration = 0
        this._lastBombRingIndex = -1;
        this._launched = true;
        this.getGrid().addObstructs(this);

        

        let state = this.playAnimation('default');
        if (state) {
            let action = cc.sequence(cc.delayTime(state.duration), cc.callFunc(()=>{
                this.getGrid().removeObstructs(this);
                this.destoryWithRecycle(true);
            }));
            this.runAction(action);
        }

        let grid = this.getGrid();
        for (let bombPos of this._bombPositions) {
            let cleanPos = this._origin.add(bombPos)
            this.getGrid().recordAttackedPosition(cleanPos);
            grid.dispatchCommand(cleanPos, GridCommand.BombClean(this._origin, this.getEntityColor(), this._canLayoutGrass), 0);
            grid.highlightedCell(cleanPos);
        }
    }

    public static create(grid:GridBase, 
                         color:EntityColor, 
                         origin:cc.Vec2,
                         layoutGrass:boolean, 
                         bombPostions:cc.Vec2[]):FlyerPopsicleBomb{

        let className = 'FlyerPopsicleBomb';
        let prefabFileName = 'match_scene/prefab/entity/flyer/FlyerPopsicleBomb';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerPopsicleBomb;
        if (component) {
            component.init(grid, color, origin, layoutGrass, bombPostions); 
            return component;       
        }
        return null;
    }

    public init(grid:GridBase, 
                color:EntityColor, 
                origin:cc.Vec2,
                layoutGrass:boolean, 
                bombPostions:cc.Vec2[]){
        super.initFlyer(grid, FlyerType.Bomb);
        this.setEntityColor(color)
        this.setCanLayoutGrass(layoutGrass);
        this.setOrigin(origin);
        this._bombPositions = bombPostions;
        this._launched = false;
        this._bombDuration = 0;
        this._lastBombRingIndex = 0;
        
    }

    onDestroy() {
        super.onDestroy();
        this._origin = null;
        this._bombPositions = null;
    }
}
