const { ccclass, property } = cc._decorator;

@ccclass
export default class PopsicleRange extends PopsicleBase {

    private static SizeToEntityInfoMap: Object = {
        '{1, 1}': {
            'EntityID': EntityID.PopsicleBoom_1x1,
            'NeedRotate': false,
            'ClearPosition': [
                cc.v2(-1, -1),
                cc.v2(0, -1),
                cc.v2(1, -1),

                cc.v2(-1, 0),
                cc.v2(0, 0),
                cc.v2(1, 0),

                cc.v2(-1, 1),
                cc.v2(0, 1),
                cc.v2(1, 1),
            ],
        },
        '{2, 2}': {
            'EntityID': EntityID.PopsicleBoom_2x2,
            'NeedRotate': false,
            'ClearPosition': [
                cc.v2(0, 0),
                cc.v2(1, 0),
                cc.v2(0, 1),
                cc.v2(1, 1),

                cc.v2(-1, -1),
                cc.v2(-1, 0),
                cc.v2(-1, 1),
                cc.v2(-1, 2),

                cc.v2(0, 0),
                cc.v2(1, 2),
                cc.v2(0, -1),
                cc.v2(1, -1),

                cc.v2(2, 2),
                cc.v2(2, 1),
                cc.v2(2, 0),
                cc.v2(2, -1),
            ],
        },
    };

    private static getEntityInfoWithSize(size: cc.Size): Object {
        let key = '{' + size.width + ', ' + size.height + '}';
        return this.SizeToEntityInfoMap[key];
    }

    public getCollectionID(): CollectionID {
        return CollectionID.PopsicleBoom;
    }

    public getEntityID(): EntityID {
        let entityInfo = PopsicleRange.getEntityInfoWithSize(this.getLogicalSize());
        if (entityInfo) {
            return entityInfo['EntityID'];
        }
        return EntityID.None;
    }

    public getScore(): number {
        let size = this.getLogicalSize()
        return 100 * size.width * size.height;
    }

    protected updatePopsicle(): void {
        super.updatePuppet();

        let rotation = 0;
        let entityInfo = PopsicleRange.getEntityInfoWithSize(this.getLogicalSize());
        if (entityInfo) {
            let needRotate = entityInfo['NeedRotate'];
            if (needRotate) {
                rotation = GameMath.randomInt(0, 100) < 50 ? -90 : 90;
            }
        }
        this._puppet.angle = -rotation;
    }

    protected _onBePopsicleClean(): void {
        super._onBePopsicleClean();
        this.getGrid().gainScoreWithEntity(this);
        let entityInfo = PopsicleRange.getEntityInfoWithSize(this.getLogicalSize());
        let bombPositions = entityInfo['ClearPosition'];
        let bomb = FlyerPopsicleBomb.create(this.getGrid(), this.getEntityColor(), this.getLogicalPos(), false, bombPositions);
        this.getGrid().setFlyerAtLogicalPos(bomb, this.getLogicalPos());

        bomb.node.position = this.node.position;
        if (this.getLogicalSize().equals(cc.size(1, 1))) {
            bomb.node.scale = 0.666;
        }

        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('popsiclesboom'));
    }

    protected init(grid: GridBase, width: number, height: number): void {
        super.initPopsicle(grid, width, height, PopsicleType.Range);
        this.updatePopsicle();
    }

    public static create(grid: GridBase, width: number, height: number): PopsicleRange {

        let className = 'PopsicleRange';
        let prefabFileName = 'match_scene/prefab/entity/popsicle/PopsicleRange';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as PopsicleRange;
        if (component) {
            component.init(grid, width, height);
            return component;
        }
        return null;
    }

    public clone(): PopsicleRange {
        let item = PopsicleRange.create(this.getGrid(), this.getLogicalSize().width, this.getLogicalSize().height);
        return item;
    }
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {

    }

    // update (dt) {}
}
