import GameMath from "../../../../extend/utils/GameMath";
import { CollectionID } from "../../defines/CollectionID";
import { EntityID, EntityType } from "../../defines/EntityID";
import TMXConfigInfoKeys from "../../defines/TMXConfigInfoKeys";
import GridBase from "../../grid/GridBase";
import EntityPrefabNodePool from "../EntityPrefabNodePool";
import { CellType } from "./CellBase";
import CellNormal from "./CellNormal";

// Learn TypeScript:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/typescript.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/typescript.html
// Learn Attribute:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/reference/attributes.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;

@ccclass
export default class CellMovablePlate extends CellNormal {

    public static create(grid:GridBase) : CellMovablePlate {
        let className = 'CellMovablePlate';
        let prefabFileName = 'match_scene/prefab/entity/cell/CellMovablePlate';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as CellMovablePlate;
        if (component) {
            component.init(grid);
            return component;       
        }
        return null;
    }

    public init(grid:GridBase) : void {
        super.initCell(grid, CellType.MovablePlate);
        this._movePath.length = 0;
        this._moveInterval = 1;
        this._moveSteps = 1;
        this._hasFirstMove = false;
        this._firstMoveSteps = 0;
        this._firstMoveInterval = 0;
        this._currentInterval = 0;
        this._currentPathIndex = 0;
    }

    public getCollectionID() : CollectionID {
        return CollectionID.CellMovablePlate;
    }

    public getEntityID() : EntityID {
        return EntityID.CellMovablePlate;
    }

    public dealWithConfigMap(configMap:Object) : void {
        super.dealWithConfigMap(configMap);
        
        let keyPoints:cc.Vec2[] = [];
        let path:Object = configMap[TMXConfigInfoKeys.CFG_MOVABLE_PLATE_PATH_KEY_TYPE];
        if (path) {
            let count:number = 0;
            for (let key in path) {
                count += 1
            }
            for (let index = 0; index < count; index++) {
                let pos = path[index];
                keyPoints.push(pos);
            }
        }

        if (keyPoints.length > 0) {
            let count = keyPoints.length;
            
            this._movePath.push(keyPoints[0]);
            for (let i = 0; i < count; i++) {
                if (i + 1 < count) {
                    let start = keyPoints[i];
                    let end = keyPoints[i + 1];
                    let offset = end.sub(start);
        
                    let length = 0;
                    let step = cc.Vec2.ZERO;
                    if (offset.x == 0) {
                        length = Math.abs(offset.y);
                        step = offset.y > 0 ? cc.v2(0, 1) : cc.v2(0, -1);
                    }else if (offset.y == 0){
                        length = Math.abs(offset.x);
                        step = offset.x > 0 ? cc.v2(1, 0) : cc.v2(-1, 0);
                    }
                    
                    for (let j = 1; j <= length; j++) {
                        let pos = start.add(step.mul(j));
                        this._movePath.push(pos);
                    }
                }
            }
            if (this._movePath[this._movePath.length - 1].equals(this._movePath[0])) {
                this._movePath.pop();
            }else{
                for (let index = this._movePath.length - 2; index > 0; index--) {
                    this._movePath.push(this._movePath[index]);
                }
            }
        }
        
        // 
        let moveSteps = configMap[TMXConfigInfoKeys.CFG_MOVABLE_PLATE_PATH_MOVE_TYPE];
        if (moveSteps) {
            this._moveSteps = Number(moveSteps); 
        }

        // 
        let moveInterval = configMap[TMXConfigInfoKeys.CFG_MOVABLE_PLATE_PATH_STEP_TYPE];
        if (moveInterval) {
            this._moveInterval = Number(moveInterval);
        }
        
        // 
        let firstMoveSteps = configMap[TMXConfigInfoKeys.CFG_MOVABLE_PLATE_PATH_FIRST_MOVE_TYPE];
        if (firstMoveSteps) {
            this._firstMoveSteps = Number(firstMoveSteps);
            this._hasFirstMove = true;
        }

        // 
        let firstMoveInterval = configMap[TMXConfigInfoKeys.CFG_MOVABLE_PLATE_PATH_FIRST_STEP_TYPE];
        if (firstMoveInterval) {
            this._firstMoveInterval = Number(firstMoveInterval);
            this._hasFirstMove = true;
        }
    }
    
    public onConvey() : cc.Vec2 {
        this._currentInterval += 1
        
        let moveStep = 0;
        if (this._hasFirstMove) {
            if (this._currentInterval >= this._firstMoveInterval) {
                moveStep = this._firstMoveSteps;
                this._hasFirstMove = false;
                this._currentInterval = 0;
            }
        }else{
            if (this._currentInterval >= this._moveInterval) {
                moveStep = this._moveSteps;
                this._currentInterval = 0;
            }
        }
    
        if (moveStep > 0) {
            let movePath:cc.Vec2[] = [];
            movePath.push(this._movePath[this._currentPathIndex]);
            let pathCount = this._movePath.length;
            for (let i = 0; i < moveStep; i++) {
                this._currentPathIndex = (this._currentPathIndex + 1) % pathCount;
                movePath.push(this._movePath[this._currentPathIndex]);
            }

            if (movePath.length > 0) {
                let conveyerTypes:EntityType[] = [
                    EntityType.Cell,
                    EntityType.Popsicle,
                    EntityType.Floor,
                    EntityType.Rug,
                    EntityType.Item,
                    EntityType.Parasite,
                ];

                for (let type of conveyerTypes) {
                    this.moveEntity(type, movePath);
                }
                return movePath[movePath.length - 1];
            }
        }
        return GameMath.INVALID_LOGICAL_POSITION;
    }
    
    private _movePath:cc.Vec2[] = [];
    private _moveInterval:number = 1;
    private _moveSteps:number = 1;
    private _hasFirstMove:boolean = false;
    private _firstMoveSteps:number = 0;
    private _firstMoveInterval:number = 0;
    private _currentInterval:number = 0;
    private _currentPathIndex:number = 0;
    private moveEntity(entityType:EntityType, path:cc.Vec2[]) : void {
        let grid = this.getGrid();
        let fristPos = path[0];
        let targetPos = path[path.length - 1];
        
        let entity = grid.getEntity(entityType, fristPos);
        if (entity) {
            
            entity.removeFromGridAndAddToEffect();
            grid.addObstructs(entity);
            let count = path.length;

            let steps:cc.FiniteTimeAction[] = [];
            for (let i = 0; i < count; i++) {
                steps.push(cc.moveTo(0.4, entity.getPixelPos(path[i])))
            }

            let callback = cc.callFunc(()=>{
                grid.setEntity(entity, targetPos);
                grid.removeObstructs(entity);
            });

            steps.push(callback);

            entity.runAction(cc.sequence(steps));

        }
    }
    
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}

    onDestroy() {
        super.onDestroy();
        this._movePath = null;
    }
}
