import { EntityColor } from "../../../defines/EntityID";
import EntityStateBase, { EntityStateType } from "../../state/EntityStateBase";
import EntityBase from "../../EntityBase";
import EntityStateWait from "../../state/concreteState/EntityStateWait";

export default class ItemStateChangeColor extends EntityStateBase {

    private _targetColor:EntityColor = null;

    constructor(color:EntityColor) {
        super(EntityStateType.ChangeColor);
        this._targetColor = color;
    }

    public static create(color:EntityColor):ItemStateChangeColor{
        return new ItemStateChangeColor(color);
    }

    public enter(entity:EntityBase):void{
        let grid = entity.getGrid();
        grid.addObstructs(entity);

        let animate = cc.sequence(cc.fadeOut(0.2 * 0.5), cc.callFunc(()=>{
            entity.setEntityColor(this._targetColor);
        }), cc.fadeIn(0.2 * 0.5));

        let callBack = cc.callFunc(()=>{
            entity.setCurrentState(EntityStateWait.create());
            grid.removeObstructs(entity);
        });
        entity.runAction(cc.sequence(animate, callBack));
        
    }

    public update(entity:EntityBase, delta:number):void{

    }

    public exit(entity:EntityBase):void{
        let grid = entity.getGrid();
        grid.removeObstructs(entity);
    }


}
