import GridBase from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateHoney from "./GridStateHoney";
import GridStateMatch from "./GridStateMatch";
import { PowerID } from "../../../defines/PowerID";
import FloorBase, { FloorType } from "../../../entity/floor/FloorBase";
import ItemBase from "../../../entity/item/ItemBase";
import ItemStateChangeColor from "../../../entity/item/state/ItemStateChangeColor";


export default class GridStateChanger extends GridStateBase {

    private _changeItems:cc.Vec2[] = [];

    public static create() : GridStateChanger{
        return new GridStateChanger();
    }
    constructor() {
        super(GridState.ChangeColor);
    }

    public setChangerPowerPoints(grid:GridBase, count:number):void{
        grid.setPowerAmount(PowerID.DyeingItem, count);
    }

    public getChangerPowerPoints(grid:GridBase):number{
        return grid.getPowerAmount(PowerID.DyeingItem);
    }

    public checkGoToNextState(grid:GridBase):void{
        if(this.getChangerPowerPoints(grid) > 0){
            this.changeCurrentItemColor(grid);
        }else{
            grid.setCurrentState(GridStateMatch.create(this._changeItems));
        }
    }

    public changeCurrentItemColor(grid:GridBase):void{
        let floorMap = grid.getFloorsMap();
        for(let key in floorMap){
            let floorTemp:FloorBase = floorMap[key];
            if(floorTemp.getFloorType() == FloorType.Changer){
                let targetPos = floorTemp.getLogicalPos();
                let itemTemp:ItemBase = grid.getItemAtLogicalPos(targetPos);
                if(itemTemp && itemTemp.canChangeColor() && itemTemp.getEntityColor() != floorTemp.getEntityColor()){
                    itemTemp.setCurrentState(ItemStateChangeColor.create(floorTemp.getEntityColor()));
                    this._changeItems.push(itemTemp.getLogicalPos());
                }
            }
        }
        this.setChangerPowerPoints(grid, 0);
    }

    public enter(grid:GridBase) {
        if(this.getChangerPowerPoints(grid) > 0){
            this.changeCurrentItemColor(grid);
        }else{
            this.setChangerPowerPoints(grid, 0);
            grid.setCurrentState(GridStateHoney.create());
        }
    }

    public update(grid:GridBase, delta:number) {
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            this.checkGoToNextState(grid);
        }
    }

    public exit(grid:GridBase) {
        this._changeItems = null;
    }
}
