export enum GameTargetType{
    None = 0,
    Single,
    Group,
}

export class GameTargetBase {
    private type:GameTargetType = GameTargetType.None;
    private targetAmount:number = 0;

    public getType():GameTargetType{
        return this.type
    }

    public getTargetAmount():number{
        return this.targetAmount;
    }

    public setTargetAmount(targetAmount:number){
        return this.targetAmount = targetAmount;
    }

    protected initWithType(type:GameTargetType){
        this.type = type;
    }

    public getCollectionIDs():CollectionID[]{
        return [];
    }

    public getIconFrameName():string{
        return "";
    }
}
