import GridStateBase, { GridState } from "../GridStateBase";
import GridBase from "../../GridBase";
import GridStateFailSummary from "./GridStateFailSummary";

export default class GridStateFail extends GridStateBase {

    public static create() : GridStateFail{
        return new GridStateFail();
    }
    constructor() {
        super(GridState.Fail);
    }

    public enter(grid:GridBase) {
        this.disappear(grid);
    }

    public update(grid:GridBase, delta:number) {
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            grid.setCurrentState(GridStateFailSummary.create());
        }
    }
}
