const { ccclass, property } = cc._decorator;

@ccclass
export default class ScoreBar extends cc.Component {

    @property(cc.ProgressBar)
    progressBar: cc.ProgressBar = null;

    @property(cc.Node)
    pointer: cc.Node = null;

    @property(cc.Node)
    pointerHead: cc.Node = null;

    @property(cc.Label)
    scoreLabel: cc.Label = null;

    @property([cc.Sprite])
    stars: cc.Sprite[] = [];

    private _currentScore: number = 0;
    private _score: number = 0;
    private _completeScore: number = 0;
    private _gameScene: GameSceneBase = null;

    private _starScores: number[] = [35600, 49100, 60600, 66350];

    private _starProgress: number[] = [0.444444444444444, 0.650793650793651, 0.873015873015873, 1.0];
    private _rotateStart: number = 103;
    private _rotateEnd: number = -10;
    private _lastStarCount: number = 0;

    private _lockScore: boolean = false;
    public getIsLockScore(): boolean {
        return this._lockScore;
    }
    public setIsLockScore(islock: boolean) {
        this._lockScore = islock;
    }

    public getScore(): number {
        return this._score;
    }
    public setScore(score: number) {
        this._score = Math.min(this._gameScene.getTotalScore(), score);
    }

    public setCurrentScore(score: number) {
        if (score > this._score) {
            score = this._score;
        }
        this._currentScore = score;

        this.scoreLabel.string = this._currentScore.toString();

        let startScore = 0
        let startProgress = 0;
        let targetScore = 0
        let targetProgress = 0;

        for (let start of this.stars) {
            this.setStarState(start, true);
        }

        for (let index = 0; index < this._starScores.length; index++) {
            if (this._currentScore <= this._starScores[index]) {
                targetScore = this._starScores[index];
                targetProgress = this._starProgress[index];
                if (index < this.stars.length) {
                    if (this._currentScore >= targetScore) {
                        this.setStarState(this.stars[index], false);
                    } else {
                        this.setStarState(this.stars[index], true);
                    }
                }
                break;
            } else {
                if (index < this.stars.length) {
                    this.setStarState(this.stars[index], false);
                }
                startScore = this._starScores[index];
                startProgress = this._starProgress[index];
            }
        }

        if (targetScore == 0) {
            targetScore = this._starScores[this._starScores.length - 1]
            targetProgress = this._starProgress[this._starProgress.length - 1];

            startScore = this._starScores[this._starScores.length - 2]
            startProgress = this._starProgress[this._starProgress.length - 2];
        }

        let progress = (this._currentScore - startScore) / (targetScore - startScore);
        progress = Math.min((targetProgress - startProgress) * progress + startProgress, 1);
        this.progressBar.progress = progress;
        this.pointer.angle = -((this._rotateEnd - this._rotateStart) * progress + this._rotateStart);
    }

    private setStarState(star: cc.Sprite, isGray: boolean) {
        if (isGray) {
            star.spriteFrame = cc.loader.getRes("match_scene/texture/userInterface/match_star_slot", cc.SpriteFrame);
        } else {
            star.spriteFrame = cc.loader.getRes("match_scene/texture/userInterface/match_star_small", cc.SpriteFrame);
        }
    }

    public getCompleteScore(): number {
        return this._completeScore;
    }

    public setCompleteScore(score: number) {
        this._completeScore = score;
    }

    public updateScore() {
        if (this._gameScene) {

            let starScores = this._gameScene.getStarScores();

            this._starScores.length = 0;
            for (let score of starScores) {
                this._starScores.push(score);
            }

            while (this._starScores.length < 4) {
                let rFirstScore = this._starScores[this._starScores.length - 1];
                let rSecondScore = this._starScores[this._starScores.length - 2];
                let delta = (rFirstScore - rSecondScore) * 0.5;
                this._starScores.push(delta + rFirstScore);
            }

            let score = this._gameScene.getTotalScore();
            this.setScore(score);
            this.setCurrentScore(score);
            let completeScore = this._gameScene.getCompleteScore();
            this.setCompleteScore(completeScore);
        }
    }

    public gainScore(delta: number) {
        if (!this._lockScore) {
            this.setScore(this.getScore() + delta)
        }
    }

    public getWorldPos() {
        return this.pointerHead.parent.convertToWorldSpaceAR(this.pointerHead.getPosition());
    }

    public getWorldPosForStar(index: number) {

        if (index < this.stars.length) {
            let star = this.stars[index];
            return star.node.parent.convertToWorldSpaceAR(star.node.getPosition());
        }
        return this.node.parent.convertToWorldSpaceAR(this.node.getPosition());
    }
    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        let canvas = cc.find("Canvas");
        if (canvas) {
            this._gameScene = canvas.getComponent(GameSceneBase);
        }
    }

    start() {

    }

    update(dt) {
        let delta = this._score - this._currentScore
        if (delta > 0) {
            delta = Math.max(Math.floor(delta / 5), 1);
            this.setCurrentScore(Math.min(this._currentScore + delta, this._score));
        }
    }
}
