const {ccclass, property} = cc._decorator;

@ccclass
export default class StepBar extends cc.Component {

    @property(cc.Label)
    label: cc.Label = null;

    private _gameScene:GameSceneBase = null;
    private _amount:number = 0;
    public getAmount() : number {
        return this._amount;
    }
    public setAmount(amount:number) : void {
        let oldAmount = this._amount;
        this._amount = amount;

        if (oldAmount < this._amount) {
            this.label.node.stopActionByTag(1001);
            this.label.node.scale = 1.0;
            let amountAction = cc.sequence(cc.scaleTo(0.1, 1.33).easing(cc.easeIn(2)),
                                           cc.scaleTo(0.3, 1.0).easing(cc.easeOut(2))
                                          );
            amountAction.setTag(1001);
            this.label.node.runAction(amountAction);
        }else{
            this.label.node.scale = 1.0;
        }

        this.label.string = this._amount.toString();
        if (this._amount <= 5) {
            let alertAction = this.label.node.getActionByTag(1);
            if (!alertAction) {
                let alertAction = cc.sequence(cc.scaleTo(0.25, 0.90).easing(cc.easeOut(2)),
                                              cc.scaleTo(0.25, 0.95).easing(cc.easeIn(2)),
                                              cc.scaleTo(0.25, 1.0).easing(cc.easeOut(2)),
                                              cc.scaleTo(0.25, 0.95).easing(cc.easeIn(2)),
                                             ).repeatForever();
                alertAction.setTag(1);                           
                this.label.node.runAction(alertAction);
                this.label.node.color = cc.color(255, 130, 98, 255);
            }
        }else{
            this.label.node.stopActionByTag(1);
            this.label.node.setScale(1);
            this.label.node.color = cc.Color.WHITE;
        }
    }
    
    public getWorldPos() {
        return this.label.node.parent.convertToWorldSpaceAR(this.label.node.getPosition());
    }
    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        let canvas = cc.find("Canvas");
        if (canvas) {
            this._gameScene = canvas.getComponent(GameSceneBase);
        }
    }

    start () {

    }

    // update (dt) {}

    onDestroy() {
        this.label = null;
        this._gameScene = null;
    }
}
