import GameMath from "../../../../../extend/utils/GameMath";
import { CollectionID } from "../../../defines/CollectionID";
import { EntityID, EntityColor, EntityCleanOption } from "../../../defines/EntityID";
import TMXConfigInfoKeys from "../../../defines/TMXConfigInfoKeys";
import GridBase from "../../../grid/GridBase";
import ItemBase, { ItemType } from "../ItemBase";
import EntityPrefabNodePool from "../../EntityPrefabNodePool";
import FlyerAnimation, { FlyerAnimationType } from "../../flyer/FlyerAnimation";
import { GameCurrencyType } from "../../../../../define/GameCurrencyType";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemBalloon extends ItemBase {

    private _curPostal:cc.Vec2 = cc.v2(-1, -1);
    private _nextLevelId:string = '';

    public static create(grid:GridBase):ItemBalloon{

        let className = 'ItemBalloon';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemBalloon';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemBalloon;
        if (component) {
            component.init(grid); 
            return component;       
        }
        return null;
    }

    public clone():ItemBalloon {
        let item = ItemBalloon.create(this.getGrid());
        return item;
    }

    public setCurPostal(val:cc.Vec2):void{
        this._curPostal = val;
    }

    public getCurPostal():cc.Vec2{
        return this._curPostal;
    }

    public setNextLevelId(val:string):void{
        this._nextLevelId = val;
    }

    public getNextLevelId():string{
        return this._nextLevelId;
    }
    
    public getProducedTrainSupplyMaps() : Object {
        return {};
    }

    public getCollectionID():CollectionID{
        return CollectionID.ItemBalloon;
    }

    public getEntityID():EntityID{
        return EntityID.ItemBalloon;
    }

    public getScore():number{
        return 100;
    }

    public dealWithConfigMap(configMap:Object):void{
        super.dealWithConfigMap(configMap);
        if(configMap[TMXConfigInfoKeys.CFG_POSTAL_VEC]){
            this.setCurPostal(GameMath.getVec2FromConfigString(configMap[TMXConfigInfoKeys.CFG_POSTAL_VEC]));
        }

        if(configMap[TMXConfigInfoKeys.CFG_NEXT_MAP_ID]){
            this.setNextLevelId(configMap[TMXConfigInfoKeys.CFG_NEXT_MAP_ID]);
        }
    }

    protected init(grid:GridBase){
        super.initItem(grid, ItemType.Balloon);
        this._curPostal = cc.v2(-1, -1);
        this._nextLevelId = '';
    }

    

    protected _canDrop():boolean{
        return  false;
    }

    protected _canSwap():boolean{
        return false;
    }

    protected _canMatch():boolean{
        return false;
    }

    protected _canCleanByNeighbour(color:EntityColor):boolean{
        return false;
    }

    protected _canCleanByBomb(cleanColor:EntityColor):boolean{
        return false;
    }

    protected _canCleanByTerminate(cleanColor:EntityColor):boolean{
        return true;
    }

    protected _onClean(cleanOption:EntityCleanOption) : void {
        this.getGrid().gainScoreWithEntity(this);
        this.setVisible(false);
        this.progressDeathWish(cleanOption);
        this.selfDestory();
        let flyer = FlyerAnimation.createAnimationWithType(this.getGrid(), FlyerAnimationType.CommonHurt);
        this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
        flyer.node.zIndex = this.getTopZOrder();
    }

    onDestroy() {
        super.onDestroy();
        this._curPostal = null;
        this._nextLevelId = null;
    }
}
