import GameMath from "../../../../extend/utils/GameMath";
import { EntityColor, EntityID } from "../../defines/EntityID";
import IdentifierManager from "../../defines/IdentifierManager";
import { KindID } from "../../defines/KindID";
import GridBase from "../../grid/GridBase";
import ItemBombBase, { ItemBombType } from "./color/bomb/ItemBombBase";
import ItemBombButterfly from "./color/bomb/ItemBombButterfly";
import ItemBombColumn from "./color/bomb/ItemBombColumn";
import ItemBombCross from "./color/bomb/ItemBombCross";
import ItemBombRainbow from "./color/bomb/ItemBombRainbow";
import ItemBombRange from "./color/bomb/ItemBombRange";
import ItemBombRow from "./color/bomb/ItemBombRow";
import ItemDogPaw from "./color/ItemDogPaw";
import ItemNormal from "./color/ItemNormal";
import ItemBase from "./ItemBase";
import ItemBalloon from "./special/ItemBalloon";
import ItemDogHouse from "./special/ItemDogHouse";
import ItemMagicCrystal from "./special/ItemMagicCrystal";
import ItemPuppy from "./special/ItemPuppy";
import ItemBlock from "./special/ItemBlock";
import ItemBone from "./special/ItemBone";
import ItemWatermelon from "./special/ItemWatermelon";
import ItemDoughnut from "./special/ItemDoughnut";
import ItemFireWormBottle from "./special/ItemFireWormBottle";
import ItemJamBottle from "./special/ItemJamBottle";
import ItemFruitCake from "./special/ItemFruitCake";
import ItemChestnut from "./special/ItemChestnut";
import ItemHoneyComb from "./special/ItemHoneyComb";
import ItemMower, { ItemMowerDirection } from "./special/ItemMower";
import ItemFireworks from "./special/ItemFireworks";
import ItemStatue from "./special/ItemStatue";
import { EntityGroupID } from "../../defines/EntityGroupID";


export default class ItemFactory  {

    

    public static generateItemWithEntityID(grid:GridBase, entityID:EntityID):ItemBase {
        let item:ItemBase = null;
        
        switch (entityID) {
            case EntityID.ItemNormalRed:
            case EntityID.ItemNormalGreen:
            case EntityID.ItemNormalBlue:
            case EntityID.ItemNormalYellow:
            case EntityID.ItemNormalOrange:
            case EntityID.ItemNormalPurple:{
                let color = entityID - EntityID.ItemNormalRed + EntityColor.Red;
                item = ItemNormal.create(grid, color);
            }
                break;

            case EntityID.ItemBombButterflyRed:
            case EntityID.ItemBombButterflyGreen:
            case EntityID.ItemBombButterflyBlue:
            case EntityID.ItemBombButterflyYellow:
            case EntityID.ItemBombButterflyOrange:
            case EntityID.ItemBombButterflyPurple:{
                let color = entityID - EntityID.ItemBombButterflyRed + EntityColor.Red;
                item = ItemBombButterfly.create(grid, color);
            }
                break;

            case EntityID.ItemBombRowRed:
            case EntityID.ItemBombRowGreen:
            case EntityID.ItemBombRowBlue:
            case EntityID.ItemBombRowYellow:
            case EntityID.ItemBombRowOrange:
            case EntityID.ItemBombRowPurple:{
                let color = entityID - EntityID.ItemBombRowRed + EntityColor.Red;
                item = ItemBombRow.create(grid, color);
            }
                break;

            case EntityID.ItemBombColumnRed:
            case EntityID.ItemBombColumnGreen:
            case EntityID.ItemBombColumnBlue:
            case EntityID.ItemBombColumnYellow:
            case EntityID.ItemBombColumnOrange:
            case EntityID.ItemBombColumnPurple:{
                let color = entityID - EntityID.ItemBombColumnRed + EntityColor.Red;
                item = ItemBombColumn.create(grid, color);
            }
                break;

            case EntityID.ItemBombCrossRed:
            case EntityID.ItemBombCrossGreen:
            case EntityID.ItemBombCrossBlue:
            case EntityID.ItemBombCrossYellow:
            case EntityID.ItemBombCrossOrange:
            case EntityID.ItemBombCrossPurple:{
                let color = entityID - EntityID.ItemBombCrossRed + EntityColor.Red;
                item = ItemBombCross.create(grid, color);
            }
                break;

            case EntityID.ItemBombRangeRed:
            case EntityID.ItemBombRangeGreen:
            case EntityID.ItemBombRangeBlue:
            case EntityID.ItemBombRangeYellow:
            case EntityID.ItemBombRangeOrange:
            case EntityID.ItemBombRangePurple:{
                let color = entityID - EntityID.ItemBombRangeRed + EntityColor.Red;
                item = ItemBombRange.create(grid, color);
            }
                break;

            case EntityID.ItemPawRed:
            case EntityID.ItemPawGreen:
            case EntityID.ItemPawBlue:
            case EntityID.ItemPawYellow:
            case EntityID.ItemPawOrange:
            case EntityID.ItemPawPurple:{
                let color = entityID - EntityID.ItemPawRed + EntityColor.Red;
                item = ItemDogPaw.create(grid, color);
            }
                break;

            case EntityID.ItemBombRainbow:{
                item = ItemBombRainbow.create(grid);
            }
                break;

            case EntityID.ItemDoughnutHp1:
            case EntityID.ItemDoughnutHp2:
            case EntityID.ItemDoughnutHp3:{
                let hp = entityID - EntityID.ItemDoughnutHp1 + 1;
                item = ItemDoughnut.create(grid, hp);
            }
                break;

            case EntityID.ItemBlockHp1:
            case EntityID.ItemBlockHp2:
            case EntityID.ItemBlockHp3:
            case EntityID.ItemBlockHp4:
            case EntityID.ItemBlockHp5:
            case EntityID.ItemBlockHp6:{
                let hp = entityID - EntityID.ItemBlockHp1 + 1;
                item = ItemBlock.create(grid, hp);
            }
                break;

            case EntityID.ItemMagicCrystal:{
                item = ItemMagicCrystal.create(grid);
            }
                break;
            case EntityID.ItemMowerUp:
            case EntityID.ItemMowerRight:
            case EntityID.ItemMowerDown:
            case EntityID.ItemMowerLeft:{
                let dirction = (entityID - EntityID.ItemMowerUp) + ItemMowerDirection.Up;
                item = ItemMower.create(grid, dirction);
            }   
                break;
            case EntityID.ItemBone:{
                item = ItemBone.create(grid);
            }
                break;
            case EntityID.ItemChestnut:{
                item = ItemChestnut.create(grid);
            }
                break;
            case EntityID.ItemPuppy:{
                item = ItemPuppy.create(grid);
            }
                break;
            case EntityID.ItemDogHouse:{
                item = ItemDogHouse.create(grid);
            }
                break;
            case EntityID.ItemBalloon:{
                item = ItemBalloon.create(grid);
            }
                break;
            case EntityID.ItemHoneyComb:{
                item = ItemHoneyComb.create(grid);
            }
                break;
            case EntityID.ItemJamBottle:{
                item = ItemJamBottle.create(grid);
            }
                break;
            case EntityID.ItemWatermelon:{
                item = ItemWatermelon.create(grid);
            }
                break;
            case EntityID.ItemFruitCake:{
                item = ItemFruitCake.create(grid);
            }
                break;
            case EntityID.ItemFireWormBottle:{
                item = ItemFireWormBottle.create(grid);
            }
                break;
            case EntityID.ItemFireworksHp1:
            case EntityID.ItemFireworksHp2:
            case EntityID.ItemFireworksHp3:
            case EntityID.ItemFireworksHp4:{
                let hp = entityID - EntityID.ItemFireworksHp1 + 1;
                item = ItemFireworks.create(grid, hp);
            }
                break;
            case EntityID.ItemStatueHp1:
            case EntityID.ItemStatueHp2:
            case EntityID.ItemStatueHp3:{
                let hp = entityID - EntityID.ItemStatueHp1 + 1;
                item = ItemStatue.create(grid, hp);
            }
                break;
            default:
                break;
        }
        if(!item){
            item = this.generateNormalRandomItem(grid);
        }
        return item;
    }

    private static generateNormalRandomItem(grid:GridBase) : ItemBase {

        let dropEntityMap = grid.getDropEntityMap();
        let avilabelEntityMap = {};
        let normalIDs = IdentifierManager.getInstance().getEntityIDsFromEntityGroupID(EntityGroupID.ItemNormal);
        let pawIDs = IdentifierManager.getInstance().getEntityIDsFromEntityGroupID(EntityGroupID.ItemPaw);
        for (let key in dropEntityMap) {
            let amount = dropEntityMap[key];
            let entityID = Number(key) as EntityID;
            if(normalIDs.indexOf(entityID) != -1 || pawIDs.indexOf(entityID) != -1){
                avilabelEntityMap[entityID] = amount;
            }
        }
        let entityID = grid.getRandomEntityID(avilabelEntityMap,grid.getLimitedEntityMap(), grid.getLimitedGroupMap(), true);
        return ItemFactory.generateItemWithEntityID(grid, entityID);
    }

    private static generateRandomItemWithGroupIDs(groupIDs:EntityGroupID[], grid:GridBase) : ItemBase {

        let dropEntityMap = grid.getDropEntityMap();
        let avilabelEntityMap = {};

        for (let groupID of groupIDs) {
            let entityIDs = IdentifierManager.getInstance().getEntityIDsFromEntityGroupID(groupID);
            for (let entityID of entityIDs) {
                let relevantEntityID = IdentifierManager.getInstance().getCorrespondingNormlaEntityID(entityID)
                let value = dropEntityMap[relevantEntityID];
                if (value) {
                    avilabelEntityMap[entityID] = value;
                }
            }
        }
        let entityID = grid.getRandomEntityID(avilabelEntityMap,{},{});
        return ItemFactory.generateItemWithEntityID(grid, entityID);
    }

    public static generateItemWithKindID(grid:GridBase, kindID:KindID) : ItemBase {
        let item:ItemBase = null;
        switch (kindID) {
            case KindID.ItemNormalRandom:{
                item = ItemFactory.generateNormalRandomItem(grid);
            }
                break;
            case KindID.ItemBombButterflyRandom:{
                item = ItemFactory.generateRandomItemWithGroupIDs([EntityGroupID.ItemBombButterfly], grid);
            }
                break;
            case KindID.ItemBombRowRandom:{
                item = ItemFactory.generateRandomItemWithGroupIDs([EntityGroupID.ItemBombRow], grid);
            }
                break;
            case KindID.ItemBombColumnRandom:{
                item = ItemFactory.generateRandomItemWithGroupIDs([EntityGroupID.ItemBombColumn], grid);
            }
                break;
            case KindID.ItemBombCrossRandom:{
                item = ItemFactory.generateRandomItemWithGroupIDs([EntityGroupID.ItemBombCross], grid);
            }
                break;
            case KindID.ItemBombRangeRandom:{
                item = ItemFactory.generateRandomItemWithGroupIDs([EntityGroupID.ItemBombRange], grid);
            }
                break;
            case KindID.ItemPawRandom:{
                item = ItemFactory.generateRandomItemWithGroupIDs([EntityGroupID.ItemPaw], grid);
            }
                break;
            default:{
                let entityID = IdentifierManager.getInstance().getEntityIDFromKindID(kindID);;
                item = ItemFactory.generateItemWithEntityID(grid, entityID);
            }
                break;
        }
        return item;
    }

    public static generateGlobalRandomItem(grid:GridBase) : ItemBase {

        let normalItemIDs:EntityID[] = [
            EntityID.ItemNormalRed,
            EntityID.ItemNormalGreen,
            EntityID.ItemNormalBlue,
            EntityID.ItemNormalYellow,
            EntityID.ItemNormalOrange,
            EntityID.ItemNormalPurple,
            
            EntityID.ItemPawRed,
            EntityID.ItemPawGreen,
            EntityID.ItemPawBlue,
            EntityID.ItemPawYellow,
            EntityID.ItemPawOrange,
            EntityID.ItemPawPurple,
        ];
        
        let normalItemMap:Object = {};
        
        let dropMap = grid.getDropEntityMap();
        
        for (let entityID in dropMap) {
            let value = dropMap[entityID];
            if(GameMath.indexOf(normalItemIDs, entityID) != -1) {
                normalItemMap[entityID] = value;
            }
        }

        let entityID = grid.getRandomEntityID(normalItemIDs, grid.getLimitedEntityMap(), grid.getLimitedGroupMap());
        return this.generateItemWithEntityID(grid, entityID);
    }

    public static generateEffectBombItem(grid:GridBase, bombType:ItemBombType, color:EntityColor) : ItemBombBase {
        let item:ItemBombBase = null;
        
        switch (bombType) {
            case ItemBombType.Rainbow:{
                item = ItemBombRainbow.create(grid);
            }
                break;
            case ItemBombType.Range:{
                item = ItemBombRange.create(grid, color);
            }
                break;
            case ItemBombType.Cross:{
                item = ItemBombCross.create(grid, color);
            }
                break;
            case ItemBombType.Row:{
                item = ItemBombRow.create(grid, color);
            }
                break;
            case ItemBombType.Column:{
                item = ItemBombColumn.create(grid, color);
            }
                break;
            case ItemBombType.Butterfly:{
                item = ItemBombButterfly.create(grid, color);
            }
                break;
        
            default:
                break;
        }
        return item;
    }

    public static generateItemWithName(grid:GridBase, name:string) : ItemBase {

        let item:ItemBase = null;
        let entityID = IdentifierManager.getInstance().getEntityIDFromString(name);
        item = this.generateItemWithEntityID(grid, entityID);
        return item;
    }

    public static isShuffleKindID(kindID:KindID) {
        return kindID == KindID.ItemNormalRandom;
    }

}
