import GridBase from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateDrop from "./GridStateDrop";
import { PowerID } from "../../../defines/PowerID";

export default class GridStateAppear extends GridStateBase {

    public static create() : GridStateAppear {
        return new GridStateAppear();
    }
    constructor() {
        super(GridState.Appear);
    }

    enter(grid:GridBase):void {
        this.appear(grid);
    }

    update(grid:GridBase, delta:number):void {

        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()){
            let gridSize = grid.getGridSize();
            let checkMatchPos:cc.Vec2[] = [];
            for (let row = 0; row < gridSize.height; row++) {
                for (let column = 0; column < gridSize.width; column++) {
                    checkMatchPos.push(cc.v2(column, row));
                }
            }
            grid.setCurrentState(GridStateDrop.create(checkMatchPos));
        }
    }
}
