const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerComboWord extends FlyerBase {

    private _comboCount = 0;
    public static create(grid:GridBase, combo:number, needObstruct:boolean = false, autoRemove:boolean = true):FlyerComboWord{

        let className = 'FlyerComboWord';
        let prefabFileName = 'match_scene/prefab/entity/flyer/FlyerComboWord';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerComboWord;
        if (component) {
            component.init(grid, combo, needObstruct, autoRemove); 
            return component;       
        }
        return null;
    }

    public init(grid:GridBase, combo:number, needObstruct:boolean = false, autoRemove:boolean){
        super.initFlyer(grid, FlyerType.Animation);
        this._comboCount = combo;
        this.setAutoRemoveOnFinish(autoRemove);
        let filePathRoot = 'texture/match/entities/effects/combo_word/';
        if(this._comboCount < 4) {
            filePathRoot += 'fresh';
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('voice_combo1'));
        }else if(this._comboCount < 5) {
            filePathRoot += 'sweet';
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('voice_combo2'));
        }else if(this._comboCount < 6) {
            filePathRoot += 'tasty';
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('voice_combo3'));
        }else if(this._comboCount < 8) {
            filePathRoot += 'fantastic';
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('voice_combo4'));
        }else {
            filePathRoot += 'incredible';
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('voice_combo5'));
        }
        this.setPuppetSpriteFrameName(filePathRoot);
        this._needObstruct = needObstruct;
    }

    private _needObstruct = false;
    private _autoRemoveOnFinish:boolean = false;
    public getAutoRemoveOnFinish():boolean {
        return this._autoRemoveOnFinish;
    }
    public setAutoRemoveOnFinish(auto:boolean) : void {
        this._autoRemoveOnFinish = auto;
    }

    public getEntityID() : EntityID {
        return EntityID.None;
    }

    public launch() {

        let size = this.getGrid().node.getContentSize();
        this.node.setPosition(cc.v2(size.width * 0.5, size.height * 0.5 + 60));

        if (this._needObstruct) {
            this.getGrid().addObstructs(this);
        }
        let state = this.playAnimation('default');
        if (this._autoRemoveOnFinish) {
            if (state) {
                let action = cc.sequence(cc.delayTime(state.duration), cc.callFunc(()=>{
                    this.getGrid().removeObstructs(this);
                    this.destoryWithRecycle(true);
                }));
                this.runAction(action);
            }else{
                this.getGrid().removeObstructs(this);
                this.destoryWithRecycle(true);
            }
        }
    }
}
