// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemNormal extends ItemBase {

    public getCollectionID() : CollectionID {
        let result = CollectionID.None;
        switch (this.getEntityColor()) {
            case EntityColor.Red:
                result = CollectionID.ItemNormalRed;
                break;
            case EntityColor.Green:
                result = CollectionID.ItemNormalGreen;
                break;
            case EntityColor.Blue:
                result = CollectionID.ItemNormalBlue;
                break;
            case EntityColor.Yellow:
                result = CollectionID.ItemNormalYellow;
                break;
            case EntityColor.Orange:
                result = CollectionID.ItemNormalOrange;
                break;
            case EntityColor.Purple:
                result = CollectionID.ItemNormalPurple;
                break;
                
            default:
                break;
        }
        return result;
    }
    public getEntityID() : EntityID {
        let result = EntityID.None;
        switch (this.getEntityColor()) {
            case EntityColor.Red:
                result = EntityID.ItemNormalRed;
                break;
            case EntityColor.Green:
                result = EntityID.ItemNormalGreen;
                break;
            case EntityColor.Blue:
                result = EntityID.ItemNormalBlue;
                break;
            case EntityColor.Yellow:
                result = EntityID.ItemNormalYellow;
                break;
            case EntityColor.Orange:
                result = EntityID.ItemNormalOrange;
                break;
            case EntityColor.Purple:
                result = EntityID.ItemNormalPurple;
                break;
            default:
                break;
        }
        return result;
    }

    public getScore():number{
        return 30;
    }

    public getButterflyPriority(color:EntityColor, layoutGrass:boolean) : number {
        if (this.getGrid().isTargetCollectionID(this.getCollectionID())) {
            return 1;
        }
        return super.getButterflyPriority(color, layoutGrass);
    }
    
    protected _canDrop() : boolean {
        return true;
    }
    protected _canSwap() : boolean {
        return true;
    }
    protected _canMatch() : boolean {
        return true;
    }
    protected _canChange() : boolean {
        return true;
    }
    protected _canShuffle() : boolean {
        return true;
    }
    protected _canShuffleWhenCreate() : boolean {
        return true;
    }
    protected _canTransformByMagicCrystal() : boolean {
        if (this.getGrid().isTargetCollectionID(this.getCollectionID())) {
            return false;
        }
        return true;
    }
    protected _canUpgradeByRainbow() : boolean {
        return true;
    }
    protected _canChangeColor() : boolean {
        return true;
    }
    protected _canCleanByBomb(cleanColor:EntityColor) : boolean {
        return true;
    }
    protected _canCleanByMatch() : boolean {
        return true;
    }
    
    protected _onClean(cleanOption:EntityCleanOption) : void {

        this.setVisible(false);  
        this.getGrid().gainScoreWithEntity(this);
        if (cleanOption.createBombType != ItemBombType.None) {
            this.removeFromGridAndAddToEffect();
            this.getGrid().addObstructs(this);
            this.setVisible(!this.isTargetCollection());
            this.progressDeathWish(cleanOption);

            let targetPos = this.getPixelPos(cleanOption.cleanRoot);
            let obstructAction = cc.sequence(cc.delayTime(0.1),
                                     cc.callFunc(function() {
                                         this.getGrid().removeObstructs(this);
                                        }, this));
            this.runAction(obstructAction);

            let removeAction = cc.sequence(cc.spawn(cc.scaleTo(0.3, 0),
                                                    cc.moveTo(0.3 * 0.5, targetPos)),
                                           cc.callFunc(function() {
                                            this.getGrid().removeObstructs(this);
                                            this.destoryWithRecycle(true);
                                           },this));
            this.runAction(removeAction);
        }else{
            this.selfDestory(true);
            if (!this.isTargetCollection()) {
                let flyer = FlyerAnimation.createDieAnimation(this.getGrid(), this.getEntityID());
                this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
                if (cleanOption.cleanType == EntityCleanType.Match) {
                    let combo = this.getGrid().getPowerAmount(PowerID.MatchCombo);
                    let fileName = 'eliminate_0';
                    switch (combo) {
                        case 0:
                            fileName = 'eliminate_0';
                            break;
                        case 1:
                            fileName = 'eliminate_1';
                            break;
                        case 2:
                            fileName = 'eliminate_2';
                            break;
                        case 3:
                            fileName = 'eliminate_3';
                            break;
                        case 4:
                            fileName = 'eliminate_4';
                            break;
                        case 5:
                            fileName = 'eliminate_5';
                            break;
                        case 6:
                            fileName = 'eliminate_6';
                            break;
                        case 7:
                            fileName = 'eliminate_7';
                            break;
                        case 8:
                            fileName = 'eliminate_8';
                            break;
                        default:
                            fileName = 'eliminate_8';
                            break;
                    }
                    IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath(fileName));
                }
            }
            this.progressDeathWish(cleanOption);
        }
    }

    protected _onChange(changeOption:EntityChangeOption) : void {
        if (changeOption.collectOld) {
            this.onCollected();
        }
        
        let grid = this.getGrid();


        let  newItem = ItemFactory.generateItemWithEntityID(grid, changeOption.targetID);
        if (newItem) {
            grid.setEntity(newItem, this.getLogicalPos());
        }
    }
    
    public bounce() : cc.AnimationState {
        let state = this.playAnimation('bounce');
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('fall_back'));
        return state;
    }

    public static create(grid:GridBase, color:EntityColor) : ItemNormal {

        let className = 'ItemNormal';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemNormal';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemNormal;
        if (component) {
            component.init(grid, color); 
            return component;       
        }
        return null;
    }

    public clone() : ItemNormal {
        let item = ItemNormal.create(this.getGrid(), this.getEntityColor());
        return item;
    }

    protected init(grid:GridBase, color:EntityColor) {
        super.initItem(grid, ItemType.Normal);
        this.setEntityColor(color);
    }

}
