import { CollectionID } from "../../../defines/CollectionID";
import { EntityCleanOption, EntityColor, EntityID, EntityCleanType } from "../../../defines/EntityID";
import { PowerID } from "../../../defines/PowerID";
import GridBase from "../../../grid/GridBase";
import ItemBase, { ItemType } from "../ItemBase";
import FlyerAnimation from "../../flyer/FlyerAnimation";
import IGame from "../../../../../base/IGame";
import IdentifierManager from "../../../defines/IdentifierManager";
import EntityPrefabNodePool from "../../EntityPrefabNodePool";
import GameMath from "../../../../../extend/utils/GameMath";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemDogPaw extends ItemBase {
    public static create(grid:GridBase, color:EntityColor):ItemDogPaw{
        
        let className = 'ItemDogPaw';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemDogPaw';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemDogPaw;
        if (component) {
            component.init(grid, color); 
            return component;       
        }
        return null;
    }

    public clone():ItemDogPaw {
        let item = ItemDogPaw.create(this.getGrid(),this.getEntityColor());
        return item;
    }

    private init(grid:GridBase, color:EntityColor):void{
        super.initItem(grid, ItemType.DogPaw);
        this.setEntityColor(color);
    }

    public getCollectionID():CollectionID{
        let result = CollectionID.None;
        switch(this.getEntityColor()){
            case EntityColor.Red:{
                result = CollectionID.ItemPawRed;
                break;
            }
            case EntityColor.Green:{
                result = CollectionID.ItemPawGreen;
                break;
            }
            case EntityColor.Blue:{
                result = CollectionID.ItemPawBlue;
                break;
            }
            case EntityColor.Yellow:{
                result = CollectionID.ItemPawYellow;
                break;
            }
            case EntityColor.Orange:{
                result = CollectionID.ItemPawOrange;
                break;
            }
            case EntityColor.Purple:{
                result = CollectionID.ItemPawPurple;
                break;
            }
            default:{
                break;
            }
        }
        return result;
    }

    public getEntityID():EntityID{
        let result:EntityID = EntityID.None;
        switch(this.getEntityColor()){
            case EntityColor.Red:{
                result = EntityID.ItemPawRed;
                break;
            }
            case EntityColor.Green:{
                result = EntityID.ItemPawGreen;
                break;
            }
            case EntityColor.Blue:{
                result = EntityID.ItemPawBlue;
                break;
            }
            case EntityColor.Yellow:{
                result = EntityID.ItemPawYellow;
                break;
            }
            case EntityColor.Orange:{
                result = EntityID.ItemPawOrange;
                break;
            }
            case EntityColor.Purple:{
                result = EntityID.ItemPawPurple;
                break;
            }
            default:{
                break;
            }
        }
        return result;
    }

    public getPowerID():PowerID{
        return PowerID.PuppyVitality;
    }

    public getScore():number{
        return 100;
    }

    protected _canDrop():boolean{
        return true;
    }

    protected _canSwap():boolean{
        return true;
    }

    protected _canMatch():boolean{
        return true;
    }

    protected _canChangeColor():boolean{
        return true;
    }

    protected _canCleanByBomb(cleanColor:EntityColor):boolean{
        return true;
    }

    protected _canCleanByMatch() : boolean {
        return true;
    }

    protected _canShuffleWhenCreate():boolean{
        return true;
    }

    protected _onClean(cleanOption:EntityCleanOption):void{
  
        this.progressDeathWish(cleanOption);
        let state = this.playAnimation('matched');
        if (state) {
            this.removeFromGridAndAddToEffect();
            this.node.zIndex = GridBase.ZORDER_PUPPY_TOP;
            this.getGrid().addObstructs(this);
            let removeAction = cc.sequence(cc.delayTime(state.duration),
                                           cc.callFunc(()=>{
                                              this._flyToDog();
                                           }));
            this.runAction(removeAction);
        }else{
            this.selfDestory(true);
        }
        this.getGrid().gainScoreWithEntity(this);

        if (cleanOption.cleanType == EntityCleanType.Match) {
            let combo = this.getGrid().getPowerAmount(PowerID.MatchCombo);
            let fileName = 'eliminate_0';
            switch (combo) {
                case 0:
                    fileName = 'eliminate_0';
                    break;
                case 1:
                    fileName = 'eliminate_1';
                    break;
                case 2:
                    fileName = 'eliminate_2';
                    break;
                case 3:
                    fileName = 'eliminate_3';
                    break;
                case 4:
                    fileName = 'eliminate_4';
                    break;
                case 5:
                    fileName = 'eliminate_5';
                    break;
                case 6:
                    fileName = 'eliminate_6';
                    break;
                case 7:
                    fileName = 'eliminate_7';
                    break;
                case 8:
                    fileName = 'eliminate_8';
                    break;
                default:
                    fileName = 'eliminate_8';
                    break;
            }
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath(fileName));
        }
    }

    protected _flyToDog() : void {

        if (this.getGrid().isTargetCollectionID(CollectionID.ItemPuppy)) {
            
            let targetPos:cc.Vec2 = cc.Vec2.ZERO; 
            let itemsMap = this.getGrid().getItemsMap();
            for (let key in itemsMap) {
                let item = itemsMap[key] as ItemBase;
                if (item.getEntityID() == EntityID.ItemPuppy) {
                    targetPos = item.getLogicalPos();
                    break;
                }
            }

            let distance = GameMath.getDistance(this.node.position, this.getPixelPos(targetPos));
            let duration = distance / 1000;
            let move = cc.sequence(cc.spawn(cc.moveTo(duration, this.getPixelPos(targetPos)),
                                            cc.scaleTo(duration, 0.5)),
                                   cc.callFunc(()=>{
                                        this.getGrid().bombCell(targetPos);
                                        this.getGrid().removeObstructs(this);
                                        this.destoryWithRecycle(true);
                                   }));
            this.runAction(move);
        }else{
            this.selfDestory(true);
        }
    }
}
