import GameMath from "../../../../../extend/utils/GameMath";
import EntityBase from "../../EntityBase";
import EntityStateBase, { EntityStateType } from "../../state/EntityStateBase";
import EntityStateWait from "../../state/concreteState/EntityStateWait";
import CurveToAction from "../../../../../extend/action/CurveToAction";
import GridBase from "../../../grid/GridBase";
import { GameCurrencyType } from "../../../../../define/GameCurrencyType";


export default class ItemStateReplace extends EntityStateBase {

    private _delayTime:number = 0;
    private _targetPos:cc.Vec2 = null;
    private _gainScore:number = 0;
    private _supplys:object = {};

    public static create(targetPos:cc.Vec2, delay:number = 0.0, score:number = 0, supplys:object = {}) : ItemStateReplace {
        return new ItemStateReplace(targetPos, delay, score, supplys);
    }

    constructor(toPos:cc.Vec2, delay:number, score:number, supplys:object) {
        super(EntityStateType.Replace);
        this._targetPos = toPos;
        this._delayTime = delay;
        this._gainScore = score;
        this._supplys = supplys;
    }

    enter(entity:EntityBase):void {
        
       let grid = entity.getGrid();
       let pixelTargetPos = entity.getPixelPos(this._targetPos);
       grid.addObstructs(entity);
       
       entity.node.zIndex = GridBase.ZORDER_EFFECT;
       entity.shiftToEffectLayer();
       
       let actions:cc.FiniteTimeAction[] = [];
       if (this._delayTime > 0) {
           actions.push(cc.delayTime(this._delayTime))
       } 

       let moveTime:number = 1.0;
       actions.push(CurveToAction.create(moveTime, entity.node.getPosition(), pixelTargetPos))

       
        let gainReward = cc.callFunc(()=>{
            if(this._gainScore > 0){
                grid.gainScoreWithPixelPos(this._gainScore, pixelTargetPos, true)
            }
            
            for (let key in this._supplys) {
                let supplyID = Number(key) as GameCurrencyType;
                let amount = this._supplys[key];
                grid.gainCollectedSupply(supplyID, amount)
            }
        })
        actions.push(gainReward);
       

       let callback = cc.callFunc(()=>{
            grid.setEntity(entity, this._targetPos);
            entity.setCurrentState(EntityStateWait.create());
            grid.removeObstructs(entity);
       });

       actions.push(callback);
       entity.runAction(cc.sequence(actions));
    }

    update(entity:EntityBase, delta:number):void {
        
    }
    
    exit(entity:EntityBase):void {
        entity.getGrid().removeObstructs(entity);
    }
}
