const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerCollectedEntity extends FlyerBase {

    private _collectionID:CollectionID = CollectionID.None;
    private _delta:number = 0;

    public static create(grid:GridBase, 
                         collectionID:CollectionID,
                         delta:number,
                         origin:cc.Vec2) : FlyerCollectedEntity {

        let className = 'FlyerCollectedEntity';
        let prefabFileName = 'match_scene/prefab/entity/flyer/FlyerCollectedEntity';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerCollectedEntity;
        if (component) {
            component.init(grid, collectionID, delta, origin); 
            return component;       
        }
        return null;
    }
    protected init(grid:GridBase, 
                collectionID:CollectionID,
                delta:number,
                origin:cc.Vec2){
        super.initFlyer(grid, FlyerType.CollectedEntity);
        this.setOrigin(origin);
        this._collectionID = collectionID;
        this._delta = delta;
        this.updatePuppet();
    }

    protected updatePuppet():void{
        let frameName = IdentifierManager.getInstance().getFrameNameFromCollectionID(this._collectionID);

        let component = this._puppet.getComponent(UISpriteExt)
        if (component) {
            component.src = frameName;
        }
    }
    
    public getCollectionID():CollectionID{
        return CollectionID.None;
    }

    public getEntityID():EntityID{
        return EntityID.FlyerCollectedEntity;
    }

    private _origin:cc.Vec2 = cc.Vec2.ZERO;
    public getOrigin():cc.Vec2 {
        return this._origin;
    }
    public setOrigin(origin:cc.Vec2) : void {
        this._origin = origin;
    }

    public launch() : void {
        
        let startPos = this.getGrid().getPixelPosFromLogicalPos(this._origin);
        let targetWorldPos = this.getGrid().getWorldPosForTarget(this._collectionID);
        let targetPos = this.getGrid().node.convertToNodeSpaceAR(targetWorldPos);
        let distance = GameMath.getDistance(startPos, targetPos);
        let speed = 1000;
        let duration = distance / speed;

        this.node.setPosition(startPos);

        let move = cc.spawn(CurveToAction.create(duration, startPos, targetPos),
                 cc.scaleTo(duration, 0.75))
        let callback = cc.callFunc(()=>{
            this.getGrid().updateTargetBarUIWithDelta(this._collectionID, this._delta);
            this.destoryWithRecycle(true);
        })
        let action = cc.sequence(move, callback);
        this.node.runAction(action);

    }

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}

    onDestroy() {
        super.onDestroy();
        this._origin = null;
    }
}
