import GameMath from "../../../../../extend/utils/GameMath";
import GridCommand from "../../../commond/GridCommand";
import EntityEventBase, { EntityEventType } from "../../../event/EntityEventBase";
import EntityEventChanged from "../../../event/EntityEventChanged";
import GridBase from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateDrop from "./GridStateDrop";
import GridStateLaunchGears from "./GridStateLaunchGears";
import FlyerComboWord from "../../../entity/flyer/FlyerComboWord";
import { PowerID } from "../../../defines/PowerID";
import GridStateLaunchResidentGear from "./GridStateLaunchResidentGear";

export default class GridStateMatch extends GridStateBase {

    private _checkMatchPositions:cc.Vec2[] = [];
    public static create(checkMatchPositions:cc.Vec2[] = []) : GridStateMatch {
        return new GridStateMatch(checkMatchPositions);
    }
    constructor(checkMatchPositions:cc.Vec2[]) {
        super(GridState.MatchItem);
        this._checkMatchPositions = checkMatchPositions;
    }

    public enter(grid:GridBase) {
        grid.clearAttackedPostionRecord();
        for (let pos of this._checkMatchPositions) {
            grid.dispatchCommand(pos, GridCommand.CheckMatch(), 0);
        }
        GameMath.clearArray(this._checkMatchPositions);
        grid.progressCommand(0.001);
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            grid.setCurrentState(GridStateLaunchResidentGear.create());
        }else{
            grid.gainPower(PowerID.MatchCombo, 1);
        }
    }

    public update(grid:GridBase, delta:number) {
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            grid.setCurrentState(GridStateDrop.create(this._checkMatchPositions));
        }
    }

    public exit(grid:GridBase) {
        this._checkMatchPositions = null;
    }

    public onEvent(grid:GridBase, event:EntityEventBase) : void {
        if (event.getEventName() == EntityEventBase.ENTITY_EVENT_KEY) {
            if (event.getEventType() == EntityEventType.EntityChanged) {
                let changeEvent = event as EntityEventChanged;
                if (changeEvent) {
                    let checkPos = changeEvent.getEntity().getLogicalPos();
                    let checkSize = changeEvent.getEntity().getLogicalSize();
                    for (let row = 0; row < checkSize.height; row++) {
                        for (let column = 0; column < checkSize.width; column++) {
                            let changedPos = checkPos.add(cc.v2(column, row))
                            GameMath.insertAsSet(this._checkMatchPositions, changedPos);        
                        }
                    }
                }
            }
        }
    }
}
