const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemBombButterfly extends ItemBombBase {

    public getCollectionID() : CollectionID {
        let result = CollectionID.None;
        switch (this.getEntityColor()) {
            case EntityColor.Red:
                result = CollectionID.ItemBombButterflyRed;
                break;
            case EntityColor.Green:
                result = CollectionID.ItemBombButterflyGreen;
                break;
            case EntityColor.Blue:
                result = CollectionID.ItemBombButterflyBlue;
                break;
            case EntityColor.Yellow:
                result = CollectionID.ItemBombButterflyYellow;
                break;
            case EntityColor.Orange:
                result = CollectionID.ItemBombButterflyOrange;
                break;
            case EntityColor.Purple:
                result = CollectionID.ItemBombButterflyPurple;
                break;
                
            default:
                break;
        }
        return result;
    }
    public getEntityID() : EntityID {
        let result = EntityID.None;
        switch (this.getEntityColor()) {
            case EntityColor.Red:
                result = EntityID.ItemBombButterflyRed;
                break;
            case EntityColor.Green:
                result = EntityID.ItemBombButterflyGreen;
                break;
            case EntityColor.Blue:
                result = EntityID.ItemBombButterflyBlue;
                break;
            case EntityColor.Yellow:
                result = EntityID.ItemBombButterflyYellow;
                break;
            case EntityColor.Orange:
                result = EntityID.ItemBombButterflyOrange;
                break;
            case EntityColor.Purple:
                result = EntityID.ItemBombButterflyPurple;
                break;
                
            default:
                break;
        }
        return result;
    }

    public getProducedTrainSupplyMaps() : Object {
        let result = {};
        result[GameCurrencyType.GoldCoin] = 1;
        result[GameCurrencyType.TrainFood] = 1;
        return result;
    }

    public getScore():number{
        return 200;
    }

    protected _onClean(cleanOption:EntityCleanOption) : void {
    
        super._onClean(cleanOption);
        this.getGrid().gainScoreWithEntity(this);
        if (!this.getIsDud()) {
            let grid = this.getGrid();
            let missile = FlyerMissile.create(grid, FlyerMissileType.Dot, this.getEntityColor(), this.getShouldLayoutGrass());
            grid.setFlyerAtLogicalPos(missile, this.getLogicalPos());
            this.setVisible(false);
        }
    }

    public static create(grid:GridBase, color:EntityColor):ItemBombButterfly{

        let className = 'ItemBombButterfly';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemBombButterfly';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemBombButterfly;
        if (component) {
            component.init(grid, color); 
            return component;       
        }
        return null;
    }

    public clone():ItemBombButterfly {
        let item = ItemBombButterfly.create(this.getGrid(),this.getEntityColor());
        return item;
    }

    protected init(grid:GridBase, entityColor:EntityColor) : void{
        super.initBomb(grid, ItemBombType.Butterfly);
        this.setEntityColor(entityColor);
    }

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}
}
