const {ccclass, property} = cc._decorator;

@ccclass
export default class FenceVine extends FenceBase {

    public static create(grid:GridBase) : FenceVine {

        let className = 'FenceVine';
        let prefabFileName = 'match_scene/prefab/entity/fence/FenceVine';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FenceVine;
        if (component) {
            component.init(grid);
            return component;       
        }
        return null;
    }

    protected init(grid:GridBase) : void {
        super.initFence(grid, FenceType.Vine);
    }

    public getCollectionID() : CollectionID {
        return CollectionID.FenceVine;
    }
   
    public getEntityID() : EntityID {
        if (this.getFencePos().posA.y == this.getFencePos().posB.y) {
            return EntityID.FenceVineVertical;
        }else{
            return EntityID.FenceVineHorizontal;
        }
    }
    
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}
}
