const {ccclass, property} = cc._decorator;

@ccclass
export default class GameOverPanel extends GameResultBasePanel {

    @property(cc.Label)
    rewardButtonLabel:cc.Label = null;

    @property(cc.Label)
    cancelButtonLable:cc.Label = null;

    private _rewardType:MatchGameEndRewardType = MatchGameEndRewardType.None;
    

    protected playSoundEffect(){
        IGame.stopBGM();
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('levelfailed'));
    }

    protected getExtraRewardType():GameEndExtraRewardType{
        let extraRewardType = GameEndExtraRewardType.None;
        let uiData = this._gameScene.getEndUIData();
        if (uiData) {
            extraRewardType = uiData.failedRewardType;
        }
        if (GameConfig.IsVerify) {
            extraRewardType = GameEndExtraRewardType.None;
        }
        return extraRewardType;
    }

    protected recordBestScore(){
        this.setFriendView();
    }

    protected initButtons(){
        super.initButtons();
        this.rewardButton.active = false;
    }

    protected initSupplyItem() {
        this._rewardType = MatchGameEndRewardType.None;
        super.initSupplyItem();
    }

    protected onConfirmButtonClicked(event:cc.Event.EventCustom){
        
    }

    protected onCancelButtonClicked(event:cc.Event.EventCustom){
        this.reportResult(MatchGameEndType.GameOver, this._rewardType, false);
    }

    protected onReportResultRespone(data):void{
        super.onReportResultRespone(data);

        switch (this._rewardType) {
            case MatchGameEndRewardType.None:{
                this.onRewardFlyToBackpackComplete();
            }
                break;
            case MatchGameEndRewardType.Normal:
            case MatchGameEndRewardType.Doubled:{
                this.rewardFlyToBackpack();
            }
                break;
        
            default:
                break;
        }
    }
    
}
