import { EntityID, EntityColor } from "../../defines/EntityID";
import IdentifierManager from "../../defines/IdentifierManager";
import { KindID } from "../../defines/KindID";
import ItemBase from "../item/ItemBase";
import ParasiteBase from "./ParasiteBase";
import ParasiteIceCube from "./ParasiteIceCube";
import ParasiteHoney from "./ParasiteHoney";
import ParasiteShackle from "./ParasiteShackle";
import GridBase from "../../grid/GridBase";
import ParasiteGiftBox from "./ParasiteGiftBox";

export default class ParasiteFactory {

    public static generateParasiteWithEntityID(grid:GridBase, entityID:EntityID) : ParasiteBase {
        let parasite:ParasiteBase = null;

        switch(entityID){
            case EntityID.ParasiteShackleHp1:
            case EntityID.ParasiteShackleHp2:
            case EntityID.ParasiteShackleHp3:{
                let hp = entityID - EntityID.ParasiteShackleHp1 + 1;
                parasite = ParasiteShackle.create(grid, hp);
            }
            break;
            case EntityID.ParasiteIceCubeHp1:
            case EntityID.ParasiteIceCubeHp2:
            case EntityID.ParasiteIceCubeHp3:
            case EntityID.ParasiteIceCubeHp4:
            case EntityID.ParasiteIceCubeHp5:
            case EntityID.ParasiteIceCubeHp6:{
                let hp = entityID - EntityID.ParasiteIceCubeHp1 + 1;
                parasite = ParasiteIceCube.create(grid, hp);
            }
            break;
            case EntityID.ParasiteHoneyHp1:
            case EntityID.ParasiteHoneyHp2:{
                let hp = entityID - EntityID.ParasiteHoneyHp1 + 1;
                parasite = ParasiteHoney.create(grid, hp);
            }
            break;
            case EntityID.ParasiteGiftBoxRed:
            case EntityID.ParasiteGiftBoxGreen:
            case EntityID.ParasiteGiftBoxBlue:
            case EntityID.ParasiteGiftBoxYellow:
            case EntityID.ParasiteGiftBoxOrange:
            case EntityID.ParasiteGiftBoxPurple:{
                let color = entityID - EntityID.ParasiteGiftBoxRed + EntityColor.Red 
                parasite = ParasiteGiftBox.create(grid, color);
            }
            break;
            default:{
                break;
            }
        }

        return parasite;
    }
    
    public static generateParasiteWithKindID(grid:GridBase, kindID:KindID) : ParasiteBase {
        let entityID = IdentifierManager.getInstance().getEntityIDFromKindID(kindID);
        return this.generateParasiteWithEntityID(grid, entityID);
    }
}
