import GameMath from "../../../../extend/utils/GameMath";
import GridCommand from "../../commond/GridCommand";
import { EntityColor, EntityID } from "../../defines/EntityID";
import GridBase from "../../grid/GridBase";
import EntityPrefabNodePool from "../EntityPrefabNodePool";
import FlyerAnimation, { FlyerAnimationType } from "./FlyerAnimation";
import FlyerBase, { FlyerType } from "./FlyerBase";

// Learn TypeScript:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/typescript.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/typescript.html
// Learn Attribute:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/reference/attributes.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const { ccclass, property } = cc._decorator;

@ccclass
export default class FlyerCannon extends FlyerBase {

    protected static FLYER_CANNON_MIN_SPEED = 500.0;
    protected static FLYER_CANNON_MAX_SPEED = 5000.0
    protected static FLYER_CANNON_START_SPEED = 500.0
    protected static FLYER_CANNON_ACCELERATION = 5000.0
    protected static FLYER_CANNON_TAIL_FADE_DURATION = 0.4

    protected _hitOrigin: boolean = false;
    protected _launched: boolean = false;
    protected _aliveArea: cc.Rect = cc.rect(0, 0, 0, 0);
    protected _speed: number = 0;
    protected _lastCellPostion: cc.Vec2 = cc.v2(-1, -1);
    protected _startFly: boolean = false;

    public getEntityID(): EntityID {
        let result = EntityID.FlyerCannonWhite;
        switch (this.getEntityColor()) {
            case EntityColor.None:
                result = EntityID.FlyerCannonWhite;
                break;
            case EntityColor.Red:
                result = EntityID.FlyerCannonRed;
                break;
            case EntityColor.Green:
                result = EntityID.FlyerCannonGreen;
                break;
            case EntityColor.Blue:
                result = EntityID.FlyerCannonBlue;
                break;
            case EntityColor.Yellow:
                result = EntityID.FlyerCannonYellow;
                break;
            case EntityColor.Orange:
                result = EntityID.FlyerCannonOrange;
                break;
            case EntityColor.Purple:
                result = EntityID.FlyerCannonPurple;
                break;

            default:
                break;
        }
        return result;
    }

    private _layoutGrass: boolean = false;
    public getLayoutGrass(): boolean {
        return this._layoutGrass;
    }
    public setLayourGrass(layoutGrass: boolean): void {
        this._layoutGrass = layoutGrass;
    }

    private _origin: cc.Vec2 = cc.Vec2.ZERO;
    public getOrigin(): cc.Vec2 {
        return this._origin;
    }
    public setOrigin(origin: cc.Vec2): void {
        this._origin = origin;
    }

    private _direction: cc.Vec2 = cc.Vec2.ZERO;
    public getDirection(): cc.Vec2 {
        return this._direction;
    }
    public setDirection(direction: cc.Vec2): void {
        this._direction = direction;
        let rotation = GameMath.getAngle(cc.Vec2.ZERO, direction);
        this._puppet.angle = -rotation;
    }

    public launch() {

        if (this._launched) {
            return;
        }

        let willAttackPos = this._origin.clone();
        let grid = this.getGrid();
        let validLogicalRect = cc.rect(0, 0, grid.getGridSize().width, grid.getGridSize().height);
        while (1) {
            if (!GameMath.rectContainsPoint(validLogicalRect, willAttackPos)) {
                break;
            }
            grid.recordAttackedPosition(willAttackPos.clone());
            willAttackPos.addSelf(this._direction);
        }

        this._launched = true;
        this.setOrigin(this.getOrigin());

        this._lastCellPostion = this._origin;
        if (this._hitOrigin) {
            this._lastCellPostion = this._lastCellPostion.sub(this._direction);
        }
        this._launched = true;
        this._speed = FlyerCannon.FLYER_CANNON_START_SPEED;



        grid.addObstructs(this);

        let state = this.playAnimation('default');
        if (state) {
            let action = cc.sequence(cc.delayTime(state.duration),
                cc.callFunc(() => { this._startFly = true }));
            this.runAction(action);
        } else {
            this._startFly = true;
        }

    }

    update(dt) {
        if (this._launched == false) {
            return;
        }

        if (this._startFly == false) {
            return;
        }

        dt = dt * cc.director.getScheduler().getTimeScale();


        let grid = this.getGrid();
        this._speed = GameMath.clamp(this._speed + FlyerCannon.FLYER_CANNON_ACCELERATION * dt,
            FlyerCannon.FLYER_CANNON_MIN_SPEED,
            FlyerCannon.FLYER_CANNON_MAX_SPEED);

        //
        let newPosition = this.node.getPosition().add(this._direction.normalize().mul(this._speed).mul(dt));
        this.node.setPosition(newPosition);
        let newCellPos = grid.getLogicalPosFromPixelPos(newPosition);

        if (!newCellPos.equals(this._lastCellPostion)) {
            let offset = newCellPos.sub(this._lastCellPostion);
            let count = offset.mag() / this._direction.mag();
            for (let i = 0; i < count; i++) {
                let pos = this._lastCellPostion.add(this._direction.mul(i + 1));

                let blocked = false;
                let item = grid.getItemAtLogicalPos(pos);
                if (item && item.canBlockBombRay()) {
                    blocked = true;
                }

                let cell = grid.getCellAtLogicalPos(pos);
                if (cell) {
                    grid.dispatchCommand(pos, GridCommand.BombClean(this.getOrigin(), this.getEntityColor(), this._layoutGrass), 0);
                    grid.highlightedCell(pos);
                }

                if (blocked) {
                    let flyer = FlyerAnimation.createAnimationWithType(this.getGrid(), FlyerAnimationType.Bomb1x1);
                    grid.setFlyerAtLogicalPos(flyer, pos);
                    grid.removeObstructs(this);
                    this.destoryWithRecycle(true);
                }

                if (grid.isGrassFloorAtLogicalPos(pos)) {
                    let item = grid.getItemAtLogicalPos(pos);
                    if (item) {
                        if (item.canMatch() && item.canSwap() && item.canLayoutGrass()) {
                            this._layoutGrass = true;
                        }
                    } else {
                        this._layoutGrass = true;
                    }
                }
            }
            this._lastCellPostion = newCellPos;//
        }

        if (!GameMath.rectContainsPoint(this._aliveArea, this.node.getPosition())) {
            grid.removeObstructs(this);
            this.destoryWithRecycle(false);
        }
    }
    public static create(grid: GridBase,
        color: EntityColor,
        layoutGrass: boolean,
        origin: cc.Vec2,
        direction: cc.Vec2,
        hitOrigin: boolean): FlyerCannon {

        let filePath = 'match_scene/prefab/entity/flyer/FlyerCannon';
        switch (color) {
            case EntityColor.Red:
                filePath += 'Red';
                break;
            case EntityColor.Green:
                filePath += 'Green';
                break;
            case EntityColor.Blue:
                filePath += 'Blue';
                break;
            case EntityColor.Yellow:
                filePath += 'Yellow';
                break;
            case EntityColor.Orange:
                filePath += 'Orange';
                break;
            case EntityColor.Purple:
                filePath += 'Purple';
                break;
            case EntityColor.None:
                filePath += 'White';
                break;

            default:
                break;
        }

        let className = 'FlyerCannon';
        let prefabFileName = filePath;
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerCannon;
        if (component) {
            component.init(grid, color, layoutGrass, origin, direction, hitOrigin);
            return component;
        }
        return null;
    }
    onDestroy() {
        super.onDestroy();
        this._aliveArea = null;
        this._lastCellPostion = null;
    }
    public init(grid: GridBase,
        color: EntityColor,
        layoutGrass: boolean,
        origin: cc.Vec2,
        direction: cc.Vec2,
        hitOrigin: boolean) {

        super.initFlyer(grid, FlyerType.Cannon);
        this.setEntityColor(color)
        this.setLayourGrass(layoutGrass);
        this.setOrigin(origin);
        this.setDirection(direction);
        this._speed = 0;
        this._lastCellPostion = cc.v2(-1, -1);
        this._hitOrigin = hitOrigin;
        this._launched = false;
        this._startFly = false;

        this._aliveArea = cc.rect(-2000, -2000,
            4000, 4000);
    }

}
