const {ccclass, property} = cc._decorator;

@ccclass
export default class ShinningStar extends cc.Component {

    public static create():ShinningStar{
        let res = cc.loader.getRes('match_scene/prefab/userInterface/ShinningStar', cc.Prefab);
        if (res) {
            let node:cc.Node = cc.instantiate(res);
            if (node) {
                let component:ShinningStar = node.getComponent(ShinningStar);
                if (component) { 
                    return component; 
                }
            }
        }
        return null;
    }

    public appear():cc.AnimationState{
        let anim = this.getComponent(cc.Animation);
        if (anim) {
            return anim.play('appear');
        }
        return null;
    }

    public fly():cc.AnimationState{
        let anim = this.getComponent(cc.Animation);
        if (anim) {
            return anim.play('fly');
        }
        return null;
    }



    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}
}
