import { GameCurrencyType } from "../../../../../define/GameCurrencyType";
import GridCommand from "../../../commond/GridCommand";
import { EntityColor, EntityType } from "../../../defines/EntityID";
import GridBase, { GridMode } from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateMatch from "./GridStateMatch";
import FlyerHammer from "../../../entity/flyer/FlyerHammer";
import BehaviourID, { BehaviourEvent } from "../../../../../extend/identify/behaviourIdentify/BehaviourDefines";
import IGame from "../../../../../base/IGame";

export default class GridStateForceClean extends GridStateBase {
    public static create() : GridStateForceClean {
        return new GridStateForceClean();
    }
    constructor(){
        super(GridState.ForceClean);
    }

    enter(grid:GridBase):void {
        
    }

    public exit(grid:GridBase) {
        this._targetPos = null;
    }

    public canChangeState() : boolean {
        return true;
    }
    public canPause() : boolean {
        return true;
    }

    private _targetPos:cc.Vec2 = cc.Vec2.ZERO;

    public onTapBegan(grid:GridBase, logicalPos:cc.Vec2) {

        let cell = grid.getCellAtLogicalPos(logicalPos);
        if (!cell) {
            //
            grid.setMode(GridMode.Normal);
            return;
        }

        let entityTypes:EntityType[] = [
            EntityType.Parasite,
            EntityType.Item,
            EntityType.Rug,
            EntityType.Floor,
            EntityType.Popsicle,
            EntityType.Cell,
        ];

        let canUseHammer = false;
        for (let entityType of entityTypes) {
            let entity = grid.getEntity(entityType, logicalPos);
            if (entity) {
                canUseHammer = entity.canExcuteCommand(GridCommand.BombClean(logicalPos, EntityColor.None, false));
                break;
            }
        }

        if (!canUseHammer) {
            return;
        }

        this._targetPos = logicalPos;
        grid.requestUseGear(GameCurrencyType.AidHammer, 1);
    }

    public onRespone(grid:GridBase, event:Object) : void {
       
        let hammer = FlyerHammer.create(grid, this._targetPos);
        grid.setFlyerAtLogicalPos(hammer,cc.Vec2.ZERO);

       
        grid.setCurrentState(GridStateMatch.create());
        grid.setMode(GridMode.Normal);
    }
}