const {ccclass, property} = cc._decorator;

@ccclass
export default class RectangleClipper extends cc.Mask {

    private _clipAreas:cc.Rect[] = [];
    setCropRectangles(arr) {
        this._clipAreas = arr;
        this._updateGraphics();
    }
    _updateGraphics () {
        let graphics = (this as any)._graphics as cc.Graphics;
        if (graphics) {
            graphics.clear(false);
            for(var i = 0; i < this._clipAreas.length; i++){
                var tmpRect = this._clipAreas[i];
                graphics.rect(tmpRect.x, tmpRect.y, tmpRect.width, tmpRect.height);
            }
            if (cc.game.renderType === cc.game.RENDER_TYPE_CANVAS) {
                graphics.stroke();
            }
            else {
                graphics.fill();
            }
        }
    }
    // update (dt) {}
}
