const {ccclass, property} = cc._decorator;

@ccclass
export default class ParasiteGiftBox extends ParasiteBase {

    @property([cc.Node])
    partsNode:cc.Node[] =[];

    private _healthPoint:number = 0;

    public static create(grid:GridBase, color:EntityColor, healthPoint:number = 1):ParasiteGiftBox{

        let className = 'ParasiteGiftBox';
        let prefabFileName = 'match_scene/prefab/entity/parasite/ParasiteGiftBox';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ParasiteGiftBox;
        if (component) {
            component.init(grid, color, healthPoint); 
            return component;       
        }
        return null;
    }

    public clone() : ParasiteGiftBox {
        return ParasiteGiftBox.create(this.getGrid(), this.getHealthPoint());
    }

    public getButterflyPriority(color:EntityColor, layoutGrass:boolean):number{
        if(color == this.getEntityColor()){
            return super.getButterflyPriority(color, layoutGrass);
        }
        return EntityBase.DEFAULT_MISSILE_PRIORITY;
    }

    public getCollectionID():CollectionID{
        let result = CollectionID.None
        switch(this.getEntityColor()){
            case EntityColor.Red:{
                result = CollectionID.ParasiteGiftBoxRed;
            }
            break;
            case EntityColor.Green:{
                result = CollectionID.ParasiteGiftBoxGreen;
            }
            break;
            case EntityColor.Blue:{
                result = CollectionID.ParasiteGiftBoxBlue;
            }   
            break;
            case EntityColor.Yellow:{
                result = CollectionID.ParasiteGiftBoxYellow;
            }
            break;
            case EntityColor.Orange:{
                result = CollectionID.ParasiteGiftBoxOrange;
            }
            break;
            case EntityColor.Purple:{
                result = CollectionID.ParasiteGiftBoxPurple;
            }
            break;
        }

        return result;
    }

    public getEntityID():EntityID{

        let result = EntityID.None
        switch(this.getEntityColor()){
            case EntityColor.Red:{
                result = EntityID.ParasiteGiftBoxRed;
            }
            break;
            case EntityColor.Green:{
                result = EntityID.ParasiteGiftBoxGreen;
            }
            break;
            case EntityColor.Blue:{
                result = EntityID.ParasiteGiftBoxBlue;
            }   
            break;
            case EntityColor.Yellow:{
                result = EntityID.ParasiteGiftBoxYellow;
            }
            break;
            case EntityColor.Orange:{
                result = EntityID.ParasiteGiftBoxOrange;
            }
            break;
            case EntityColor.Purple:{
                result = EntityID.ParasiteGiftBoxPurple;
            }
            break;
        }
        return result;
    }

    public getScore():number{
        return 200;
    }

    public dealWithConfigMap(configMap:Object):void{
        super.dealWithConfigMap(configMap);
        if(configMap[TMXConfigInfoKeys.CFG_GIFT_BOX_MAX]){
            let healthPoint = configMap[TMXConfigInfoKeys.CFG_GIFT_BOX_MAX];
            this.setHealthPoint(healthPoint);
        }
    }

    protected _hostCanDrop():boolean{
        return false;
    }

    protected _hostCanSwap():boolean{
        return false;
    }

    protected _hostCanMatch():boolean{
        return false;
    }

    protected _canCleanByBomb(cleanColor:EntityColor):boolean{
        return cleanColor == this.getEntityColor() 
    }

    protected _canCleanByNeighbour(cleanColor:EntityColor):boolean{
        return cleanColor == this.getEntityColor() 
    }

    protected _onClean(cleanOption:EntityCleanOption):void{
        
        if (cleanOption.cleanColor != this.getEntityColor()) {
            return;
        }
        let grid = this.getGrid();
        grid.gainScoreWithEntity(this);
        if(this.getHealthPoint() <=  1){

            let pos = this.getLogicalPos();
            let size = this.getLogicalSize();

            for (let row = 0; row < size.height; row++) {
                for (let column = 0; column < size.width; column++) {
                    let checkPos = pos.add(cc.v2(column, row));
                    let item = grid.getItemAtLogicalPos(checkPos);
                    if (item) {
                        item.setImmuneDamage(true);
                    }
                }
            }

            if (!this.isTargetCollection()) {
                let flyer = FlyerAnimation.createDieAnimation(this.getGrid(), this.getEntityID());
                this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
                flyer.node.setPosition(this.getPixelPos(this.getLogicalPos()));
                IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('giftbox'));
            }
            this.setVisible(false);
            let changedEvent = EntityEventChanged.create(this);
            this.node.dispatchEvent(changedEvent);
            this.onCollected();
            this.selfDestory();

        }else{
            let changedEvent = EntityEventChanged.create(this);
            this.node.dispatchEvent(changedEvent);
            this.selfHurt();
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_0'));
            this.setHealthPoint(this.getHealthPoint() - 1);
            let flyer = FlyerAnimation.createAnimationWithType(this.getGrid(), FlyerAnimationType.CommonHurt);
            this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
            flyer.node.setPosition(this.getPixelPos(this.getLogicalPos()));
            flyer.node.zIndex = this.getTopZOrder();
        }
    }

    protected init(grid:GridBase, color:EntityColor, healthPoint):void{
        super.initParasite(grid, ParasiteType.GiftBox, 2, 2);
        this.setEntityColor(color)
        this.setHealthPoint(healthPoint);
    }

    protected getHealthPoint():number{
        return this._healthPoint;
    }

    protected setHealthPoint(val:number):void{
        this._healthPoint = val;
        this.updatePuppet();
    }

    protected updatePuppet():void{
        super.updatePuppet();
        for (let i = 0; i < 4; i++) {
            this.partsNode[i].active = i < (this._healthPoint - 1);
        }
    }
}
