import UISpriteExt from "../../../../../component/UISpriteExt";
import { CollectionID } from "../../../defines/CollectionID";
import { EntityChargeOption, EntityID } from "../../../defines/EntityID";
import IdentifierManager from "../../../defines/IdentifierManager";
import TMXConfigInfoKeys from "../../../defines/TMXConfigInfoKeys";
import GridBase from "../../../grid/GridBase";
import EntityPrefabNodePool from "../../EntityPrefabNodePool";
import FlyerMower, { FlyerMowerDirection } from "../../flyer/FlyerMower";
import ItemBase, { ItemType } from "../ItemBase";

export enum ItemMowerDirection{
    Up = 0,
    Right,
    Down,
    Left,
};


const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemMower extends ItemBase {

    private static targetFramePositions:Object = {
        0 : cc.v2(20, -25),
        1 : cc.v2(-25, -20),
        2 : cc.v2(-20, 25),
        3 : cc.v2(25 ,20),
    }

    private _direction:ItemMowerDirection = ItemMowerDirection.Up;
    private _targetIcon:cc.Sprite = null;
    private _targetFrame:cc.Node = null;
    private _targetLabel:cc.Label = null;

    public static create(grid:GridBase, direction:ItemMowerDirection):ItemMower{

        let className = 'ItemMower';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemMower';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemMower;
        if (component) {
            component.init(grid, direction); 
            return component;       
        }
        return null;
    }
    protected init(grid:GridBase, direction:ItemMowerDirection){
        super.initItem(grid, ItemType.Mower);
        this._targetIcon = this._puppet.getChildByName('targetIcon').getComponent(cc.Sprite);
        this._targetFrame = this._puppet.getChildByName('targetFrame');
        this._targetLabel = this._targetFrame.getChildByName('targetAmountLabel').getComponent(cc.Label);
        this.setDirection(direction);
        //this.setChargeID(CollectionID.ItemNormalRed);
        this.setChargeTargetAmount(0);
    }

    public dealWithConfigMap(configMap:Object):void{
        super.dealWithConfigMap(configMap);
        if(configMap[TMXConfigInfoKeys.CFG_MOWER_CHARGE_TARGET]){
            let targetName = configMap[TMXConfigInfoKeys.CFG_MOWER_CHARGE_TARGET];
            let collectionID = IdentifierManager.getInstance().getCollectionIDFromString(targetName);
            if (collectionID != CollectionID.None) {
                this.setChargeID(collectionID);
            }

            let count = configMap[TMXConfigInfoKeys.CFG_MOWER_CHARGE_COUNT];
            if (count) {
                this.setChargeTargetAmount(Number(count));
            }

            let priority = configMap[TMXConfigInfoKeys.CFG_MOWER_CHARGE_PRIORITY];
            if (priority) {
                this._chargePriority = priority;
            }
        }
    }

    public setChargeID(collectionID:CollectionID) : void {
        super.setChargeID(collectionID);
        let frameName = IdentifierManager.getInstance().getFrameNameFromCollectionID(this._chargetCollectionID);
        let component = this._targetIcon.getComponent(UISpriteExt)
        if (component) {
            component.src = frameName;
        }
    } 

    public setChargeAmount(amount:number) : void {
        super.setChargeAmount(amount);
        this.updateTargetAmount();
    }

    public setChargeTargetAmount(amount:number) : void {
        super.setChargeTargetAmount(amount);
        this.updateTargetAmount();
    }

    private updateTargetAmount() : void {
        let amount = Math.max(this._targetCount - this._chargeCount, 0); 
        this._targetLabel.string = amount.toString();
    }

    public getDirection() : ItemMowerDirection {
        return this._direction;
    }
    public setDirection(direction:ItemMowerDirection) : void {
        this._direction = direction;
        this.updatePuppet();
        let targetFramePos = ItemMower.targetFramePositions[this._direction];
        if(targetFramePos){
            this._targetFrame.setPosition(targetFramePos);
        }
    }
    public clone():ItemMower {
        let item = ItemMower.create(this.getGrid(), this.getDirection());
        return item;
    }

    public getCollectionID():CollectionID{
        return CollectionID.ItemMower;
    }

    public getEntityID():EntityID{

        let result = EntityID.None;
        switch (this._direction) {
            case ItemMowerDirection.Up:
                result = EntityID.ItemMowerUp;
                break;
            case ItemMowerDirection.Right:
                result = EntityID.ItemMowerRight;
                break;
            case ItemMowerDirection.Down:
                result = EntityID.ItemMowerDown;
                break;
            case ItemMowerDirection.Left:
                result = EntityID.ItemMowerLeft;
                break;
        
            default:
                break;
        }

        return result;
    }

    public getScore():number{
        return 50 * this.getChargeTargetAmount();
    }

    protected _onCharge(option:EntityChargeOption) {
        this.absorbWillChargeAmount(option.chargeID, option.chargeAmount);
        if (this.getChargeAmount() >= this.getChargeTargetAmount()) {
            this.getGrid().gainScoreWithEntity(this);
            this.setVisible(false);
            this.selfDestory();
            let layoutGrass = this.getGrid().isGrassFloorAtLogicalPos(this.getLogicalPos());
            let direction = FlyerMowerDirection.Up;
            switch (this._direction) {
                case ItemMowerDirection.Up:
                    direction = FlyerMowerDirection.Up;
                    break;
                case ItemMowerDirection.Right:
                    direction = FlyerMowerDirection.Right;
                    break;
                case ItemMowerDirection.Down:
                    direction = FlyerMowerDirection.Down;
                    break;
                case ItemMowerDirection.Left:
                    direction = FlyerMowerDirection.Left;
                    break;
                default:
                    break;
            }
            let flyer = FlyerMower.create(this.getGrid(),layoutGrass, this.getLogicalPos(),direction);
            this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
        }
    }

    onDestroy() {
        super.onDestroy();
        this._targetIcon = null;
        this._targetFrame = null;
        this._targetLabel = null;
    }
}
