import { PowerID } from "../../../defines/PowerID";
import GridBase from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateCollection from "./GridStateCollection";
import GridStateMatch from "./GridStateMatch";


export default class GridStateAutoActivateBomb extends GridStateBase {
    public static create() : GridStateAutoActivateBomb {
        return new GridStateAutoActivateBomb();
    }
    constructor() {
        super(GridState.AutoActivatedBomb);
    }

    public enter(grid:GridBase) {
        let activeIndex = grid.getPowerAmount(PowerID.ActivateBomb);
        
        if (activeIndex > 0) {
            grid.setCurrentState(GridStateMatch.create());
        }else{
            grid.setCurrentState(GridStateCollection.create());
        }
    }
}
