/**
 * comber
 * combo
 */

import GridStateBase, { GridState } from "../GridStateBase";
import GridBase from "../../GridBase";
import GridStateSwapNeighbour from "./GridStateSwapNeighbour";
import { PowerID } from "../../../defines/PowerID";
import FlyerComboWord from "../../../entity/flyer/FlyerComboWord";


export default class GridStateComboWord extends GridStateBase {

    public static create() : GridStateComboWord{
        return new GridStateComboWord();
    }
    constructor() {
        super(GridState.ComobWord);
    }

    public enter(grid:GridBase) {

        if (!grid.hasShowCompleteBanner()) {
            let comboCount = grid.getPowerAmount(PowerID.MatchCombo);
            if (comboCount >= 3) {
                let needObstruct = false;
                if (grid.isComplete()) {
                    needObstruct = true;
                }else {
                    if (grid.isFailed()) {
                        needObstruct = true;
                    }
                }
                let flyer = FlyerComboWord.create(grid, comboCount, needObstruct);
                grid.setFlyerAtLogicalPos(flyer, cc.Vec2.ZERO);
            }
        }
        
        this.gainComboScore(grid);
        
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            grid.setCurrentState(GridStateSwapNeighbour.create());
        }
    }

    private gainComboScore(grid:GridBase){
        let comboCount = grid.getPowerAmount(PowerID.MatchCombo);
        let scoreMap = {
            '1' : 500,
            '2' : 500,
            '3' : 500,
            '4' : 750,
            '5' : 750,
            '6' : 750,
            '7' : 1000,
            '8' : 1000,
            '9' : 1000,
            '10' : 1500
        }
        let score = 0;
        for (let i = 0; i < comboCount - 1; i++) {
            let delta = 0
            if (i < 10) {
                delta = scoreMap[(i + 1)]
            }else{
                delta = 1500
            }
            score += delta
        }

        if (score > 0) {
            let size = grid.node.getContentSize();
            let pixelPos = cc.v2(size.width * 0.5, size.height * 0.5 + 60);
            grid.gainScoreWithPixelPos(score, pixelPos)
        }
    }

    public update(grid:GridBase, delta:number) {
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            grid.setCurrentState(GridStateSwapNeighbour.create());
        }
    }
}
