import IGame from "../../../../base/IGame";
import { GameCurrencyType } from "../../../../define/GameCurrencyType";
import IdentifierManager from "../../defines/IdentifierManager";
import { MatchGameEndRewardType, MatchGameEndType } from "../../GameSceneBase";
import GameResultBasePanel, { GameEndExtraRewardType } from "./GameResultBasePanel";
import GameConfig from "../../../../define/GameConfig";
import GameMath from "../../../../extend/utils/GameMath";

const {ccclass, property} = cc._decorator;

@ccclass
export default class GameCongratulationPanel extends GameResultBasePanel {
    
    @property(cc.Label)
    rewardButtonLabel:cc.Label = null;

    @property(cc.Label)
    cancelButtonLable:cc.Label = null;

    @property(cc.Node)
    giveUpButton:cc.Node = null;

    @property(cc.Node)
    terminateButton:cc.Node = null;

    protected _rewardType:MatchGameEndRewardType = MatchGameEndRewardType.Normal;
    protected sendMarkID() {
        this._gameScene.reportStepCongratulation();
    }

    protected playSoundEffect(){
        IGame.stopBGM();
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('levelcomplete'));
        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('voice_level_complete2'));
    }

    protected getExtraRewardType():GameEndExtraRewardType{
        let extraRewardType = GameEndExtraRewardType.None;
        let uiData = this._gameScene.getEndUIData();
        if (uiData) {
            extraRewardType = uiData.succeedRewardType;
        }
        if (GameConfig.IsVerify) {
            extraRewardType = GameEndExtraRewardType.None;
        }
        return extraRewardType;
    }

    protected initSupplyItem() {

        let extraRewardType = this.getExtraRewardType();
        this._rewardType = MatchGameEndRewardType.Normal;
        switch (extraRewardType) {
            case GameEndExtraRewardType.ShareForReward:{
                this._rewardType = MatchGameEndRewardType.None;
            }
                break;
            default:
                break;
        }
        super.initSupplyItem();
    }

    protected initButtons(){
        super.initButtons();
        this.giveUpButton.on('touchend', this.onCancelButtonClicked, this);
        this.terminateButton.on('touchend', this.onTerminateButtonClicked, this);
        this.terminateButton.active = this._gameScene.getLevel() >= 5;
        this.rewardButton.active = false;
        this.giveUpButton.active = false;
    }

    protected onConfirmButtonClicked(event:cc.Event.EventCustom){
        this.reportResult(MatchGameEndType.Complete, this._rewardType, false);
    }

    protected onCancelButtonClicked(event:cc.Event.EventCustom){
        this.reportResult(MatchGameEndType.Complete, this._rewardType, false);
    }

    protected onTerminateButtonClicked(event:cc.Event.EventCustom){
        this.reportResult(MatchGameEndType.Complete, this._rewardType, false);
    }
    
    protected onReportResultRespone(data):void{
        //this._gameScene.reportStepNextGame()
        console.log('GameCongratulationPanel.onReportResultRespone');
        super.onReportResultRespone(data);
        this.terminateButton.active = false;
        switch (this._rewardType) {
            case MatchGameEndRewardType.None:{
                this.onRewardFlyToBackpackComplete();
            }
                break;
            case MatchGameEndRewardType.Normal:
            case MatchGameEndRewardType.Doubled:{
                this.rewardFlyToBackpack();
            }
                break;
        
            default:
                break;
        }
    }

    protected rewardFlyToBackpack():void{
        super.rewardFlyToBackpack();
        this.giveUpButton.active = false;
    }

    protected onRewardFlyToBackpackComplete(){
        
    }
}
