import GridStateBase, { GridState } from "../GridStateBase";
import GridBase from "../../GridBase";
import GridStateCongratulation from "./GridStateCongratulation";

export default class GridStateComplete extends GridStateBase {

    private _hadDisappearUI = false;
    public static create() : GridStateComplete{
        return new GridStateComplete();
    }
    constructor() {
        super(GridState.Complete);
    }

    public enter(grid:GridBase) {
    }

    public update(grid:GridBase, delta:number) {
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty() && grid.isFlyerObstructsEmpty()) {
            if (this._hadDisappearUI){
                grid.setCurrentState(GridStateCongratulation.create());
            }else{
                this.disappear(grid);    
                this._hadDisappearUI = true;
            }
        }
    }
}
