export enum MarkerType {
    None,
    Normal,
};


const {ccclass, property} = cc._decorator;

@ccclass
export default class MarkerBase extends EntityBase {

    private _markerType:MarkerType = MarkerType.None;
    public getMarkerType() : MarkerType {
        return this._markerType;
    }
    protected initMarker(grid:GridBase, markerType:MarkerType, width:number = 1, height:number = 1) : void {
        super.initEntity(grid, EntityType.Marker, width, height);
        this._markerType = markerType;
    }
}
