import GameMath from "../../../../../extend/utils/GameMath";
import { EntityDropStep, EntityDropStepType } from "../../../defines/EntityID";
import { PowerID } from "../../../defines/PowerID";
import FencePos, { FencePosType } from "../../../entity/fence/FencePos";
import ItemBase from "../../../entity/item/ItemBase";
import ItemFactory from "../../../entity/item/ItemFactory";
import ItemStateDrop from "../../../entity/item/state/ItemStateDrop";
import { EntityStateType } from "../../../entity/state/EntityStateBase";
import GridBase from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateMatch from "./GridStateMatch";
import FlyerCollectedSupply from "../../../entity/flyer/FlyerCollectedSupply";
import { GameCurrencyType } from "../../../../../define/GameCurrencyType";
import CellBase from "../../../entity/cell/CellBase";
import FenceBase from "../../../entity/fence/FenceBase";

export default class GridStateDrop extends GridStateBase {

    private _checkMatchPositions:cc.Vec2[] = [];
    public static create(checkMatchPositions:cc.Vec2[] = []) : GridStateDrop {
        return new GridStateDrop(checkMatchPositions);
    }
    constructor(checkMatchPositions:cc.Vec2[], gridState:GridState = GridState.DropItem) {
        super(gridState);
        this._checkMatchPositions = checkMatchPositions;
    }

    protected _deadCellsPositions:cc.Vec2[] = [];
    protected _livingCellsPositions:cc.Vec2[] = [];
    protected clearDeadAndLivingCellRecord(grid:GridBase) : void {
        GameMath.clearArray(this._deadCellsPositions);
        GameMath.clearArray(this._livingCellsPositions);
    }
    protected isDeadCell(grid:GridBase, pos:cc.Vec2) : boolean {

        if (GameMath.indexOf(this._livingCellsPositions, pos) != -1) {
            return false;
        }

        if (GameMath.indexOf(this._deadCellsPositions, pos) != -1) {
            return true;
        }

        let cell = grid.getCellAtLogicalPos(pos);
        
        if (!cell) {
            GameMath.insertAsSet(this._deadCellsPositions, pos);
            return true;
        }

        if (this.canGenerateItemAtPos(grid, pos)) {
            GameMath.insertAsSet(this._livingCellsPositions, pos);
            return false
        }

        if (cell.getIsWormholeExit()) {
            if (!this.isDeadCell(grid, cell.getWormholeSource())) {
                GameMath.insertAsSet(this._livingCellsPositions, pos);
                return false;
            }
        }

        let availableMoveOffsets:cc.Vec2[] = [cc.v2(0, 1), cc.v2(-1, 1), cc.v2(1, 1)];
        for (let offset of availableMoveOffsets) {
            let fromPos = pos.add(offset);
            if (this.canPassThrough(grid, fromPos, pos)) {
                if (!this.isDeadCell(grid, fromPos)) {
                    GameMath.insertAsSet(this._livingCellsPositions, pos);
                    return false;
                }
            }
        }
        
        GameMath.insertAsSet(this._deadCellsPositions, pos);
        return true;
    }

    protected _fencesPositions:FencePos[] = [];
    protected _fencesMap:Object = {};
    protected markFences(grid:GridBase) : void {
        GameMath.clearArray(this._fencesPositions);
        GameMath.clearObject(this._fencesMap);

        let horizontalOffsets:cc.Vec2[] = [cc.v2(-1, 0), cc.v2(0, 0), cc.v2(1, 0),];
        let verticalOffsets:cc.Vec2[] = [cc.v2(0, 1), cc.v2(0, 0), cc.v2(0, -1),];
        let wallOffsets:cc.Vec2[] = [cc.v2(0, 1), cc.v2(0, -1), cc.v2(1, 0), cc.v2(-1, 0),];
            
        let fencesMap = grid.getFencesMap();

        for (const key in fencesMap) {
            let fence = fencesMap[key] as FenceBase 
            let fencePos = fence.getFencePos();
            let fencePosType = fencePos.getFencePosType();
            GameMath.insertAsSet(this._fencesPositions, fencePos);

            switch (fencePosType) {
                case FencePosType.Horizontal:{
                    for (let offset of horizontalOffsets) {
                        let checkPos = fencePos.posA.add(offset);
                        let cell = grid.getCellAtLogicalPos(checkPos);
                        if (!cell) {
                            for (let wallOffset of wallOffsets) {
                                let wallPos = FencePos.create(checkPos, checkPos.add(wallOffset));
                                GameMath.insertAsSet(this._fencesPositions, wallPos);
                            }
                        }

                        checkPos = fencePos.posB.add(offset);
                        cell = grid.getCellAtLogicalPos(checkPos);
                        if (!cell) {
                            for (let wallOffset of wallOffsets) {
                                let wallPos = FencePos.create(checkPos, checkPos.add(wallOffset));
                                GameMath.insertAsSet(this._fencesPositions, wallPos);
                            }
                        }
                    }
                }
                    break;
                case FencePosType.Vertical:{
                    for (let offset of verticalOffsets) {
                        let checkPos = fencePos.posA.add(offset);
                        let cell = grid.getCellAtLogicalPos(checkPos);
                        if (!cell) {
                            for (const wallOffset of wallOffsets) {
                                let wallPos = FencePos.create(checkPos, checkPos.add(wallOffset));
                                GameMath.insertAsSet(this._fencesPositions, wallPos);
                            }
                        }

                        checkPos = fencePos.posB.add(offset);
                        cell = grid.getCellAtLogicalPos(checkPos);
                        if (!cell) {
                            for (const wallOffset of wallOffsets) {
                                let wallPos = FencePos.create(checkPos, checkPos.add(wallOffset));
                                GameMath.insertAsSet(this._fencesPositions, wallPos);
                            }
                        }
                    }
                }
            
                default:
                    break;
            }
        }
    }
    protected hasFence(grid:GridBase, fromPos:cc.Vec2, toPos:cc.Vec2) : boolean {
        let fencePos = FencePos.create(fromPos, toPos);
        if (GameMath.indexOf(this._fencesPositions, fencePos) == -1) {
            return false;
        }
        return true;
    }
    protected blockByFence(grid:GridBase, fromPos:cc.Vec2, toPos:cc.Vec2) : boolean {
        let offset = toPos.sub(fromPos);
        if (offset.equals(cc.v2(0,1)) || offset.equals(cc.v2(0, -1))) {
            if (this.hasFence(grid, fromPos, toPos)) {
                return true;
            }
        }else if (offset.equals(cc.v2(1, -1))  || offset.equals(cc.v2(-1, 1))) {
            //     /*
            //     F
            //     - -
            //     T
            //     */
            //     // 
            {
                let fenceA = this.hasFence(grid, fromPos, fromPos.add(cc.v2(0, -1)));
                let fenceB = this.hasFence(grid, toPos, toPos.add(cc.v2(0, 1)));
                if (fenceA && fenceB) {
                    return true;
                }
            }
            //     /*
            //     F |
            //     | T
            //     */
            //     // 
            {
                let fenceA = this.hasFence(grid, fromPos, fromPos.add(cc.v2(1, 0)));
                let fenceB = this.hasFence(grid, toPos, toPos.add(cc.v2(-1, 0)));
                if (fenceA && fenceB) {
                    return true;
                }
            }

            //     /*
            //     F |
            //     --
            //         T
            //     */
            //     // 
            {
                let fenceA = this.hasFence(grid, fromPos, fromPos.add(cc.v2(1, 0)));
                let fenceB = this.hasFence(grid, fromPos, fromPos.add(cc.v2(0, -1)));
                if (fenceA && fenceB) {
                    return true;
                }
            }

            //     /*
            //     F
            //         --
            //     | T
            //     */
            //     // 
            {
                let fenceA = this.hasFence(grid, toPos, toPos.add(cc.v2(0, 1)));
                let fenceB = this.hasFence(grid, toPos, toPos.add(cc.v2(-1, 0)));
                if (fenceA && fenceB) {
                    return true;
                }
            }

        }else if (offset.equals(cc.v2(-1, -1))  || offset.equals(cc.v2(1, 1))) {
            //     /*
            //         F
            //     - -
            //     T
            //     */
            //     // 
            {
                let fenceA = this.hasFence(grid, fromPos, fromPos.add(cc.v2(0, -1)));
                let fenceB = this.hasFence(grid, toPos, toPos.add(cc.v2(0, 1)));
                if (fenceA && fenceB) {
                    return true;
                }
            }

            //     /*
            //     | F
            //     T |
            //     */
            //     // 
            {
                let fenceA = this.hasFence(grid, fromPos, fromPos.add(cc.v2(-1, 0)));
                let fenceB = this.hasFence(grid, toPos, toPos.add(cc.v2(1, 0)));
                if (fenceA && fenceB) {
                    return true;
                }
            }

            //     /*
            //     | F
            //     __
            //     T
            //     */
            //     // 
            {
                let fenceA = this.hasFence(grid, fromPos, fromPos.add(cc.v2(-1, 0)));
                let fenceB = this.hasFence(grid, fromPos, fromPos.add(cc.v2(0, -1)));
                if (fenceA && fenceB) {
                    return true;
                }
            }

            //     /*
            //         F
            //     --
            //     T |
            //     */
            //     // 
            {
                let fenceA = this.hasFence(grid, toPos, toPos.add(cc.v2(0, 1)));
                let fenceB = this.hasFence(grid, toPos, toPos.add(cc.v2(1, 0)));
                if (fenceA && fenceB) {
                    return true;
                }
            }
        }

        return false;
    }

    protected canGenerateItemAtPos(grid:GridBase, pos:cc.Vec2) : boolean {

        let cell = grid.getCellAtLogicalPos(pos);
        if (cell) {
            if(cell.canGenerateItem() && !(this.canPassThrough(grid, pos.add(cc.v2(0, 1)), pos))){
                return true;
            }
        }
        return false;
    }
    protected isCellAvailableForDrop(grid:GridBase, pos:cc.Vec2) : boolean {
        let cell = grid.getCellAtLogicalPos(pos);
        if (cell) {
            let parasite = grid.getParasiteAtLogicalPos(pos);
            let item = grid.getItemAtLogicalPos(pos);
            if ((item && !item.canDrop() || (parasite && !parasite.hostCanDrop()))) {
                return false;
            }else{
                return true
            }
        }else{
            return false;
        }
    }
    protected canPassThrough(grid:GridBase, fromPos:cc.Vec2, toPos:cc.Vec2) : boolean {

        if (Math.abs(fromPos.x - toPos.x) > 1) {
            return false;
        }
        
        if (Math.abs(fromPos.y - toPos.y) > 1) {
            return false;
        }
        
        if (this.blockByFence(grid, fromPos, toPos)) {
            return false;
        }
        
        if (this.isCellAvailableForDrop(grid, fromPos)) {
            return this.isCellAvailableForDrop(grid, toPos);
        }else{
            return false;
        }
    }
    protected canArrive(grid:GridBase, fromPos:cc.Vec2, toPos:cc.Vec2) : boolean {

        if (Math.abs(fromPos.x - toPos.x) > 1) {
            return false;
        }
        
        if (Math.abs(fromPos.y - toPos.y) > 1) {
            return false;
        }
        
        if (this.blockByFence(grid, fromPos, toPos)) {
            return false;
        }
        
        if (this.canGenerateItemAtPos(grid, toPos)){
            return false;
        }
    
        if (this.isCellAvailableForDrop(grid, fromPos)) {

            let cell = grid.getCellAtLogicalPos(toPos);
            if (cell) {
                return cell.canPassThrough();
            }else{
                return false;
            }
        }else{
            return false;
        }
        return false;
    }
    protected tryDropItem(grid:GridBase, item:ItemBase) : EntityDropStep {
        let result:EntityDropStep = EntityDropStep.create(EntityDropStepType.Stay, item.getLogicalPos());
        if (item.canDrop()) {
            let cell = grid.getCellAtLogicalPos(item.getLogicalPos());
            if (cell.getIsWormholeEntrance()) {
                let targetItem = grid.getItemAtLogicalPos(cell.getWormholeDestination());
                let targetParasite = grid.getParasiteAtLogicalPos(cell.getWormholeDestination());
                if (!targetItem && ! targetParasite) {
                    result.type = EntityDropStepType.Wormhole;
                    result.logicalPos = cell.getWormholeDestination();
                }
            }else{
                let currentPos = item.getLogicalPos();
                let checkPos = currentPos.add(cc.v2(0, -1));
                if (this.canArrive(grid, currentPos, checkPos)) {
                    result.type = EntityDropStepType.Drop;
                    result.logicalPos = checkPos;
                }
            }
        }
        return result;
    }
    protected tryDiagonalItem(grid:GridBase, item:ItemBase) : EntityDropStep {
        let result:EntityDropStep = EntityDropStep.create(EntityDropStepType.Stay, item.getLogicalPos());
        let dropResult = this.tryDropItem(grid, item);
        switch (dropResult.type) {
            case EntityDropStepType.Stay:{
                let currentPos = item.getLogicalPos();
                let moveOffsets:cc.Vec2[] = [cc.v2(-1, -1), cc.v2(1, -1),];

                for (let offset  of moveOffsets) {
                    let checkPos = currentPos.add(offset);
                    let checkBlockPos = checkPos.add(cc.v2(0, 1));
                    let isBlockPosDead = this.isDeadCell(grid, checkBlockPos);
                    let canNotPassThrough = !(this.canPassThrough(grid, checkBlockPos, checkPos));

                    if (isBlockPosDead || canNotPassThrough) {
                        if (this.canArrive(grid, currentPos, checkPos)) {
                            result.type = EntityDropStepType.Drop;
                            result.logicalPos = checkPos;
                        }
                    }
                }
                return result;
            }
                break;
            case EntityDropStepType.Drop:
            case EntityDropStepType.Wormhole:{
                result.type = dropResult.type;
                result.logicalPos = dropResult.logicalPos;
                return result;
            }
                break;
                
            default:
                break;
        }
        return result;
    }
    
    protected _dropedItems:ItemBase[] = [];
    protected tryDropPos(grid:GridBase, pos:cc.Vec2, canSlide:boolean) : boolean {
        let droped = false;

        let item = grid.getItemAtLogicalPos(pos);
        if (!item) {
            return droped;
        }
    
        if (!item.canDrop()) {
            return droped;
        }    
        
        if (GameMath.indexOf(this._dropedItems, item) != -1) {
            droped = true;
            return droped;
        }

        let result:EntityDropStep = EntityDropStep.create(EntityDropStepType.Stay, item.getLogicalPos());
        
        if (droped == false) {
            let cell = grid.getCellAtLogicalPos(item.getLogicalPos());
            if (cell.getIsWormholeEntrance()) {

                let destinationCell = grid.getCellAtLogicalPos(cell.getWormholeDestination());
                if (destinationCell && destinationCell.canPassThrough()) {
                    result.type = EntityDropStepType.Wormhole;
                    result.logicalPos = cell.getWormholeDestination();
                    droped = true;
                }
                // let targetItem = grid.getItemAtLogicalPos(cell.getWormholeDestination());
                // let targetParasite = grid.getParasiteAtLogicalPos(cell.getWormholeDestination());
                // if (!targetItem && !targetParasite) {
                //     result.type = EntityDropStepType.Wormhole;
                //     result.logicalPos = cell.getWormholeDestination();
                //     droped = true;
                // }
            }
        }

        if (droped == false) {
            let checkPos = pos.add(cc.v2(0, -1));
            if (this.canArrive(grid, pos, checkPos)) {
                result.type = EntityDropStepType.Drop;
                result.logicalPos = checkPos;
            }else{
                if (canSlide) {
                    let moveOffsets:cc.Vec2[] = [cc.v2(-1, -1), cc.v2(1, -1),];
                    for (let offset of moveOffsets) {
                        let checkPos = pos.add(offset);
                        let checkBlockPos = checkPos.add(cc.v2(0, 1));

                        let isBlockPosDead = this.isDeadCell(grid, checkBlockPos);
                        let canNotPassThrough = !(this.canPassThrough(grid, checkBlockPos, checkPos));

                        if (isBlockPosDead || canNotPassThrough) {
                            if (this.canArrive(grid, pos, checkPos)) {
                                result.type = EntityDropStepType.Drop;
                                result.logicalPos = checkPos;
                            }
                        }
                    }
                }
            }
        }

        switch (result.type) {
            case EntityDropStepType.Wormhole:
            case EntityDropStepType.Drop: {
                grid.swapItem(pos, result.logicalPos);

                if (item.getPath().length <= 0) {
                    item.addStepToPath(EntityDropStep.create(EntityDropStepType.Stay, pos));
                }
                item.addStepToPath(result);
                let path = item.getPath();
                item.setPixelPos(path[0].logicalPos);
                GameMath.insertAsSet(this._dropedItems, item);

                let cell = grid.getCellAtLogicalPos(pos);
                if (cell.getIsWormholeExit()) {
                    this.tryDropPos(grid, cell.getWormholeSource(), canSlide);
                }
                let affectOffsets:cc.Vec2[] = [cc.v2(0, 1), cc.v2(-1, 1), cc.v2(1, 1)];
                for (let offset of affectOffsets) {
                    this.tryDropPos(grid, pos.add(offset), canSlide);
                }
            }
                break;
        
            default:
                break;
        }
        return droped;
    }
    
    protected dropAllItemsByStep(grid:GridBase, step:number) : boolean {
        this._dropedItems.length = 0;

        let someItemMoved = false;
        let gridSize = grid.getGridSize();
    
        for (let row = 0; row < gridSize.height; row++) {
            for (let column = 0; column < gridSize.width; column++) {
                if (this.tryDropPos(grid, cc.v2(column, row), false)) {
                    someItemMoved = true;
                }
            }
        }

        for (let row = 0; row < gridSize.height; row++) {
            for (let column = 0; column < gridSize.width; column++) {
                if (this.tryDropPos(grid, cc.v2(column, row), true)) {
                    someItemMoved = true;
                }
            }
        }

        let itemsMap = grid.getItemsMap();

        for (let key in itemsMap) {
            let item = itemsMap[key] as ItemBase;
            let pos = item.getLogicalPos();
            if (GameMath.indexOf(this._dropedItems, item) == -1) {
                if (item.getPath().length <= 0) {
                    item.addStepToPath(EntityDropStep.create(EntityDropStepType.Stay, pos));
                }
                item.addStepToPath(EntityDropStep.create(EntityDropStepType.Stay, pos));
            }
        }

        let someItemBorned = false;

        let cellsMap = grid.getCellsMap();
        for (let key in cellsMap) {
            let cell = cellsMap[key] as CellBase;
            let pos = cell.getLogicalPos();
            if (this.canGenerateItemAtPos(grid, pos)) {
                let item = grid.getItemAtLogicalPos(pos);
                if (item) {
                    continue;
                }

                let entityID = cell.nextItemID();
                let bornPos = pos;
                let newItem:ItemBase = ItemFactory.generateItemWithEntityID(grid, entityID);
                if (newItem) {
                    newItem.setVisible(false);
                    someItemBorned = true;
                    grid.setItemAtLogicalPos(newItem, bornPos);

                    for (let i = 0; i <= step; i++) {
                        newItem.addStepToPath(EntityDropStep.create(EntityDropStepType.Hide, bornPos)); 
                    }
                    newItem.addStepToPath(EntityDropStep.create(EntityDropStepType.Born, bornPos));
                }
            } 
        }
        return someItemMoved || someItemBorned;
    }

    protected _stepCount:number = 0;
    private _finishDrop:boolean = false;
    protected runNextStep(grid:GridBase) : void {
        if (this.dropAllItemsByStep(grid, this._stepCount)) {
            this._stepCount++;
        }else{
            cc.log("Finish Drop");
        }
    }

    protected caculatePathForItems(grid:GridBase) : void {
        let itemsMap = grid.getItemsMap();
        for (let key in itemsMap) {
            const item = itemsMap[key];
            item.clearPath();
        }
        
        this.markFences(grid);
        this.clearDeadAndLivingCellRecord(grid);

        let stepCount:number = 0;
        while (1) {
            if (!this.dropAllItemsByStep(grid, stepCount)) {
                break;
            }
            stepCount++;
        }
    }

    protected runPathForAllItems(grid:GridBase) : void {

        let itemsMap = grid.getItemsMap();
        for (let key in itemsMap) {
            let item = itemsMap[key] as ItemBase;
            let pos = item.getLogicalPos();
            item.setCurrentState(ItemStateDrop.create());
            if (item.getCurrentStateType() != EntityStateType.Wait) {
                GameMath.insertAsSet(this._checkMatchPositions, pos);
            }
        }
    }
    
    protected tryDropItemsForStep(grid:GridBase, step:number) : boolean {
        return this.dropAllItemsByStep(grid, step);
    }
    
    public enter(grid:GridBase) {
        grid.unimmuneAllEntities();
        this.markFences(grid);
        this.clearDeadAndLivingCellRecord(grid);
        this._stepCount = 0;
        this._finishDrop = false;
        this.update(grid, 0);
    }

    public update(grid:GridBase, delta:number) {
        if (this._finishDrop == false) {

            let stateDate = new Date();
            let startTime = stateDate.getTime();
            let caculateCount = 0;

            while (1) {
                let currentDate = new Date();
                let timeDelta = currentDate.getTime() - startTime;
                if (timeDelta > 20 && caculateCount > 0) {
                    break;
                }
                caculateCount += 1;
                this._finishDrop = !this.tryDropItemsForStep(grid, this._stepCount);
                
                if (this._finishDrop) {
                    this.runPathForAllItems(grid);
                    break;
                }else{
                    this._stepCount++;
                }
            }
        }else{
            if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
                grid.setCurrentState(GridStateMatch.create(this._checkMatchPositions));
            }
        }
    }
    public exit(grid:GridBase) {
        this._checkMatchPositions = null;
        this._deadCellsPositions = null;
        this._livingCellsPositions = null;
        this._fencesPositions = null;
        this._fencesMap = null;
        this._dropedItems = null;
    }
}
