// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;

@ccclass
export default class CollectedTrainSupplyBar extends cc.Component {

    @property([cc.Node])
    itemNodes: cc.Node[] = [];

    private _gameScene:GameSceneBase = null;
    private _supplyItems:CollectedTrainSupplyItem[] = [];


    public setSupplyData(data:Object) {
        for (const key in data) {
            let amount = data[key]
            let itemID = Number(key) as GameCurrencyType;
            if (itemID) {
                let item = this.getItemWithSupplyItemID(itemID);
                if (item) {
                    item.setAmount(amount);
                }
            }
        }
    }

    public getItemWithSupplyItemID(itemID:GameCurrencyType) : CollectedTrainSupplyItem {
        let result:CollectedTrainSupplyItem = null;
        for (let supplyItem of this._supplyItems) {
            if (supplyItem.getItemID() == itemID) {
                result = supplyItem;
                break;
            }
        }
        return result;
    }

    public getWorldPosWithSupplyItemID(itemID:GameCurrencyType):cc.Vec2 {
        let result = cc.Vec2.ZERO;
        let item = this.getItemWithSupplyItemID(itemID);
        if (item) {
            result = item.node.parent.convertToWorldSpaceAR(item.node.getPosition());
        }
        return result;
    }

    // LIFE-CYCLE CALLBACKS:
    onLoad () {

        let canvas = cc.find("Canvas");
        if (canvas) {
            this._gameScene = canvas.getComponent(GameSceneBase);
        }
        let supplyItemIDs:GameCurrencyType[] = [
            GameCurrencyType.TrainFood,
            GameCurrencyType.TrainSoap,
            GameCurrencyType.TrainEyepatch,
            GameCurrencyType.TrainHoe,
            GameCurrencyType.GoldCoin,
        ];

        for (let index = 0; index < this.itemNodes.length; index++) {
            let itemNode = this.itemNodes[index];
            let supplyItem = itemNode.getComponent(CollectedTrainSupplyItem);
            if (supplyItem) {
                supplyItem.setItemID(supplyItemIDs[index]);
                this._supplyItems.push(supplyItem);
            }
            
        }
    }

    start () {
        
    }

    // update (dt) {}

    onDestroy() {
        this.itemNodes = null;
        this._gameScene = null;
        this._supplyItems = null;
    }
}
