import { RugBase, RugType } from "./RugBase";
import GridBase from "../../grid/GridBase";
import { CollectionID } from "../../defines/CollectionID";
import { EntityID, EntityCleanType, EntityColor, EntityCleanOption } from "../../defines/EntityID";
import FlyerAnimation from "../flyer/FlyerAnimation";
import IGame from "../../../../base/IGame";
import IdentifierManager from "../../defines/IdentifierManager";
import EntityPrefabNodePool from "../EntityPrefabNodePool";

const {ccclass, property} = cc._decorator;

@ccclass
export default class RugJam extends RugBase {
    public static create(grid:GridBase):RugJam{

        let className = 'RugJam';
        let prefabFileName = 'match_scene/prefab/entity/rug/RugJam';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as RugJam;
        if (component) {
            component.init(grid); 
            return component;       
        }
        return null;
    }

    public clone() : RugJam {
        let item = RugJam.create(this.getGrid());
        return item;
    }

    public getCollectionID():CollectionID{
        return CollectionID.RugJam
    }

    public getEntityID():EntityID{
        return EntityID.RugJam;
    }
    public getScore():number{
        return 100;
    }

    public getButterflyPriority(color:EntityColor, layoutGrass:boolean):number{
        if(this.getGrid().isTargetCollectionID(CollectionID.RugJam)){
            return 1;
        }
        return super.getButterflyPriority(color, layoutGrass);
    }

    protected _canCleanByTerminate(cleanColor:EntityColor) : boolean {
        return true;
    }

    public _canCleanByBomb(cleanColor:EntityColor):boolean{
        return true;
    }

    public _canBeFloorClean():boolean{
        return true;
    }

    public _onClean(cleanOption:EntityCleanOption):void{
        this.getGrid().gainScoreWithEntity(this);
        if (!this.isTargetCollection()) {
            let flyer = FlyerAnimation.createDieAnimation(this.getGrid(), this.getEntityID());
            this.getGrid().setFlyerAtLogicalPos(flyer, this.getLogicalPos());
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('jam'));
        }
        this.setVisible(false);
        this.onCollected();
        this.selfDestory(true);
    }

    public grow():void{
        this.playAnimation('grow');
    }

    protected init(grid:GridBase):void{
        super.initRug(grid, RugType.Jam);
    }
}
