import { CollectionID } from "../../defines/CollectionID";
import { EntityID } from "../../defines/EntityID";
import GridBase from "../../grid/GridBase";
import FloorBase, { FloorType } from "./FloorBase";
import EntityPrefabNodePool from "../EntityPrefabNodePool";

export enum FloorRoadDirection{
    Left = 0,
    Up,
    Right,
    Down,
};

const {ccclass, property} = cc._decorator;

@ccclass
export default class FloorRoad extends FloorBase {
    private _isStartPoint:boolean = false;

    private _inDirection:FloorRoadDirection = FloorRoadDirection.Left;
    private _outDirection:FloorRoadDirection = FloorRoadDirection.Up;


    public static create(grid:GridBase, inDirection:FloorRoadDirection, 
                        outDirection:FloorRoadDirection):FloorRoad{
        let className = 'FloorRoad';
        let prefabFileName = 'match_scene/prefab/entity/floor/FloorRoad';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FloorRoad;
        if (component) {
            component.init(grid, inDirection, outDirection); 
            return component;       
        }
        return null;
    }


    public clone():FloorRoad {
        return FloorRoad.create(this.getGrid(),this._inDirection, this._outDirection);
    }

    public init(grid:GridBase, inDircetion:FloorRoadDirection, outDirection:FloorRoadDirection){
        super.initFloor(grid, FloorType.Road);
        this.setInDirection(inDircetion);
        this.setOutDirection(outDirection);
        this.updatePuppet();
    }

    public getCollectionID():CollectionID{
        return CollectionID.FloorRoad;
    }

    public getEntityID():EntityID{
        
        if(this.getIsStartPoint()){
            return EntityID.FloorRoadStartPoint;
        }

        let result = EntityID.None;

        switch(this._inDirection){
            case FloorRoadDirection.Left:{
                switch(this._outDirection){
                    case FloorRoadDirection.Right:{
                        result = EntityID.FloorRoadLineHR;
                        break;
                    }
                    case FloorRoadDirection.Up:{
                        result = EntityID.FloorRoadTurnLuU;
                        break;
                    }
                    case FloorRoadDirection.Down:{
                        result = EntityID.FloorRoadTurnLdD;
                        break;
                    }
                    default:{
                        break;
                    }
                }
            }
            break;
            case FloorRoadDirection.Right:{
                switch(this._outDirection){
                    case FloorRoadDirection.Left:{
                        result = EntityID.FloorRoadLineHL;
                        break;
                    }
                    case FloorRoadDirection.Up:{
                        result = EntityID.FloorRoadTurnRuU;
                        break;
                    }
                    case FloorRoadDirection.Down:{
                        result = EntityID.FloorRoadTurnRdD;
                        break;
                    }
                    default:{
                        break;
                    }
                }
            }
            break;
            case FloorRoadDirection.Up:{
                switch(this._outDirection){
                    case FloorRoadDirection.Left:{
                        result = EntityID.FloorRoadTurnLuL;
                        break;
                    }
                    case FloorRoadDirection.Right:{
                        result = EntityID.FloorRoadTurnRuR;
                        break;
                    }
                    case FloorRoadDirection.Down:{
                        result = EntityID.FloorRoadLineVD;
                        break;
                    }
                    default:{
                        break;
                    }
                }
            }
            break;
            case FloorRoadDirection.Down:{
                switch(this._outDirection){
                    case FloorRoadDirection.Left:{
                        result = EntityID.FloorRoadTurnLdL;
                        break;
                    }
                    case FloorRoadDirection.Right:{
                        result = EntityID.FloorRoadTurnRdR;
                        break;
                    }
                    case FloorRoadDirection.Up:{
                        result = EntityID.FloorRoadLineVU;
                        break;
                    }
                    default:{
                        break;
                    }
                }
            }
            break;
            default:{
                break;
            }
        }
        return result;
    }

    public getIsStartPoint():boolean{
        return this._isStartPoint;
    }

    public setIsStartPoint(val:boolean){
        this._isStartPoint = val;
        this.updatePuppet();
    }

    public getInDirection():FloorRoadDirection{
        return this._inDirection;
    }

    public setInDirection(val:FloorRoadDirection):void{
        this._inDirection = val;
    }

    public getOutDirection():FloorRoadDirection{
        return this._outDirection;
    }

    public setOutDirection(val:FloorRoadDirection):void{
        this._outDirection = val;
    }
}
