export enum FenceType {
    None = 0,
    Vine,
};

const {ccclass, property} = cc._decorator;

@ccclass
export default class FenceBase extends EntityBase {

    private _fenceType:FenceType = FenceType.None;
    public getFenceType() : FenceType {
        return this._fenceType;
    }

    private _fencePos:FencePos = FencePos.create(cc.Vec2.ZERO, cc.v2(0, 1));
    public getFencePos() : FencePos {
        return this._fencePos;
    }
    public setFencePos(fencePos:FencePos) : void {
        this._fencePos = fencePos;

        let pixelPosA = this.getGrid().getPixelPosFromLogicalPos(this._fencePos.posA);
        let pixelPosB = this.getGrid().getPixelPosFromLogicalPos(this._fencePos.posB);
        let center = pixelPosA.add(pixelPosB).mul(0.5);
        this.node.setPosition(center);
        this.node.zIndex = this.getGrid().getBottomZOrder(this.getEntityType(), this._fencePos.posA);
        this.updatePuppet();
    }

    public removeFromGridAndAddToEffect() : void {
        this.removeFromGrid();
        this.node.parent = this.getGrid().node;
        this.setFencePos(this.getFencePos());
        this.node.zIndex = this.getTopZOrder(); 
    }

    protected initFence(grid:GridBase, fenceType:FenceType) {
        super.initEntity(grid, EntityType.Fences, 1, 1);
        this._fenceType = fenceType;
        this._fencePos = FencePos.create(cc.Vec2.ZERO, cc.v2(0, 1));
    }

    onDestroy() {
        super.onDestroy();
        this._fencePos = null;
    }
}
