export enum EntityColor {
    None = -1,
    Begin = 0,
    Red = 0,
    Green,
    Blue,
    Yellow,
    Orange,
    Purple,
    End = Purple,
};

export enum EntityCleanType {
    None,
    Bomb,
    Match,
    Neighbor,
    Floor,
    Popsicle,
    Suicide,
    Activate,
    LayoutGrass,
    Terminate,
};

export class EntityCleanOption {
    public cleanType:EntityCleanType;
    public cleanRoot:cc.Vec2;
    public cleanColor:EntityColor;
    public createBombType:ItemBombType;
    public hitNeighbour:boolean;
    public hitFloor:boolean;
    public layoutGrass:boolean;

    public static create(CleanType:EntityCleanType,  
                         CleanRoot:cc.Vec2,
                         CleanColor:EntityColor = EntityColor.None,
                         CreateBombType:ItemBombType = ItemBombType.None,
                         HitNeighbour:boolean = false,
                         HitFloor:boolean = false,
                         LayoutGrass:boolean = false) : EntityCleanOption {
        return new EntityCleanOption(CleanType, 
                                     CleanRoot, 
                                     CleanColor, 
                                     CreateBombType, 
                                     HitNeighbour,
                                     HitFloor,
                                     LayoutGrass)
    }

    constructor(CleanType:EntityCleanType,  
                CleanRoot:cc.Vec2,
                CleanColor:EntityColor,
                CreateBombType:ItemBombType,
                HitNeighbour:boolean,
                HitFloor:boolean,
                LayoutGrass:boolean) {
        this.cleanType = CleanType;
        this.cleanRoot = CleanRoot;
        this.cleanColor = CleanColor;
        this.createBombType = CreateBombType;
        this.hitNeighbour = HitNeighbour;
        this.hitFloor = HitFloor;
        this.layoutGrass = LayoutGrass;
    }
};

export class EntityChangeOption {
    public targetID : EntityID;
    public collectOld : boolean;

    public static create(TargetID:EntityID, 
                         CollectOld:boolean) : EntityChangeOption {
        return new EntityChangeOption(TargetID, CollectOld);
    }
    
    constructor(TargetID:EntityID, 
                CollectOld:boolean) {
        this.targetID = TargetID;
        this.collectOld = CollectOld;
    }
};

export class EntityChargeOption {
    public chargeID : CollectionID = CollectionID.None;
    public chargeAmount : number = 0;

    public static create(ChargeID:CollectionID, 
                         ChargeAmount:number) : EntityChargeOption {
        return new EntityChargeOption(ChargeID, ChargeAmount);
    }
    
    constructor(ChargeID:CollectionID, 
                ChargeAmount:number) {
        this.chargeID = ChargeID;
        this.chargeAmount = ChargeAmount;
    }
};

export enum EntityDropStepType{
    Hide,
    Born,
    Stay,
    Drop,
    Wormhole,
};

export class EntityDropStep {

    public static create(Type:EntityDropStepType,
                         LogicalPos:cc.Vec2) : EntityDropStep {
        return new EntityDropStep(Type, LogicalPos);
    }

    public type:EntityDropStepType;
    public logicalPos:cc.Vec2;
    constructor(Type:EntityDropStepType,
                LogicalPos:cc.Vec2){
        this.type = Type;
        this.logicalPos = LogicalPos;
    }
}

export class HurtAnimationInfo {
    public fileName:string = '';
    public entityID:EntityID = 0;

    public static create(FileName:string, EntityID:EntityID) : HurtAnimationInfo {
        return new HurtAnimationInfo(FileName, EntityID);
    }
    constructor(FileName:string, EntityID:EntityID){
        this.fileName = FileName;
        this.entityID = EntityID;
    }
}

