import GameConfig from "../../../../define/GameConfig";
import { GameCurrencyType } from "../../../../define/GameCurrencyType";
import { CollectionID } from "../../defines/CollectionID";
import ItemInfoManager from "../../defines/ItemInfoManager";
import GameSceneBase from "../../GameSceneBase";
import { GameType } from "../../statistics/GameStatistics";
import TargetItem from "../TargetItem";

