const {ccclass, property} = cc._decorator;

@ccclass
export default class NoMoreStepBasePanel extends cc.Component {

    @property(cc.Label)
    title:cc.Label = null;

    @property(cc.Node)
    targetFrame:cc.Node = null;

    @property(cc.Node)
    bottomFrame:cc.Node = null;

    @property(cc.Node)
    closeButton:cc.Node = null;

    protected _gameScene:GameSceneBase = null;
    protected _report:Object = {};
    protected _unilimitedItems:Object = {};
    //
    onOpen(param:Object){
        this.initPanel(param);
    }

    protected initPanel(param:Object){
        let canvas = cc.find("Canvas");
        if (canvas) {
            this._gameScene = canvas.getComponent(GameSceneBase);
        }
        this._report = param['report'];
        this.closeButton.on('touchend', this.onCloseButtonTabbed, this);

        this.initLeftTarget();
        this.initLostUnlimitedItems();
    }

    protected initLostUnlimitedItems(){
        let unlimitedItemIDs = ItemInfoManager.getInstance().getUnlimitedItemIDs();
        let usedGears = this._report['usedGear_GN'];
        for (let key in usedGears) {
            let itemID = Number(key) as GameCurrencyType;
            let amount = usedGears[key];
            if(unlimitedItemIDs.indexOf(itemID) != -1){
                this._unilimitedItems[itemID] = amount;
            }
        }
    }

    protected initLeftTarget() {
        if (this._report) {
            let config = this._report['config'];
            let levelType = config['LevelType'];

            switch (levelType) {
                case GameType.Order:
                    this.title.string = '';
                    break;
                case GameType.Popsicle:
                    this.title.string = '';
                break;
                case GameType.Path:
                    this.title.string = '';
                break;
                case GameType.Grass:
                    this.title.string = '';
                break;
            
                default:
                    break;
            }

            let leftTargets = this._report['leftTarget_TN'];
            for (let key in leftTargets) {
                let collectionID = Number(key);
                let amount = leftTargets[key];   
                
                if (collectionID != CollectionID.None) {
                    let needAmount = amount;
                    let showCollectionID = collectionID;
                    if (collectionID == CollectionID.ItemPuppy) {
                        showCollectionID = CollectionID.ItemPawChromatic;
                        needAmount = this._gameScene.getPuppyLeftSteps();
                    }
                    let item = TargetItem.create(showCollectionID, false);
                    item.setAmount(needAmount);
                    this.targetFrame.addChild(item.node);
                }
            }
        }
    }

     //
    onAnimationDone(){
       
    }

    protected onCloseButtonTabbed(event:cc.Event.EventCustom) {
        this.giveUp();
    }

    public getLevel():number{
        return this._gameScene.getLevel();
    }
    public giveUp() {

    }

    public requestBuyMoreStep(currencyID:GameCurrencyType, 
                            price:number, 
                            itemID:GameCurrencyType, 
                            callbackFunc: Function = null, 
                            callbackObj: any = null) {
        this._gameScene.requestBuyMoreStep(currencyID, price, itemID, callbackFunc, callbackObj);
    }
}
