const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerPopsicleLine extends FlyerCannon {
    
    public getEntityID() : EntityID {
        return EntityID.FlyerPopsicelLine;;
    }

    protected updatePuppet():void{
        
    }
    
    public static create(grid:GridBase, 
                         color:EntityColor,
                         layoutGrass:boolean, 
                         origin:cc.Vec2, 
                         direction:cc.Vec2, 
                         hitOrigin:boolean):FlyerPopsicleLine{

        let className = 'FlyerPopsicleLine';
        let prefabFileName = 'match_scene/prefab/entity/flyer/FlyerPopsicleLine';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerPopsicleLine;
        if (component) {
            component.init(grid, color, layoutGrass, origin, direction, hitOrigin); 
            return component;       
        }
        return null;
    }
    public init(grid:GridBase, 
                color:EntityColor,
                layoutGrass:boolean, 
                origin:cc.Vec2, 
                direction:cc.Vec2, 
                hitOrigin:boolean){
        super.initFlyer(grid, FlyerType.PopsicleLine);
        this.setEntityColor(EntityColor.None);
        this.setLayourGrass(layoutGrass);
        this.setOrigin(origin);
        this.setDirection(direction);
        this._hitOrigin = hitOrigin;

        this._speed = 0;
        this._lastCellPostion = cc.v2(-1, -1);
        this._aliveArea = cc.rect(-2000, -2000, 4000, 4000);
        this._launched = false;
        this._startFly = false;   
    }

}
