import GridStateBase, { GridState } from "../GridStateBase";
import GridBase, { GridMode } from "../../GridBase";
import { GameCurrencyType } from "../../../../../define/GameCurrencyType";
import GridCommand from "../../../commond/GridCommand";
import GridStateMatch from "./GridStateMatch";
import { EntityColor } from "../../../defines/EntityID";
import { Pair } from "../../../../../base/Pair";
import FlyerCannon from "../../../entity/flyer/FlyerCannon";
import IGame from "../../../../../base/IGame";
import IdentifierManager from "../../../defines/IdentifierManager";
import BehaviourID, { BehaviourEvent } from "../../../../../extend/identify/behaviourIdentify/BehaviourDefines";

export default class GridStateCrossBomb extends GridStateBase {
    public static create() : GridStateCrossBomb {
        return new GridStateCrossBomb();
    }
    constructor(){
        super(GridState.CrossBomb);
    }

    enter(grid:GridBase):void {
        
    }
    public exit(grid:GridBase) {
        this._targetPos = null;
    }

    public canChangeState() : boolean {
        return true;
    }
    public canPause() : boolean {
        return true;
    }

    private _targetPos:cc.Vec2 = cc.Vec2.ZERO;

    public onTapBegan(grid:GridBase, logicalPos:cc.Vec2) {
        let cell = grid.getCellAtLogicalPos(logicalPos);
        if (cell) {
            this._targetPos = logicalPos;
            grid.requestUseGear(GameCurrencyType.AidBomb, 1);
        }else{
            grid.setMode(GridMode.Normal);
        }
    }

    public onRespone(grid:GridBase, event:Object) : void {

        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('prop_crossbomb'));
        let rayDirections:Pair<cc.Vec2, boolean>[] = [
            new Pair<cc.Vec2, boolean>(cc.v2(1, 0), true),
            new Pair<cc.Vec2, boolean>(cc.v2(-1, 0), false),
            new Pair<cc.Vec2, boolean>(cc.v2(0, 1), false),
            new Pair<cc.Vec2, boolean>(cc.v2(0, -1), false),
        ];

        let layoutGrass = grid.isGrassFloorAtLogicalPos(this._targetPos);
        for (let ray of rayDirections) {
            let cannon = FlyerCannon.create(grid, 
                                            EntityColor.None, 
                                            layoutGrass, 
                                            this._targetPos, 
                                            ray.first, 
                                            ray.second);
            grid.setFlyerAtLogicalPos(cannon, this._targetPos);
        }
  
        
        grid.setCurrentState(GridStateMatch.create());
        grid.setMode(GridMode.Normal);
    }
}