import GridStateBase, { GridState } from "../GridStateBase";
import GridBase from "../../GridBase";

export default class GridStateCongratulation extends GridStateBase {

    public static create() : GridStateCongratulation{
        return new GridStateCongratulation();
    }
    constructor() {
        super(GridState.Congratulation);
    }

    public enter(grid:GridBase) {
        grid.congratulation();
    }
}
