import ItemBase, { ItemType } from "../ItemBase";
import GridBase from "../../../grid/GridBase";
import { CollectionID } from "../../../defines/CollectionID";
import { EntityID, EntityColor, EntityCleanOption } from "../../../defines/EntityID";
import GameMath from "../../../../../extend/utils/GameMath";
import { Pair } from "../../../../../base/Pair";
import FlyerNuclear from "../../flyer/FlyerNuclear";
import IdentifierManager from "../../../defines/IdentifierManager";
import IGame from "../../../../../base/IGame";
import EntityPrefabNodePool from "../../EntityPrefabNodePool";
import EntityBase from "../../EntityBase";


const { ccclass, property } = cc._decorator;

@ccclass
export default class ItemFruitCake extends ItemBase {

    private _pieces: Pair<EntityColor, cc.Node>[] = [];

    public static create(grid: GridBase): ItemFruitCake {

        let className = 'ItemFruitCake';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemFruitCake';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemFruitCake;
        if (component) {
            component.init(grid);
            return component;
        }
        return null;
    }

    public getCollectionID(): CollectionID {
        return CollectionID.ItemFruitCake;
    }

    public getEntityID(): EntityID {
        return EntityID.ItemFruitCake;
    }

    public getScore(): number {
        return 200;
    }

    public getButterflyPriority(color: EntityColor, layoutGrass: boolean): number {
        let hasPiece: boolean = false;
        for (let pari of this._pieces) {
            if (pari.first == color) {
                hasPiece = true;
                break;
            }
        }

        if (hasPiece) {
            return 1;
        }
        return super.getButterflyPriority(color, layoutGrass);
    }

    protected init(grid: GridBase): void {
        super.initItem(grid, ItemType.FruitCake, 2, 2);
        this._pieces.length = 0;
        let availableColors = grid.getAvailableColors();
        let pieceCount = availableColors.length;
        let index = 0;
        for (let color of availableColors) {
            let sprite = this.createPiece(pieceCount, color);
            if (sprite) {
                sprite.setAnchorPoint(cc.v2(0.5, 0.5));
                this._puppet.addChild(sprite);
                let ratation = 360 / pieceCount;
                sprite.angle = -(ratation * index - 90);
                index++;

                // this._pieces[color] = sprite;
                let pair = new Pair<EntityColor, cc.Node>(color, sprite);
                this._pieces.push(pair);
            }
        }
    }

    protected _canDrop(): boolean {
        return false;
    }

    protected _canSwap(): boolean {
        return false;
    }

    protected _canMatch(): boolean {
        return false;
    }

    protected _canCleanByBomb(cleanColor: EntityColor): boolean {
        return true;
    }

    protected _canCleanByNeighbour(color: EntityColor): boolean {
        return true;
    }

    protected _canImmuneDamageAfterHurt(): boolean {
        return false;
    }

    protected _onClean(cleanOption: EntityCleanOption): void {
        let index = GameMath.indexOf(this._pieces, cleanOption, 0, function (pair: Pair<EntityColor, cc.Node>) {
            return pair.first == cleanOption.cleanColor;
        });

        if (index != -1) {
            this.getGrid().gainScoreWithEntity(this);
            if (this._pieces.length <= 1) {

                this.progressDeathWish(cleanOption);
                this.removeFromGridAndAddToEffect();
                this.getGrid().addObstructs(this);
                let action = cc.sequence(cc.scaleTo(0.25, 1.2).easing(cc.easeInOut(2.0)),
                    cc.scaleTo(0.25, 1.0).easing(cc.easeInOut(2.0)),
                    cc.delayTime(0.25),
                    cc.callFunc(() => {



                        let rect = this.getOccupyArea();
                        let shouldLayoutGrass: boolean = false;
                        for (let row: number = 0; row < rect.size.height; row++) {
                            for (let column: number = 0; column < rect.size.width; column++) {
                                if (this.getGrid().isGrassFloorAtLogicalPos(rect.origin.add(cc.v2(column, row)))) {
                                    shouldLayoutGrass = true;
                                    break;
                                }
                            }
                            if (shouldLayoutGrass) {
                                break;
                            }
                        }
                        let bomb = FlyerNuclear.create(this.getGrid(), shouldLayoutGrass, this.getLogicalPos());
                        this.getGrid().setFlyerAtLogicalPos(bomb, this.getLogicalPos());
                        bomb.node.setPosition(this.node.getPosition());
                        bomb.launch();
                        IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('fruitCake'));
                        this.selfDestory();
                    })
                );

                this.runAction(action);
            } else {
                this.selfHurt();
            }

            let piece = this._pieces[index].second;
            let action = cc.sequence(cc.fadeOut(0.33).easing(cc.easeOut(2.0)), cc.callFunc(() => {
                piece.removeFromParent();
            }))
            piece.runAction(action);
            this._pieces.splice(index, 1);
        }
    }

    private createPiece(pieceCount: number, color: EntityColor): cc.Node {
        let result: cc.Node = new cc.Node();

        let frameName = IdentifierManager.getInstance().getFrameNameFromEntityID(EntityID.ItemFruitCake);

        if (frameName != '') {
            frameName = frameName + '_' + pieceCount + '_' + (color + 1);
            let sprite = result.addComponent(cc.Sprite);
            sprite.sizeMode = cc.Sprite.SizeMode.RAW;
            sprite.trim = false;
            sprite.spriteFrame = EntityBase.getEntitySpriteFarme(frameName);
        }
        return result;
    }

    onDestroy() {
        super.onDestroy();
        this._pieces = null;
    }
}
