const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemBombCross extends ItemBombBase {

    public getCollectionID() : CollectionID {
        let result = CollectionID.None;
        switch (this.getEntityColor()) {
            case EntityColor.Red:
                result = CollectionID.ItemBombCrossRed;
                break;
            case EntityColor.Green:
                result = CollectionID.ItemBombCrossGreen;
                break;
            case EntityColor.Blue:
                result = CollectionID.ItemBombCrossBlue;
                break;
            case EntityColor.Yellow:
                result = CollectionID.ItemBombCrossYellow;
                break;
            case EntityColor.Orange:
                result = CollectionID.ItemBombCrossOrange;
                break;
            case EntityColor.Purple:
                result = CollectionID.ItemBombCrossPurple;
                break;
                
            default:
                break;
        }
        return result;
    }
    
    public getEntityID():EntityID {
        
        let result = EntityID.None;
        switch (this.getEntityColor()) {
            case EntityColor.Red:
                result = EntityID.ItemBombCrossRed;
                break;
            case EntityColor.Green:
                result = EntityID.ItemBombCrossGreen;
                break;
            case EntityColor.Blue:
                result = EntityID.ItemBombCrossBlue;
                break;
            case EntityColor.Yellow:
                result = EntityID.ItemBombCrossYellow;
                break;
            case EntityColor.Orange:
                result = EntityID.ItemBombCrossOrange;
                break;
            case EntityColor.Purple:
                result = EntityID.ItemBombCrossPurple;
                break;
                
            default:
                break;
        }
        return result;
    }

    public getProducedTrainSupplyMaps() : Object {
        let result = {};
        result[GameCurrencyType.GoldCoin] = 1;
        result[GameCurrencyType.TrainHoe] = 1;
        return result;
    }

    public getScore():number{
        return 400;
    }

    protected _onClean(cleanOption:EntityCleanOption) {
        super._onClean(cleanOption);
        this.getGrid().gainScoreWithEntity(this);


        if (!this.getIsDud()) {
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_cross'));
            let rayDirections:Pair<cc.Vec2, boolean>[] = [
                new Pair<cc.Vec2, boolean>(cc.v2(1, 1), true),
                new Pair<cc.Vec2, boolean>(cc.v2(-1, -1), false),
                new Pair<cc.Vec2, boolean>(cc.v2(1, -1), false),
                new Pair<cc.Vec2, boolean>(cc.v2(-1, 1), false),
            ];
            let grid = this.getGrid();
            for (let ray of rayDirections) {
                let cannon = FlyerCannon.create(grid, 
                                                this.getEntityColor(), 
                                                this.getShouldLayoutGrass(), 
                                                this.getLogicalPos(), 
                                                ray.first, 
                                                ray.second);
                grid.setFlyerAtLogicalPos(cannon, this.getLogicalPos());
            }

            this.setVisible(false);
        }
    }
    public static create(grid:GridBase, color:EntityColor):ItemBombCross{

        let className = 'ItemBombCross';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemBombCross';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemBombCross;
        if (component) {
            component.init(grid, color); 
            return component;       
        }
        return null;
    }

    public clone():ItemBombCross {
        let item = ItemBombCross.create(this.getGrid(),this.getEntityColor());
        return item;
    }

    protected init(grid:GridBase, entityColor:EntityColor) : void{
        super.initBomb(grid, ItemBombType.Cross);
        this.setEntityColor(entityColor);
    }
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}
}
