import StateBase from "./StateBase";


export default class StateMachineBase {
    protected _owner:Object = null;
    protected _currentState:StateBase = null;
    protected _globalState:StateBase = null;

    constructor (owner:Object) {
        this._owner = owner;
    }

    public setCurrentState(state:StateBase){

        if (this._currentState === state) {
            return;
        }

        if (this._currentState) {
            this._currentState.exit(this._owner);
        }
        this._currentState = state;
        if (this._currentState) {
            this._currentState.enter(this._owner);
        }
    }
    public getCurrentState() : StateBase {
        return this._currentState;
    }
    public setGlobalState(state:StateBase){

        if (this._globalState === state) {
            return;
        }
          
        if (this._globalState) {
            this._globalState.exit(this._owner);
        }
        this._globalState = state;
        if (this._globalState) {
            this._globalState.enter(this._owner);
        }
    }
    public getGlobalState() : StateBase {
        return this._globalState;
    }
  
    public update(delta:number) : void {
        if (this._currentState) {
            this._currentState.update(this._owner, delta);
        }

        if (this._globalState) {
            this._globalState.update(this._owner, delta);
        }
    }
}
