const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerCollectedSupply extends FlyerBase {
    private _itemID:GameCurrencyType = GameCurrencyType.NoneType;
    private _delay:number = 0;
    public getItemID() : GameCurrencyType {
        return this._itemID;
    }
    public setItemID(itemID:GameCurrencyType) {
        this._itemID = itemID;
        this.updatePuppet();
    }

    private _itemAmount:number = 0;
    public getAmount() : number {
        return this._itemAmount;
    }
    public setAmount(amount:number) {
        this._itemAmount = amount;
    }

    public static create(grid:GridBase, 
                         itemID:GameCurrencyType,
                         itemAmount:number,
                         origin:cc.Vec2,
                         delay:number = 0) : FlyerCollectedSupply {

        let className = 'FlyerCollectedSupply';
        let prefabFileName = 'match_scene/prefab/entity/flyer/FlyerCollectedSupply';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerCollectedSupply;
        if (component) {
            component.init(grid, itemID, itemAmount, origin, delay); 
            return component;       
        }
        return null;
    }
    protected init(grid:GridBase, 
                itemID:GameCurrencyType,
                itemAmount:number,
                origin:cc.Vec2,
                delay:number = 0){
        super.initFlyer(grid, FlyerType.CollectedSupply);
        this.setOrigin(origin);
        this.setAmount(itemAmount);
        this.setItemID(itemID);
        this._delay = delay;
    }

    protected updatePuppet():void{
        
        let frameName = '';
        switch (this.getItemID()) {
            case GameCurrencyType.TrainFood:
                frameName = 'match_scene/texture/entities/flyers/90202'
                break;
            case GameCurrencyType.TrainSoap:
                frameName = 'match_scene/texture/entities/flyers/90205'
                break;
            case GameCurrencyType.TrainEyepatch:
                frameName = 'match_scene/texture/entities/flyers/90201'
                break;
            case GameCurrencyType.TrainHoe:
                frameName = 'match_scene/texture/entities/flyers/90204'
                break;
            case GameCurrencyType.GoldCoin:
                frameName = 'match_scene/texture/entities/flyers/90203'
                break;
            default:
                break;
        }

        this.setPuppetSpriteFrameName(frameName);
            
        // let component = this._puppet.getComponent(UISpriteExt)
        // if (component) {
        //     component.src = frameName;
        // }
        
    }
    
    public getCollectionID():CollectionID{
        return CollectionID.None;
    }

    public getEntityID():EntityID{
        return EntityID.FlyerCollectedSupply;
    }

    private _origin:cc.Vec2 = cc.Vec2.ZERO;
    public getOrigin():cc.Vec2 {
        return this._origin;
    }
    public setOrigin(origin:cc.Vec2) : void {
        this._origin = origin;
    }

    public launch() : void {
        
        let startPos = this.getGrid().getPixelPosFromLogicalPos(this._origin);
        let targetWorldPos = this.getGrid().getWorldPosForSupply(this._itemID);
        let targetPos = this.getGrid().node.convertToNodeSpaceAR(targetWorldPos);
        let distance = GameMath.getDistance(startPos, targetPos);
        let speed = 750;
        let targetScale = 1.25;
        let endScale = 1.5;
        if (this._itemID == GameCurrencyType.GoldCoin) {
            targetScale = 1.0;
            endScale = 1.0;
            speed = 1100;
        }

        
        this.getGrid().addFlyerObstructs(this);
        this.node.setPosition(startPos);
        this.node.setScale(0);
        let acitons:cc.FiniteTimeAction[] = []
        let minRadius = Math.round(this.getGrid().getCellSize().width * 0.75);
        let maxRadius = Math.round(this.getGrid().getCellSize().width * 1.5);
        let radius = GameMath.randomInt(minRadius,maxRadius);
        let angle = GameMath.randomInt(0, 360);
        let radian =  angle / 180.0 * Math.PI;
        let jumpTargetPos = cc.v2(startPos.x + radius * Math.cos(radian), startPos.y + radius * Math.sin(radian));
        
        if (this._delay > 0) {
            acitons.push(cc.delayTime(this._delay));
        }
        
        acitons.push(cc.spawn(cc.scaleTo(0.3, targetScale).easing(cc.easeBackOut()),
                              cc.moveTo(0.3, jumpTargetPos).easing(cc.easeOut(2))));
        
        acitons.push(cc.delayTime(0.1));
        let duration = distance / speed;
        
        acitons.push(cc.spawn(CurveToAction.create(duration, jumpTargetPos, targetPos),
                              cc.scaleTo(duration, endScale)));
        acitons.push(cc.callFunc(()=>{
            this.getGrid().updateSupplyBarUIWithDelta(this._itemID, this._itemAmount);
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('coincollect'));
            this.getGrid().removeFlyerObstructs(this);
            this.destoryWithRecycle(true);
        }));
  
        let action = cc.sequence(acitons);
        this.node.runAction(action);
    }

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}

    onDestroy() {
        super.onDestroy();
        this._origin = null;
    }
}
