import { EntityID } from "../../defines/EntityID";
import IdentifierManager from "../../defines/IdentifierManager";
import { KindID } from "../../defines/KindID";
import GridBase from "../../grid/GridBase";
import PopsicleBase from "./PopsicleBase";
import PopsicleNormal from "./PopsicleNormal";
import PopsicleLine from "./PopsicleLine";
import FlyerPopsicleBomb from "../flyer/FlyerPopsicleBomb";
import PopsicleRange from "./PopsicleRange";

export default class PopsicleFactory {
    public static generatePopsicleWithEntityID(grid:GridBase, width:number, height:number, entityID:EntityID) : PopsicleBase {
        let popsicle:PopsicleBase = null;

        switch (entityID) {
            case EntityID.PopsicleNormal:{
                popsicle = PopsicleNormal.create(grid, width, height);
            }
                break;
            case EntityID.PopsicleLine:{
                popsicle = PopsicleLine.create(grid, width, height);
            }
                break;
            case EntityID.PopsicleBoom:{
                popsicle = PopsicleRange.create(grid, width, height);
            }
                break;
                
            default:
                break;
        }
        return popsicle;
    }
    
    public static generatePopsicleWithKindID(grid:GridBase, width:number, height:number, kindID:KindID) : PopsicleBase {
        let entityID = IdentifierManager.getInstance().getEntityIDFromKindID(kindID);
        return this.generatePopsicleWithEntityID(grid, width, height, entityID);
    }
}
