import { CollectionID } from "../../../defines/CollectionID";
import { EntityID } from "../../../defines/EntityID";
import GridBase from "../../../grid/GridBase";
import ItemBase, { ItemType } from "../ItemBase";
import EntityPrefabNodePool from "../../EntityPrefabNodePool";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemDogHouse extends ItemBase {

    public static create(grid:GridBase):ItemDogHouse{

        let className = 'ItemDogHouse';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemDogHouse';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemDogHouse;
        if (component) {
            component.init(grid); 
            return component;       
        }
        return null;
    }

    public clone():ItemDogHouse {
        let item = ItemDogHouse.create(this.getGrid());
        return item;
    }

    public getCollectionID():CollectionID{
        return CollectionID.ItemDogHouse;
    }

    public getEntityID():EntityID{
        return EntityID.ItemDogHouse;
    }

    protected init(grid:GridBase){
        super.initItem(grid, ItemType.DogHouse);
    }

    protected _canDrop():boolean{
        return  false;
    }

    protected _canSwap():boolean{
        return false;
    }

    protected _canMatch():boolean{
        return false;
    }

    protected _converUnderForever():boolean{
        return true;
    }

}
