const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerChargeEntity extends FlyerBase {

    private _collectionID:CollectionID = CollectionID.None;

    public static create(grid:GridBase, 
                         collectionID:CollectionID, 
                         origin:cc.Vec2,
                         targetPos:cc.Vec2) : FlyerChargeEntity {

        let className = 'FlyerChargeEntity';
        let prefabFileName = 'match_scene/prefab/entity/flyer/FlyerChargeEntity';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerChargeEntity;
        if (component) {
            component.init(grid, collectionID, origin, targetPos); 
            return component;       
        }
        return null;
    }
    protected init(grid:GridBase, 
                collectionID:CollectionID, 
                origin:cc.Vec2,
                targetPos:cc.Vec2){
        super.initFlyer(grid, FlyerType.Charger);
        this.setOrigin(origin);
        this.setTargetPos(targetPos);
        this._collectionID = collectionID;
        this.updatePuppet();
    }

    protected updatePuppet():void{
        let frameName = IdentifierManager.getInstance().getFrameNameFromCollectionID(this._collectionID);
        let component = this._puppet.getComponent(UISpriteExt)
        if (component) {
            component.src = frameName;
        }
    }
    
    public getCollectionID():CollectionID{
        return CollectionID.None;
    }

    public getEntityID():EntityID{
        return EntityID.FlyerChargeEntity;
    }

    private _origin:cc.Vec2 = cc.Vec2.ZERO;
    public getOrigin():cc.Vec2 {
        return this._origin;
    }
    public setOrigin(origin:cc.Vec2) : void {
        this._origin = origin;
    }

    private _targetPos:cc.Vec2 = cc.Vec2.ZERO;
    public getTargetPos():cc.Vec2 {
        return this._targetPos;
    }
    public setTargetPos(target:cc.Vec2) : void {
        this._targetPos = target;
    }

    public launch() : void {
        
        let startPos = this.getGrid().getPixelPosFromLogicalPos(this._origin);
        let targetPos = this.getGrid().getPixelPosFromLogicalPos(this._targetPos);
        let distance = GameMath.getDistance(startPos, targetPos);
        let speed = 600;
        let duration = distance / speed;

        this.node.setPosition(startPos);
        this.getGrid().addObstructs(this);
        let move = cc.spawn(CurveToAction.create(duration, startPos, targetPos).easing(cc.easeOut(2)),
                 cc.scaleTo(duration, 0.35))
        let callback = cc.callFunc(()=>{
            this.getGrid().dispatchCommand(this._targetPos, GridCommand.Charge(this._collectionID, 1), 0);
            this.getGrid().removeObstructs(this);
            this.destoryWithRecycle(true);
        })

        let action = cc.sequence(move, callback);
        this.node.runAction(action);

    }

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    onDestroy() {
        super.onDestroy();
        this._origin = null;
        this._targetPos = null;
    }
}
