import GridStateBase, { GridState } from "../GridStateBase";
import GridBase from "../../GridBase";
import GameMath from "../../../../../extend/utils/GameMath";
import GridStateStalemate from "./GridStateStalemate";
import ItemStateShuffle from "../../../entity/item/state/ItemStateShuffle";
import GridStateSwapNeighbour from "./GridStateSwapNeighbour";
import ItemBase from "../../../entity/item/ItemBase";

export default class GridStateShuffle extends GridStateBase {
    public static create() : GridStateShuffle {
        return new GridStateShuffle();
    }

    constructor() {
        super(GridState.Shuffle);
    }

    public enter(grid:GridBase) {
        let positions:cc.Vec2[] = [];
        let itemsMap = grid.getItemsMap();

        for (let key in itemsMap) {
            let item = itemsMap[key] as ItemBase;
            positions.push(item.getLogicalPos());
        }
        
        let result = this.shuffleGrid(grid,positions);
        
        if (result.length <= 0) {
            grid.setCurrentState(GridStateStalemate.create());
        }else{

            for (let info of result) {
                let item = info.first;
                let swapPair = info.second;
                let fromPos = swapPair.first;
                let toPos = swapPair.second;
                item.setCurrentState(ItemStateShuffle.create(fromPos, toPos));
            }
        }
    }

    public update(grid:GridBase, delta:number) {
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            grid.setCurrentState(GridStateSwapNeighbour.create());
        }
    }
}
