import GridBase from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateChanger from "./GridStateChanger";
import GridStateDrop from "./GridStateDrop";
import { PowerID } from "../../../defines/PowerID";
import CellBase from "../../../entity/cell/CellBase";
import GameMath from "../../../../../extend/utils/GameMath";

export default class GridStateConveyer extends GridStateBase {

    public static create() : GridStateConveyer{
        return new GridStateConveyer();
    }
    constructor() {
        super(GridState.Conveyer);
    }

    private _conveyerPosions:cc.Vec2[] = [];

    public enter(grid:GridBase) {
        if (grid.getPowerAmount(PowerID.ConveyerMove)) {
            let cells:CellBase[] = [];
            let cellsMap = grid.getCellsMap();
            for (const key in cellsMap) {
                let cell = cellsMap[key];
                cells.push(cell);
            }

            for (let cell of cells) {
                let conveyPos = cell.onConvey();
                if (!conveyPos.equals(GameMath.INVALID_LOGICAL_POSITION)) {
                    GameMath.insertAsSet(this._conveyerPosions, conveyPos);
                }
            }

            grid.lossPower(PowerID.ConveyerMove, 1);

            if (this._conveyerPosions.length <= 0) {
                grid.setCurrentState(GridStateChanger.create());
            }
        }else{
            grid.setCurrentState(GridStateChanger.create());
        }
    }

    public update(grid:GridBase, delta:number) {
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            grid.setCurrentState( GridStateDrop.create(this._conveyerPosions));
        }
    }
    public exit(grid:GridBase) {
        this._conveyerPosions = null;
    }
}
