const {ccclass, property} = cc._decorator;

@ccclass
export default class UINodeIdentifier extends cc.Component {
    @property
    identifier: string = '';

    private _identifier:string = '';
    onLoad(){
        if (this._identifier == '') {
            this.setIdentifier(this.identifier);    
        }
    }

    onDestroy(){
        this.setIdentifier('');
    }

    public setIdentifier(identifier:string) {
        IdentifyNodeManager.getInstance().logout(this._identifier);
        this._identifier = identifier;
        IdentifyNodeManager.getInstance().regist(this._identifier, this.node);
    }

    public getIdentifier() : string {
        return this._identifier
    }
}
