import { PowerID } from "../../../defines/PowerID";
import GridBase from "../../GridBase";
import GridStateBase, { GridState } from "../GridStateBase";
import GridStateAutoActivateBomb from "./GridStateAutoActivateBomb";
import ItemBase from "../../../entity/item/ItemBase";
import { EntityColor } from "../../../defines/EntityID";
import GameMath from "../../../../../extend/utils/GameMath";
import { GameCurrencyType } from "../../../../../define/GameCurrencyType";
import { ItemBombType } from "../../../entity/item/color/bomb/ItemBombBase";
import ItemFactory from "../../../entity/item/ItemFactory";
import ItemStateReplace from "../../../entity/item/state/ItemStateReplace";



export default class GridStateLaunchGears extends GridStateBase {

    public static create() : GridStateLaunchGears {
        return new GridStateLaunchGears();
    }
    constructor() {
        super(GridState.LaunchGears);
    }

    private _availablePositions:cc.Vec2[] = [];
    public enter(grid:GridBase) {

        if (grid.getPowerAmount(PowerID.LaunchGears) > 0) {
            grid.setPowerAmount(PowerID.LaunchGears, 0);

            let itemsMap = grid.getItemsMap();
            for (let key in itemsMap) {
                let item = itemsMap[key] as ItemBase;
                if (item.canUpgradeByRainbow()) {
                    this._availablePositions.push(item.getLogicalPos());
                }
            }
            GameMath.shuffleArray(this._availablePositions);
            let gearMap = grid.getLaunchGearMap();

            for (let key in gearMap) {
                let gearID = Number(key) as GameCurrencyType;
                let amount = gearMap[key];
                this.progressGear(grid, gearID, amount);
            }
            
            grid.clearLaunchGears();
        }else{
            grid.setCurrentState(GridStateAutoActivateBomb.create());
        }
    }
    
    public update(grid:GridBase, delta:number) {
        if (grid.isObstructsEmpty() && grid.isCommandListEmpty()) {
            grid.setCurrentState(GridStateAutoActivateBomb.create());
        }
    }

    public exit(grid:GridBase) {
        this._availablePositions = null;
    }

    public progressGear(grid:GridBase, gearID:GameCurrencyType, amount:number) : void {
        for (let index = 0; index < amount; index++) {
            switch (gearID) {
                case GameCurrencyType.BeginLine:
                case GameCurrencyType.InfiniteBeginLine:
                case GameCurrencyType.OneRoundBeginLine:
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Row);
                    this.createBomb(grid, ItemBombType.Column);
                    break;
                case GameCurrencyType.BeginRange:
                case GameCurrencyType.InfiniteBeginRange:
                case GameCurrencyType.OneRoundBeginRange:
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Range);
                    this.createBomb(grid, ItemBombType.Cross);
                    break;
                case GameCurrencyType.BeginRainbow:
                case GameCurrencyType.InfiniteBeginRainbow:
                case GameCurrencyType.OneRoundBeginRainbow:
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Rainbow);
                    break;
                case GameCurrencyType.ExtraStep1:
                case GameCurrencyType.ExtraStep2:
                case GameCurrencyType.ExtraStep3:
                case GameCurrencyType.ExtraStep5:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createExtraStep(grid, gearID); 
                }
                    break;
                case GameCurrencyType.LuckyWheelRow:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Row);
                }   break;
                case GameCurrencyType.LuckyWheelColumn:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Column);
                }   break;
                case GameCurrencyType.LuckyWheelCross:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Cross);
                }   break;
                case GameCurrencyType.LuckyWheelRange:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Range);
                }   break;
                case GameCurrencyType.LuckyWheelMissile_Row:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Row);
                    this.createBomb(grid, ItemBombType.Butterfly);
                }   break;
                case GameCurrencyType.LuckyWheelMissile_Column:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Column);
                    this.createBomb(grid, ItemBombType.Butterfly);
                }   break;
                case GameCurrencyType.LuckyWheelMissile_Cross:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Cross);
                    this.createBomb(grid, ItemBombType.Butterfly);
                }   break;
                case GameCurrencyType.LuckyWheelMissile_Range:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Range);
                    this.createBomb(grid, ItemBombType.Butterfly);
                }   break;
    
                case GameCurrencyType.LuckyWheelRow_Cross:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Row);
                    this.createBomb(grid, ItemBombType.Cross);
                }   break;
                case GameCurrencyType.LuckyWheelColumn_Cross:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Column);
                    this.createBomb(grid, ItemBombType.Cross);
                }   break;
                case GameCurrencyType.LuckyWheelRow_Range:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Row);
                    this.createBomb(grid, ItemBombType.Range);
                }   break;
                case GameCurrencyType.LuckyWheelColumn_Range:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Column);
                    this.createBomb(grid, ItemBombType.Range);
                }   break;
                case GameCurrencyType.LuckyWheelMissile_Row_Column:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Butterfly);
                    this.createBomb(grid, ItemBombType.Row);
                    this.createBomb(grid, ItemBombType.Column);
                }   break;
                case GameCurrencyType.LuckyWheelMissile_Rainbow:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Butterfly);
                    this.createBomb(grid, ItemBombType.Rainbow);
                }   break;
                case GameCurrencyType.LuckyWheelMissile_Row_Cross:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Butterfly);
                    this.createBomb(grid, ItemBombType.Row);
                    this.createBomb(grid, ItemBombType.Cross);
                }   break;
                case GameCurrencyType.LuckyWheelMissile_Column_Range:{
                    grid.setUsedGearAmount(gearID, grid.getUsedGearAmount(gearID) + 1);
                    this.createBomb(grid, ItemBombType.Butterfly);
                    this.createBomb(grid, ItemBombType.Column);
                    this.createBomb(grid, ItemBombType.Range);
                }   break;
    
                default:
                    break;
            }
        }
    }

    private createBomb(grid:GridBase, bombType:ItemBombType) {
        if (this._availablePositions.length > 0) {
            let targetPos = this._availablePositions[0];
            let color = EntityColor.None;
            if (bombType != ItemBombType.Rainbow) {
                let item = grid.getItemAtLogicalPos(targetPos);
                color = item.getEntityColor();
            }

            let bomb = ItemFactory.generateEffectBombItem(grid, bombType, color);
            if (bomb) {
                this._availablePositions.splice(0, 1); 
                bomb.node.setPosition(cc.v2(-100, grid.node.getContentSize().height * 0.5));
                grid.node.addChild(bomb.node, grid.getTopZOrder(bomb.getEntityType(), targetPos));
                bomb.setCurrentState(ItemStateReplace.create(targetPos));
            }
        }
    }

    private createExtraStep(grid:GridBase, itemID:GameCurrencyType) {
        let startPos = cc.v2(-2, Math.round(grid.getGridSize().height / 2));
        grid.gainStepWithItemID(itemID, true, startPos);
    }
}
