import EntityBase from "../entity/EntityBase";
import EntityEventBase, { EntityEventType } from "./EntityEventBase";

export default class EntityEventChanged extends EntityEventBase {

   private _entity:EntityBase;
   public getEntity() : EntityBase {
       return this._entity;
   }
   public static create(entity:EntityBase) : EntityEventChanged {
       return new EntityEventChanged(entity);
   }
   constructor(entity:EntityBase) {
       super(EntityEventType.EntityChanged);
       this._entity = entity;
   }
}
