import { CollectionID } from "../../../defines/CollectionID";
import { EntityID } from "../../../defines/EntityID";
import GridBase from "../../../grid/GridBase";
import ItemBase, { ItemType } from "../ItemBase";
import EntityPrefabNodePool from "../../EntityPrefabNodePool";

export enum PuppyDirection{
    None,
    Left,
    Right,
    Front,
    Back,
};

const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemPuppy extends ItemBase {
    
    public static create(grid:GridBase):ItemPuppy{

        let className = 'ItemPuppy';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemPuppy';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemPuppy;
        if (component) {
            component.init(grid); 
            return component;       
        }
        return null;
    }

    public clone() : ItemPuppy {
        let item = ItemPuppy.create(this.getGrid());
        return item;
    }

    public getTopZOrder() : number {
        return GridBase.ZORDER_PUPPY_TOP;
    }
    public getBottomZOrder() : number {
        return GridBase.ZORDER_PUPPY_BOTTOM;
    }

    public getCollectionID():CollectionID{
        return CollectionID.ItemPuppy;
    }

    public getEntityID():EntityID{
        return EntityID.ItemPuppy;
    }

    public setLogicalPos(pos:cc.Vec2):void{
        super.setLogicalPos(pos);
        this.node.zIndex = GridBase.ZORDER_PUPPY_BOTTOM;
    }

    public onCollected():void{
        if (this.getGrid().isTargetCollectionID(this.getCollectionID())) {
            this.getGrid().updateTargetBarUIWithDelta(this.getCollectionID(), -1);
        }
        this.getGrid().collectEntity(this);
    }

    protected init(grid:GridBase){
        super.initItem(grid, ItemType.Puppy);
    }

    protected _canDrop():boolean{
        return false;
    }

    protected _canSwap():boolean{
        return false;
    }

    protected _canMatch():boolean{
        return false;
    }

    public stay():void{
        this.playAnimation('item_puppy_idle');
    }

    public attack(direction:PuppyDirection):void{
        let animationName = 'item_puppy_idle';
        switch(direction){
            case PuppyDirection.Left:{
                animationName = 'item_puppy_attack_left';
                break;
            }
            case PuppyDirection.Right:{
                animationName = 'item_puppy_attack_right';
                break;
            }
            case PuppyDirection.Front:{
                animationName = 'item_puppy_attack_front';
                break;
            }
            case PuppyDirection.Back:{
                animationName = 'item_puppy_attack_back';
                break;
            }
            default:{
                break;
            }
        }

        this.playAnimation(animationName);
    }

    public move(direction:PuppyDirection):void{
        let animationName = 'item_puppy_idle';
        switch(direction){
            case PuppyDirection.Left:{
                animationName = 'item_puppy_move_left';
                break;
            }
            case PuppyDirection.Right:{
                animationName = 'item_puppy_move_right';
                break;
            }
            case PuppyDirection.Front:{
                animationName = 'item_puppy_move_front';
                break;
            }
            case PuppyDirection.Back:{
                animationName = 'item_puppy_move_back';
                break;
            }
            default:{
                break;
            }
        }
        
        if (this.getCurrentAnimationClipName() != animationName) {
            this.playAnimation(animationName);
        }
    }

    // public turn(direction:PuppyDirection):void{
    //     let animationName = 'item_puppy_idle';
    //     switch(direction){
    //         case PuppyDirection.Front:{
    //             animationName = 'backToFront';
    //             break;
    //         }
    //         case PuppyDirection.Back:{
    //             animationName = 'frontToBack';
    //             break;
    //         }
    //         default:{
    //             break;
    //         }
    //     }
    // }

    public fly():void{
        this.playAnimation('item_puppy_fly');
    }

    public win():void{
        this.playAnimation('item_puppy_win');
    }
}
