import EntityStateBase, { EntityStateType } from "../../state/EntityStateBase";
import EntityBase from "../../EntityBase";
import EntityStateWait from "../../state/concreteState/EntityStateWait";
import EntityEventChanged from "../../../event/EntityEventChanged";
import GridBase from "../../../grid/GridBase";
import FlyerAnimation, { FlyerAnimationType } from "../../flyer/FlyerAnimation";

export default class ItemStateGenerate extends EntityStateBase {

    private _targetPos:cc.Vec2 = cc.Vec2.ZERO;
    
    public static create(pos:cc.Vec2) : ItemStateGenerate {
        return new ItemStateGenerate(pos);
    }
    constructor(pos:cc.Vec2) {
        super(EntityStateType.Generate);
        this._targetPos = pos;
    }

    enter(entity:EntityBase):void {
        let grid = entity.getGrid();
        let pixelTargetPos = entity.getPixelPos(this._targetPos);
        entity.node.setPosition(pixelTargetPos);
        entity.node.setScale(0);
        grid.addObstructs(entity);
        entity.node.zIndex = entity.getTopZOrder();

        let flyer = FlyerAnimation.createAnimationWithType(entity.getGrid(), FlyerAnimationType.CommonHurt);
        grid.setFlyerAtLogicalPos(flyer, this._targetPos);
        flyer.node.zIndex = entity.getBottomZOrder() - 499;
        let sequence = cc.sequence(cc.scaleTo(0.3, 1.0).easing(cc.easeBackOut()),
                                   cc.callFunc(()=> {
                                        grid.setEntity(entity, this._targetPos);
                                        entity.setCurrentState(EntityStateWait.create());
                                        let changedEvent = EntityEventChanged.create(entity);
                                        entity.node.dispatchEvent(changedEvent);
                                        entity.getGrid().removeObstructs(entity);
                                        entity.node.zIndex = GridBase.ZORDER_ITEM_BOTTOM;
                                   }));
        entity.runAction(sequence);
    }

    update(entity:EntityBase, delta:number):void {
        
    }
    
    exit(entity:EntityBase):void {
        entity.getGrid().removeObstructs(entity);
    }
}
