import GridStateBase, { GridState } from "../GridStateBase";
import GridBase from "../../GridBase";


export default class GridStateNoMoreStep extends GridStateBase {

    public static create() : GridStateNoMoreStep {
        return new GridStateNoMoreStep();
    }
    constructor() {
        super(GridState.NoMoreStep);
    }
    
    public enter(grid:GridBase) {
        grid.noMoreStep();
    }
}
