// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerBullet extends FlyerBase {

    private _duration:number = 0;
    private _startPos:cc.Vec2 = cc.Vec2.ZERO;
    private _targetPos:cc.Vec2 = cc.Vec2.ZERO;
    private _bombType:ItemBombType = ItemBombType.None;
    private _launched:boolean = false;

    public getEntityID():EntityID {
        return EntityID.FlyerLaserBullet;
    }

    public launch() {
        if (this._launched) {
            return;
        }
        this._launched = true;

        if (this._bombType != ItemBombType.None) {
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_color'));    
        }else{
            IGame.playAudio(IdentifierManager.getInstance().getMatchAudioFilePath('eliminate_special_colorline'));    
        }
        
        let grid = this.getGrid();
        let startPos = this.getPixelPos(this._startPos);
        let targetPos = grid.getPixelPosFromLogicalPos(this._targetPos);
        this.setPixelPos(this._startPos);

        this.node.scale = 0.25;
        let move = cc.spawn(cc.moveTo(this._duration, targetPos).easing(cc.easeInOut(2)),
                            cc.scaleTo(this._duration, 1.0));
        let callback = cc.callFunc(()=>{
            if (this._bombType != ItemBombType.None) {
                
                grid.highlightedCell(this._targetPos);
                let item = grid.getItemAtLogicalPos(this._targetPos);
                if (item && item.canUpgradeByRainbow()) {
                    
                    let bombType = this._bombType;
                    if (this._bombType == ItemBombType.Row || this._bombType == ItemBombType.Column) {
                        if (GameMath.randomInt(0, 100) < 50) {
                            bombType = ItemBombType.Row;
                        }else{
                            bombType = ItemBombType.Column;
                        }
                    }
                    let item = ItemFactory.generateEffectBombItem(grid, bombType, this.getEntityColor());
                    if (item) {
                        grid.dispatchCommand(this._targetPos,GridCommand.Change(item.getEntityID(), true),0);    
                    }
                }
            }

            grid.removeObstructs(this);
            this.destoryWithRecycle(true);
        });
        this.runAction(cc.sequence(move, callback));
    }
    
    public static create(grid:GridBase, 
                         duration:number, 
                         startPos:cc.Vec2, 
                         targetPos:cc.Vec2,
                         color:EntityColor, 
                         bombType:ItemBombType,
                         layoutGrass:boolean):FlyerBullet{

        let className = 'FlyerBullet';
        let prefabFileName = 'match_scene/prefab/entity/flyer/FlyerBullet';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerBullet;
        if (component) {
            component.init(grid, duration, startPos, targetPos, color, bombType, layoutGrass); 
            return component;       
        }
        return null;
    }

    public init(grid:GridBase,
                duration:number,  
                startPos:cc.Vec2, 
                targetPos:cc.Vec2,
                color:EntityColor, 
                bombType:ItemBombType,
                layoutGrass:boolean){
        super.initFlyer(grid, FlyerType.LasetBullet);
        this.setEntityColor(color)

        this._duration = duration;
        this._startPos = startPos;
        this._targetPos = targetPos;
        this._bombType = bombType;
        this._launched = false;
    }
}
