const {ccclass, property} = cc._decorator;

@ccclass
export default class FlyerScore extends FlyerBase {

    private _score = 0;
    private _pixelPos:cc.Vec2 = cc.v2(0, 0)
    public static create(grid:GridBase, pixelPos:cc.Vec2, score:number, needObstruct:boolean = false, autoRemove:boolean = true):FlyerScore{

        let className = 'FlyerScore';
        let prefabFileName = 'match_scene/prefab/entity/flyer/FlyerScore';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as FlyerScore;
        if (component) {
            component.init(grid, pixelPos, score, needObstruct, autoRemove); 
            return component;       
        }
        return null;
    }

    public init(grid:GridBase, pixelPos:cc.Vec2, score:number, needObstruct:boolean = false, autoRemove:boolean){
        super.initFlyer(grid, FlyerType.Score);
        this._score = score;
        this._pixelPos = pixelPos;
        this.setAutoRemoveOnFinish(autoRemove);
        this._needObstruct = needObstruct;
    }

    private _needObstruct = false;
    private _autoRemoveOnFinish:boolean = false;
    public getAutoRemoveOnFinish():boolean {
        return this._autoRemoveOnFinish;
    }
    public setAutoRemoveOnFinish(auto:boolean) : void {
        this._autoRemoveOnFinish = auto;
    }

    public getEntityID() : EntityID {
        return EntityID.None;
    }

    public launch() {

        this.getGrid().addFlyerObstructs(this);
        this.node.setPosition(this._pixelPos);
        let speed = 800;
        let scoreWorldPos = this.getGrid().getWorldPosForScore();
        let targetPos = this.node.parent.convertToNodeSpaceAR(scoreWorldPos);
        let startPos = this.node.getPosition();
        let duration = GameMath.getDistance(startPos, targetPos) / speed

        let move = cc.spawn(CurveToAction.create(duration, startPos, targetPos),
                            cc.fadeTo(duration, 92));
        let action = cc.sequence(move,
                                cc.callFunc(()=>{
                                    this.getGrid().updateScoreBarUIWithDelta(this._score);
                                    this.getGrid().removeFlyerObstructs(this);
                                    this.destoryWithRecycle(true);
                                }));
        this.runAction(action);
        
    }
}
