    private _mode: GridMode = GridMode.Normal;
    public getMode(): GridMode {
        return this._mode;
    }
    public setMode(mode: GridMode) {
        this._mode = mode;
        this._gameScene.setGridMode(mode);
    }
    public tryEnableMode(mode: GridMode): boolean {
        this._mode = mode;
        if (this.getCurrentState().canChangeState()) {
            switch (mode) {
                case GridMode.Normal: {
                    this.setCurrentState(GridStateSwapNeighbour.create());
                    return true;
                }
                    break;
                case GridMode.ForceClean: {
                    this.setCurrentState(GridStateForceClean.create());
                    return true;
                }
                    break;
                case GridMode.ForceSwap: {
                    this.setCurrentState(GridStateForceSwap.create());
                    return true;
                }
                    break;
                case GridMode.CrossBomb: {
                    this.setCurrentState(GridStateCrossBomb.create());
                    return true;
                }
                    break;

                default:
                    break;
            }
        }
        return false;
    }

    /**
     * 
     * @param pixelPos 
     */
    public getLogicalPosFromPixelPos(pixelPos: cc.Vec2): cc.Vec2 {

        let contentSize = this.node.getContentSize();
        let origin = cc.Vec2.ZERO;

        let column = Math.floor((pixelPos.x - origin.x) / this._cellSize.width);
        let row = Math.floor((pixelPos.y - origin.y) / this._cellSize.height)
        return cc.v2(column, row);
    }
    /**
     * 
     * @param logicalPos 
     */
    public getPixelPosFromLogicalPos(logicalPos: cc.Vec2): cc.Vec2 {
        let origin = cc.v2(this._cellSize.width * 0.5, this._cellSize.height * 0.5)
        return cc.v2(origin.x + logicalPos.x * this._cellSize.width,
            origin.y + logicalPos.y * this._cellSize.height);
    }

    public getBottomZOrder(entityType: EntityType, logicalPos: cc.Vec2): number {
        let baseZorder = 0;
        switch (entityType) {
            case EntityType.Frame:
                baseZorder = GridBase.ZORDER_FRAME_BOTTOM;
                break;
            case EntityType.Cell:
                baseZorder = GridBase.ZORDER_CELL_BOTTOM;
                break;
            case EntityType.Popsicle:
                baseZorder = GridBase.ZORDER_POPSICLES_BOTTOM;
                break;
            case EntityType.Floor:
                baseZorder = GridBase.ZORDER_FLOOR_BOTTOM;
                break;
            case EntityType.Rug:
                baseZorder = GridBase.ZORDER_RUG_BOTTOM;
                break;
            case EntityType.Item:
                baseZorder = GridBase.ZORDER_ITEM_BOTTOM;
                break;
            case EntityType.Parasite:
                baseZorder = GridBase.ZORDER_PARASITE_BOTTOM;
                break;
            case EntityType.Fences:
                baseZorder = GridBase.ZORDER_FENCE_BOTTOM;
                break;
            case EntityType.Flyer:
                baseZorder = GridBase.ZORDER_FLYER_BOTTOM;
                break;
            case EntityType.Marker:
                baseZorder = GridBase.ZORDER_MARKER_BOTTOM;
                break;
            default:
                break;
        }
        return baseZorder + (this._gridSize.height - logicalPos.y) * this._gridSize.width + (this._gridSize.width - logicalPos.x);
    }
    public getBottomZOrderForEnity(entity: EntityBase): number {
        return this.getBottomZOrder(entity.getEntityType(), entity.getLogicalPos())
    }
