    public swapItem(logicalPosA: cc.Vec2, logicalPosB: cc.Vec2): void {

        if (logicalPosA.equals(logicalPosB)) {
            return;
        }

        let itemA = this.getItemAtLogicalPos(logicalPosA);
        let itemB = this.getItemAtLogicalPos(logicalPosB);

        if (itemA == null && itemB == null) {
            return;
        }

        this.removeItemAtLogicalPos(logicalPosA);
        this.removeItemAtLogicalPos(logicalPosB);

        if (itemA) {
            this.setItemAtLogicalPos(itemA, logicalPosB);
        }

        if (itemB) {
            this.setItemAtLogicalPos(itemB, logicalPosA);
        }
    }
    public generateBomb(targetPos: cc.Vec2, bombType: ItemBombType, color: EntityColor, changeOld: boolean = false): void {
        let bomb = ItemFactory.generateEffectBombItem(this, bombType, color);
        if (bomb) {
            bomb.setPixelPos(targetPos);
            bomb.setImmuneDamage(true);
            this._itemLayer.addChild(bomb.node, this.getTopZOrder(bomb.getEntityType(), targetPos));
            if (changeOld) {
                bomb.setCurrentState(ItemStateReplace.create(targetPos));
            } else {
                bomb.setCurrentState(ItemStateGenerate.create(targetPos));
            }
        } else {
            // cc.log('Can not generate bomb');
        }
    }
