    public setFenceAtFencePos(fence: FenceBase, fencePos: FencePos): void {
        if (!fence) {
            return;
        }
        this.removeFenceAtFencePos(fencePos);
        let key = GameMath.getStringFromFencePos(fencePos);
        this._fencesMap[key] = fence;
        fence.node.removeFromParent(false);
        this._fenceLayer.addChild(fence.node);
        fence.setFencePos(fencePos);
    }
    public removeFenceAtFencePos(fencePos: FencePos): void {
        let fence = this.getFenceAtFencePos(fencePos);
        if (fence) {
            let key = GameMath.getStringFromFencePos(fencePos);
            delete this._fencesMap[key];
            fence.node.removeFromParent();
        }
    }
