    private _commandPairs: GridCommandPair[] = [];
    private _nextCommandIndex = 0;
    private _insertNewCommand: boolean = false;
    public dispatchCommand(pos: cc.Vec2, command: GridCommand, delayTime: number): void {
        if (pos.x < 0 || pos.x >= this._gridSize.width || pos.y < 0 || pos.y >= this._gridSize.height) {
            return;
        }

        if (this._usePriorityCommandQueue) {
            let insertCommand = GridCommandPair.create(pos, command, delayTime, this._nextCommandIndex);
            let index = this._priorityCommandQueue.indexOf(insertCommand);
            if (index == -1) {
                this._priorityCommandQueue.push(insertCommand);
                this._nextCommandIndex++;
            }
        } else {
            if (command.type == GridCommandType.MatchClean) {
                let index = GameMath.indexOf(this._commandPairs, command, 0, function (lhs: GridCommandPair, rhs: GridCommand) {
                    if (lhs.command.type == rhs.type) {
                        if (pos.equals(lhs.targetPos)) {
                            return true;
                        }
                    }
                    return false;
                });
                if (index != -1) {
                    return;
                }
            }

            let insertCommand = GridCommandPair.create(pos, command, delayTime, this._nextCommandIndex);
            this._commandPairs.push(insertCommand);
            this._nextCommandIndex++;
            this._insertNewCommand = true;
        }
    }
    public isCommandListEmpty(): boolean {

        if (this._usePriorityCommandQueue) {
            return this._priorityCommandQueue.isEmpty();
        } else {
            return this._commandPairs.length <= 0;
        }
    }
    public sortCommandList(): void {
        if (this._insertNewCommand) {
            this._insertNewCommand = false;
            this._commandPairs.sort(function (lhs: GridCommandPair, rhs: GridCommandPair) {
                if (lhs.delayTime == rhs.delayTime) {

                    if (lhs.command.getPriority() == rhs.command.getPriority()) {
                        if (lhs.index == rhs.index) {
                            return 0;
                        } else {
                            if (lhs.index < rhs.index) {
                                return -1;
                            } else {
                                return 1
                            }
                        }
                    } else {
                        if (lhs.command.getPriority() > rhs.command.getPriority()) {
                            return -1;
                        } else {
                            return 1;
                        }
                    }

                } else if (lhs.delayTime < rhs.delayTime) {
                    return - 1;
                } else {
                    return 1;
                }
            });
        }
    }
    public progressCommand(delta: number): void {

        if (this._usePriorityCommandQueue) {
            let commandPairs = this._priorityCommandQueue.getElements();
            for (let commandPair of commandPairs) {
                commandPair.delayTime -= delta;
            }
            while (!this._priorityCommandQueue.isEmpty()) {
                let commandPair = this._priorityCommandQueue.peek();
                if (commandPair.delayTime < 0) {
                    commandPair = this._priorityCommandQueue.pop();
                    this.excuteCommandPair(commandPair);
                } else {
                    break
                }
            }
        } else {
            this.sortCommandList();
            for (let commandPair of this._commandPairs) {
                commandPair.delayTime -= delta;
            }

            let index = 0;
            // cc.log('CommandPairs')
            // cc.log(this._commandPairs)
            while (index < this._commandPairs.length) {
                let commandPair = this._commandPairs[index];
                if (commandPair.delayTime > 0) {
                    this._commandPairs.splice(0, index);
                    break;
                }
                this.excuteCommandPair(commandPair);
                index += 1;
                if (index >= this._commandPairs.length) {
                    this._commandPairs.length = 0;
                }
            }
        }
    }
    public excuteCommandPair(commandPair: GridCommandPair): void {

        let entityTypes: EntityType[] = [
            EntityType.Parasite,
            EntityType.Item,
            EntityType.Rug,
            EntityType.Floor,
            EntityType.Popsicle,
            EntityType.Cell,
        ];

        if (commandPair.command.type == GridCommandType.CheckMatch) {
            entityTypes = [EntityType.Item];
        }

        for (let entityType of entityTypes) {
            let entity = this.getEntity(entityType, commandPair.targetPos);
            if (entity) {
                entity.excuteCommand(commandPair.command);
                if (commandPair.command.type != GridCommandType.TerminateClean) {
                    break;
                }
            }
        }
    }

