    public chargeFlyer(entity: EntityBase): void {

        let chargeItems: ItemBase[] = [];
        for (let key in this._itemsMap) {
            let item = this._itemsMap[key] as ItemBase;
            if (item.canWillCharge(entity.getCollectionID())) {
                chargeItems.push(item);
            }
        }

        if (chargeItems.length > 0) {
            chargeItems.sort(function (itemA, itemB): number {
                if (itemA.getChargePriority() == itemB.getChargePriority()) {
                    let posA = itemA.getLogicalPos();
                    let posB = itemB.getLogicalPos();
                    if (!posA.equals(posB)) {
                        if (GameMath.vec2Less(itemA.getLogicalPos(), itemB.getLogicalPos())) {
                            return -1;
                        } else {
                            return 1;
                        }
                    }
                    return 0;
                }
                return itemA.getChargePriority() - itemB.getChargePriority();
            });
            let targetItem = chargeItems[0] as ItemMower;
            if (targetItem) {
                targetItem.setWillChargeAmount(targetItem.getWillChargeAmount() + 1);
                let flyer = FlyerChargeEntity.create(this, entity.getCollectionID(), entity.getLogicalPos(), targetItem.getLogicalPos());
                this.setFlyerAtLogicalPos(flyer, entity.getLogicalPos());
            }
        }
    }
