    public lossCollectedSupply(itemID: GameCurrencyType, amount: number): void {
        this.gainCollectedSupply(itemID, -amount);
    }
    public gainCollectedSupplyWithLogicalPos(itemID: GameCurrencyType, amount: number, logicalPos: cc.Vec2): void {
        if (this.canGenerateTrainSupply()) {
            if (itemID != GameCurrencyType.NoneType && amount > 0) {
                this.gainCollectedSupply(itemID, amount);
                let flyer = FlyerCollectedSupply.create(this, itemID, amount, logicalPos, 0);
                this.setFlyerAtLogicalPos(flyer, logicalPos);
            }
        }
    }
    public gainCollectedSupplyWithEntity(entity: EntityBase) {
        if (this.canGenerateTrainSupply()) {
            let supplyMaps = entity.getProducedTrainSupplyMaps();
            for (let supplyID in supplyMaps) {
                let itemID = Number(supplyID) as GameCurrencyType;
                let amount = supplyMaps[supplyID];
                this.gainCollectedSupplyWithLogicalPos(itemID, amount, entity.getLogicalPos());
            }
        }
    }


