    public getWorldAreas(type: number, data: string): cc.Rect[] {
        let result: cc.Rect[] = [];

        switch (type) {
            case 1: {
                let posStrings = data.split('|');
                let positions: cc.Vec2[] = [];
                for (let posString of posStrings) {
                    let pos = GameMath.getVec2FromConfigString(posString);
                    if (pos) {
                        positions.push(pos);
                    }
                }
                result = this.getWorldAreasWithPositions(positions);
            }
                break;
            case 2: {
                let names = data.split('|')
                for (let name of names) {
                    let areas = this.getWorldAreasWithName(name);
                    for (let area of areas) {
                        result.push(area);
                    }
                }

            }
                break;
            default:
                break;
        }
        return result;
    }

    public getWorldAreasWithPositions(positions: cc.Vec2[]): cc.Rect[] {

        let result: cc.Rect[] = [];
        for (let pos of positions) {
            let area = this.getWorldAreaWithLogicalPos(pos);
            if (area) {
                result.push(area);
            }
        }
        return result;
    }

    public getWorldAreaWithLogicalPos(logicalPos: cc.Vec2): cc.Rect {
        let result: cc.Rect = null;

        let originOffset = cc.v2(-this._cellSize.width * 0.5, -this._cellSize.height * 0.5);
        let origin = this.getPixelPosFromLogicalPos(logicalPos).add(originOffset);
        let conner = this.getPixelPosFromLogicalPos(logicalPos.add(cc.v2(1, 1))).add(originOffset);
        let originWorld = this.node.convertToWorldSpaceAR(origin);
        let connerWorld = this.node.convertToWorldSpaceAR(conner);

        result = cc.rect(originWorld.x, originWorld.y,
            connerWorld.x - originWorld.x, connerWorld.y - originWorld.y);
        return result;
    }

    private getWorldAreasWithName(name: string): cc.Rect[] {
        let result: cc.Rect[] = [];

        if (name == 'popsicles') {
            for (let key in this._popsiclesMap) {
                let popsicle = this._popsiclesMap[key];
                let occupyArea = popsicle.getOccupyArea();
                for (let row = 0; row < occupyArea.height; row++) {
                    for (let column = 0; column < occupyArea.width; column++) {
                        let checkPos = cc.v2(occupyArea.x + column, occupyArea.y + row);
                        let area = this.getWorldAreaWithLogicalPos(checkPos);
                        if (area) {
                            result.push(area);
                        }
                    }
                }
            }
        } else if (name == 'unfilled_grass') {
            for (let key in this._cellsMap) {
                let cell = this._cellsMap[key];
                let pos = cell.getLogicalPos();
                if (!this.isGrassFloorAtLogicalPos(pos)) {
                    let area = this.getWorldAreaWithLogicalPos(pos);
                    if (area) {
                        result.push(area);
                    }
                }
            }
        } else if (name == 'doughnut') {
            for (let key in this._itemsMap) {
                let item = this._itemsMap[key] as ItemBase;
                if (item.getItemType() == ItemType.Doughnut) {
                    let pos = item.getLogicalPos();
                    let area = this.getWorldAreaWithLogicalPos(pos);
                    if (area) {
                        result.push(area);
                    }
                }
            }
        } else if (name == 'block') {
            for (let key in this._itemsMap) {
                let item = this._itemsMap[key] as ItemBase;
                if (item.getItemType() == ItemType.Block) {
                    let pos = item.getLogicalPos();
                    let area = this.getWorldAreaWithLogicalPos(pos);
                    if (area) {
                        result.push(area);
                    }
                }
            }
        } else if (name == 'iceCube') {
            for (let key in this._itemsMap) {
                let item = this._itemsMap[key] as ItemBase;
                let parasite = item.getTopParasite() as ParasiteBase;
                if (parasite && parasite.getParasiteType() == ParasiteType.IceCube) {
                    let pos = item.getLogicalPos();
                    let area = this.getWorldAreaWithLogicalPos(pos);
                    if (area) {
                        result.push(area);
                    }
                }
            }
        } else if (name == 'nut') {
            for (let key in this._itemsMap) {
                let item = this._itemsMap[key] as ItemBase;
                if (item.getItemType() == ItemType.Chestnut) {
                    let pos = item.getLogicalPos();
                    let area = this.getWorldAreaWithLogicalPos(pos);
                    if (area) {
                        result.push(area);
                    }
                }
            }
        } else if (name == 'paw') {
            for (let key in this._itemsMap) {
                let item = this._itemsMap[key] as ItemBase;
                if (item.getItemType() == ItemType.DogPaw) {
                    let pos = item.getLogicalPos();
                    let area = this.getWorldAreaWithLogicalPos(pos);
                    if (area) {
                        result.push(area);
                    }
                }
            }
        } else if (name == 'red') {
            for (let key in this._itemsMap) {
                let item = this._itemsMap[key] as ItemBase;
                if (item.getItemType() == ItemType.Normal && item.getEntityColor() == EntityColor.Red) {
                    let pos = item.getLogicalPos();
                    let area = this.getWorldAreaWithLogicalPos(pos);
                    if (area) {
                        result.push(area);
                    }
                }
            }
        } else if (name == 'green') {
            for (let key in this._itemsMap) {
                let item = this._itemsMap[key] as ItemBase;
                if (item.getItemType() == ItemType.Normal && item.getEntityColor() == EntityColor.Green) {
                    let pos = item.getLogicalPos();
                    let area = this.getWorldAreaWithLogicalPos(pos);
                    if (area) {
                        result.push(area);
                    }
                }
            }
        } else if (name == 'butterfly') {
            for (let key in this._itemsMap) {
                let item = this._itemsMap[key] as ItemBase;
                if (item.getItemType() == ItemType.EffectBomb) {
                    let bombItem = item as ItemBombBase;
                    if (bombItem.getBombType() == ItemBombType.Butterfly) {
                        let pos = item.getLogicalPos();
                        let area = this.getWorldAreaWithLogicalPos(pos);
                        if (area) {
                            result.push(area);
                        }
                    }
                }
            }
        } else if (name == 'giftbox') {
            for (let key in this._parasitesMap) {
                let parasite = this._parasitesMap[key] as ParasiteBase;
                if (parasite.getParasiteType() == ParasiteType.GiftBox) {
                    let pos = parasite.getLogicalPos();
                    let size = parasite.getLogicalSize();
                    for (let row = 0; row < size.height; row++) {
                        for (let column = 0; column < size.width; column++) {
                            let checkPos = pos.add(cc.v2(column, row));
                            let area = this.getWorldAreaWithLogicalPos(checkPos);
                            if (area) {
                                result.push(area);
                            }
                        }
                    }
                }
            }
        }
        return result;
    }
