    public getPuppyLeftSteps(): number {
        let pathCount: number = 0;
        for (let key in this._floorsMap) {
            let floor: FloorBase = this._floorsMap[key];
            if (floor.getFloorType() == FloorType.Road) {
                pathCount += 1
            }
        }

        let movedSteps = this.getPowerAmount(PowerID.PuppyMovedStep);
        return pathCount - movedSteps - 1;
    }

