    private _lastTouchLogicalPos: cc.Vec2 = cc.Vec2.ZERO;
    private onTouchStart(evt: cc.Event.EventTouch) {
        let pixelPos = this.node.convertToNodeSpaceAR(evt.getLocation());
        let logicalPos = this.getLogicalPosFromPixelPos(cc.v2(pixelPos.x, pixelPos.y));
        this._stateMachine.onTapBegan(logicalPos);
    }
    private onTouchMove(evt: cc.Event.EventTouch) {
        let pixelPos = this.node.convertToNodeSpaceAR(evt.getLocation());
        let logicalPos = this.getLogicalPosFromPixelPos(cc.v2(pixelPos.x, pixelPos.y));
        if (logicalPos.equals(this._lastTouchLogicalPos)) {
            return;
        }
        this._lastTouchLogicalPos = logicalPos;
        this._stateMachine.onTapMoved(logicalPos);
    }
    private onTouchEnd(evt: cc.Event.EventTouch) {
        let pixelPos = this.node.convertToNodeSpaceAR(evt.getLocation());
        let logicalPos = this.getLogicalPosFromPixelPos(cc.v2(pixelPos.x, pixelPos.y));
        this._stateMachine.onTapEnded(logicalPos);
    }
    private onEvent(event: EntityEventBase) {
        this._stateMachine.onEvent(event);
    }


