const {ccclass, property} = cc._decorator;

@ccclass
export default class ItemBombRainbow extends ItemBombBase {

    public getCollectionID():CollectionID {
        return CollectionID.ItemBombRainbow;
    }
    public getEntityID() : EntityID {
        return EntityID.ItemBombRainbow;
    }
    public getProducedTrainSupplyMaps() : Object {
        let result = {};
        result[GameCurrencyType.GoldCoin] = 1;
        return result;
    }
    public getScore():number{
        return 1000;
    }
    public selfDestory() : void {

        this.getGrid().gainScoreWithEntity(this);
        if (this.getGrid().canGenerateTrainSupply()) {
            this.getGrid().gainStepWithItemID(GameCurrencyType.ExtraStep1, true, this.getLogicalPos());    
        }
        let entity = this.getGrid().getEntity(this.getEntityType(), this.getLogicalPos());
        if (entity == this) {
            this.removeFromGridAndAddToEffect();    
        }
        this.getGrid().addObstructs(this);
        this.progressDeathWish(this._cleanOption);

        let dispear = cc.spawn(cc.scaleTo(0.15, 0),
                               cc.fadeOut(0.15));
        let action = cc.sequence(dispear, cc.callFunc(function() {
            this.getGrid().removeObstructs(this);
            this.destoryWithRecycle(true);
           }, this));
        this.runAction(action);
    }

    protected _canMatch() : boolean {
        return false;
    }
    protected _canHitFloor() : boolean {
        return true;
    }
    protected _canHitNeighbour() : boolean {
        return true;
    }
    protected _canLayoutGrass() : boolean {
        return true;
    }
    protected _canChangeColor() : boolean {
        return false;
    }
    protected _canCleanBySuicide() : boolean {
        return true;
    }
    protected _canCleanByMatch() : boolean {
        return false;
    }
    protected _canCleanByBomb(cleanColor:EntityColor) : boolean  {
        return false;
    }
    protected _canCleanByTerminate(cleanColor:EntityColor) : boolean {
        return true;
    }
    protected _canCleanByActivate() : boolean {
        return true;
    }

    private _cleanOption:EntityCleanOption = EntityCleanOption.create(EntityCleanType.None, 
                                                                      cc.Vec2.ZERO, 
                                                                      EntityColor.None,
                                                                      ItemBombType.None,
                                                                      false, 
                                                                      false, 
                                                                      false);  
    public getCleanOption() : EntityCleanOption {
        return this._cleanOption;
    }
    public setCleanOption(cleanOption:EntityCleanOption) : void {
        this._cleanOption = cleanOption;
    }

    protected _canMatchWithNeighbourSwap(swapPos:cc.Vec2) : boolean {
        if (this._canBombCombineWithNeighbour(swapPos)) {
            return true;
        }else{
            let neighbourItem = this.getGrid().getItemAtLogicalPos(swapPos);
            if (neighbourItem &&
                neighbourItem.canMatch() &&
                neighbourItem.getEntityColor() >= EntityColor.Begin &&
                neighbourItem.getEntityColor() <= EntityColor.End){
                return true;
            }
        }
        
        return false;
    }
    protected _onMatchWithNeighbourSwap(swapPos:cc.Vec2) : void {
   
        let matchColor = EntityColor.None;
        let bombType = ItemBombType.None;
        let layoutGrass = this.getShouldLayoutGrass() || this.getGrid().isGrassFloorAtLogicalPos(swapPos);
        let neighbourItem = this.getGrid().getItemAtLogicalPos(swapPos);
        if (neighbourItem) {
            matchColor = neighbourItem.getEntityColor();
        }
        
        if (this._canBombCombineWithNeighbour(swapPos)){
            let bombB = neighbourItem as ItemBombBase;
            bombType = bombB.getBombType();

            if (bombB.getBombType() == ItemBombType.Rainbow) {
                
                let grid = this.getGrid();
                let nuclear = FlyerNuclear.create(this.getGrid(), layoutGrass, this.getLogicalPos());
                grid.setFlyerAtLogicalPos(nuclear, this.getLogicalPos());
            
                this.setIsDud(true);
                this._cleanOption = EntityCleanOption.create(EntityCleanType.Match, 
                                                            this.getLogicalPos(), 
                                                            EntityColor.None, 
                                                            ItemBombType.None, 
                                                            true, 
                                                            true, 
                                                            layoutGrass);

                this.selfDestory();
                let rainbowB = bombB as ItemBombRainbow;
                if (rainbowB) {
                    rainbowB.setIsDud(true);
                    rainbowB.setCleanOption(EntityCleanOption.create(EntityCleanType.Match, 
                                                                    this.getLogicalPos(), 
                                                                    EntityColor.None, 
                                                                    ItemBombType.None, 
                                                                    true, 
                                                                    true, 
                                                                    layoutGrass));
                    rainbowB.selfDestory();
                }
            }else{
                this._launchLaser(matchColor, bombType, layoutGrass);
            }
        }else{
            this._launchLaser(matchColor, bombType, layoutGrass);
        }
    }
    protected _canBombCombineWithNeighbour(swapPos:cc.Vec2) : boolean {
        let neighbourItem = this.getGrid().getItemAtLogicalPos(swapPos);
    
        if (this.getIsDud() == false &&
            this.getTopParasite() == null &&
            neighbourItem &&
            neighbourItem.getTopParasite() == null &&
            neighbourItem.getItemType() == ItemType.EffectBomb) {
            return true;
        }
        return false;
    }
    protected _onClean(cleanOption:EntityCleanOption) : void {
        if (cleanOption.layoutGrass) {
            this.setIsGrassBomb(true);
        }
        if (!this.getIsDud()) {
            this.setIsDud(true);
            let colors:EntityColor[] = [];
            let itemMap = this.getGrid().getItemsMap();
            for (let key in itemMap) {
                let item = itemMap[key] as ItemBase;
                if (item.canMatch()){
                    let entityColor = item.getEntityColor();
                    if (entityColor != EntityColor.None) {
                        GameMath.insertAsSet(colors, entityColor);
                    }
                }
            }
            if (colors.length > 0) {
                let index = GameMath.randomInt(0, colors.length - 1);
                let color = colors[index];
                this._launchLaser(color, ItemBombType.None, this.getShouldLayoutGrass());  
            }
        }else{
            this.selfDestory();
        }
    }
    protected _launchLaser(color:EntityColor, type:ItemBombType, layoutGrass:boolean) : void {
        this.setIsDud(true);  
        
        this._cleanOption = EntityCleanOption.create(EntityCleanType.Match, 
                                                    this.getLogicalPos(), 
                                                    color, 
                                                    ItemBombType.None,
                                                    true, 
                                                    true, 
                                                    layoutGrass);
        this.removeFromGridAndAddToEffect();
        let grid = this.getGrid();
        let laser = FlyerLaser.create(grid, this, this.getLogicalPos(), color, type, layoutGrass);
        grid.setFlyerAtLogicalPos(laser, this.getLogicalPos());
    }

    public static create(grid:GridBase) : ItemBombRainbow {

        let className = 'ItemBombRainbow';
        let prefabFileName = 'match_scene/prefab/entity/item/ItemBombRainbow';
        let pool = EntityPrefabNodePool.getInstance();
        let component = pool.getPrefabNodeWithFileNameAndClassName(prefabFileName, className) as ItemBombRainbow;
        if (component) {
            component.init(grid); 
            return component;       
        }
        return null;
    }

    public clone():ItemBombRainbow {
        let item = ItemBombRainbow.create(this.getGrid());
        return item;
    }

    protected init(grid:GridBase) : void {
        super.initBomb(grid, ItemBombType.Rainbow);
        this.setEntityColor(EntityColor.None);

        this._cleanOption = EntityCleanOption.create(EntityCleanType.None, 
                                                    cc.Vec2.ZERO, 
                                                    EntityColor.None,
                                                    ItemBombType.None,
                                                    false, 
                                                    false, 
                                                    false);
    }
    
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {

    }

    // update (dt) {}
}
