import GridCommand from "./GridCommand";

export default class GridCommandPair {

    public static create(TargetPos:cc.Vec2, Command:GridCommand, DelayTime:number, Index:number) : GridCommandPair {
        return new GridCommandPair(TargetPos, Command, DelayTime, Index);
    }
    public targetPos:cc.Vec2 = cc.Vec2.ZERO;
    public delayTime:number = 0;
    public command:GridCommand = null;
    public index:number = 0;
    constructor(TargetPos:cc.Vec2, Command:GridCommand, DelayTime:number, Index:number) {
        this.targetPos = TargetPos;
        this.command = Command;
        this.delayTime = DelayTime;
        this.index = Index;
    }
}
